import React from 'react';
import { string } from 'prop-types';
import { SvgIcon } from '@material-ui/core';
import { mdiAccountPlus, mdiAccountEdit } from '@mdi/js';
import moment from 'moment';

import './UserDatesBox.local.scss';

export const UserDatesBox = ({ created, updated }) => (
  <div className="dates-box">
    {created && <DateCreated date={created} />}
    {updated && <DateUpdated date={updated} />}
  </div>
);

UserDatesBox.propTypes = { created: string, updated: string };

export const DateCreated = ({ date }) => (
  <div className="date-entry">
    <SvgIcon fontSize="small">
      <path d={mdiAccountPlus} />
    </SvgIcon>
    <span>Created on:</span>
    <span className="date-text">{parseDate(date)}</span>
  </div>
);

DateCreated.propTypes = { date: string };

export const DateUpdated = ({ date }) => (
  <div className="date-entry">
    <SvgIcon fontSize="small">
      <path d={mdiAccountEdit} />
    </SvgIcon>
    <span>Updated on:</span>
    <span className="date-text">{parseDate(date)}</span>
  </div>
);

DateUpdated.propTypes = { date: string };

function parseDate(date) {
  return moment(date).format('MMM DD YYYY');
}
