import { getOutputText } from './getOutputText';

export const copyToClipboard = (output, invocation) => {
  const text = getOutputText(output, invocation);
  navigator.permissions.query({ name: 'clipboard-write' }).then(result => {
    if (result.state === 'granted' || result.state === 'prompt') {
      /* write to the clipboard */
      navigator.clipboard.writeText(text).then(
        () => {
          /* successful */
          console.log('[copyTextToClipboard] Copied:', text); // eslint-disable-line
        },
        () => {
          /* write failed */
          console.warn('[copyTextToClipboard] --- unable to write to clipboard!'); // eslint-disable-line
        }
      );
    } else {
      legacyMethod(text);
    }
  }, legacyMethod.bind(this, text));
};

function legacyMethod(text) {
  const textArea = document.createElement('textarea');

  textArea.value = text;
  document.body.appendChild(textArea);
  textArea.classList.add('copyToClipboardInput');
  textArea.focus();
  textArea.select();
  document.execCommand('copy');
  document.body.removeChild(textArea);
  console.log('[copyTextToClipboard-legacy] Copied:', text); // eslint-disable-line
}
