import React, { useState, useRef } from 'react';
import { connect } from 'react-redux';
import { Formik, Form } from 'formik';

import { postNamespace } from 'shared/api/namespace';
import { Switch } from 'shared/components';
import { Button } from 'shared/components/controls';
import { NamespaceSelect } from 'shared/components/form/editors/Custom';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Result } from 'shared/components/overlays/Result/Result';
import { SubmitButton } from 'shared/components/form/SubmitButton';
import { Paper, Section, Row, Column } from 'shared/components/layout';

import { Text } from 'shared/components/form/editors/Generic';

import SvgIcon from '@material-ui/core/SvgIcon';
import FolderIcon from '@material-ui/icons/Folder';

import { fetchEntity } from '../../actions';

export const ConnectedNewNamespace = connect(null, {
  request: fetchEntity
})(NewNamespace);

function NewNamespace({ namespace, request }) {
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
      setResult('');
    }
  };
  const [result, setResult] = useState('');
  const isDisabled = namespace?.meta?.appearance === 'Greyed';
  const formRef = useRef();

  return (
    <>
      <Button
        disabled={isDisabled}
        classes={{ root: 'entity-button' }}
        onClick={() => {
          setOpen(true);
        }}
      >
        New Namespace
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            minWidth: '600px',
            minHeight: '600px'
          }}
        >
          <ModalHeader
            title="New Namespace"
            icon={
              <SvgIcon>
                <FolderIcon />
              </SvgIcon>
            }
            formikRef={formRef}
          />
          <Switch value={result}>
            <Switch.Case value="success">
              <Result type="success">
                <Result.Text>Namespace was added successfully!</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                      setResult('');
                    }}
                  >
                    Done
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="fail">
              <Result type="fail">
                <Result.Text>An error occurred and the namespace could not be added.</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                      setResult('');
                    }}
                  >
                    Close
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="">
              <Formik
                initialValues={{
                  name: '',
                  namespace: namespace ? namespace.name : '',
                  description: ''
                }}
                onSubmit={values => {
                  handleSubmitNew(setResult, request, values);
                }}
                innerRef={formRef}
              >
                {({ values }) => {
                  return (
                    <>
                      <Paper.Content>
                        <Form>
                          <Section>
                            <Section.Header />
                            <Row>
                              <Text name="name" label="Name" value={values.name} required />
                            </Row>
                            <Row>
                              <Column xs={12}>
                                <NamespaceSelect
                                  name="namespace"
                                  label="Parent namespace"
                                  value={values.namespace}
                                />
                              </Column>
                            </Row>
                            <Row>
                              <Text
                                name="description"
                                label="Description"
                                value={values.description}
                                multiline
                                required
                              />
                            </Row>
                          </Section>
                        </Form>
                      </Paper.Content>
                      <Paper.Footer>
                        <Paper.Footer.Right>
                          <SubmitButton variant="contained">Save</SubmitButton>
                        </Paper.Footer.Right>
                      </Paper.Footer>
                    </>
                  );
                }}
              </Formik>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
}

async function handleSubmitNew(setResult, request, values) {
  const name = values.namespace ? `${values.namespace}.${values.name}` : values.name;
  const body = {
    name,
    description: values.description
  };

  postNamespace(body)
    .then(() => {
      setResult('success');
      request('namespace');
    })
    .catch(error => {
      console.log('[edit namespace] patch namespace error: ', error); // eslint-disable-line
      setResult('fail');
    });
}
