import React, { useState } from 'react';
import { connect } from 'react-redux';

import PostAddIcon from '@material-ui/icons/PostAdd';

import { Modal, ModalHeader, Result } from 'shared/components/overlays';
import { Switch } from 'shared/components';
import { Paper, Center } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';
import { Button } from 'shared/components/controls';
import { LimitlessTooltip } from 'shared/components/tooltip/Tooltip';

import { postFile } from 'shared/api/accounts';

import { loadAllAccountsThunk } from '../../store/thunks';
import { downloadTemplate } from './downloadTemplate';

import './ImportCSV.local.scss';

export const ImportCSV = ({ disabled }) => {
  const [open, setOpen] = useState(false);

  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };

  return (
    <>
      <Button className="action-btn" onClick={modalActions.open} disabled={disabled}>
        Import from CSV
      </Button>
      <Modal open={open} actions={modalActions}>
        <ImportCSVContainer />
      </Modal>
    </>
  );
};

export const ImportCSVFile = ({ reloadAccounts }) => {
  const [selectedFile, setSelectedFile] = useState(null);
  const [result, setResult] = useState('');
  const fileName = selectedFile?.name || 'No file chosen';

  const onChangeHandler = event => {
    setSelectedFile(event.target.files[0]);
  };

  const uploadFile = () => {
    if (!selectedFile) {
      setResult('error');

      return;
    }
    setResult('loading');
    const data = new FormData();
    data.append('csv', selectedFile);

    postFile(data)
      .then(() => {
        setResult('');
        reloadAccounts();
      })
      .catch(() => {
        // at the current BE version, even though the call is a success
        // a "false" error is returned, so the reload account call should be made
        setResult('');
        reloadAccounts();
      });
  };

  return (
    <Paper
      style={{
        width: '50vw',
        height: '50vh'
      }}
    >
      <ModalHeader icon={<PostAddIcon />} title="Import CSV" />
      <Switch value={result}>
        <Switch.Case value="">
          <div className="container">
            <label htmlFor="file-upload" className="csv-file-upload">
              Choose file
              <input type="file" id="file-upload" name="csv" onChange={onChangeHandler} />
            </label>
            <LimitlessTooltip placement="bottom" title={fileName}>
              <span className="file-name">{fileName}</span>
            </LimitlessTooltip>
          </div>
          <Paper.Footer>
            <Paper.Footer.Left>
              <Button
                onClick={() => {
                  downloadTemplate();
                }}
              >
                Download template
              </Button>
            </Paper.Footer.Left>
            <Paper.Footer.Right>
              <Button onClick={uploadFile}>Upload</Button>
            </Paper.Footer.Right>
          </Paper.Footer>
        </Switch.Case>
        <Switch.Case value="loading">
          <Center>
            <Loader>Uploading file...</Loader>
          </Center>
        </Switch.Case>
        <Switch.Case value="error">
          <Result type="fail">
            <Result.Text>You forgot to select a file</Result.Text>
          </Result>
          <Paper.Footer>
            <Paper.Footer.Left>
              <Button onClick={() => setResult('')}>Back</Button>
            </Paper.Footer.Left>
          </Paper.Footer>
        </Switch.Case>
      </Switch>
    </Paper>
  );
};

export const ImportCSVContainer = connect(
  () => ({}),
  dispatch => ({
    reloadAccounts: () => dispatch(loadAllAccountsThunk())
  })
)(ImportCSVFile);
