import React from 'react';
import moment from 'moment';
import { connect } from 'react-redux';
import { object, func } from 'prop-types';

import { Loader } from 'shared/components/loaders';
import { Overflow, Paper } from 'shared/components/layout';
import { Center, WorkloadGraph } from 'shared/components';
import { actions } from 'shared/store/actions';
import { jobsSelector, globalFiltersSelector } from 'shared/store/state';
import { getWorkloadData } from 'shared/lib/workload';
import { JobResults } from 'shared/components/JobResults';

import { changeStateThunk } from '../../store/thunks';
import { NavigationTab } from '../../components/NavigationTab/NavigationTab';

import './Workload.local.scss';

export class Workload extends React.PureComponent {
  componentDidMount() {
    this.getJobsData();
  }

  componentDidUpdate(prevProps) {
    if (
      this.props.storeState.path !== prevProps.storeState.path ||
      this.props.globalFilters !== prevProps.globalFilters
    ) {
      this.getJobsData();
    }
  }

  render() {
    const {
      jobs,
      storeState: { path, level }
    } = this.props;
    const pageLevel = path.split('--').filter(e => e).length;
    const [protocol] = path.split('--');
    return (
      <>
        <Paper.Header>
          {pageLevel === 3 && (
            <NavigationTab active={level} onClick={this.changeTab} protocol={protocol} />
          )}
        </Paper.Header>
        <Paper.Content>
          {jobs.get('fetching') ? (
            <Center>
              <Loader>Loading jobs</Loader>
            </Center>
          ) : (
            <>
              {jobs.get('total_count') > 0 ? (
                <>
                  <div className="container">
                    <div className="graph-container">
                      <WorkloadGraph
                        data={getWorkloadData(this.props.jobs.get('content').toJS(), {
                          currentDate: moment.unix(
                            this.props.globalFilters.get('startDate'),
                            'YYYY-MM-DD'
                          ),
                          endDate: moment.unix(
                            this.props.globalFilters.get('endDate'),
                            'YYYY-MM-DD'
                          )
                        })}
                        isSingleDay={
                          moment
                            .unix(this.props.globalFilters.get('startDate'), 'YYYY-MM-DD')
                            .format('YYYY-MM-DD') ===
                          moment
                            .unix(this.props.globalFilters.get('endDate'), 'YYYY-MM-DD')
                            .format('YYYY-MM-DD')
                        }
                        setSingleDay={this.props.setGlobalFilters}
                      />
                    </div>
                    <div className="jobs-container">
                      <Overflow>
                        <JobResults jobs={jobs.get('content')} />
                      </Overflow>
                    </div>
                  </div>
                </>
              ) : (
                <Center>Job list is empty</Center>
              )}
            </>
          )}
        </Paper.Content>
      </>
    );
  }

  changeTab = newTab => {
    this.props.changeState({ level: newTab });
  };

  getJobsData() {
    const {
      storeState: { path }
    } = this.props;
    const levelFilters = path.split('--').filter(e => e);

    const days = moment
      .unix(this.props.globalFilters.get('endDate'), 'YYYY-MM-DD')
      .diff(moment.unix(this.props.globalFilters.get('startDate'), 'YYYY-MM-DD'), 'days');
    this.props.getJobs({
      date_invoke: moment.unix(this.props.globalFilters.get('startDate')).format('YYYY-MM-DD'),
      period: days + 1,
      page_size: 2147483645,
      protocol_filter: levelFilters[0],
      shell_host_address_filter: levelFilters[1],
      database_name_filter: levelFilters[2]
    });
  }
}

Workload.propTypes = {
  storeState: object,
  globalFilters: object,
  jobs: object,
  getJobs: func,
  changeState: func
};

export const ConnectedWorkload = connect(
  state => ({
    storeState: state.explorer.state,
    globalFilters: globalFiltersSelector(state),
    jobs: jobsSelector(state)
  }),
  dispatch => ({
    getJobs: data => dispatch(actions.getJobs.dispatchLoading(data)),
    changeState: newState => dispatch(changeStateThunk(newState)),
    setGlobalFilters: data => dispatch(actions.setGlobalFilters.dispatchLoading(data))
  })
)(Workload);
