import React, { useState } from 'react';
import { useFormikContext } from 'formik';

import { Button } from 'shared/components/controls';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Paper } from 'shared/components/layout';
import { Result } from 'shared/components/overlays/Result/Result';
import WarningIcon from '@material-ui/icons/Warning';

export const SubmitButton = ({ children = 'Submit', confirmation, ...props }) => {
  const { values, submitForm, isSubmitting, isValidating, dirty } = useFormikContext();

  const [open, setOpen] = useState(false);
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };

  return (
    <>
      <Button
        type="submit"
        disabled={!dirty || isSubmitting || isValidating}
        onClick={() => {
          console.log('submitting', values); // eslint-disable-line
          if (confirmation) {
            setOpen(true);
          } else {
            submitForm();
          }
        }}
        {...props}
      >
        {children}
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            minWidth: '350px',
            maxHeight: '368px',
            maxWidth: '65vw'
          }}
        >
          <ModalHeader title="Warning" icon={<WarningIcon />} />
          <Paper.Content scrollable={false}>
            <Result type="fail">
              <BodyText>Are you sure you want to save?</BodyText>
            </Result>
          </Paper.Content>
          <Paper.Footer>
            <Paper.Footer.Left>
              <Button
                onClick={() => {
                  setOpen(false);
                }}
              >
                No
              </Button>
            </Paper.Footer.Left>
            <Paper.Footer.Right>
              <Button
                onClick={() => {
                  setOpen(false);
                  submitForm();
                }}
              >
                Yes
              </Button>
            </Paper.Footer.Right>
          </Paper.Footer>
        </Paper>
      </Modal>
    </>
  );
};

function BodyText({ children }) {
  return (
    <h2
      style={{
        fontFamily: 'Open Sans',
        fontStyle: 'normal',
        fontWeight: 'bold',
        fontSize: '14px',
        lineHeight: '20px',
        letterSpacing: '0.25px',
        marginTop: '24px'
      }}
    >
      {children}
    </h2>
  );
}
