import React from 'react';
import { connect } from 'react-redux';

import { Row, Column } from 'shared/components/layout';
import { ServersTile } from '../../components/ServersTile/ServersTile';
import { DatabasesTile } from '../../components/DatabasesTile/DatabasesTile';
import { UsedOptions } from '../../components/UsedOptions/UsedOptions';

function ServerView({ selected }) {
  return (
    <>
      <Row>
        <Column xs={6}>
          <ServersTile servers={selected} isSingle />
        </Column>
        <Column xs={6}>
          <DatabasesTile servers={selected} />
        </Column>
      </Row>
      <UsedOptions servers={selected} />
    </>
  );
}

export const ConnectedServerView = connect(state => {
  return {
    selected: state.executiveDashboard.selectedServers
  };
})(ServerView);
