import React from 'react';
import * as PropTypes from 'prop-types';

import { DayFilterEditor } from '../day-filter-editor/day-filter-editor';
import { UserFilterEditor } from '../user-filter-editor/user-filter-editor';
import { StatusFilterEditor } from '../status-filter-editor/status-filter-editor';

export const FilterLoader = ({ type, setFilterValue, ...others }) => {
  const FILTERS_MAP = {
    status: StatusFilterEditor,
    day: DayFilterEditor,
    user: UserFilterEditor,
  };

  const FilterComponent = FILTERS_MAP[type];

  return <FilterComponent setFilterValue={setFilterValue} {...others} />;
};

FilterLoader.propTypes = {
  type: PropTypes.string.isRequired,
  setFilterValue: PropTypes.func.isRequired,
};
