import React, { useEffect, useState } from 'react';
import { Header } from 'core/components/Header';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Select from '@material-ui/core/Select';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import FilterCenterFocusIcon from '@material-ui/icons/FilterCenterFocus';
import FolderIcon from '@material-ui/icons/Folder';
import AssignmentTurnedInIcon from '@material-ui/icons/AssignmentTurnedIn';
import CircularProgress from '@material-ui/core/CircularProgress';

import './root.local.scss';
import { MenuItem } from '@material-ui/core';
import { fetchEntity, selectEntity, setFilterType, setFilterSearch, openModal } from './actions';
import { getEntities, getBreadcrumbs, ENTITY_TYPES, getSelectedEntity } from './selectors';
import Entity from './components/Entity';
import SelectedEntity from './containers/SelectedEntity';
import EventNoteIcon from '@material-ui/icons/EventNote';
import VpnKeyIcon from '@material-ui/icons/VpnKey';
import SlowMotionVideoIcon from '@material-ui/icons/SlowMotionVideo';
import DeveloperModeIcon from '@material-ui/icons/DeveloperMode';
import WarningIcon from '@material-ui/icons/Warning';
import AssignmentIcon from '@material-ui/icons/Assignment';
import CachedIcon from '@material-ui/icons/Cached';
import { SimpleSearchInput } from 'shared/components/simple-search-input/simple-search-input';
import debounce from 'lodash/debounce';

/* eslint-disable */


const ENTITY_ICONS = {
  root: <FolderIcon />,
  namespace: <FolderIcon />,
  constant: <FilterCenterFocusIcon />,
  form: <AssignmentTurnedInIcon />,
  schedule: <EventNoteIcon />,
  account: <VpnKeyIcon />,
  process: <SlowMotionVideoIcon />,
  type: <DeveloperModeIcon />,
  exception: <WarningIcon />,
  prototype: <AssignmentIcon />,
  CachedIcon:<CachedIcon />
};

let searchWithDebounce = () => {};

function RootModule({
  fetchEntity,
  selectEntity,
  columns,
  breadcrumbs,
  filters,
  setFilterType,
  setFilterSearch,
  selectedEntity,
  entitiesStatus,
  job
}) {
  const [searchInput, setSearchInput] = useState('');
  useEffect(() => {
    const entitiesKey = Object.keys(entitiesStatus);
    for (const key of entitiesKey) {
      if (entitiesStatus[key] === 'not-fetched' || entitiesStatus[key] === 'failed') {
        fetchEntity(key);
      }
    }

    searchWithDebounce = debounce(value => {
      setFilterSearch(value);
    }, 700);
    if (filters.search) {
      setSearchInput(filters.search);
    }
  }, []);

  function renderBreadcrumb(breadcrumb) {
    return (
      <span key={breadcrumb}>
        <span className="breadcrumb">{breadcrumb}</span> /{' '}
      </span>
    );
  }

  function renderColumn(columns, colNum) {
    return (
      <div key={colNum}>
        {columns.rows.map(entity => (
          <Entity
            key={entity.name}
            entity={entity}
            selectEntity={selectEntity}
            columns={columns}
            entityIcons={ENTITY_ICONS}
          />
        ))}
      </div>
    );
  }

  function handleSearchChange(e) {
    const val = e.target.value;
    searchWithDebounce(val);
    setSearchInput(val);
  }

  function loadingData() {
    const entitiesKey = Object.keys(entitiesStatus);
    for (const key of entitiesKey) {
      if (entitiesStatus[key] === 'fetching') {
        return (
          <div className="loading">
            <CircularProgress size={15} />
            <span>Loading {key} entities...</span>
          </div>
        );
      }
    }

    return null;
  }


  function renderColumns() {
    if (Array.isArray(columns) && columns[0]?.rows?.length) {
      return columns.map(renderColumn);
    }

    if (Object.values(entitiesStatus).find(e => e === 'fetching')) {
      return null;
    } else {
      return 'No Entities Found.';
    }
  }

  function clearCach(){
     window.location.reload(false);
     sessionStorage.removeItem("persist:root");
    
  }

  return (
    <>
      <Header title="System / Entities" />
      <Container maxWidth={false} classes={{ root: 'define-height-container' }}>
        <div className="entities-breadcrumb">{['root', ...breadcrumbs].map(renderBreadcrumb)}</div> 
        <br />
        {sessionStorage.getItem("persist:root") && <div><CachedIcon style={{ color: 'green',cursor: 'pointer' }}  onClick={e => clearCach()} /></div> }
        <div className="entities-filters">
          <Select value={filters.type} onChange={ev => setFilterType(ev.target.value)}>
            {[{ type: 'all', title: 'ALL' }, ...ENTITY_TYPES].map(type => (
              <MenuItem key={type.type} value={type.type}>
                {type.title}
              </MenuItem>
            ))}
          </Select>
          <div className="entities-search-wrap">
            <SimpleSearchInput value={searchInput} onChange={handleSearchChange} />
          </div>
        </div>
        {loadingData()}
        <Grid container spacing={3} classes={{ root: 'define-height-grid-root' }}>
          <Grid item xs={9} classes={{ root: 'define-height' }}>
            <div className="columns">{renderColumns()}</div>
          </Grid>
          <Grid item xs={3}>
            <SelectedEntity selectedEntity={selectedEntity} entityIcons={ENTITY_ICONS} />
          </Grid>
        </Grid>
      </Container>
    </>
  );
}

RootModule.propTypes = {
  fetchEntity: PropTypes.func,
  selectEntity: PropTypes.func,
  setFilterType: PropTypes.func,
  setFilterSearch: PropTypes.func,
  columns: PropTypes.array,
  breadcrumbs: PropTypes.array,
  filters: PropTypes.object,
  selectedEntity: PropTypes.object
};

export default connect(
  state => ({
    columns: getEntities(state),
    breadcrumbs: getBreadcrumbs(state),
    filters: state.entities.filters,
    selectedEntity: getSelectedEntity(state),
    entitiesStatus: state.entities.fetch_status
  }),
  { fetchEntity, selectEntity, setFilterType, setFilterSearch, openModal }
)(RootModule);
