import React, { useState } from 'react';
import * as PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import moment from 'moment';
import { mdiCalendarBlank } from '@mdi/js';
import Icon from '@mdi/react';
import SvgIcon from '@material-ui/core/SvgIcon';
import { SingleDatePicker } from 'shared/components';

export const DayFilterEditor = ({ value: defaultValue, setFilterValue }) => {
  const [value, setValue] = useState(defaultValue ? moment(defaultValue, 'D MMM YYYY') : undefined);
  const onChange = (date) => {
    setValue(date);
    setFilterValue({ value: date.format('D MMM YYYY'), type: DayFilterEditor.TypeDescriptor.value });
  };

  return (
    <SingleDatePicker
      date={value}
      onDateChange={onChange}
    />
  );
};

DayFilterEditor.propTypes = {
  value: PropTypes.string,
  setFilterValue: PropTypes.func.isRequired,
};

DayFilterEditor.defaultProps = {
  value: '',
};

DayFilterEditor.TypeDescriptor = {
  icon: () => <SvgIcon><Icon path={mdiCalendarBlank} /></SvgIcon>,
  name: () => <FormattedMessage id="dashboard.day" />,
  value: 'day',
};
