import React from 'react';
import * as PropTypes from 'prop-types';
import moment from 'moment';
import { FormattedMessage } from 'react-intl';

import './custom-tooltip.local.scss';

export class CustomTooltipComponent extends React.Component {
  shouldComponentUpdate(nextProps) {
    if (nextProps.label !== this.props.label) {
      return true;
    }

    return false;
  }

  render() {
    if (!this.props.active) return null;

    const label = this.props.isSingleDay
      ? this.props.payload[0].payload.name
      : moment(this.props.payload[0].payload.name).format('MMM DD YYYY');

    return (
      <div className={this.props.className} position={{ x: '100%', y: '-100%' }}>
        {this.props.payload && (
          <>
            <div className="date">{label}</div>
            <div className="info-container">
              <div className="info-row">
                <div className="label">
                  <FormattedMessage id="dashboard.succeeded" />
                </div>
                <div className="value success">{this.props.payload[0].payload.SUCCEEDED}</div>
              </div>
              <div className="info-row">
                <div className="label">
                  <FormattedMessage id="dashboard.aborted" />
                </div>
                <div className="value error">{this.props.payload[0].payload.FAILED}</div>
              </div>
              <div className="info-row">
                <div className="label">
                  <FormattedMessage id="dashboard.failed" />
                </div>
                <div className="value error">{this.props.payload[0].payload.ABORTED}</div>
              </div>
            </div>
          </>
        )}
      </div>
    );
  }
}

export default CustomTooltipComponent;

CustomTooltipComponent.propTypes = {
  className: PropTypes.string,
  payload: PropTypes.array,
  active: PropTypes.bool,
  label: PropTypes.string
};

CustomTooltipComponent.defaultProps = {
  className: 'custom-tooltip',
  payload: [],
  active: false,
  label: ''
};
