import { put, takeEvery } from '@redux-saga/core/effects';

import { actions } from 'shared/store/actions';
import { RestClient } from 'shared/lib/rest';
import { CONSTANTS } from 'config';

function* connections() {
  try {
    const response = yield RestClient.get(CONSTANTS.BASE.ENDPOINTS.ACCOUNTS, {
      page_size: 2147483645
    });

    yield put(actions.getConnections.dispatchSuccess(response.data));
  } catch (errors) {
    yield put(actions.getConnections.dispatchFail(errors));
  }
}

export function* JobsConnectionsEffect() {
  yield takeEvery(actions.getConnections.getLoadingAction(), connections);
}
