import React, { useState, useCallback, useRef } from 'react';
import { useDispatch } from 'react-redux';

import { Button } from 'shared/components/controls';
import { Modal, ModalHeader, Result } from 'shared/components/overlays';
import { Paper } from 'shared/components/layout';
import { Switch } from 'shared/components';

import SvgIcon from '@material-ui/core/SvgIcon';

import { mdiImageFilterCenterFocus } from '@mdi/js';

import { ProcessEditor } from './ProcessEditor';

import { fetchEntity } from '../../actions';

export const EditProcessModal = ({ process }) => {
  const dispatch = useDispatch();
  const [open, setOpen] = useState(false);
  const [result, setResult] = useState('editing');
  const formRef = useRef();
  const modalActions = {
    open: useCallback(() => {
      setResult('editing');
      setOpen(true);
    }, []),
    close: useCallback(() => {
      setOpen(false);

      if (result === 'success') {
        dispatch(fetchEntity('process'));
      }
    }, [result])
  };

  return (
    <>
      <Button classes={{ root: 'entity-button' }} onClick={modalActions.open}>
        Edit Process
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            width: '75vw',
            height: '75vh'
          }}
        >
          <ModalHeader
            title="Edit Process"
            icon={
              <SvgIcon>
                <path d={mdiImageFilterCenterFocus} />
              </SvgIcon>
            }
            desc={process.name}
            formikRef={formRef}
          />
          <Switch value={result}>
            <Switch.Case value="editing">
              <ProcessEditor process={process} setResult={setResult} formRef={formRef} />
            </Switch.Case>
            <Switch.Case value="success">
              <Result type="success">
                <Result.Text>Process was edited successfully!</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button onClick={modalActions.close}>Done</Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="fail">
              <Result type="fail">
                <Result.Text>An error occurred and the process could not be edited.</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button onClick={modalActions.close}>Close</Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
};
