import React from 'react';
import * as PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import Typography from '@material-ui/core/Typography';
import LoginGraphic from '../../../../assets/images/login_graphic.svg';
import VenditaLogo from '../../../../assets/images/vendita_logo_blue.svg';
import MasLogo from '../../../../assets/images/mas_logo_blue.svg';

import './container.local.scss';

export class ContainerComponent extends React.PureComponent {
  render() {
    const { className } = this.props;

    return (
      <div className={`${className} flex`}>
        <div className={`${className}__wrapper`}>
          <div className="header">
            <div className="mas-logo-container">
              <img className="img-responsive" src={MasLogo} alt="MAS logo" />
            </div>
            <Typography className={`${className}__title`} variant="h3" component="h2">
              <FormattedMessage id={this.props.title} />
            </Typography>
            {!!this.props.description && (
              <div className={`${className}__description`}>
                {this.props.description}
              </div>
            )}
          </div>
          {this.props.children}
        </div>
        <div className={`${className}__graphics`}>
          <div className="logo-container">
            <img className="img-responsive" src={VenditaLogo} alt="Vendita logo" />
          </div>
          <div className="graphic-container">
            <img className="img-responsive" src={LoginGraphic} alt="Login graphic" />
          </div>
        </div>
      </div>
    );
  }
}

ContainerComponent.propTypes = {
  title: PropTypes.string.isRequired,
  children: PropTypes.node.isRequired,
  description: PropTypes.string,
  className: PropTypes.string,
};

ContainerComponent.defaultProps = {
  className: 'container',
  description: '',
};
