import React from 'react';

import { CircleImg, NumberLabel, StringLabel } from 'shared/components';
import { Paper } from 'shared/components/layout';
import HorizontalBar from '../../utils/HorizontalStackedBar/HorizontalStackedBar';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiDatabase } from '@mdi/js';

import './DatabasesTile.local.scss';

export const DatabasesTile = ({ servers }) => {
  const total = servers.reduce((acc, sv) => {
    return acc + sv.totalDBs;
  }, 0);

  const dbs = {
    eelic_count: 0,
    selic_count: 0
  };
  servers.map(sv => {
    dbs.eelic_count += sv.eelic_count;
    dbs.selic_count += sv.selic_count;

    return null;
  });

  return (
    <Paper style={{ height: '160px', padding: '16px' }}>
      <Paper.Header style={{ alignItems: 'center', marginBottom: '24px' }}>
        <CircleImg
          image={
            <SvgIcon>
              <Icon path={mdiDatabase} />
            </SvgIcon>
          }
        />
        <NumberLabel number={total} />
        <StringLabel text="Total databases" />
      </Paper.Header>
      <div className="content">
        <div className="info">
          <div className="first-column">
            <div className="value-eed">{dbs.eelic_count}</div>
            <div className="label">Enterprise Edition Databases</div>
          </div>
          <div className="second-column">
            <div className="value-sed">{dbs.selic_count}</div>
            <div className="label">Standard Edition Databases</div>
          </div>
        </div>
        <div className="bar-chart">
          <HorizontalBar
            height={8}
            data={[
              { value: dbs.eelic_count, color: '#19AADE' },
              { value: dbs.selic_count, color: '#7D3AC1' }
            ]}
          />
        </div>
      </div>
    </Paper>
  );
};
