import React, { useState } from 'react';
import { connect } from 'react-redux';
import { func } from 'prop-types';
import { Link, useHistory, useLocation } from 'react-router-dom';
import * as Yup from 'yup';

import { Button, Input } from 'shared/components/controls';

import { Paper, Section, Center, Row, Column } from 'shared/components/layout';
import { Result } from 'shared/components/overlays';

import { Formik, Form, Field, FieldArray } from 'formik';

import { NewUserGroups } from './components/NewUserGroups';

import { createUserThunk } from '../../../store/thunks';
import { getGroupDescription } from '../../../store/selectors';

import './CreateNewUser.local.scss';

const validationSchema = Yup.object().shape({
  username: Yup.string()
    .min(3, 'Invalid length')
    .required('Username is required'),
  password: Yup.string().required('Password is required'),
  confirmpassword: Yup.string()
    .required('Please confim the password you entered')
    .oneOf([Yup.ref('password'), null], 'Passwords do not match'),
  groups: Yup.array()
});

const CreateNewUser = ({ submitUserValues, allUsers, retrieveGroupDescription }) => {
  const history = useHistory();
  const location = useLocation();

  const [operation, setOperation] = useState({ done: false });

  const extraValidations = values => {
    const errors = {};

    if (
      !/^(?:[a-z_][a-z0-9_.]*)$/i.test(values.username) ||
      (values.username && values.username.substr(-1) === '.')
    ) {
      errors.username = 'A valid name is required';
    }

    let exists = false;

    allUsers.map(user => {
      if (exists) return;

      if (user.name === values.username) {
        exists = true;
      }
      return null;
    });

    if (exists) {
      errors.username = 'A user with this name already exists';
    }

    if (values.password) {
      if (/[$\\/":;']/.test(values.password)) {
        errors.password = '$ \\ / " : ; \' are not an allowed characters';
      } else if (values.password.length < 8) {
        errors.password = 'Password must contain at least eight characters';
      } else if (!/[a-z]/.test(values.password)) {
        errors.password = 'Password must have at least a lower case letter (ex: a, b, etc.)';
      } else if (!/[A-Z]/.test(values.password)) {
        errors.password = 'Password must have at least a upper case letter (ex: A, B, etc.)';
      } else if (!/[0-9]/.test(values.password)) {
        errors.password = 'Password must have at least one number digit (ex: 0, 1, 2, etc.)';
      } else if (!/[!@#%^&*()[\]{}+=\-_.,<>]/.test(values.password)) {
        errors.password =
          'Password must have at least one special character (! @ # % ^ & * ( ) [ ] { } + = - _ , .<>)';
      }
    }

    return errors;
  };

  if (operation.done) {
    /** Not used. Navigating dirrectly to new user */
    if (operation.result === 'success') {
      return (
        <>
          <Paper>
            <Paper.Content scrollable={false}>
              <Center>
                <Result type={operation.result}>
                  <Result.Text>User created successfully</Result.Text>
                </Result>
              </Center>
            </Paper.Content>
            <Paper.Footer>
              <Button to="/system/mas-accounts/users" component={Link}>
                Go to user
              </Button>
            </Paper.Footer>
          </Paper>
        </>
      );
    }

    let errorMessage = 'Could not create new user';
    if (
      operation.result === 'fail' &&
      operation.error &&
      operation.error[0] &&
      operation.error[0].text &&
      operation.error[0].text.split('\n').length > 0
    ) {
      [errorMessage] = operation.error[0].text.split('\n');
    }

    return (
      <>
        <Paper>
          <Paper.Content scrollable={false}>
            <Center>
              <Result type={operation.result}>
                <Result.Text>{errorMessage}</Result.Text>
              </Result>
            </Center>
          </Paper.Content>
          <Paper.Footer>
            <Button
              color="primary"
              onClick={() => {
                setOperation({ done: false, result: undefined });
              }}
            >
              Retry
            </Button>
          </Paper.Footer>
        </Paper>
      </>
    );
  }

  return (
    <Formik
      initialValues={{
        username: '',
        password: '',
        confirmpassword: '',
        description: '',
        groups: []
      }}
      validationSchema={validationSchema}
      validate={extraValidations}
      onSubmit={async values => {
        try {
          const result = await submitUserValues(values);

          if (result.result === 'success') {
            history.push(location.pathname.replace('create-new', values.username));
          }

          if (result.result === 'fail') {
            setOperation({
              done: true,
              result: 'fail',
              error: result.messages
            });
          }
        } catch (error) {
          setOperation({
            done: true,
            result: 'fail',
            error: error?.messages
          });
        }
      }}
    >
      {({ values, isSubmitting, isValid }) => (
        <Paper>
          <Form className="create-new-user-form">
            <Paper.Title>New User</Paper.Title>
            <Paper.Content>
              <Section>
                <Section.Title>User Details</Section.Title>
                <Section.Content>
                  <Row>
                    <Column style={{ maxWidth: '220px', padding: '0', marginRight: '24px' }}>
                      <Field name="username">
                        {({ field, meta }) => (
                          <Input
                            required
                            {...field}
                            label="Username"
                            placeholder="Username"
                            error={!!meta.error}
                            helperText={meta.error}
                          />
                        )}
                      </Field>
                    </Column>

                    <Column style={{ maxWidth: '220px' }}>
                      <Field name="description">
                        {({ field, meta }) => (
                          <Input
                            {...field}
                            label="Short Description"
                            placeholder="Short description"
                            error={!!meta.error}
                            helperText={meta.error}
                          />
                        )}
                      </Field>
                    </Column>
                  </Row>
                  <Row>
                    <Column style={{ maxWidth: '220px', padding: '0', marginRight: '24px' }}>
                      <Field name="password">
                        {({ field, meta }) => (
                          <Input
                            required
                            {...field}
                            label="Password"
                            placeholder="Password"
                            type="password"
                            error={!!meta.error}
                            helperText={meta.error}
                          />
                        )}
                      </Field>
                    </Column>
                    <Column style={{ maxWidth: '220px' }}>
                      <Field name="confirmpassword">
                        {({ field, meta }) => (
                          <Input
                            {...field}
                            autoComplete="off"
                            required
                            label="Confirm Password"
                            placeholder="Confirm Passwords"
                            type="password"
                            error={!!meta.error}
                            helperText={meta.error}
                          />
                        )}
                      </Field>
                    </Column>
                  </Row>
                </Section.Content>
              </Section>
              <Section full>
                <Section.Title>Member of groups</Section.Title>
                <FieldArray name="groups">
                  {helpers => (
                    <NewUserGroups
                      groups={values.groups}
                      helpers={helpers}
                      retrieveGroupDescription={retrieveGroupDescription}
                    />
                  )}
                </FieldArray>
              </Section>
            </Paper.Content>

            <Paper.Footer>
              <div className="left-side-action">
                <Button to="/system/mas-accounts/users" color="primary">
                  Cancel
                </Button>
              </div>

              <Button type="submit" variant="contained" disabled={isSubmitting || !isValid}>
                Save
              </Button>
            </Paper.Footer>
          </Form>
        </Paper>
      )}
    </Formik>
  );
};

CreateNewUser.propTypes = { submitUserValues: func };

export const CreateNewUserPage = connect(
  state => ({
    allUsers: state.usersManager.users.all,
    retrieveGroupDescription: name => getGroupDescription(name, state.groups.all)
  }),
  dispatch => ({
    submitUserValues: values => dispatch(createUserThunk(values))
  })
)(CreateNewUser);
