import React, { useState, useEffect, useRef } from 'react';
import { connect } from 'react-redux';

import { Modal, ModalHeader } from 'shared/components/overlays';

import { Paper, Center } from 'shared/components/layout';

import { loadAllAccountsThunk } from '../../store/thunks';

import { ServerForm } from '../Forms/ServerForm';

import { mdiSettings, mdiDns } from '@mdi/js';
import IconButton from '@material-ui/core/IconButton';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';

import { Switch } from 'shared/components';
import { Loader } from 'shared/components/loaders';
import { fetchProtocols } from 'shared/api/protocols';

export const ServerSettings = ({ server, disabled }) => {
  const [open, setOpen] = useState(false);

  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };

  if (disabled) {
    return (
      <SvgIcon style={{ color: 'rgba(0, 0, 0, 0.26)' }}>
        <Icon path={mdiSettings} />
      </SvgIcon>
    );
  }

  return (
    <>
      <IconButton onClick={modalActions.open}>
        <SvgIcon>
          <Icon path={mdiSettings} />
        </SvgIcon>
      </IconButton>

      <Modal open={open} actions={modalActions}>
        <EditServerContainer server={server} />
      </Modal>
    </>
  );
};

export const EditServer = ({ server, reloadAccounts }) => {
  const [state, setState] = useState({ loading: true, protocols: [] });
  const formRef = useRef();
  useEffect(() => {
    // result and protocols provided by fetch
    fetchProtocols().then(resp => {
      if (resp.result === 'success') {
        setState({
          loading: false,
          result: 'success',
          protocols: resp.protocols || []
        });
      } else {
        setState({
          loading: false,
          result: 'fail',
          protocols: []
        });
      }
    });
  }, []);

  return (
    <Paper
      style={{
        width: '75vw',
        height: '75vh'
      }}
    >
      <ModalHeader
        icon={
          <SvgIcon>
            <path d={mdiDns} />
          </SvgIcon>
        }
        title="Edit Server"
        desc="Edit the server connection information"
        formikRef={formRef}
      />
      <Switch value={state.loading}>
        <Switch.Case value>
          <Center>
            <Loader />
          </Center>
        </Switch.Case>
        <Switch.Case value={false}>
          <ServerForm
            server={server}
            protocols={state.protocols}
            handleSubmission={reloadAccounts}
            editing
            formRef={formRef}
          />
        </Switch.Case>
      </Switch>
    </Paper>
  );
};

export const EditServerContainer = connect(null, dispatch => ({
  reloadAccounts: () => dispatch(loadAllAccountsThunk())
}))(EditServer);
