import React from 'react';
import * as PropTypes from 'prop-types';
import { Link, withRouter } from 'react-router-dom';
import { FormattedMessage } from 'react-intl';

import Tooltip from '@material-ui/core/Tooltip';
import { withStyles } from '@material-ui/core/styles';
import MenuList from '@material-ui/core/MenuList';
import MenuItem from '@material-ui/core/MenuItem';
import Paper from '@material-ui/core/Paper';
import Popover from '@material-ui/core/Popover';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Avatar from '@material-ui/core/Avatar';

import { Logout } from 'session/pages';

import './user-menu.local.scss';

export class UserMenu extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      isVisible: false
    };

    this.menuAnchorRef = React.createRef();
  }

  render() {
    return (
      <>
        <div
          className="user-menu qa-user-menu"
          ref={this.menuAnchorRef}
          onClick={this.setToggleDropdown}
          role="presentation"
        >
          <LightTooltip placement="right" title={this.props.user}>
            <Avatar className="user-avatar">{this.props.user.substring(0, 2).toUpperCase()}</Avatar>
          </LightTooltip>
        </div>

        <Popover
          className="user-menu-list-container"
          anchorEl={this.menuAnchorRef.current}
          open={this.state.isVisible}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'right'
          }}
          transformOrigin={{
            vertical: 'bottom',
            horizontal: 'left'
          }}
        >
          <Paper>
            <ClickAwayListener onClickAway={this.setCloseDropdown}>
              <MenuList
                className="user-menu-list"
                autoFocusItem={this.state.isVisible}
                id="menu-list-grow"
              >
                <MenuItem
                  className="qa-user-menu-groups"
                  onClick={event => this.setCloseDropdown(event, 'help')}
                >
                  <FormattedMessage id="navigation.help" />
                </MenuItem>
                <MenuItem className="qa-user-menu-logout" to={Logout.path} component={Link}>
                  <FormattedMessage id="navigation.logout" />
                </MenuItem>
              </MenuList>
            </ClickAwayListener>
          </Paper>
        </Popover>
      </>
    );
  }

  setCloseDropdown = (event, page) => {
    if (this.menuAnchorRef.current && this.menuAnchorRef.current.contains(event.target)) {
      return;
    }

    if (page === 'help') {
      window.open(
        'https://masmanual.venditabeta.com/index.php/Vendita_MAS_Documentation_Library#User_Guides_MAS_3.0',
        '_blank'
      );
    }

    this.setState({ isVisible: false });
  };

  setToggleDropdown = () => {
    this.setState(prevState => ({
      isVisible: !prevState.isVisible
    }));
  };
}

UserMenu.propTypes = {
  user: PropTypes.string.isRequired
};

export default withRouter(UserMenu);

const LightTooltip = withStyles(() => ({
  tooltip: {
    color: '#fff',
    backgroundColor: '#787E89',
    letterSpacing: '0.4px',
    fontSize: '12px',
    lineHeight: '16px',
    fontFamily: 'Open Sans'
  }
}))(Tooltip);
