import React from 'react';

import { Switch } from 'shared/components';

import { EmptyContent } from '../EmptyContent/EmptyContent';
import { ConnectedDashboard } from '../../containers/Dashboard/Dashboard';
import { ConnectedServerView } from '../../containers/ServerView/ServerView';

import './SwitchContent.local.scss';

export const SwitchContent = ({ nrSelected }) => {
  let page = '';
  if (nrSelected === 0) {
    page = 'empty-content';
  } else if (nrSelected === 1) {
    page = 'server-details';
  } else if (nrSelected > 1) {
    page = 'dashboard';
  }

  return (
    <section className="dashboard-view-section">
      <Switch value={page}>
        <Switch.Case value="empty-content">
          <EmptyContent />
        </Switch.Case>
        <Switch.Case value="server-details">
          <ConnectedServerView />
        </Switch.Case>
        <Switch.Case value="dashboard">
          <ConnectedDashboard />
        </Switch.Case>
      </Switch>
    </section>
  );
};
