import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { string, object } from 'prop-types';
import classnames from 'classnames';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiAccountOff } from '@mdi/js';
import Avatar from '@material-ui/core/Avatar';

import { LightTooltip } from 'shared/components/tooltip/Tooltip';

import './UserCard.local.scss';

/* eslint-disable jsx-a11y/no-noninteractive-tabindex */

export const UserCard = ({ href, user }) => {
  const { pathname } = useLocation();
  const { name, src } = user;
  const noGroups = user?.member_of === null || user?.member_of?.length === 0;
  return (
    <Link
      to={href}
      className={classnames('user-card', { selected: pathname.endsWith(`/${name}`) })}
      data-qa={`user-card-of-${name}`}
    >
      <Avatar className="user-avatar" alt={name} src={src}>
        {user.name[0].toUpperCase()}
      </Avatar>
      <span className={classnames('user-name', { groupless: noGroups })}>{name}</span>
      {noGroups && (
        <LightTooltip placement="bottom" title="Deactivated user">
          <SvgIcon className="deactivated-icon">
            <Icon path={mdiAccountOff} />
          </SvgIcon>
        </LightTooltip>
      )}
    </Link>
  );
};

UserCard.propTypes = {
  href: string,
  user: object
};
