import { CONSTANTS } from 'config';
import { RestClient } from 'shared/lib/rest';
import { creators } from 'shared/lib/redux-utils';

import {
  LOAD_ALL_GROUPS,
  LOAD_GROUP,
  GRANT_PRIVILEGE,
  REVOKE_PRIVILEGE,
  REMOVE_USERS_FROM_GROUP,
  REMOVE_GROUPS_FROM_GROUP,
  ADD_USERS_TO_GROUP,
  CREATE_GROUP,
  DELETE_GROUP
} from './actions';

export function loadAllGroupsThunk() {
  return async dispatch => {
    dispatch(creators.loading(LOAD_ALL_GROUPS));

    try {
      const response = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.GROUPS, {
        page_size: 2147483645
      });
      dispatch(creators.success(LOAD_ALL_GROUPS, response.data));
    } catch (error) {
      dispatch(creators.fail(LOAD_ALL_GROUPS, error.data));

      return error.data;
    }
  };
}

export function loadGroupThunk(name) {
  return async dispatch => {
    dispatch(creators.loading(LOAD_GROUP, name));

    try {
      const response = await RestClient.get(`${CONSTANTS.BASE.ENDPOINTS.GROUPS}/${name}`);
      dispatch(creators.success(LOAD_GROUP, response.data));
    } catch (error) {
      dispatch(creators.fail(LOAD_GROUP, error.data));
    }
  };
}

export function createGroupThunk(values) {
  return async dispatch => {
    const body = {
      name: values.groupname,
      description: values.description,
      groups: values.groups
    };
    dispatch(creators.loading(CREATE_GROUP, body));

    try {
      const response = await RestClient.post(`${CONSTANTS.BASE.ENDPOINTS.GROUPS}`, body);

      const refreshed = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.GROUPS, {
        page_size: 2147483645
      });
      dispatch(creators.success(LOAD_ALL_GROUPS, refreshed.data));

      dispatch(creators.success(CREATE_GROUP));

      return response.data;
    } catch (error) {
      dispatch(creators.fail(CREATE_GROUP, error));
      return error?.response?.data;
    }
  };
}

export function grantPrivilegeThunk(params) {
  return async dispatch => {
    const { group, key, type } = params;
    dispatch(creators.loading(GRANT_PRIVILEGE, group));

    try {
      const response = await RestClient.patch(
        `${CONSTANTS.BASE.ENDPOINTS.GROUPS}/${group}/grant/${type}/${key}`
      );
      dispatch(creators.success(GRANT_PRIVILEGE, response.data));

      return response.data;
    } catch (error) {
      dispatch(creators.fail(GRANT_PRIVILEGE, error));
      return error.data;
    }
  };
}

