export function removeUsersFromGroupThunk(params) {
  return async dispatch => {
    dispatch(creators.loading(REMOVE_USERS_FROM_GROUP, params));

    try {
      await RestClient.patch(`${CONSTANTS.BASE.ENDPOINTS.USERS}/${params.user}/revoke`, {
        groups: [params.group]
      });

      const group = await RestClient.get(`${CONSTANTS.BASE.ENDPOINTS.GROUPS}/${params.group}`);

      dispatch(creators.success(REMOVE_USERS_FROM_GROUP, group.data));
    } catch (error) {
      let errorMsg = error;
      if (
        error?.response?.data?.messages &&
        error.response.data.messages.length > 0 &&
        error.response.data.messages[0]?.text
      ) {
        errorMsg = error.response.data.messages[0]?.text.split('\n')[0];
      }

      dispatch(creators.fail(REMOVE_USERS_FROM_GROUP, { result: 'fail', messages: errorMsg }));

      return { result: 'fail', messages: errorMsg };
    }
  };
}

export function removeSubGroupsThunk(params) {
  return async dispatch => {
    dispatch(creators.loading(REMOVE_GROUPS_FROM_GROUP, params));

    try {
      const response = await RestClient.patch(
        `${CONSTANTS.BASE.ENDPOINTS.GROUPS}/${params.group}/revoke`,
        { groups: params.groups }
      );
      dispatch(creators.success(REMOVE_GROUPS_FROM_GROUP, response.data));
    } catch (error) {
      let errorMsg = error;
      if (
        error?.response?.data?.messages &&
        error.response.data.messages.length > 0 &&
        error.response.data.messages[0]?.text
      ) {
        errorMsg = error.response.data.messages[0]?.text.split('\n')[0];
      }

      dispatch(creators.fail(REMOVE_GROUPS_FROM_GROUP, { result: 'fail', messages: errorMsg }));

      return { result: 'fail', messages: errorMsg };
    }
  };
}

export function addUsersToGroupThunk(params) {
  return async dispatch => {
    const { group, users } = params;

    dispatch(creators.loading(ADD_USERS_TO_GROUP, params));

    try {
      await Promise.all(
        users.map(async user => {
          return RestClient.patch(`${CONSTANTS.BASE.ENDPOINTS.USERS}/${user}/grant`, {
            groups: [group]
          });
        })
      );

      const response = await RestClient.get(`${CONSTANTS.BASE.ENDPOINTS.GROUPS}/${params.group}`);

      dispatch(creators.success(ADD_USERS_TO_GROUP, response.data));

      return response.data;
    } catch (error) {
      let errorMsg = error;
      if (
        error?.response?.data?.messages &&
        error.response.data.messages.length > 0 &&
        error.response.data.messages[0]?.text
      ) {
        errorMsg = error.response.data.messages[0]?.text.split('\n')[0];
      }

      dispatch(creators.fail(ADD_USERS_TO_GROUP, errorMsg));
      return { result: 'fail', messages: errorMsg };
    }
  };
}

export function removeGroupThunk(group) {
  return async dispatch => {
    dispatch(creators.loading(DELETE_GROUP, group));

    try {
      await RestClient.delete(`${CONSTANTS.BASE.ENDPOINTS.GROUPS}/${group}`);
      dispatch(creators.success(DELETE_GROUP));

      dispatch(loadAllGroupsThunk());

      return 'done';
    } catch (error) {
      dispatch(creators.fail(DELETE_GROUP, error));

      return error.data;
    }
  };
}
