import React from 'react';
import Groups from '../../components/notification-groups/notification-groups';
import Users from '../../components/notification-users/notification-users';
import Events from '../../components/notification-events/notification-events';
import NotificationList from '../../components/notification-list/notification-list';
import PlayCircleOutlineIcon from '@material-ui/icons/PlayCircleOutline';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';
import ErrorOutlineIcon from '@material-ui/icons/ErrorOutline';

import { Button } from 'shared/components/controls';

import classnames from 'classnames';

import './notifications.scss';
import { RestClient } from 'shared/lib/rest';

export class Notifications extends React.PureComponent {
  constructor(props) {
    super(props);

    const { recievedData } = props;

    const startFullLog = [1, 3, 5, 7];
    const completionFullLog = [2, 3, 6, 7];
    const abnormalFullLog = [4, 6, 7];

    this.state = {
      groupsData: [],
      usersData: [],
      jobStart: {
        notice: false,
        fullLog: startFullLog.includes(recievedData.events)
      },
      jobCompletion: {
        notice: false,
        fullLog: completionFullLog.includes(recievedData.events)
      },
      abnormalEvent: {
        notice: false,
        fullLog: abnormalFullLog.includes(recievedData.events)
      },
      isEditing: false
    };
  }

  async componentDidMount() {
    const { recievedData = { users: [] } } = this.props;

    RestClient.get('/group', { page_size: 2147483645 }).then(res => {
      if (res && res.data && res.data.data && res.data.data.groups) {
        this.setState({
          groupsData: res.data.data.groups.map(item => {
            return { data: item, selected: false, displayIcons: false };
          })
        });
      }
    });
    RestClient.get('/user', { page_size: 2147483645 }).then(res => {
      if (res && res.data && res.data.data && res.data.data.users) {
        this.setState({
          usersData: res.data.data.users.map(item => {
            return {
              data: item,
              selected: recievedData.users.includes(item.name),
              display: false
            };
          })
        });
      }
    });
  }

  render() {
    const { usersData, groupsData, jobStart, jobCompletion, abnormalEvent, isEditing } = this.state;
    const selectedGroup = this.getSelectedGroup();
    const areUsersSelected = usersData.find(item => item.selected);
    const isClearActionAvailable = this.checkIfAllUsersSelectedCurrentGroup();
    return (
      <div
        className={classnames('monitor-and-notifications', {
          disabled: this.props.disabled
        })}
      >
        {isEditing ? (
          <div className="notifications-editor">
            <Groups onSelect={this.onSelectGroup} groups={groupsData} />
            <Users
              onSelect={this.onSelectUser}
              onSelectUserAction={this.onSelectUserAction}
              isClearActionAvailable={isClearActionAvailable}
              users={usersData}
              isGroupSelected={!!selectedGroup}
              eventTypeIcons={this.getEventTypeIcons()}
            />
            <div className="right-side-container">
              <Events
                jobStart={jobStart}
                jobCompletion={jobCompletion}
                abnormalEvent={abnormalEvent}
                onSelect={this.onSelectEventType}
                areUsersSelected={areUsersSelected}
              />
              <Button
                variant="contained"
                color="primary"
                size="small"
                onClick={() => {
                  this.props.onApply(mappedState(this.state));
                  this.toggleApplyButton();
                }}
              >
                Apply
              </Button>
            </div>
          </div>
        ) : (
          <NotificationList
            users={usersData}
            eventTypeIcons={this.getEventTypeIcons()}
            toggleApplyButton={this.toggleApplyButton}
          />
        )}
      </div>
    );
  }

  getEventTypeIcons = () => {
    const { jobStart, jobCompletion, abnormalEvent } = this.state;
    const icons = [];
    if (jobStart.notice || jobStart.fullLog) {
      icons.push(<PlayCircleOutlineIcon size="small" style={{ color: 'var(--primary-800)' }} />);
    }
    if (jobCompletion.notice || jobCompletion.fullLog) {
      icons.push(<CheckCircleOutlineIcon size="small" style={{ color: 'var(--primary-800)' }} />);
    }
    if (abnormalEvent.notice || abnormalEvent.fullLog) {
      icons.push(<ErrorOutlineIcon size="small" style={{ color: 'var(--primary-800)' }} />);
    }
    return icons;
  };

  toggleApplyButton = () => {
    this.setState(state => ({ isEditing: !state.isEditing }));
  };

  checkIfAllUsersSelectedCurrentGroup = () => {
    const selectedGroup = this.getSelectedGroup();
    if (selectedGroup) {
      return this.state.usersData
        .filter(item => selectedGroup.data.users.indexOf(item.data.name) !== -1)
        .every(item => item.selected);
    }
  };

  onSelectGroup = group => {
    this.setState(state => ({
      groupsData: state.groupsData.map(item => ({
        ...item,
        selected: group.data.name === item.data.name
      })),
      usersData: state.usersData.map(item => ({
        ...item,
        display: group.data.users.indexOf(item.data.name) !== -1
      }))
    }));
  };

  onSelectUser = user => {
    /* eslint-disable react/no-access-state-in-setstate */
    const usersData = this.state.usersData.map(item => {
      if (item.data.name === user.data.name) {
        return { ...item, selected: !item.selected };
      }
      return item;
    });
    this.setState({ usersData, groupsData: this.setFlagGroupsIfContainSelectedUser(usersData) });
  };

  onSelectUserAction = shouldClear => {
    const selectedGroup = this.getSelectedGroup();
    if (selectedGroup) {
      const usersData = this.state.usersData.map(item => {
        if (selectedGroup.data.users.indexOf(item.data.name) !== -1) {
          return { ...item, selected: !shouldClear };
        }
        return item;
      });
      this.setState({
        usersData,
        groupsData: this.setFlagGroupsIfContainSelectedUser(usersData)
      });
    }
  };

  setFlagGroupsIfContainSelectedUser = usersData => {
    return this.state.groupsData.map(group => ({
      ...group,
      displayIcons: !!usersData.find(user => {
        return user.selected && user.data.member_of.indexOf(group.data.name) !== -1;
      })
    }));
  };

  onSelectEventType = (type, key) => {
    this.setState(state => ({ [type]: { ...state[type], [key]: !state[type][key] } }));
  };

  getSelectedGroup() {
    return this.state.groupsData.find(item => item.selected);
  }
}

export default Notifications;

/**
 * State mapper for backend request.
 *
 * events is a composite number,
 *
 * notice unsuported for now
 * groups are redundant now that the ui preselects all users in a group.
 */
/* eslint-disable no-unused-vars */
function mappedState({ groupsData, usersData, jobStart, jobCompletion, abnormalEvent }) {
  const users = usersData.filter(user => user.selected).map(user => user.data.name);

  let events = 0;

  if (jobStart.fullLog) {
    events += 1;
  }

  if (jobCompletion.fullLog) {
    events += 2;
  }

  if (abnormalEvent.fullLog) {
    events += 4;
  }

  return { users, events };
}
