import React from 'react';
import classnames from 'classnames';
import {
  mdiFileTableBox,
  mdiDatabase,
  mdiFileTree,
  mdiTable,
  mdiAlertOutline,
  mdiTableColumnWidth,
  mdiAlphaPBox,
  mdiInboxFull,
  mdiInbox
} from '@mdi/js';
import SvgIcon from '@material-ui/core/SvgIcon';
import { Center } from 'shared/components';
import { Loader } from 'shared/components/loaders';

import './root.local.scss';

const VerticalUsageBar = ({ usage, className }) => {
  let fill;
  if (usage > 75) {
    fill = '#ED3A4A';
  } else if (usage > 50) {
    fill = '#F1BD5C';
  } else {
    fill = '#34CC88';
  }
  return (
    <div className={className}>
      <svg viewBox="-2 -2 62 102">
        <rect x="0" y="0" rx="8" ry="8" height="100" width="60" fill="rgba(12, 50, 110, 0.38)" />
        <rect x="0" y={100 - usage} rx="8" ry="8" height={usage} width="60" fill={fill} />
      </svg>
    </div>
  );
};
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/interactive-supports-focus */
const SidebarStorageItem = ({ ns, usage, setItem, isSelected }) => (
  <div
    className={classnames('sidebar-item', { selected: isSelected })}
    role="button"
    onClick={() => setItem(ns)}
  >
    <VerticalUsageBar usage={usage} className="usage-icon" />
    {ns}
  </div>
);

export const StorageSidebar = ({ list, setItem, selectedItem, dbName, isLoading }) => {
  return (
    <div className="sidebar">
      <div className="title">
        <SvgIcon>
          <path d={mdiFileTableBox} />
        </SvgIcon>
        {dbName} Used Storage
      </div>
      <div className="content">
        {isLoading ? (
          <Center>
            <Loader>Loading data...</Loader>
          </Center>
        ) : (
          list.map((element, index) => (
            <SidebarStorageItem
              isSelected={selectedItem === element.ns}
              key={index}
              {...element}
              setItem={setItem}
            />
          ))
        )}
      </div>
    </div>
  );
};

const iconMapper = {
  triggers: { icon: mdiAlertOutline, label: 'Triggers' },
  tables: { icon: mdiTable, label: 'Tables' },
  indexes: { icon: mdiTableColumnWidth, label: 'Indexes' },
  procedures: { icon: mdiAlphaPBox, label: 'Procedures' },
  packages: { icon: mdiInbox, label: 'Packages' },
  packageBodies: { icon: mdiInboxFull, label: 'Package Bodies' }
};

const objectsSidebarMapper = (setItem, selectedItem, invalidObjects) => (
  { owner, objects },
  index
) => {
  const isSelected = owner === selectedItem.owner;
  const invalidChildren = invalidObjects.filter(({ owner: name }) => name === owner);
  return (
    <React.Fragment key={index}>
      <div
        className={classnames('sidebar-item', {
          selected: isSelected,
          'rotate-cw': !isSelected,
          'rotate-ccw': isSelected
        })}
        role="button"
        onClick={() => setItem({ owner: isSelected ? null : owner, object: null })}
      >
        <SvgIcon>
          <path d={mdiFileTree} />
        </SvgIcon>
        {owner}
      </div>
      {isSelected &&
        objects.map((object, i) => {
          const isObjectSelected = object === selectedItem.object;
          const isInvalid = invalidChildren.findIndex(({ object: name }) => name === object) !== -1;
          return (
            <div
              key={`${index}_${i}`}
              className={classnames('child', 'sidebar-item', {
                selected: isObjectSelected,
                invalid: isInvalid
              })}
              role="button"
              onClick={() => setItem({ owner, object })}
            >
              <SvgIcon>
                <path d={iconMapper[object].icon} />
              </SvgIcon>
              {iconMapper[object].label}
            </div>
          );
        })}
    </React.Fragment>
  );
};

export const ObjectsSidebar = ({
  dbName,
  setItem,
  selectedItem,
  list,
  isLoading,
  invalidObjects
}) => {
  return (
    <div className="sidebar">
      <div className="title">
        <SvgIcon>
          <path d={mdiDatabase} />
        </SvgIcon>
        {dbName} Owners
      </div>
      <div className="content">
        {isLoading ? (
          <Center>
            <Loader>Loading data...</Loader>
          </Center>
        ) : (
          <div className="sidebar-container">
            {list.map(objectsSidebarMapper(setItem, selectedItem, invalidObjects))}
          </div>
        )}
      </div>
    </div>
  );
};
