import React, { useState, useCallback } from 'react';
import { object, func } from 'prop-types';
import { connect } from 'react-redux';
import { Section, Paper } from 'shared/components/layout';

import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiDeleteCircleOutline, mdiTrashCan } from '@mdi/js';

import { Result } from 'shared/components/overlays/Result/Result';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Switch, Center } from 'shared/components';
import { Button } from 'shared/components/controls';
import { Loader } from 'shared/components/loaders';
import { capitalizeFirstLetter } from 'shared/lib/capitalize-first-letter';

import { AvatarCard } from '../../../../components/AvatarCard/AvatarCard';
import './GroupUsers.local.scss';
import { EditGroupUsersContainer } from './EditGroupUsers';

import { removeUsersFromGroupThunk } from '../../../store/thunks';

export const GroupUsers = ({ group, removeUsersFromGroup }) => {
  const [selectedUser, setSelectedUser] = useState('');
  const [errorMessage, setErrorMessage] = useState('An error occurred.');
  const [result, setResult] = useState('attempt');
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: useCallback(() => {
      setOpen(true);
    }, []),
    close: useCallback(() => {
      setResult('attempt');
      setErrorMessage('An error occurred.');
      setOpen(false);
    }, [])
  };

  const remove = useCallback(() => {
    removeUsersFromGroup(group.name, selectedUser)
      .then(response => {
        if (typeof response === 'object' && response.result === 'fail') {
          setResult('error');
          setErrorMessage(response.messages);
        } else {
          setResult('attempt');
          setErrorMessage('An error occurred.');
          setOpen(false);
        }
      })
      .catch(() => {
        setResult('attempt');
        setOpen(false);
        console.log('Could not remove user from group!'); // eslint-disable-line
      });
  }, [group, selectedUser]);
  return (
    <>
      <Section>
        <Section.Header>
          <Section.Title>Users in this group</Section.Title>
        </Section.Header>
        <div>
          <ul className="card-list">
            {group.users.map(user => (
              <li className="card-item" key={user}>
                <AvatarCard
                  label={user}
                  avatar={{ initial: user.charAt(0) }}
                  action={
                    <SvgIcon
                      onClick={e => {
                        e.preventDefault();
                        e.stopPropagation();
                        setSelectedUser(user);
                        modalActions.open();
                      }}
                    >
                      <path d={mdiDeleteCircleOutline} />
                    </SvgIcon>
                  }
                />
              </li>
            ))}
            <li className="card-item" key="add-user-to-groups">
              <EditGroupUsersContainer />
            </li>
          </ul>
        </div>
      </Section>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            width: '50vw',
            height: '50vh'
          }}
        >
          <ModalHeader
            title="Remove user from group"
            icon={
              <SvgIcon>
                <path d={mdiTrashCan} />
              </SvgIcon>
            }
          />
          <Switch value={result}>
            <Switch.Case value="attempt">
              <Paper.Content scrollable={false}>
                <Result type="fail">
                  <BodyText>
                    Are you sure you want to remove user {selectedUser} from group {group.name}?
                    This could affect the way the user will be able to interact with the app.
                  </BodyText>
                </Result>
              </Paper.Content>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    color="primary"
                    variant="contained"
                    onClick={() => {
                      setResult('removing');
                      remove();
                    }}
                  >
                    Yes
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="removing">
              <Paper.Content scrollable={false}>
                <Center>
                  <Loader>Removing...</Loader>
                </Center>
              </Paper.Content>
            </Switch.Case>
            <Switch.Case value="error">
              <Paper.Content scrollable={false}>
                <Result type="fail">
                  <BodyText>{capitalizeFirstLetter(errorMessage)}</BodyText>
                </Result>
                <Paper.Footer>
                  <Button
                    color="primary"
                    variant="contained"
                    onClick={() => {
                      setResult('attempt');
                      setErrorMessage('An error occurred.');
                      setOpen(false);
                    }}
                  >
                    Close
                  </Button>
                </Paper.Footer>
              </Paper.Content>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
};

GroupUsers.propTypes = {
  group: object,
  removeUsersFromGroup: func
};

export const GroupUsersContainer = connect(
  state => ({
    group: state.groups.selected
  }),
  dispatch => ({
    removeUsersFromGroup: (group, user) => dispatch(removeUsersFromGroupThunk({ group, user }))
  })
)(GroupUsers);

function BodyText({ children }) {
  return (
    <h2
      style={{
        fontFamily: 'Open Sans',
        fontStyle: 'normal',
        fontWeight: 'bold',
        fontSize: '14px',
        lineHeight: '20px',
        letterSpacing: '0.25px',
        marginTop: '24px'
      }}
    >
      {children}
    </h2>
  );
}
