import React from 'react';
import * as PropTypes from 'prop-types';
import { Link, withRouter } from 'react-router-dom';

import Paper from '@material-ui/core/Paper';
import Popover from '@material-ui/core/Popover';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import MenuList from '@material-ui/core/MenuList';
import MenuItem from '@material-ui/core/MenuItem';
import IconButton from '@material-ui/core/IconButton';

import { navigationItems } from './constants';

import './navigation.local.scss';

export class Navigation extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      activeTab: 0,
      navId: '',
      anchorElement: null
    };

    this.anchorRef = React.createRef();
  }

  componentDidMount() {
    this.setCurrentNavItem();
  }

  componentDidUpdate() {
    this.setCurrentNavItem();
  }

  render() {
    return (
      <div className={this.props.className}>
        {navigationItems.map((item, index) => {
          if (item.items && item.items.length > 0) {
            return (
              <div key={index}>
                <div
                  role="presentation"
                  key={index}
                  className={`nav-item ${
                    this.state.activeTab === index || this.state.navId === item.id ? ' active ' : ''
                  } ${item.qaClass ? item.qaClass : ''}`}
                  onClick={e => this.setToggleDropdown(e, item.id)}
                >
                  <IconButton className="icon">{item.icon}</IconButton>
                  <p>{item.title}</p>
                </div>
                <Popover
                  className="nav-sublist-container"
                  anchorEl={this.state.anchorElement}
                  open={this.state.navId === item.id}
                  anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right'
                  }}
                  transformOrigin={{
                    vertical: 'top',
                    horizontal: 'left'
                  }}
                >
                  <Paper>
                    <ClickAwayListener onClickAway={this.setCloseDropdown}>
                      <MenuList className="nav-sublist-menu" id="menu-list-grow">
                        {item.items.map(subItem => (
                          <MenuItem
                            key={subItem.to}
                            className={subItem.qaClass}
                            to={subItem.to}
                            component={Link}
                            onClick={this.setCloseDropdown}
                          >
                            {subItem.title}
                          </MenuItem>
                        ))}
                      </MenuList>
                    </ClickAwayListener>
                  </Paper>
                </Popover>
              </div>
            );
          }

          return (
            <div
              role="presentation"
              key={item.to}
              className={`nav-item ${this.state.activeTab === index ? 'active' : ''} ${
                item.qaClass ? item.qaClass : ''
              }`}
              onClick={() => this.setActiveTab(index, item.to)}
            >
              <IconButton className="icon">{item.icon}</IconButton>
              <p>{item.title}</p>
            </div>
          );
        })}
      </div>
    );
  }

  setCloseDropdown = (event, page) => {
    if (this.anchorRef.current && this.anchorRef.current.contains(event.target)) {
      return;
    }

    this.props.history.push(page);
    this.setState({ navId: '' });
  };

  setToggleDropdown = (e, name) => {
    if (name === this.state.navId) {
      this.setState({ navId: '', anchorElement: null });
    } else {
      this.setState({ navId: name, anchorElement: e.target });
    }
  };

  setActiveTab = (newActiveTab, page) => {
    this.props.history.push(page);
    this.setState({ activeTab: newActiveTab, navId: '' });
  };

  setCurrentNavItem() {
    const index = navigationItems.findIndex(
      item =>
        item.to === this.props.location.pathname || this.props.location.pathname.startsWith(item.to)
    );

    if (this.state.activeTab !== index) {
      this.setState({ activeTab: index });
    }
  }
}

Navigation.propTypes = {
  className: PropTypes.string,
  location: PropTypes.object.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired
  }).isRequired
};

Navigation.defaultProps = {
  className: 'navigation'
};

export default withRouter(Navigation);
