import React from 'react';
import { connect } from 'react-redux';
import * as PropTypes from 'prop-types';
import axios from 'axios';
import Tooltip from '@material-ui/core/Tooltip';
import { withStyles } from '@material-ui/core/styles';

import { actions } from 'session/store/actions';
import { currentUserSelector, beVersionSelector } from 'session/store/state';
import { Navigation, UserMenu, Compile } from './components';
import pkg from '../../../../../package.json';

import MasLogo from '../../../../assets/images/mas_logo_white.svg';
import VenditaLogo from '../../../../assets/images/vendita_logo_white.svg';
import './sidebar.local.scss';

export class SidebarContainer extends React.PureComponent {
  componentDidMount() {
    this.props.getBeVersion();
    axios.get('http //127.0.0.1:5000/uptime');
    axios.get('http //127.0.0.1:5000/postgre');
    axios.get('http //127.0.0.1:5000/count');
  }

  render() {
    return (
      <div className={this.props.className}>
        <div className="logo-container">
          <img className="vendita-logo img-responsive" src={VenditaLogo} alt="Vendita logo" />
          <LightTooltip
            placement="right"
            title={
              <>
                <div>
                  <span style={{ paddingRight: '2px' }}>UI </span>version:&nbsp;
                  {pkg.version}
                </div>
                <div>
                  <span>BE </span>version:&nbsp;
                  {this.props.beVersion}
                </div>
              </>
            }
          >
            <img className="mas-logo img-responsive" src={MasLogo} alt="MAS logo" />
          </LightTooltip>
        </div>
        <div>
          <br />
<span style={service}>PostgreSQL <img style={{width:10}} src="https://icons.iconarchive.com/icons/custom-icon-design/flatastic-10/128/Trafficlight-green-icon.png" />
</span>
<br /><br />
<span style={service}>load average <img style={{width:10}} src="https://icons.iconarchive.com/icons/custom-icon-design/flatastic-10/128/Trafficlight-green-icon.png" /> 0.01, 0.02, 0.05
</span><br /><br />
<span style={service}>Count <img style={{width:10}} src="https://icons.iconarchive.com/icons/custom-icon-design/flatastic-10/128/Trafficlight-green-icon.png" />
</span>
<ul style={service}>
  <li>33 apache</li>
  <li>1 chrony</li>
  <li>1 dbus</li>
  <li> 21 mas</li>
  <li>1 polkitd</li>
  <li> 5 postfix</li>
  <li>24 postgres</li>
  <li> 115 root</li>      
  <li>1 rpc</li>
</ul>

        </div>
        <div className="nav-area">
          <Navigation />
        </div>
        <div className="bottom">
          <Compile />
          <UserMenu user={this.props.currentUser.get('name')} />
        </div>
      </div>
    );
  }
}

SidebarContainer.propTypes = {
  className: PropTypes.string,
  currentUser: PropTypes.object.isRequired
};

export const mapStateToProps = state => ({
  currentUser: currentUserSelector(state),
  beVersion: beVersionSelector(state)
});
export const mapDispatchToProps = dispatch => ({
  getBeVersion: data => dispatch(actions.getBeVersion.dispatchLoading(data))
});

SidebarContainer.defaultProps = {
  className: 'sidebar'
};

export default connect(mapStateToProps, mapDispatchToProps)(SidebarContainer);

const LightTooltip = withStyles(() => ({
  tooltip: {
    color: '#fff',
    backgroundColor: '#787E89',
    letterSpacing: '0.4px',
    fontSize: '12px',
    lineHeight: '16px',
    fontFamily: 'Open Sans'
  },

}))(Tooltip);

const service = {
  color: 'white',
  fontSize: '13px',
  fontWeight: 'bolder'
};

