import React, { useState, useCallback } from 'react';
import moment from 'moment';

import { Link } from 'react-router-dom';

import AccountCircleOutlinedIcon from '@material-ui/icons/AccountCircleOutlined';
import { mdiClockOutline, mdiHexagonMultipleOutline, mdiDatabase } from '@mdi/js';
import SvgIcon from '@material-ui/core/SvgIcon';
import StorageIcon from '@material-ui/icons/Storage';
import { Button } from 'shared/components/controls';
import { LimitlessTooltip } from 'shared/components/tooltip/Tooltip';
import { ConnectedDeleteJobModal } from '../../actions/delete/DeleteJobModal';
import { JobStatus } from '../job-status';

import './job-item.local.scss';

export const JobItem = ({ job, filters }) => {
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: useCallback(() => {
      setOpen(true);
    }, []),
    close: useCallback(() => {
      setOpen(false);
    }, [])
  };
  const [uuid, setUuid] = useState('');

  const formatName = () => {
    let name = job.get('process');
    const nameContainerWidth =
      (document.getElementById('jobs-content') &&
        document.getElementById('jobs-content').offsetWidth -
          (5 / 100) * document.getElementById('jobs-content').offsetWidth) ||
      600; // time container
    const starPath = <span className="word cutted">.../</span>;
    const formattedName = [];
    name = name.split('.');
    for (let i = 0; i < name.length; i++) {
      if (i < name.length - 1) {
        formattedName.push(
          <span key={i}>
            <span className="word">{name[i]}</span>.
          </span>
        );
      } else {
        formattedName.push(
          <span key={i} className="word">
            {name[i]}
          </span>
        );
      }
    }

    const canvas = formatName.canvas || (formatName.canvas = document.createElement('canvas'));
    const context = canvas.getContext('2d');
    let metrics = context.measureText(formattedName);

    if (metrics.width > nameContainerWidth) {
      // add '../' as the first word in the name
      formattedName.unshift(starPath);
    }

    while (metrics.width > nameContainerWidth) {
      // remove first word from the name (it is ../)
      formattedName.shift();
      // remove first word form the actual name
      formattedName.shift();
      // add '../' as the first word in the name
      formattedName.unshift(starPath);
      // recheck width
      metrics = context.measureText(formattedName);
    }

    return formattedName;
  };
  return (
    <>
      <Link to={`/jobs/job/?invocation=${job.get('uuid')}`}>
        <div className="job-item">
          <div className="status-icon">
            <JobStatus status={job.get('status')} />
          </div>
          <div className="job-metadata">
            <div className="first-row">
              <LimitlessTooltip
                placement="bottom-start"
                title={
                  <>
                    <p>Invocation: {job.get('process')}</p>
                    {job.get('schedule') && <p>Schedule: {job.get('schedule')}</p>}
                  </>
                }
              >
                <div className="name">{formatName()}</div>
              </LimitlessTooltip>
            </div>
            <div className="second-row">
              <LimitlessTooltip placement="bottom-start" title="MAS system time (UTC)">
                <div className="metadata-element time">
                  <SvgIcon>
                    <path d={mdiClockOutline} />
                  </SvgIcon>
                  {moment(job.get('date_invoke')).format('MMM DD YYYY, HH:mm:ss')}
                </div>
              </LimitlessTooltip>
              {job.get('user_owner') && (
                <div className="metadata-element run-by">
                  <AccountCircleOutlinedIcon />
                  {job.get('user_owner')}
                </div>
              )}
              {job.get('connections') &&
                job.get('connections').get('dbms') &&
                job
                  .get('connections')
                  .get('dbms')
                  .get('name') && (
                  <div className="metadata-element dbms">
                    <SvgIcon>
                      <path d={mdiHexagonMultipleOutline} />
                    </SvgIcon>
                    {job
                      .get('connections')
                      .get('dbms')
                      .get('name')}
                  </div>
                )}
              {job.get('connections') &&
                job.get('connections').get('shell') &&
                job
                  .get('connections')
                  .get('shell')
                  .get('host_address') && (
                  <div className="metadata-element server">
                    <StorageIcon />
                    {job.get('connections') &&
                      job.get('connections').get('shell') &&
                      job
                        .get('connections')
                        .get('shell')
                        .get('host_address')}
                  </div>
                )}
              {job.get('connections') &&
                job.get('connections').get('dbms') &&
                job
                  .get('connections')
                  .get('dbms')
                  .get('protocol_special') &&
                job
                  .get('connections')
                  .get('dbms')
                  .get('protocol_special')
                  .get('database') && (
                  <div className="metadata-element database">
                    <SvgIcon>
                      <path d={mdiDatabase} />
                    </SvgIcon>
                    {job.get('connections') &&
                      job.get('connections').get('dbms') &&
                      job
                        .get('connections')
                        .get('dbms')
                        .get('protocol_special')
                        .get('database')}
                  </div>
                )}
            </div>
          </div>
          {job.get('status') === 'DELAYED' && (
            <div className="job-actions">
              <Button
                color="secondary"
                onClick={event => {
                  event.preventDefault();
                  setOpen(true);
                  setUuid(job.get('uuid'));
                }}
              >
                Delete
              </Button>
            </div>
          )}
        </div>
      </Link>

      {open && (
        <ConnectedDeleteJobModal open={open} actions={modalActions} uuid={uuid} filters={filters} />
      )}
    </>
  );
};
