import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import {
  changeStateThunk,
  loadStoragePointsThunk,
  setStorageItemThunk,
  loadStorageDetailsThunk
} from '../../store/thunks';
import { Paper } from 'shared/components/layout';
import { NavigationTab } from '../../components/NavigationTab/NavigationTab';
import { DbObjects } from '../../components/DbObjects';
import {
  getFullPath,
  getFormattedStorage,
  haveStorageData,
  getStorageDetailsData
} from '../../store/selectors';

const Storage = ({
  storeState: { level, path },
  changeTab,
  fullPath,
  getStoragePoints,
  getStorageDetails,
  storagePoints,
  storageDetails,
  selectedItem,
  setStorageItem,
  loadingFlags,
  storedEntries
}) => {
  const dbName = path.split('--')[2];
  const [protocol] = path.split('--');

  useEffect(() => {
    if (!storedEntries) {
      getStoragePoints({
        fullPath
      });
      getStorageDetails({
        selectedItem,
        fullPath
      });
    }
  }, [fullPath]);

  return (
    <>
      <Paper.Header>
        <NavigationTab active={level} onClick={changeTab} protocol={protocol} />
      </Paper.Header>
      <Paper.Content>
        <DbObjects
          loadingFlags={loadingFlags}
          type={level}
          data={storageDetails}
          list={storagePoints}
          selectedItem={selectedItem}
          setItem={setStorageItem}
          dbName={dbName}
        />
      </Paper.Content>
    </>
  );
};

Storage.propTypes = {
  storeState: PropTypes.object,
  storagePoints: PropTypes.array,
  storageDetails: PropTypes.object,
  loadingFlags: PropTypes.object,
  fullPath: PropTypes.string,
  changeTab: PropTypes.func,
  setStorageItem: PropTypes.func,
  selectedItem: PropTypes.string,
  getStoragePoints: PropTypes.func
};

export const ConnectedStorage = connect(
  state => ({
    storeState: state.explorer.state,
    storagePoints: getFormattedStorage(state),
    selectedItem: state.explorer.storage.selected,
    loadingFlags: state.explorer.flags.storage,
    storedEntries: haveStorageData(state),
    storageDetails: getStorageDetailsData(state),
    fullPath: getFullPath(state)
  }),
  dispatch => ({
    changeTab: level => dispatch(changeStateThunk({ level })),
    setStorageItem: path => dispatch(setStorageItemThunk(path)),
    getStorageDetails: filters => dispatch(loadStorageDetailsThunk(filters)),
    getStoragePoints: filters => dispatch(loadStoragePointsThunk(filters))
  })
)(Storage);
