import React from 'react';
import PropTypes from 'prop-types';
import MUITable from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';

import './Table.local.scss';

export const Table = ({ columns, children, sortBehavior, changeSort, className }) => {
  return (
    <MUITable stickyHeader className={className}>
      <TableHead className="sticky-head">
        <TableRow>
          {columns.map(HeaderCell(sortBehavior, changeSort))}
        </TableRow>
      </TableHead>
      <TableBody>
        {children}
      </TableBody>
    </MUITable>
  );
};

const HeaderCell = (sortBehavior, changeSort) => (column, index) => (
  <TableCell
    key={`header_column_${index}`}
    align={column.align}
    component={column.component}
    padding={column.padding}
    sortDirection={sortBehavior.order}
    variant={column.variant}
  >
    { column.sortKey ? (
      <TableSortLabel
        active={sortBehavior.sortKey === column.sortKey}
        direction={sortBehavior.order}
        onClick={changeSort(column.sortKey, sortBehavior.order === 'asc' ? 'desc' : 'asc')}
      >
        {column.label}
      </TableSortLabel>
    ) : column.label }
  </TableCell>
);

Table.Row = TableRow;
Table.Cell = TableCell;

Table.propTypes = {
  columns: PropTypes.array.isRequired,
  sortBehavior: PropTypes.shape({
    order: PropTypes.string,
    sortKey: PropTypes.string
  }),
  changeSort: PropTypes.func
};
