import React from 'react';
import { Link } from 'react-router-dom';
import { array, object, oneOfType } from 'prop-types';
import moment from 'moment';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import {
  mdiProgressClock,
  mdiCheckCircleOutline,
  mdiCalendarArrowRight,
  mdiStopCircleOutline,
  mdiTimelineClock,
  mdiAlertRhombusOutline,
  mdiCancel,
  mdiHexagonMultipleOutline,
  mdiDatabase
} from '@mdi/js';
import ScheduleIcon from '@material-ui/icons/Schedule';
import AccountCircleOutlinedIcon from '@material-ui/icons/AccountCircleOutlined';
import StorageIcon from '@material-ui/icons/Storage';

import { LightTooltip, LimitlessTooltip } from 'shared/components/tooltip/Tooltip';
import { Center } from 'shared/components';

import './JobResults.local.scss';

export class JobResults extends React.PureComponent {
  render() {
    const { jobs, fromSearch } = this.props;

    if (jobs && jobs.size === 0) {
      if (fromSearch) {
        return <></>;
      }
      return <Center>Job list is empty</Center>;
    }
    return <>{this.renderJobsResults(jobs)}</>;
  }

  renderJobsResults = jobs =>
    jobs.map((job, index) => (
      <Link key={index} to={`/jobs/job?invocation=${job.get('uuid')}`}>
        <div className="result-job-row">
          <LightTooltip placement="bottom-start" title={job.get('status').toUpperCase()}>
            <div className="job-status-container">{this.jobStatus(job.get('status'))}</div>
          </LightTooltip>
          <div className="job-content">
            <LimitlessTooltip
              placement="bottom-start"
              title={
                <>
                  <p>Invocation: {job.get('process')}</p>
                  {job.get('schedule') && <p>Schedule: {job.get('schedule')}</p>}
                </>
              }
            >
              <div className="first-row">{this.formatName(job.get('process'))}</div>
            </LimitlessTooltip>

            <div className="second-row">
              <LimitlessTooltip placement="bottom-start" title="MAS system time (UTC)">
                <div className="time">
                  <ScheduleIcon />
                  {moment(job.get('date_invoke')).format('MMM DD YYYY, HH:mm:ss')}
                </div>
              </LimitlessTooltip>
              {job.get('user_owner') && (
                <div className="run-by">
                  <AccountCircleOutlinedIcon />
                  {job.get('user_owner')}
                </div>
              )}
              {job.get('connections') &&
                job.get('connections').get('dbms') &&
                job
                  .get('connections')
                  .get('dbms')
                  .get('name') && (
                  <div className="dbms">
                    <SvgIcon>
                      <Icon path={mdiHexagonMultipleOutline} />
                    </SvgIcon>
                    {job
                      .get('connections')
                      .get('dbms')
                      .get('name')}
                  </div>
                )}
              {job.get('connections') &&
                job.get('connections').get('shell') &&
                job
                  .get('connections')
                  .get('shell')
                  .get('host_address') && (
                  <div className="server">
                    <StorageIcon />
                    {job.get('connections') &&
                      job.get('connections').get('shell') &&
                      job
                        .get('connections')
                        .get('shell')
                        .get('host_address')}
                  </div>
                )}
              {job.get('connections') &&
                job.get('connections').get('dbms') &&
                job
                  .get('connections')
                  .get('dbms')
                  .get('protocol_special') &&
                job
                  .get('connections')
                  .get('dbms')
                  .get('protocol_special')
                  .get('database') && (
                  <div className="database">
                    <SvgIcon>
                      <Icon path={mdiDatabase} />
                    </SvgIcon>
                    {job.get('connections') &&
                      job.get('connections').get('dbms') &&
                      job
                        .get('connections')
                        .get('dbms')
                        .get('protocol_special')
                        .get('database')}
                  </div>
                )}
            </div>
          </div>
        </div>
      </Link>
    ));

  jobStatus = status => {
    switch (status) {
      case 'EXECUTING':
        return (
          <div className="job-status executing">
            <SvgIcon>
              <Icon path={mdiProgressClock} />
            </SvgIcon>
          </div>
        );
      case 'SUCCEEDED':
        return (
          <div className="job-status succeeded">
            <SvgIcon>
              <Icon path={mdiCheckCircleOutline} />
            </SvgIcon>
          </div>
        );
      case 'SCHEDULED':
        return (
          <div className="job-status scheduled">
            <SvgIcon>
              <Icon path={mdiCalendarArrowRight} />
            </SvgIcon>
          </div>
        );
      case 'ABORTED':
        return (
          <div className="job-status aborted">
            <SvgIcon>
              <Icon path={mdiStopCircleOutline} />
            </SvgIcon>
          </div>
        );
      case 'DELAYED':
        return (
          <div className="job-status delayed">
            <SvgIcon>
              <Icon path={mdiTimelineClock} />
            </SvgIcon>
          </div>
        );
      case 'FAILED':
        return (
          <div className="job-status failed">
            <SvgIcon>
              <Icon path={mdiAlertRhombusOutline} />
            </SvgIcon>
          </div>
        );
      case 'KILLED':
        return (
          <div className="job-status killed">
            <SvgIcon>
              <Icon path={mdiCancel} />
            </SvgIcon>
          </div>
        );
      default:
        return '';
    }
  };

  formatName = jobName => {
    const formattedName = [];
    const name = jobName.split('.');
    for (let i = 0; i < name.length; i++) {
      if (i < name.length - 1) {
        formattedName.push(
          <span key={i}>
            <span className="word">{name[i]}</span>.
          </span>
        );
      } else {
        formattedName.push(
          <span key={i} className="word">
            {name[i]}
          </span>
        );
      }
    }

    return formattedName;
  };
}

JobResults.propTypes = {
  jobs: oneOfType([array, object])
};

export default JobResults;
