import React from 'react';
import * as PropTypes from 'prop-types';

import './string-label.local.scss';

export class StringLabelComponent extends React.PureComponent {
  render() {
    return <div className={this.props.className}>{this.props.text}</div>;
  }
}

export default StringLabelComponent;

StringLabelComponent.propTypes = {
  className: PropTypes.string,
  text: PropTypes.node.isRequired
};

StringLabelComponent.defaultProps = {
  className: 'string-label'
};
