import { CONSTANTS } from 'config';
import { RestClient } from 'shared/lib/rest';
import { creators } from 'shared/lib/redux-utils';

import {
  LOAD_ALL_USERS,
  LOAD_USER,
  REVOKE_PRIVILEGE,
  GRANT_PRIVILEGE,
  ADD_USER_TO_GROUPS,
  DEACTIVATE_USER,
  CREATE_USER
} from '../actions';

export function loadAllUsersThunk() {
  return async dispatch => {
    dispatch(creators.loading(LOAD_ALL_USERS));

    try {
      const response = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.USERS, {
        page_size: 2147483645
      });
      dispatch(creators.success(LOAD_ALL_USERS, response.data));
    } catch (error) {
      dispatch(creators.fail(LOAD_ALL_USERS, error));
    }

    return 'done';
  };
}

export function loadUserThunk(name) {
  return async dispatch => {
    dispatch(creators.loading(LOAD_USER, name));

    try {
      const response = await RestClient.get(`${CONSTANTS.BASE.ENDPOINTS.USERS}/${name}`);
      dispatch(creators.success(LOAD_USER, response.data));
    } catch (error) {
      dispatch(creators.fail(LOAD_USER, error));
    }
  };
}

export function grantPrivilegeThunk(params) {
  return async dispatch => {
    const { user, key, type } = params;

    dispatch(creators.loading(GRANT_PRIVILEGE, user));

    try {
      const response = await RestClient.patch(
        `${CONSTANTS.BASE.ENDPOINTS.USERS}/${user}/grant/${type}/${key}`
      );
      dispatch(creators.success(GRANT_PRIVILEGE, response.data));
    } catch (error) {
      dispatch(creators.fail(GRANT_PRIVILEGE, error));
    }
  };
}

export function revokePrivilegeThunk(params) {
  return async dispatch => {
    const { user, key, type } = params;

    dispatch(creators.loading(REVOKE_PRIVILEGE, user));

    try {
      const response = await RestClient.patch(
        `${CONSTANTS.BASE.ENDPOINTS.USERS}/${user}/revoke/${type}/${key}`
      );
      dispatch(creators.success(REVOKE_PRIVILEGE, response.data));
    } catch (error) {
      dispatch(creators.fail(REVOKE_PRIVILEGE, error));
    }
  };
}

export function addUserToGroupsThunk(params) {
  return async dispatch => {
    dispatch(creators.loading(ADD_USER_TO_GROUPS, params));

    try {
      const response = await RestClient.patch(
        `${CONSTANTS.BASE.ENDPOINTS.USERS}/${params.user}/grant`,
        { groups: params.groups }
      );
      dispatch(creators.success(ADD_USER_TO_GROUPS, response.data));

      const allUsers = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.USERS, {
        page_size: 2147483645
      });
      dispatch(creators.success(LOAD_ALL_USERS, allUsers.data));

      return 'done';
    } catch (error) {
      let errorMsg = error;
      if (
        error?.response?.data?.messages &&
        error.response.data.messages.length > 0 &&
        error.response.data.messages[0]?.text
      ) {
        errorMsg = error.response.data.messages[0]?.text.split('\n')[0];
      }
      dispatch(creators.fail(ADD_USER_TO_GROUPS, errorMsg));

      return { type: 'fail', message: errorMsg };
    }
  };
}

export function removeUserFromGroupsThunk(params) {
  return async dispatch => {
    dispatch(creators.loading(ADD_USER_TO_GROUPS, params));

    try {
      const response = await RestClient.patch(
        `${CONSTANTS.BASE.ENDPOINTS.USERS}/${params.user}/revoke`,
        { groups: params.groups }
      );
      dispatch(creators.success(ADD_USER_TO_GROUPS, response.data));
      return 'done';
    } catch (error) {
      let errorMsg = error;
      if (
        error?.response?.data?.messages &&
        error.response.data.messages.length > 0 &&
        error.response.data.messages[0]?.text
      ) {
        errorMsg = error.response.data.messages[0]?.text.split('\n')[0];
      }

      dispatch(creators.fail(ADD_USER_TO_GROUPS, errorMsg));
      return { type: 'fail', message: errorMsg };
    }
  };
}

export function deactivateUserThunk(user) {
  return async dispatch => {
    dispatch(creators.loading(DEACTIVATE_USER, user));

    try {
      await RestClient.patch(`${CONSTANTS.BASE.ENDPOINTS.USERS}/${user.name}/revoke`, {
        groups: user.member_of
      });

      const allUsers = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.USERS, {
        page_size: 2147483645
      });
      dispatch(creators.success(DEACTIVATE_USER, allUsers.data));

      const response = await RestClient.get(`${CONSTANTS.BASE.ENDPOINTS.USERS}/${user.name}`);
      dispatch(creators.success(LOAD_USER, response.data));

      return 'done';
    } catch (error) {
      dispatch(creators.fail(DEACTIVATE_USER, error));

      return error.data;
    }
  };
}

export function createUserThunk(values) {
  return async dispatch => {
    const body = {
      name: values.username,
      password: values.password,
      description: values.description,
      groups: values.groups
    };
    dispatch(creators.loading(CREATE_USER, body));

    try {
      const response = await RestClient.post(`${CONSTANTS.BASE.ENDPOINTS.USERS}`, body);

      const refreshed = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.USERS, {
        page_size: 2147483645
      });
      dispatch(creators.success(LOAD_ALL_USERS, refreshed.data));

      dispatch(creators.success(CREATE_USER));

      return response.data;
    } catch (error) {
      dispatch(creators.fail(CREATE_USER, error));
      return error?.response?.data;
    }
  };
}
