import React from 'react';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import {
  mdiSourceRepository,
  mdiFolderHome,
  mdiLanConnect,
  mdiHexagonMultipleOutline,
  mdiCubeOutline
} from '@mdi/js';

import extractDefault from './extractDefault';

const supportedDataTypes = [
  'boolean',
  'ename',
  'float',
  'integer',
  'mas0000.os.exec.system_user',
  'mas0000.sys.email_address',
  'mas0000.sys.phone_number',
  'mas0000.sys.process.process',
  'mas0000.sys.process.variadic_parameter',
  'string',
  'unsigned_float',
  'unsigned_integer'
];

export default (parameter, dataTypes, prototypes) => {
  parameter = { ...parameter };
  parameter.position = parameter.meta.position || Number.MAX_SAFE_INTEGER;
  if (typeof parameter.is_required !== 'boolean') {
    parameter.is_required = true;
  }
  if (parameter.data_type.startsWith('optional_')) {
    // An optional_ required parameter makes no sense
    parameter.is_required = false;
    parameter.data_type = parameter.data_type.substring(parameter.data_type.indexOf('_') + 1);
  }
  if (parameter.deflt !== null) {
    // A required parameter with a default makes no sense
    parameter.is_required = false;
  }
  parameter.is_supported = true;
  if (parameter.is_required) {
    parameter.is_supported = false;
    if (prototypes.find(({ name }) => name === parameter.data_type)) {
      parameter.is_supported = true;
    } else {
      const dataType = dataTypes.find(({ name }) => name === parameter.data_type);
      if (dataType) {
        if (Array.isArray(dataType.enumerations)) {
          parameter.is_supported = dataType.enumerations.length > 0;
        } else if (
          dataType.name.startsWith('mas0000.sys.account.conn.') ||
          dataType.name === 'mas0000.sys.account.vsphere'
        ) {
          parameter.is_supported = true;
        } else {
          parameter.is_supported = supportedDataTypes.includes(parameter.data_type);
        }
      }
    }
  }
  // Attempt to convert the deflt from Python to JS.
  parameter.deflt = extractDefault(parameter.deflt);
  if (parameter.data_type === 'mas0300.fmt.format' && parameter.value === null) {
    parameter.value = 'TEXT';
  }

  parameter.label = getLabel(parameter.name);
  parameter.icon = getIcon(parameter.name);

  return parameter;
};

const getLabel = name => {
  return labels[name] || name;
};

const getIcon = name => {
  return icons[name] || null;
};

const labels = {
  target_working_dir: 'Working directory',
  target_shell: 'Target server SSH',
  repo_shell: 'Repo server SSH',
  repo_working_dir: 'Repo directory',
  form: 'Database form',
  dbms: 'Target DBMS',
  vsphere_account: 'vSphere account',
  vcenterservername: 'vCenter server name'
};

const icons = {
  target_working_dir: (
    <SvgIcon>
      <Icon path={mdiFolderHome} />
    </SvgIcon>
  ),
  target_shell: (
    <SvgIcon>
      <Icon path={mdiLanConnect} />
    </SvgIcon>
  ),
  repo_shell: (
    <SvgIcon>
      <Icon path={mdiLanConnect} />
    </SvgIcon>
  ),
  repo_working_dir: (
    <SvgIcon>
      <Icon path={mdiSourceRepository} />
    </SvgIcon>
  ),
  dbms: (
    <SvgIcon>
      <Icon path={mdiHexagonMultipleOutline} />
    </SvgIcon>
  ),
  form: (
    <SvgIcon>
      <Icon path={mdiFolderHome} />
    </SvgIcon>
  ),
  vsphere_account: (
    <SvgIcon>
      <Icon path={mdiCubeOutline} />
    </SvgIcon>
  ),
  vcenterservername: (
    <SvgIcon>
      <Icon path={mdiCubeOutline} />
    </SvgIcon>
  )
};
