import React, { forwardRef } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

import './small-input.local.scss';

export const SmallInputComponent = forwardRef(({ value, icon, disabled, ...others }, ref) => (
  <div
    ref={ref}
    className={classnames('small-input', { disabled, active: value.length })}
  >
    { icon && <div className="icon">{icon}</div> }
    <input
      className="input"
      type="text"
      value={value}
      {...others}
    />
  </div>
));

SmallInputComponent.propTypes = {
  icon: PropTypes.node,
  value: PropTypes.string,
  disabled: PropTypes.bool,
};

SmallInputComponent.defaultProps = {
  icon: null,
  value: '',
  disabled: false,
};

export default SmallInputComponent;
