/**
 * This file will be updated with any new module (adding the module reducer)
 */
import { applyMiddleware, combineReducers, createStore } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import { persistReducer } from 'redux-persist';
import storageSession from 'redux-persist/lib/storage/session';
import thunk from 'redux-thunk';
import createSagaMiddleware from 'redux-saga';

import { sessionEffects, sessionReducer } from 'session';
import { dashboardReducer } from 'dashboard';
import { sharedEffects, sharedReducer } from 'shared/store/';
import { createReducer } from 'shared/lib/redux-utils';

import usersManagerReducer from 'users-manager/store/reducers';
import groupsReducer from 'users-manager/groups/store/reducer';
import explorerReducer from 'explorer/store/reducers';
import executiveDashboardReducer from 'executive-dashboard/store/reducers';
import * as entities from 'entities/reducer';
import * as daemon from 'dashboard/store/reducers/daemon';

import * as invocations from 'job-process/store/reducer';

import { api } from './middlewares';
import { reducer as core } from './reducers';

const saga = createSagaMiddleware();

const persistConfig = {
  key: 'root',
  storage:storageSession,
  whitelist: ['entities']
  /*
  //Well I just whitelist entities,
  since it's the one that has all expensive network requests
  */
};

export const configStore = () => {
  
  const appReducer = persistReducer(persistConfig,combineReducers({
    core,
    session: sessionReducer,
    dashboard: dashboardReducer,
    shared: sharedReducer,
    usersManager: usersManagerReducer,
    groups: groupsReducer,
    entities: createReducer(entities),
    explorer: explorerReducer,
    invocation_viewer: createReducer(invocations),
    executiveDashboard: executiveDashboardReducer,
    daemon: createReducer(daemon)
  }));

  const reducer = (state, action) => {
    // clear redux store on logout action
    if (action.type === '[session] LOGOUT__SUCCESS') {
      const { routing } = state;
      state = { routing };
      window.location.reload();
    }
    return appReducer(state, action);
  };

  const effects = [...sessionEffects, ...sharedEffects];

  const store = createStore(reducer, composeWithDevTools(applyMiddleware(saga, thunk, api)));

  effects.forEach(effect => {
    saga.run(effect);
  });

  return store;
};

export default configStore();
