import extractDefault from './extractDefault';

import { sortBy } from 'lodash';

import check from '../editors/field-level-validations';

const supportedDataTypes = [
  'boolean',
  'ename',
  'string',
  'integer',
  'float',
  'uuid',
  'unsigned_float',
  'unsigned_integer',
  'mas0000.os.exec.system_user',
  'mas0000.sys.email_address',
  'mas0000.sys.phone_number',
  'mas0000.sys.process.process',
  'mas0000.sys.process.variadic_parameter'
];

export function space_begone(name) {
  return name.replace(/\s/g, '___');
}

export function space_summon(name) {
  return name.replace(/___/g, ' ');
}

export default function normalize(param, dataTypes, prototypes, forms) {
  const parameter = { ...param };

  defaults(parameter, dataTypes, prototypes, forms);

  parameter.editor = defineEditor(parameter, dataTypes, prototypes, forms);

  parameter.field_name = space_begone(parameter.name);

  maybeDisabled(parameter);

  return parameter;
}

function maybeDisabled(parameter) {
  const { appearance } = parameter.meta;

  if (appearance === 'Grayed' && parameter.editor) {
    parameter.editor.disabled = true;
  }
}

function defaults(parameter, dataTypes, prototypes) {
  parameter.position = parameter.meta?.position || Number.MAX_SAFE_INTEGER;
  if (typeof parameter.is_required !== 'boolean') {
    parameter.is_required = true;
  }

  // hardcode stuff for replvalues params
  if (parameter.data_type === 'optional_dict' && parameter.name === 'replvalues') {
    // An optional_ required parameter makes no sense
    parameter.is_required = false;
  } else if (parameter.data_type.startsWith('optional_')) {
    // An optional_ required parameter makes no sense
    parameter.is_required = false;
    parameter.data_type = parameter.data_type.substring(parameter.data_type.indexOf('_') + 1);
  }

  if (parameter.deflt !== null) {
    // A required parameter with a default makes no sense
    parameter.is_required = false;
  }
  parameter.is_supported = true;
  if (parameter.is_required) {
    /**
     * TODO: this was done to help exclude parameters like, any
     * Or other stuff that cannot be parsed correctly
     *
     * Will remove them for now.
     * Any types mostly are multiline input data, so we'll default them to that.
     */
    parameter.is_supported = false;

    if (prototypes.find(({ name }) => name === parameter.data_type)) {
      parameter.is_supported = true;
    } else {
      const dataType = dataTypes.find(({ name }) => name === parameter.data_type);
      if (dataType) {
        if (Array.isArray(dataType.enumerations)) {
          parameter.is_supported = dataType.enumerations.length > 0;
        } else if (
          dataType.name.startsWith('mas0000.sys.account.conn.') ||
          dataType.name === 'mas0000.sys.account.vsphere'
        ) {
          parameter.is_supported = true;
        } else {
          /**
           * Allow support for all types of parameters, defaulting to a Text multiline.
           */
          parameter.is_supported = supportedDataTypes.includes(parameter.data_type);

          parameter.editor = {
            type: 'string',
            multiline: true,
            deflt: ''
          };
        }
      }
    }
  }
  // Attempt to convert the deflt from Python to JS.
  parameter.deflt = extractDefault(parameter.deflt);

  if (parameter.data_type === 'mas0300.fmt.format' && parameter.value === null) {
    parameter.deflt = 'TEXT';
  }

  // hardcode default value for oformat parameter to "TEXT"
  // was decided to be done on the UI because for the BE alot of processess had to be manually changed
  // and at the moment we are pressed on time
  if (parameter.name === 'oformat') {
    parameter.deflt = parameter.value ? parameter.value : 'TEXT';
  }
}

function parseProtocols(protocols) {
  if (!protocols) {
    return [];
  }

  if (!Array.isArray(protocols)) {
    return [protocols];
  }

  return protocols.reduce((bucket, protocol) => {
    // quick and dirty (as he said)
    if (protocol === 'DBMS') {
      bucket.push('MSSQL', 'MYSQL', 'ORACLE', 'PGSQL');
    } else {
      bucket.push(protocol);
    }

    return bucket;
  }, []);
}

function defineEditor(parameter, dataTypes, prototypes, forms) {
  if (!parameter.is_supported) {
    return { type: 'hidden', disabled: true, value: '' };
    // return { type: 'text', disabled: true, value: '' };
  }

  const { meta } = parameter;
  const { editor: metaEditor, appearance } = meta;

  if (appearance === 'Hidden') {
    return { type: 'hidden' };
  }

  if (metaEditor === 'Hidden') {
    return { type: 'text', disabled: true };
  }

  const prototype = prototypes.find(proto => proto.name === parameter.data_type);

  if (prototype) {
    const options = forms
      .filter(form => form.prototype === prototype.name)
      .map(form => ({ label: form.name, value: form.name }));
    return {
      type: 'select',
      options
    };
  }

  const dataType = dataTypes.find(type => type.name === parameter.data_type);

  if (!dataType) {
    return { type: 'text', disabled: true, readOnly: true };
  }

  if (dataType.enumerations !== null && dataType.enumerations.length > 0) {
    const enumerations = sortBy(dataType.enumerations, 'weight');
    const options = enumerations.map(({ label }) => ({ label, value: label }));

    return { type: 'select', options };
  }

  if (
    dataType.name.startsWith('mas0000.sys.account.conn.') ||
    dataType.name === 'mas0000.sys.account.vsphere'
  ) {
    return { type: 'account', protocols: parseProtocols(dataType.protocol) };
  }

  switch (dataType.name) {
    case 'ename':
      switch (metaEditor) {
        case 'account':
          return { type: 'account', protocols: parseProtocols(meta.protocol) };
        default:
          return { type: 'fqname' };
      }
    case 'string':
      switch (metaEditor) {
        case 'password':
          return { type: 'password' };
        case 'fqname':
        case 'ename':
          return { type: 'fqname' };
        case 'multiline':
          // return { type: 'text', multiline: true };
          return { type: 'multiline' };
        case 'key':
          return { type: 'rsa', multiline: true };
        case 'account':
          return { type: 'account', protocols: parseProtocols(meta.protocol) };
        default:
          return { type: 'text' };
      }
    case 'boolean':
      return { type: 'boolean' };
    case 'integer':
      return { type: 'number' };
    case 'float':
      return { type: 'number', validate: [check.signed_float] };
    case 'uuid':
      return { type: 'uuid' };
    case 'unsigned_float':
      return { type: 'number', validate: [check.unsigned_float] };
    case 'unsigned_integer':
      return { type: 'number', validate: [check.unsigned_int] };
    case 'mas0000.os.exec.system_user':
      return { type: 'account', protocols: parseProtocols(dataType.protocol) };
    case 'mas0000.sys.email_address':
      return { type: 'email' };
    case 'mas0000.sys.phone_number':
      return { type: 'phone' };
    case 'mas0000.sys.process.process':
      return { type: 'process' };
    case 'mas0000.sys.process.variadic_parameter':
      return { type: 'variadic_parameter' /** needsSelectedProcessName */ };
    case 'optional_dict':
      // hardcord for replvalues
      if (parameter.name === 'replvalues') {
        return { type: 'replvalues' };
      }
      return { type: 'text', multiline: true };
    default:
      return { type: 'text', multiline: true };
  }

  // return { type: 'hidden' };
}
