import Cookie from 'js-cookie';
import axios from 'axios';

import { CONSTANTS } from 'config';
import { createUrl } from 'shared/lib';


class RestClientClass {
  constructor() {
    this.authentications = {
      basicAuth: {
        name: 'Authorization',
        accessToken: '',
      },
    };

    this.baseUrl = CONSTANTS.BASE.HOST;

    if (CONSTANTS.BASE.VERSION) {
      this.baseUrl = `${CONSTANTS.BASE.HOST}/${CONSTANTS.BASE.VERSION}`;
    }

    this.client = axios.create({
      timeout: CONSTANTS.BASE.TIMEOUT,
      baseURL: this.baseUrl,
      // withCredentials: true,
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
    });

    this.get = this.get.bind(this);
    this.delete = this.delete.bind(this);
    this.head = this.head.bind(this);
    this.post = this.post.bind(this);
    this.put = this.put.bind(this);
    this.patch = this.patch.bind(this);
  }

  setBasicAuth() {
    this.authentications.basicAuth.accessToken = `Basic ${Cookie.get('Authorization')}`;
  }

  removeBasicAuth() {
    this.authentications.basicAuth.accessToken = Cookie.remove('Authorization');
  }

  getRequestConfig() {
    return {
      headers: {
        [this.authentications.basicAuth.name]: this.authentications.basicAuth.accessToken,
      },
    };
  }

  get(path, query) {
    const url = createUrl(path, query);
    return this.client.get(url, this.getRequestConfig());
  }

  delete(path, query) {
    const url = createUrl(path, query);
    return this.client.delete(url, this.getRequestConfig());
  }

  head(path, query) {
    const url = createUrl(path, query);
    return this.client.head(url, this.getRequestConfig());
  }

  post(path, body, query) {
    const url = createUrl(path, query);
    return this.client.post(url, body, this.getRequestConfig());
  }

  put(path, body, query) {
    const url = createUrl(path, query);
    return this.client.put(url, body, this.getRequestConfig());
  }

  patch(path, body, query) {
    const url = createUrl(path, query);
    return this.client.patch(url, body, this.getRequestConfig());
  }
}

export const RestClient = new RestClientClass();
