import React from 'react';
import * as PropTypes from 'prop-types';

import './custom-axis-tick.local.scss';

export class CustomAxisTickComponent extends React.PureComponent {
  render() {
    return (
      <g transform={`translate(${this.props.x},${this.props.y})`}>
        <text className="label" x={0} y={0} dy={20} textAnchor="middle" fill="rgba(12, 50, 110, 0.38)">{this.props.payload.value}</text>
      </g>
    );
  }
}

export default CustomAxisTickComponent;

CustomAxisTickComponent.propTypes = {
  x: PropTypes.number,
  y: PropTypes.number,
  payload: PropTypes.object,
};

CustomAxisTickComponent.defaultProps = {
  x: null,
  y: null,
  payload: {},
};
