import React, { useState, useEffect, useContext, useRef } from 'react';
import { connect } from 'react-redux';

import { Modal, ModalHeader, ModalContext, Result } from 'shared/components/overlays';

import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiDns } from '@mdi/js';

import { Switch } from 'shared/components';
import { Paper, Center } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';
import { Button } from 'shared/components/controls';

import { loadAllAccountsThunk } from '../../store/thunks';

import { fetchProtocols } from 'shared/api/protocols';

import { ServerForm } from '../Forms/ServerForm';

import './AddServerCard.local.scss';

export const AddServerCard = ({ disabled }) => {
  const [open, setOpen] = useState(false);

  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };

  return (
    <>
      <Button className="action-btn" onClick={modalActions.open} disabled={disabled}>
        Add New Server
      </Button>
      <Modal open={open} actions={modalActions}>
        <AddNewServerContainer />
      </Modal>
    </>
  );
};

export const AddNewServer = ({ server = { protocol: 'SSH' }, reloadAccounts }) => {
  const [state, setState] = useState({ loading: true, protocols: [] });
  const { actions } = useContext(ModalContext);
  const formRef = useRef();

  useEffect(() => {
    fetchProtocols().then(resp => {
      setState({
        loading: false,
        result: resp.result,
        protocols: resp.protocols || []
      });
    });
  }, []);

  return (
    <Paper
      style={{
        width: '75vw',
        height: '75vh'
      }}
    >
      <ModalHeader
        icon={
          <SvgIcon>
            <path d={mdiDns} />
          </SvgIcon>
        }
        title="Add New Server"
        desc="Input the server connection information"
        formikRef={formRef}
      />
      <Switch value={state.loading}>
        <Switch.Case value>
          <Center>
            <Loader>Loading protocols list</Loader>
          </Center>
        </Switch.Case>
        <Switch.Case value={false}>
          <Switch value={state.result}>
            <Switch.Case value="success">
              <ServerForm
                server={server}
                handleSubmission={reloadAccounts}
                protocols={state.protocols}
                isNew
                formRef={formRef}
              />
            </Switch.Case>
            <Switch.Case value="fail">
              <Paper.Content>
                <Result>
                  <Result.Text>Could not load protocols list.</Result.Text>
                </Result>
              </Paper.Content>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button color="primary" onClick={actions.close}>
                    Close
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
          </Switch>
        </Switch.Case>
      </Switch>
    </Paper>
  );
};

export const AddNewServerContainer = connect(
  () => ({}),
  dispatch => ({
    reloadAccounts: () => dispatch(loadAllAccountsThunk())
  })
)(AddNewServer);
