import React from 'react';
import _ from 'lodash';
import { Formik, Form } from 'formik';
import { SubmitButton } from 'shared/components/form/SubmitButton';
import { Paper, Section, Row, Column } from 'shared/components/layout';

import { QualifiedName } from 'shared/components/form/editors/Custom';
import { Text } from 'shared/components/form/editors/Generic';

import { patchProcess } from 'shared/api/process';

import './ProcessEditor.local.scss';

export function ProcessEditor({ process, setResult, formRef }) {
  return (
    <Formik
      initialValues={{}}
      onSubmit={values => {
        saveProcessEdits(values, process, setResult);
      }}
      innerRef={formRef}
    >
      {() => {
        return (
          <>
            <Paper.Content>
              <Form>
                <Section>
                  <Section.Header>
                    <Section.Title>Details</Section.Title>
                  </Section.Header>
                  <Section.Content>
                    <Row>
                      <Column>
                        <QualifiedName
                          name="process_name"
                          label="Name"
                          value={process.name}
                          required
                        />
                      </Column>
                    </Row>
                  </Section.Content>
                </Section>
                <Section>
                  <Section.Content>
                    {process.steps.map((step, index) => {
                      return <Step step={step} step_index={index} />;
                    })}
                  </Section.Content>
                </Section>
              </Form>
            </Paper.Content>

            <Paper.Footer>
              <Paper.Footer.Right>
                <SubmitButton variant="contained">Save</SubmitButton>
              </Paper.Footer.Right>
            </Paper.Footer>
          </>
        );
      }}
    </Formik>
  );
}

function Step({ step, step_index }) {
  const expressions = parseEvaluatableExpressions(step, step_index);
  const noEvaluatableExpressions = !expressions.length;

  if (noEvaluatableExpressions) {
    return null;
  }

  return (
    <div className="step">
      <div className="title">Step: {step.label}</div>
      <div className="description">{step.description}</div>
      <div className="expressions">
        <div className="list">
          {expressions.map((exp, index) => (
            <StepExpression key={index} {...exp} />
          ))}
        </div>
      </div>
    </div>
  );
}

function StepExpression({ expression, exp_index, step, step_index }) {
  const name = `step-${step_index}-expression-${exp_index}`;
  return (
    <Row>
      <Text
        multiline
        value={expression.evaluate}
        label={`Expression ${expression.assign || exp_index} of ${step.label}`}
        name={name}
        required
      />
    </Row>
  );
}

function parseEvaluatableExpressions(step, step_index) {
  const { expressions } = step;

  if (!Array.isArray(expressions) || expressions.length === 0) {
    return [];
  }

  return expressions.reduce((acc, exp, index) => {
    if (exp.evaluate) {
      acc.push({
        expression: exp,
        exp_index: index,
        step,
        step_index
      });
    }

    return acc;
  }, []);
}

function saveProcessEdits(values, process, setResult) {
  const { process_name, ...evaluations } = values;
  const toSend = {};

  const steps = _.cloneDeep(process.steps);

  /**
   * extract and remap field values.
   */
  const extracted = extractFieldInfo(evaluations);

  extracted.forEach(({ step, expression, value }) => {
    const path = `[${step}].expressions[${expression}].evaluate`;

    _.set(steps, path, value);
  });

  /** Prepare patch request body */
  toSend.steps = steps;

  if (process.name !== process_name) {
    toSend.rename = values.process_name;
  }

  patchProcess(process.name, toSend)
    .then(() => {
      setResult('success');
    })
    .catch(() => {
      setResult('fail');
    });
}

function extractFieldInfo(all) {
  const key_struct = /^step-(\d+)-expression-(\d+)$/;

  return Object.entries(all).map(([key, value]) => {
    const matched = key_struct.exec(key);

    return {
      step: matched[1],
      expression: matched[2],
      value
    };
  });
}
