import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import { fetchEntity } from '../../../entities/actions';
import { entitySelectors } from '../../../entities/selectors';

import { Loader, ParameterLoader } from 'shared/components/loaders';
import { Center } from 'shared/components/layout';
import { Result } from 'shared/components/overlays/Result/Result';

export const Ensure = connect(
  (state, { type }) => {
    const selector = entitySelectors[type];

    return selector(state);
  },
  {
    request: fetchEntity
  }
)(Ensurer);

function Ensurer({ children, fresh, pipe = false, type, inlineLoader, data, status, request }) {
  useEffect(() => {
    if (status === 'fetching') return;
    if (status === 'fetching') return;

    if (status === 'fetched' && !fresh) return;

    request(type);
  }, []);

  if (!status || status === 'not-fetched') {
    return null;
  }

  if (status === 'fetching' && !pipe) {
    const Loading = inlineLoader ? ParameterLoader : Loader;

    return (
      <Center>
        <Loading>Loading {type}...</Loading>
      </Center>
    );
  }

  if (status === 'failed' && !pipe) {
    return (
      <Result type="fail">
        <Result.Text>Could not fetch {type} data!</Result.Text>
      </Result>
    );
  }

  if (typeof children === 'function') {
    return <>{children({ status, data, request: () => request(type) })}</>;
  }

  return <>{children}</>;
}

export default Ensure;
