export const initialState = {
  data: {},
  fetch_status: {
    account: 'not-fetched',
    constant: 'not-fetched',
    exception: 'not-fetched',
    form: 'not-fetched',
    namespace: 'not-fetched',
    process: 'not-fetched',
    prototype: 'not-fetched',
    schedule: 'not-fetched',
    type: 'not-fetched',
    alias: 'not-fetched'
  },
  isLoading: true,
  breadcrumbs: [],
  filters: {
    type: 'all',
    search: ''
  }
};

export function FETCH_ENTITIES_REQUEST(nextState, { entityType }) {
  nextState.fetch_status[entityType] = 'fetching';
}

export function FETCH_ENTITIES_SUCCESS(nextState, { entityType, result }) {
  nextState.data[entityType] = result;
  nextState.fetch_status[entityType] = 'fetched';
}

export function FETCH_ENTITIES_FAILURE(nextState, { entityType }) {
  nextState.fetch_status[entityType] = 'failed';
}

export function FETCH_ENTITIES_ALL_REQUEST(nextState) {
  nextState.isLoading = true;
}

export function FETCH_ENTITIES_ALL_SUCCESS(nextState) {
  nextState.isLoading = false;
}

export function FETCH_ENTITIES_ALL_FAILURE(nextState) {
  nextState.isLoading = false;
}

export function SELECT_ENTITY(nextState, { entity, index }, state) {
  nextState.breadcrumbs.splice(index, state.breadcrumbs.length - 1);
  nextState.breadcrumbs[index] = entity.name.split('.').pop();
}

export function CLEAR_SELECTED_ENTITY(nextState) {
  nextState.breadcrumbs = [];
}

export function UPDATE_FILTERS(nextState, { name, value }) {
  nextState.filters[name] = value;
  if (name === 'type' || name === 'search') {
    nextState.breadcrumbs = [];
  }
}
