import React from 'react';
import { Link } from 'react-router-dom';
import * as PropTypes from 'prop-types';
import moment from 'moment';

import AccountCircleOutlinedIcon from '@material-ui/icons/AccountCircleOutlined';
import { mdiHexagonMultipleOutline, mdiDatabase } from '@mdi/js';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import StorageIcon from '@material-ui/icons/Storage';
import { LimitlessTooltip } from 'shared/components/tooltip/Tooltip';
import './job-item.local.scss';

export class JobItemComponent extends React.PureComponent {
  render() {
    const { extended } = this.props;
    if (extended) {
      return (
        <div className={`${this.props.className} ${this.jobStatus()}`}>
          <div className="status-icon">{this.props.statusChild}</div>
          <div className="job-metadata">
            <div className="first-row">
              <LimitlessTooltip
                placement="bottom-start"
                title={
                  <>
                    <p>Invocation: {this.props.job.get('process')}</p>
                    {this.props.job.get('schedule') && (
                      <p>Schedule: {this.props.job.get('schedule')}</p>
                    )}
                  </>
                }
              >
                <div className="name">{this.formatName()}</div>
              </LimitlessTooltip>
            </div>
            <div className="second-row">
              <LimitlessTooltip placement="bottom-start" title="MAS system time (UTC)">
                <div className="time">
                  {moment(this.props.job.get('date_invoke')).format('HH:mm:ss')}
                </div>
              </LimitlessTooltip>
              {this.props.job.get('user_owner') && (
                <div className="run-by">
                  <AccountCircleOutlinedIcon />
                  {this.props.job.get('user_owner')}
                </div>
              )}
              {this.props.job.get('connections') &&
                this.props.job.get('connections').get('dbms') &&
                this.props.job
                  .get('connections')
                  .get('dbms')
                  .get('name') && (
                  <div className="dbms">
                    <SvgIcon>
                      <Icon path={mdiHexagonMultipleOutline} />
                    </SvgIcon>
                    {this.props.job
                      .get('connections')
                      .get('dbms')
                      .get('name')}
                  </div>
                )}
              {this.props.job.get('connections') &&
                this.props.job.get('connections').get('shell') &&
                this.props.job
                  .get('connections')
                  .get('shell')
                  .get('host_address') && (
                  <div className="server">
                    <StorageIcon />
                    {this.props.job.get('connections') &&
                      this.props.job.get('connections').get('shell') &&
                      this.props.job
                        .get('connections')
                        .get('shell')
                        .get('host_address')}
                  </div>
                )}
              {this.props.job.get('connections') &&
                this.props.job.get('connections').get('dbms') &&
                this.props.job
                  .get('connections')
                  .get('dbms')
                  .get('protocol_special') &&
                this.props.job
                  .get('connections')
                  .get('dbms')
                  .get('protocol_special')
                  .get('database') && (
                  <div className="database">
                    <SvgIcon>
                      <Icon path={mdiDatabase} />
                    </SvgIcon>
                    {this.props.job.get('connections') &&
                      this.props.job.get('connections').get('dbms') &&
                      this.props.job
                        .get('connections')
                        .get('dbms')
                        .get('protocol_special')
                        .get('database')}
                  </div>
                )}
              <LimitlessTooltip placement="bottom-start" title="MAS system time (UTC)">
                <div className="date">
                  {moment(this.props.job.get('date_invoke'))
                    .local()
                    .format('MMM DD YYYY')}
                </div>
              </LimitlessTooltip>
            </div>
          </div>
        </div>
      );
    }

    return (
      <div className={`${this.props.className} ${this.jobStatus()}`}>
        <Link to={`/jobs/job?invocation=${this.props.job.get('uuid')}`}>
          <div className="first-row">
            <LimitlessTooltip
              placement="bottom-start"
              title={
                <>
                  <p>Invocation: {this.props.job.get('process')}</p>
                  {this.props.job.get('schedule') && (
                    <p>Schedule: {this.props.job.get('schedule')}</p>
                  )}
                </>
              }
            >
              <div className="name">{this.formatName()}</div>
            </LimitlessTooltip>
            <LimitlessTooltip placement="bottom-start" title="MAS system time (UTC)">
              <div className="time">
                {moment(this.props.job.get('date_invoke')).format('HH:mm:ss')}
              </div>
            </LimitlessTooltip>
          </div>
          <div className="second-row">
            {this.props.job.get('user_owner') && (
              <div className="run-by">
                <AccountCircleOutlinedIcon />
                {this.props.job.get('user_owner')}
              </div>
            )}
            {this.props.job.get('connections') &&
              this.props.job.get('connections').get('dbms') &&
              this.props.job
                .get('connections')
                .get('dbms')
                .get('name') && (
                <div className="dbms">
                  <SvgIcon>
                    <Icon path={mdiHexagonMultipleOutline} />
                  </SvgIcon>
                  {this.props.job
                    .get('connections')
                    .get('dbms')
                    .get('name')}
                </div>
              )}
            {this.props.job.get('connections') &&
              this.props.job.get('connections').get('shell') &&
              this.props.job
                .get('connections')
                .get('shell')
                .get('host_address') && (
                <div className="server">
                  <StorageIcon />
                  {this.props.job.get('connections') &&
                    this.props.job.get('connections').get('shell') &&
                    this.props.job
                      .get('connections')
                      .get('shell')
                      .get('host_address')}
                </div>
              )}
            {this.props.job.get('connections') &&
              this.props.job.get('connections').get('dbms') &&
              this.props.job
                .get('connections')
                .get('dbms')
                .get('protocol_special') &&
              this.props.job
                .get('connections')
                .get('dbms')
                .get('protocol_special')
                .get('database') && (
                <div className="database">
                  <SvgIcon>
                    <Icon path={mdiDatabase} />
                  </SvgIcon>
                  {this.props.job.get('connections') &&
                    this.props.job.get('connections').get('dbms') &&
                    this.props.job
                      .get('connections')
                      .get('dbms')
                      .get('protocol_special')
                      .get('database')}
                </div>
              )}
            <LimitlessTooltip placement="bottom-start" title="MAS system time (UTC)">
              <div className="date">
                {moment(this.props.job.get('date_invoke')).format('MMM DD YYYY')}
              </div>
            </LimitlessTooltip>
          </div>
        </Link>
      </div>
    );
  }

  jobStatus = () => {
    switch (this.props.job.get('status')) {
      case 'EXECUTING':
        return 'executing';
      case 'SUCCEEDED':
        return 'succeeded';
      case 'SCHEDULED':
        return 'scheduled';
      case 'DELAYED':
        return 'delayed';
      case 'ABORTED':
        return 'aborted';
      case 'FAILED':
        return 'failed';
      case 'KILLED':
        return 'killed';
      default:
        return '';
    }
  };

  formatName = () => {
    let name = this.props.job.get('process');
    const nameContainerWidth =
      (document.getElementById('jobs-content') &&
        document.getElementById('jobs-content').offsetWidth -
          (5 / 100) * document.getElementById('jobs-content').offsetWidth) ||
      600; // time container
    const starPath = <span className="word cutted">.../</span>;
    const formattedName = [];
    name = name.split('.');
    for (let i = 0; i < name.length; i++) {
      if (i < name.length - 1) {
        formattedName.push(
          <span key={i}>
            <span className="word">{name[i]}</span>.
          </span>
        );
      } else {
        formattedName.push(
          <span key={i} className="word">
            {name[i]}
          </span>
        );
      }
    }

    const canvas =
      this.formatName.canvas || (this.formatName.canvas = document.createElement('canvas'));
    const context = canvas.getContext('2d');
    let metrics = context.measureText(formattedName);

    if (metrics.width > nameContainerWidth) {
      // add '../' as the first word in the name
      formattedName.unshift(starPath);
    }

    while (metrics.width > nameContainerWidth) {
      // remove first word from the name (it is ../)
      formattedName.shift();
      // remove first word form the actual name
      formattedName.shift();
      // add '../' as the first word in the name
      formattedName.unshift(starPath);
      // recheck width
      metrics = context.measureText(formattedName);
    }

    return formattedName;
  };
}

JobItemComponent.propTypes = {
  className: PropTypes.string,
  job: PropTypes.object.isRequired,
  extended: PropTypes.bool,
  statusChild: PropTypes.element
};

JobItemComponent.defaultProps = {
  className: 'job-item',
  extended: false,
  statusChild: null
};
