import React, { useState, useRef } from 'react';
import { connect } from 'react-redux';
import { Formik, Form } from 'formik';

import { getNamespace, patchNamespace } from 'shared/api/namespace';
import { Switch } from 'shared/components';
import { Button } from 'shared/components/controls';
import { NamespaceSelect } from 'shared/components/form/editors/Custom';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Result } from 'shared/components/overlays/Result/Result';
import { SubmitButton } from 'shared/components/form/SubmitButton';
import { Paper, Section, Row, Column } from 'shared/components/layout';

import { Text } from 'shared/components/form/editors/Generic';

import SvgIcon from '@material-ui/core/SvgIcon';
import FolderIcon from '@material-ui/icons/Folder';

import { fetchEntity } from '../../actions';
import { namespaceChildrenListSelector } from '../../selectors';

function parseName(namespace) {
  const split = namespace.name.split('.');
  const tail = split.length - 1;
  const parsed = {
    name: split[tail],
    fq: namespace.name
  };

  if (split.length > 1) {
    parsed.parent = split.slice(0, tail).join('.');
  } else {
    parsed.parent = '';
  }

  return parsed;
}

export const ConnectedEditNamespace = connect(
  state => ({
    nestedNames: namespaceChildrenListSelector(state)
  }),
  {
    request: fetchEntity
  }
)(EditNamespace);

function EditNamespace({ namespace, request, nestedNames }) {
  const canUpdateName = nestedNames.length === 0;
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };
  const [result, setResult] = useState('');
  const [description, setDescription] = useState('');
  const formRef = useRef();
  const split_name = parseName(namespace);

  const getInitialValues = () => {
    const values = {};

    values.name = split_name.name || '';
    values.namespace = split_name.parent;
    values.description = description;

    return values;
  };

  const getNamespaceDetails = name => {
    getNamespace(name)
      .then(data => {
        if (
          data.data.data &&
          data.data.data.namespaces &&
          data.data.data.namespaces[0] &&
          data.data.data.namespaces[0].description
        ) {
          setDescription(data.data.data.namespaces[0].description);
          setOpen(true);
        }
      })
      .catch(error => {
        console.log('[get namespace] error getting namespace details: ', error); // eslint-disable-line
      });
  };
  const isDisabled = namespace?.meta?.appearance === 'Greyed';

  return (
    <>
      <Button
        disabled={isDisabled}
        classes={{ root: 'entity-button' }}
        onClick={() => {
          getNamespaceDetails(namespace.name);
        }}
      >
        Edit
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            minWidth: '600px',
            minHeight: '600px'
          }}
        >
          <ModalHeader
            title="Edit Namespace"
            icon={
              <SvgIcon>
                <FolderIcon />
              </SvgIcon>
            }
            formikRef={formRef}
          />
          <Switch value={result}>
            <Switch.Case value="success">
              <Result type="success">
                <Result.Text>Namespace was edited successfully!</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                      setResult('');
                    }}
                  >
                    Done
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="fail">
              <Result type="fail">
                <Result.Text>An error occurred and the namespace could not be edited.</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                      setResult('');
                    }}
                  >
                    Close
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="">
              <Formik
                initialValues={getInitialValues()}
                onSubmit={values => {
                  handleSubmit(setResult, request, values, split_name);
                }}
                innerRef={formRef}
              >
                {({ values }) => {
                  return (
                    <>
                      <Paper.Content>
                        <Form>
                          <Section>
                            <Section.Header />
                            <Row>
                              {canUpdateName && (
                                <Text name="name" label="Name" value={values.name} required />
                              )}
                              {!canUpdateName && (
                                <Text
                                  name="name"
                                  label="Name"
                                  value={values.name}
                                  required
                                  disabled
                                  description="To update a namespace name, namespace must be empty!"
                                />
                              )}
                            </Row>
                            <Row>
                              <Column xs={12}>
                                {canUpdateName && (
                                  <NamespaceSelect
                                    name="namespace"
                                    label="Parent namespace"
                                    value={values.namespace}
                                  />
                                )}
                                {!canUpdateName && (
                                  <NamespaceSelect
                                    name="namespace"
                                    label="Parent namespace"
                                    value={values.namespace}
                                    disabled
                                    description="To update a namespace parent name, namespace must be empty!"
                                  />
                                )}
                              </Column>
                            </Row>
                            <Row>
                              <Text
                                name="description"
                                label="Description"
                                value={values.description}
                                multiline
                                required
                              />
                            </Row>
                          </Section>
                        </Form>
                      </Paper.Content>
                      <Paper.Footer>
                        <Paper.Footer.Right>
                          <SubmitButton variant="contained">Save</SubmitButton>
                        </Paper.Footer.Right>
                      </Paper.Footer>
                    </>
                  );
                }}
              </Formik>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
}

async function handleSubmit(setResult, request, values, split_name) {
  const name = values.namespace ? `${values.namespace}.${values.name}` : values.name;
  const body = {
    description: values.description
  };

  /*
    If the old entity fully-qualified name does not match with the form value,
    then we need to add a rename key with the new name.
   */
  if (name !== split_name.fq) {
    body.rename = name;
  }

  patchNamespace(split_name.fq, body)
    .then(() => {
      setResult('success');
      request('namespace');
    })
    .catch(error => {
      console.log('[edit namespace] patch namespace error: ', error); // eslint-disable-line
      setResult('fail');
    });
}
