import { object, func } from 'prop-types';
import { connect } from 'react-redux';
import _ from 'lodash';

import { Formik, Field, Form } from 'formik';
import * as Yup from 'yup';

import GroupAddIcon from '@material-ui/icons/GroupAdd';
import Avatar from '@material-ui/core/Avatar';
import { CONSTANTS } from 'config';
import { RestClient } from 'shared/lib/rest';

import { Center } from 'shared/components';
import { Button, Input } from 'shared/components/controls';

import { Paper, Row } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';
import { Modal, ModalHeader, Result } from 'shared/components/overlays';

