export const EditUserInfo = ({ user }) => {
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };

  const formRef = useRef();

  return (
    <>
      <Button color="primary" onClick={modalActions.open}>
        Contact Info
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            width: '392px',
            maxWidth: '392px',
            minHeight: '632px'
          }}
        >
          <ModalHeader
            avatar={
              <Avatar className="generic-avatar" alt="edit-user-groups">
                <GroupAddIcon />
              </Avatar>
            }
            title="Contact Information"
            formikRef={formRef}
          />
          <UserInfoForm user={user} modalActions={modalActions} formRef={formRef} />
        </Paper>
      </Modal>
    </>
  );
};

EditUserInfo.propTypes = {
  user: object
};

export const EditUserInfoContainer = connect(
  state => {
    const user = selectedUser(state);

    return { user };
  },
  () => ({})
)(EditUserInfo);

function selectedUser(state) {
  return state.usersManager.users.selected;
}

export const UserInfoForm = ({ user, modalActions, formRef }) => {
  const [state, setState] = useState({ loading: true });

  useEffect(() => {
    const formEndpoint = `${CONSTANTS.BASE.ENDPOINTS.FORM}/mas0000.user.${user.name}.form.user_information`;
    const prototypeEndpoint = 'prototype/mas0300.sys.user.information?version=1';

    RestClient.get(prototypeEndpoint).then(formproto => {
      const proto = formproto.data.data.prototypes[0];

      RestClient.get(formEndpoint)
        .then(formdata => {
          setState({
            loading: false,
            result: 'success',
            create: false,
            data: formdata.data.data.forms[0],
            proto
          });
        })
        .catch(() => {
          setState({
            loading: false,
            result: 'success',
            create: true,
            proto
          });
        });
    });
  }, []);

  if (state.loading) {
    return (
      <>
        <Center>
          <Loader />
        </Center>
        <Paper.Footer />
      </>
    );
  }

  if (state.submitResult === 'success') {
    return (
      <>
        <Center>
          <Result type="success">
            <Result.Text>Updated user information!</Result.Text>
          </Result>
        </Center>
        <Paper.Footer>
          <Paper.Footer.Right>
            <Button variant="contained" color="primary" onClick={modalActions.close}>
              Done
            </Button>
          </Paper.Footer.Right>
        </Paper.Footer>
      </>
    );
  }

  if (state.submitResult === 'fail') {
    return (
      <>
        <Center>
          <Result type="fail">
            <Result.Text>Could not update user information!</Result.Text>
          </Result>
        </Center>
        <Paper.Footer>
          <Paper.Footer.Left>
            <Button color="primary" onClick={modalActions.close}>
              Cancel
            </Button>
          </Paper.Footer.Left>
          <Paper.Footer.Right>
            <Button
              variant="contained"
              color="primary"
              onClick={() => {
                setState(prev => ({ ...prev, submitResult: undefined }));
              }}
            >
              Retry
            </Button>
          </Paper.Footer.Right>
        </Paper.Footer>
      </>
    );
  }

  if (state.result === 'success') {
    const { fields, values } = mapFormValues(state.proto, state.data);

    return (
      <>
        <Formik
          validateOnChange={false}
          initialValues={values}
          onSubmit={(submited, actions) => {
            if (_.isEqual(values, submited)) {
              actions.setSubmitting(false);
              modalActions.close();
            }

            const sendable_data = fields.reduce((d, entry) => {
              /** Empty values have to be nulled to be sent in the data :( */
              let value = submited[entry.name] || null;

              /**
                TODO: backend accepts array for is_repeatable values
               */
              if (entry.is_repeatable && !Array.isArray(value)) {
                value = [value];
              }

              d.push({
                name: entry.label, // for this particular form, the names contain spaces.
                value
              });

              return d;
            }, []);
            // parse and add user information form data.

            /**
            Send user.info data to backend
            */
            const formEndpoint = `${CONSTANTS.BASE.ENDPOINTS.FORM}/mas0000.user.${user.name}.form.user_information`;

            if (state.create) {
              const body = {
                name: `mas0000.user.${user.name}.form.user_information`,
                description: `User information for ${user.name}`,
                values: sendable_data,
                prototype: state.proto.name
              };

              RestClient.post(`${CONSTANTS.BASE.ENDPOINTS.FORM}`, body)
                .then(() => {
                  setState(prev => ({ ...prev, submitResult: 'success' }));
                })
                .catch(() => {
                  actions.setSubmitting(false);

                  setState(prev => ({ ...prev, submitResult: 'fail' }));
                });
            } else {
              RestClient.patch(formEndpoint, {
                description: state.data.description,
                rename: state.data.name,
                values: sendable_data
              })
                .then(() => {
                  setState(prev => ({ ...prev, submitResult: 'success' }));
                })
                .catch(() => {
                  actions.setSubmitting(false);

                  setState(prev => ({ ...prev, submitResult: 'fail' }));
                });
            }
          }}
          innerRef={formRef}
        >
          {({ dirty, isSubmitting, submitForm }) => (
            <>
              <Paper.Content scrollable>
                <Form style={{ paddingTop: '16px' }}>
                  {fields.map(entry => (
                    <Row key={entry.name}>
                      <FieldSwitch parameter={entry} />
                    </Row>
                  ))}
                </Form>
              </Paper.Content>
              <Paper.Footer>
                <Paper.Footer.Left>
                  <Button color="primary" onClick={modalActions.close}>
                    Cancel
                  </Button>
                </Paper.Footer.Left>
                <Paper.Footer.Right>
                  <Button
                    variant="contained"
                    color="primary"
                    disabled={!dirty || isSubmitting}
                    onClick={submitForm}
                  >
                    Save
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </>
          )}
        </Formik>
      </>
    );
  }
};

function mapFormValues(proto, provided) {
  const { fields } = proto;
  const data = provided && provided.values;

  return fields.reduce(
    (dict, entry, index) => {
      const normalized = entry.name.replace(' ', '_');
      const value = data && data[index].value;

      if (entry.is_repeatable && Array.isArray(entry.value)) {
        /**
          TODO: array values have to be unpacked
         */
        dict.values[normalized] = value ? value[0] : '';
      } else {
        dict.values[normalized] = value || '';
      }

      dict.fields.push({
        ...entry,
        name: normalized,
        label: entry.name
      });

      return dict;
    },
    { values: {}, fields: [] }
  );
}

export const TextField = ({ parameter, validate: propsValidate }) => {
  const defaultValidation = value => {
    const schema = Yup.string();

    if (parameter.is_required) {
      schema.required('Field is required');
    }

    try {
      schema.validateSync(value);
    } catch (e) {
      return e.message;
    }
  };

  const validate = propsValidate || defaultValidation;

  return (
    <Field name={parameter.name} validate={validate}>
      {({ field, meta }) => {
        return (
          <Input
            {...field}
            type="text"
            error={meta.error && meta.touched}
            helperText={meta.touched ? meta.error : undefined}
            required={parameter.is_required}
            label={parameter.label}
          />
        );
      }}
    </Field>
  );
};

TextField.propTypes = {
  parameter: object,
  validate: func
};

export const EmailField = ({ parameter, validate: propsValidate }) => {
  const defaultValidation = value => {
    let schema = Yup.string().email('Please input a valid Email');

    if (parameter.is_required) {
      schema = schema.required('Field is required');
    }

    schema = schema.email('Please provide a valid email adress');

    try {
      schema.validateSync(value);
    } catch (e) {
      return e.message;
    }
  };

  const validate = propsValidate || defaultValidation;

  return (
    <Field name={parameter.name} validate={validate}>
      {({ field, meta }) => {
        return (
          <Input
            {...field}
            type="text"
            error={meta.error && meta.touched}
            helperText={meta.touched ? meta.error : undefined}
            required={parameter.is_required}
            label={parameter.label}
          />
        );
      }}
    </Field>
  );
};

EmailField.propTypes = {
  parameter: object,
  validate: func
};

export const PhoneNumberField = ({ parameter }) => {
  const validate = value => {
    let schema = Yup.string();

    if (parameter.is_required) {
      schema = schema.required('Field is required');
    }

    schema = schema
      .notRequired()
      .matches(/^(?:\+?1)? *(?:\( *[0-9]{3} *\)|[0-9]{3}(?: *-|)) *[0-9]{3} *- *[0-9]{4}$/, {
        excludeEmptyString: true,
        message: 'A valid phone number of form xxx-xxx-xxxx is required.'
      });

    try {
      schema.validateSync(value);
    } catch (e) {
      return e.message;
    }
  };

  return (
    <Field name={parameter.name} validate={validate}>
      {({ field, meta }) => {
        return (
          <Input
            {...field}
            type="text"
            error={meta.error && meta.touched}
            helperText={meta.touched ? meta.error : undefined}
            required={parameter.is_required}
            label={parameter.label}
          />
        );
      }}
    </Field>
  );
};

PhoneNumberField.propTypes = {
  parameter: object
};

const Field_Types = {
  string: TextField,
  'mas0000.sys.phone_number': PhoneNumberField,
  'mas0000.sys.email_address': EmailField
};

export class FieldSwitch extends React.PureComponent {
  render() {
    const FieldComponent = this.getComponent();

    return <>{FieldComponent && <FieldComponent {...this.props} />}</>;
  }

  getComponent() {
    const { parameter } = this.props;
    const { data_type } = parameter;

    return Field_Types[data_type];
  }
}

FieldSwitch.propTypes = {
  parameter: object
};
