import React from 'react';
import { bool, node } from 'prop-types';
import classnames from 'classnames';

import './Section.local.scss';

export const Section = ({ children, full }) => (
  <section
    className={classnames('section-block', {
      full
    })}
  >
    {children}
  </section>
);

Section.propTypes = {
  children: node,
  full: bool
};

export const SectionTitle = ({ children, info }) => (
  <>
    <h3 className="section-title">{children}</h3>
    {info && <h4 className="section-title-info">{info}</h4>}
  </>
);

SectionTitle.propTypes = {
  children: node,
  info: node
};

export const SectionHeader = ({ children }) => (
  <>
    <header className="section-header">{children}</header>
  </>
);

SectionHeader.propTypes = {
  children: node
};

export const SectionInfo = ({ children }) => (
  <>
    <h4 className="section-title-info">{children}</h4>
  </>
);

SectionInfo.propTypes = {
  children: node
};

export const SectionContent = ({ children }) => (
  <>
    <main className="section-content">{children}</main>
  </>
);

Section.Header = SectionHeader;
Section.Content = SectionContent;
Section.Title = SectionTitle;
Section.Info = SectionInfo;
