import { put, takeEvery } from '@redux-saga/core/effects';

import { actions } from 'shared/store/actions';
import { RestClient } from 'shared/lib/rest';
import { CONSTANTS } from 'config';

function* searchProcesses(query) {
  try {
    const response = yield RestClient.get(CONSTANTS.BASE.ENDPOINTS.PROCESSES, query.payload);

    yield put(actions.searchProcesses.dispatchSuccess(response.data));
  } catch (errors) {
    yield put(actions.searchProcesses.dispatchFail(errors));
  }
}

export function* SearchProcessesEffect() {
  yield takeEvery(actions.searchProcesses.getLoadingAction(), searchProcesses);
}
