import React, { useState, useRef } from 'react';
import { connect } from 'react-redux';

import { Formik, Form } from 'formik';

import { fetchEntity } from '../actions';
import { postForm } from 'shared/api/form';
import { Switch } from 'shared/components';
import { Button } from 'shared/components/controls';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Result } from 'shared/components/overlays/Result/Result';
import { SubmitButtonNoDirty } from 'shared/components/form/SubmitButtonNoDirty';
import { Paper, Section, Row } from 'shared/components/layout';

import { Text } from 'shared/components/form/editors/Generic';

import { Ensure } from 'core/components/Ensure';

import { ConnectedFormModal } from './form-actions/FormModal';

import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiClipboardCheck } from '@mdi/js';

import { hasWritePrivileges } from '../utils/privileges';

function findFormPrototype(form, prototypes) {
  const name = form.prototype;
  const found = prototypes.find(item => item.name === name);

  return found;
}

function findFormEntry(entity, forms) {
  if (entity.alias) {
    return forms.find(entry => entry.name === entity.alias);
  }

  return entity;
}

const ActionsForm = ({ entity }) => {
  const disableActions = entity?.meta?.appearance === 'Greyed';
  const canWrite = hasWritePrivileges(entity);
  return (
    <>
      {canWrite && (
        <Ensure type="prototype" inlineLoader>
          {({ data: prototypes }) => (
            <Ensure type="form" inlineLoader>
              {({ data: forms }) => (
                <ConnectedFormModal
                  disabled={disableActions}
                  form={findFormEntry(entity, forms)}
                  prototype={findFormPrototype(entity, prototypes)}
                />
              )}
            </Ensure>
          )}
        </Ensure>
      )}
      <ConnectedCopyForm form={entity} />
    </>
  );
};

export default ActionsForm;

export const ConnectedCopyForm = connect(null, {
  request: fetchEntity
})(CopyForm);

function CopyForm({ form, request }) {
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };
  const formRef = useRef();
  const [result, setResult] = useState('');
  const disableActions = form?.meta?.appearance === 'Greyed';

  return (
    <>
      <Button
        disabled={disableActions}
        classes={{ root: 'entity-button' }}
        onClick={() => {
          setOpen(true);
        }}
      >
        Copy
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            minWidth: '600px',
            minHeight: '300px'
          }}
        >
          <ModalHeader
            title="Copy Form"
            icon={
              <SvgIcon>
                <path d={mdiClipboardCheck} />
              </SvgIcon>
            }
            formikRef={formRef}
          />
          <Switch value={result}>
            <Switch.Case value="success">
              <Result type="success">
                <Result.Text>Form was copied successfully!</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                      setResult('');
                    }}
                  >
                    Done
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="fail">
              <Result type="fail">
                <Result.Text>An error occurred and the form could not be copied.</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                      setResult('');
                    }}
                  >
                    Close
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="">
              <Formik
                initialValues={{ name: `${form.name}_copy` }}
                onSubmit={values => {
                  handleSubmit(setResult, request, form.name, values);
                }}
                innerRef={formRef}
              >
                {() => {
                  return (
                    <>
                      <Paper.Content>
                        <Form>
                          <Section>
                            <Section.Header>
                              Please choose a new name for the copy of form &apos;{form.name}&apos;.
                            </Section.Header>
                            <Row>
                              <Text
                                name="name"
                                label="Name"
                                value={`${form.name}_copy`}
                                description={form.description}
                              />
                            </Row>
                          </Section>
                        </Form>
                      </Paper.Content>
                      <Paper.Footer>
                        <Paper.Footer.Right>
                          <SubmitButtonNoDirty variant="contained">Save</SubmitButtonNoDirty>
                        </Paper.Footer.Right>
                      </Paper.Footer>
                    </>
                  );
                }}
              </Formik>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
}

async function handleSubmit(setResult, request, formName, values) {
  const body = {
    name: values.name
  };

  postForm(formName, body)
    .then(() => {
      setResult('success');
      request('form');
    })
    .catch(error => {
      console.log('[copy form] put form error: ', error); // eslint-disable-line
      setResult('fail');
    });
}
