import React, { useState, useRef } from 'react';
import { connect } from 'react-redux';

import { fetchEntity } from '../../actions';

import { Switch } from 'shared/components';
import { Button } from 'shared/components/controls';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Result } from 'shared/components/overlays/Result/Result';
import { Paper } from 'shared/components/layout';

import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiClipboardCheck } from '@mdi/js';

import { FormEditor } from './FormEditor';

export const ConnectedFormModal = connect(null, {
  refresh: fetchEntity
})(FormModal);

function FormModal({ disabled, form = null, prototype, refresh }) {
  const isNew = form === null;
  const [open, setOpen] = useState(false);
  const [result, setResult] = useState('use_form');
  const formRef = useRef();
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setResult('use_form');
      setOpen(false);
      // should refresh be here as well?
      // refresh('form');
    }
  };

  return (
    <>
      <Button disabled={disabled} classes={{ root: 'entity-button' }} onClick={modalActions.open}>
        {isNew ? 'New Form' : 'Edit'}
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            minWidth: '600px',
            minHeight: '368px',
            maxHeight: '80vh'
          }}
        >
          <ModalHeader
            title={`${isNew ? 'New' : 'Edit'} Form`}
            icon={
              <SvgIcon>
                <path d={mdiClipboardCheck} />
              </SvgIcon>
            }
            formikRef={formRef}
          />
          <Switch value={result}>
            <Switch.Case value="use_form">
              <FormEditor
                isNew={isNew}
                form={form}
                prototype={prototype}
                setResult={setResult}
                formRef={formRef}
              />
            </Switch.Case>
            <Switch.Case value="success">
              <Result type="success">
                <Result.Text>Form was {isNew ? 'created' : 'updated'} successfully!</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                      refresh('form');
                    }}
                  >
                    Done
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="fail">
              <Result type="fail">
                <Result.Text>An error occurred and the form could not be saved.</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                    }}
                  >
                    Close
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
}
