import React, { useState, useEffect, useRef } from 'react';
import { connect } from 'react-redux';

import { Modal, ModalHeader, Result } from 'shared/components/overlays';

import { Switch } from 'shared/components';

import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiDatabase } from '@mdi/js';

import Button from '@material-ui/core/Button';

import { Loader } from 'shared/components/loaders';

import { Paper, Center } from 'shared/components/layout';

import { fetchProtocols } from 'shared/api/protocols';
import { fetchDataTypes } from 'shared/api/datatypes';
import { loadAllAccountsThunk } from '../../store/thunks';

import { DatabaseForm } from '../Forms/DatabaseForm';

import './AddDatabaseCard.local.scss';

export const AddDatabaseCard = ({ disabled }) => {
  const [open, setOpen] = useState(false);

  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };

  return (
    <>
      <Button className="action-btn" onClick={modalActions.open} disabled={disabled}>
        Add New Database
      </Button>
      <Modal open={open} actions={modalActions}>
        <AddNewDatabaseContainer />
      </Modal>
    </>
  );
};

export const AddNewDatabase = ({ account, reloadAccounts }) => {
  const [state, setState] = useState({ loading: true, protocols: [] });
  const formRef = useRef();

  useEffect(() => {
    // result and protocols provided by fetch
    Promise.all([
      fetchProtocols(),
      fetchDataTypes('mas0300.sys.account.privilege_mode', true)
    ]).then(([pr, mr]) => {
      if (pr.result === 'success' && mr.result === 'success') {
        setState({
          loading: false,
          result: 'success',
          protocols: pr.protocols,
          modes: mr.data_types[0]?.enumerations
        });
      } else {
        setState({
          loading: false,
          result: 'fail',
          protocols: [],
          modes: []
        });
      }
    });
  }, []);

  return (
    <Paper
      style={{
        width: '75vw',
        height: '75vh'
      }}
    >
      <ModalHeader
        icon={
          <SvgIcon>
            <path d={mdiDatabase} />
          </SvgIcon>
        }
        title="Add New Database"
        desc="Input the database connection information"
        formikRef={formRef}
      />

      <Switch value={state.loading}>
        <Switch.Case value>
          <Center>
            <Loader>Loading Required Protocols and Privilege Modes</Loader>
          </Center>
        </Switch.Case>
        <Switch.Case value={false}>
          <Switch value={state.result}>
            <Switch.Case value="success">
              <DatabaseForm
                protocols={state.protocols}
                modes={state.modes}
                account={account}
                handleSubmission={reloadAccounts}
                isNew
                formRef={formRef}
              />
            </Switch.Case>
            <Switch.Case value="fail">
              <Center>
                <Result type="fail">
                  <Result.Text>
                    Failed to load required protocols or privilege modes list.
                  </Result.Text>
                </Result>
              </Center>
              <Paper.Footer></Paper.Footer>
            </Switch.Case>
          </Switch>
        </Switch.Case>
      </Switch>
    </Paper>
  );
};

export const AddNewDatabaseContainer = connect(
  state => () => {
    const path = state.explorer.state.path.split('--');
    return {
      account: {
        protocol: path[0],
        address: path[1]
      }
    };
  },
  dispatch => ({
    reloadAccounts: () => dispatch(loadAllAccountsThunk())
  })
)(AddNewDatabase);
