import React from 'react';
import { array } from 'prop-types';
import { ConnectedDBMS } from './layers/Dbms/Dbms';

import './root.local.scss';

export class Treeview extends React.PureComponent {
  render() {
    const { data } = this.props;

    return (
      <ul className="treeview-container">
        {data.map(dbms => (
          <ConnectedDBMS key={dbms.protocol} data={dbms} />
        ))}
      </ul>
    );
  }
}

Treeview.propTypes = {
  data: array
};

export default Treeview;
