import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import classnames from 'classnames';
import { arrayOf, node, string, shape } from 'prop-types';

import './HeaderPageSwitch.local.scss';

export const HeaderPageSwitch = ({ routes }) => {
  const location = useLocation();

  return (
    <div className="header-page-switch" data-qa="header-page-switch">
      {routes.map(({ to, label }) => (
        <div
          key={to}
          className={classnames('header-page-switch-link', {
            'active-link': location.pathname.includes(to)
          })}
        >
          <Link key={to} to={to} data-qa={`header-page-switch-link-to-${to}`}>
            {label}
          </Link>
        </div>
      ))}
    </div>
  );
};

HeaderPageSwitch.propTypes = {
  routes: arrayOf(
    shape({
      to: string,
      label: node
    })
  )
};

HeaderPageSwitch.defaultProps = {
  routes: []
};
