import React, { useState, useEffect, useContext } from 'react';

import Tooltip from '@material-ui/core/Tooltip';

import { Modal, ModalHeader, ModalContext, Result } from 'shared/components/overlays';
import { Paper, Center } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';
import { Button } from 'shared/components/controls';
import { Switch } from 'shared/components';

import VideoLibraryOutlinedIcon from '@material-ui/icons/VideoLibraryOutlined';
import WarningIcon from '@material-ui/icons/Warning';
import { postCompile } from 'shared/api/compile';

import { fetchEntity } from '../../../../../entities/actions';

import './compile.local.scss';
import { useDispatch } from 'react-redux';

export const Compile = () => {
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: () => setOpen(true),
    close: () => {
      setOpen(false);
      setStep('confirmation');
    }
  };
  const [step, setStep] = useState('confirmation');

  return (
    <>
      <div className="compile">
        <Tooltip title="Compile">
          <VideoLibraryOutlinedIcon onClick={modalActions.open} />
        </Tooltip>
      </div>
      <Modal open={open} actions={modalActions}>
        {step === 'confirmation' && <Confirmation setStep={setStep} />}
        {step === 'compiler' && <Compiler />}
      </Modal>
    </>
  );
};

const Confirmation = ({ setStep }) => {
  const { actions } = useContext(ModalContext);

  return (
    <Paper
      style={{
        minWidth: '350px',
        maxHeight: '368px',
        maxWidth: '65vw'
      }}
    >
      <ModalHeader title="Warning" icon={<WarningIcon />} />
      <Paper.Content scrollable={false}>
        <Result type="fail">
          <BodyText>
            <p style={{ fontWeight: 'normal' }}>
              This command will recompile all the processes and refresh the process list. This
              usually takes about 1 to 2 minutes.
            </p>
            <p style={{ textAlign: 'center' }}>Do you want to proceed?</p>
          </BodyText>
        </Result>
      </Paper.Content>
      <Paper.Footer>
        <Paper.Footer.Left>
          <Button
            onClick={() => {
              actions.close();
            }}
          >
            No
          </Button>
        </Paper.Footer.Left>
        <Paper.Footer.Right>
          <Button
            onClick={() => {
              setStep('compiler');
            }}
          >
            Yes
          </Button>
        </Paper.Footer.Right>
      </Paper.Footer>
    </Paper>
  );
};

const Compiler = () => {
  const [compilation, setCompilation] = useState('compiling');
  const dispatch = useDispatch();

  const { actions } = useContext(ModalContext);

  useEffect(() => {
    console.log('[started compilation'); // eslint-disable-line
    postCompile()
      .then(() => {
        setCompilation('refreshing');

        Promise.all([dispatch(fetchEntity('process')), dispatch(fetchEntity('namespace'))])
          .then(() => {
            setCompilation('success');
            console.log('[succeded compilation'); // eslint-disable-line
          })
          .catch(() => {
            setCompilation('success-no-reload');
            console.log(results, '[succeded compilation. failed to reload entities.'); // eslint-disable-line
          });
      })
      .catch(() => {
        setCompilation('failed');
        console.log('[failed compilation'); // eslint-disable-line
      });
  }, []);

  return (
    <Paper
      style={{
        minWidth: '430px',
        minHeight: '330px'
      }}
    >
      <ModalHeader icon={<VideoLibraryOutlinedIcon />} title="Compile" />
      <Switch value={compilation}>
        <Switch.Case value="compiling">
          <Center>
            <Loader>Running compilation...</Loader>
          </Center>
          <Paper.Footer>
            <Paper.Footer.Right>
              <Button onClick={actions.close}>Close</Button>
            </Paper.Footer.Right>
          </Paper.Footer>
        </Switch.Case>
        <Switch.Case value="refreshing">
          <Center>
            <Loader>Loading processes...</Loader>
          </Center>
          <Paper.Footer>
            <Paper.Footer.Right>
              <Button onClick={actions.close}>Close</Button>
            </Paper.Footer.Right>
          </Paper.Footer>
        </Switch.Case>
        <Switch.Case value="failed">
          <Center>
            <Result type="fail">
              <Result.Text compact>Compilation failed.</Result.Text>
            </Result>
          </Center>
          <Paper.Footer>
            <Paper.Footer.Right>
              <Button onClick={actions.close}>Close</Button>
            </Paper.Footer.Right>
          </Paper.Footer>
        </Switch.Case>
        <Switch.Case value="success">
          <Center>
            <Result type="success">
              <Result.Text compact>Compilation succeeded.</Result.Text>
            </Result>
          </Center>
          <Paper.Footer>
            <Paper.Footer.Right>
              <Button onClick={actions.close}>Done</Button>
            </Paper.Footer.Right>
          </Paper.Footer>
        </Switch.Case>
        <Switch.Case value="success-no-reload">
          <Center>
            <Result type="success">
              <Result.Text compact>Compilation succeeded</Result.Text>
              <Result.Text compact>Could not refresh processes list.</Result.Text>
            </Result>
          </Center>
          <Paper.Footer>
            <Paper.Footer.Right>
              <Button onClick={actions.close}>Done</Button>
            </Paper.Footer.Right>
          </Paper.Footer>
        </Switch.Case>
      </Switch>
    </Paper>
  );
};

function BodyText({ children }) {
  return (
    <h2
      style={{
        fontFamily: 'Open Sans',
        fontStyle: 'normal',
        fontWeight: 'bold',
        fontSize: '14px',
        lineHeight: '20px',
        letterSpacing: '0.25px',
        marginTop: '24px'
      }}
    >
      {children}
    </h2>
  );
}
