import React from 'react';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiDns } from '@mdi/js';

import './UsageEntry.local.scss';

export const UsageEntry = ({ server }) => {
  const { dbms, address, usage } = server;

  const getServerUsageClass = () => {
    if (usage <= 50) {
      return 'low-usage';
    }

    if (usage > 50 && usage <= 75) {
      return 'medium-usage';
    }

    return 'high-usage';
  };

  return (
    <div className={`server-usage-item ${getServerUsageClass()}`}>
      <div className="icon-container">
        <SvgIcon>
          <Icon path={mdiDns} />
        </SvgIcon>
      </div>
      <div className="name-container">
        <span className="name">
          <span className="dbms">{dbms}</span>
          <span className="separator">/</span>
          <span className="server">{address}</span>
        </span>
        <div className="full-percentage">
          <div className="filled" style={{ width: `${usage}%` }} />
        </div>
      </div>
      <div className="percent-container">
        <span>{usage}%</span>
      </div>
    </div>
  );
};
