import React, { useState, useCallback } from 'react';
import { connect } from 'react-redux';

import { Switch, Center } from 'shared/components';
import { Paper, Section } from 'shared/components/layout';
import { Button } from 'shared/components/controls';
import { Result } from 'shared/components/overlays/Result/Result';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Loader } from 'shared/components/loaders';

import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiTrashCan } from '@mdi/js';

import { PrivilegesContainer } from './containers/Privileges';
import { UserGroupsContainer } from './containers/UserGroups';

import './UserView.local.scss';
import { deactivateUserThunk } from '../../../store/thunks';
import { EditUserDetailsContainer } from './containers/EditUserDetails';
import { EditUserInfoContainer } from './containers/EditUserInfo';

import { currentUserSelector } from 'session/store/state';
import { getCurrentUserDetails } from 'session/store/selectors';

import { isAdmin } from 'entities/utils/privileges';

export const UserView = ({ user, deactivateUser, deactivating, loggedUser, userDetails }) => {
  const defaultResult = () => {
    if (loggedUser.get('name') === user?.name) {
      return 'self';
    }

    if (!isAdmin(userDetails)) {
      return 'not-admin';
    }

    return 'attempt';
  };
  const [result, setResult] = useState(defaultResult);
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: useCallback(() => {
      setOpen(true);
    }, []),
    close: useCallback(() => {
      setResult(defaultResult);
      setOpen(false);
    }, [])
  };
  const noGroups = user?.member_of === null || user?.member_of?.length === 0;
  const deactivate = useCallback(() => {
    deactivateUser(user)
      .then(() => {
        setResult('attempt');
        setOpen(false);
      })
      .catch(() => {
        setResult('attempt');
        setOpen(false);
        console.log('Could not deactivate user!'); // eslint-disable-line
      });
  }, [user]);
  const titleStyles = { textTransform: 'none' };

  if (noGroups) {
    titleStyles.fontStyle = 'italic';
  }

  /* This needs to be written as a result check or 404 page for a user. */
  if (!user) {
    return <></>;
  }

  return (
    <>
      <Paper flex="column">
        <Paper.Header>
          <Paper.Title style={titleStyles}>{user.name}</Paper.Title>
        </Paper.Header>
        <Paper.Content>
          <Section>
            <Section.Title>Global Privileges</Section.Title>
            <PrivilegesContainer />
          </Section>
          <Section>
            <Section.Title>Member of Groups</Section.Title>
            <UserGroupsContainer />
          </Section>
        </Paper.Content>
        <Paper.Footer>
          <Paper.Footer.Left>
            {user?.name !== 'mas' && (
              <Button
                color="secondary"
                disabled={deactivating || noGroups}
                onClick={modalActions.open}
              >
                Deactivate user
              </Button>
            )}
            <EditUserDetailsContainer />
            <EditUserInfoContainer />
          </Paper.Footer.Left>
        </Paper.Footer>
      </Paper>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            minWidth: '400px',
            maxHeight: '368px',
            maxWidth: '65vw'
          }}
        >
          <ModalHeader
            title="Deactivate user"
            icon={
              <SvgIcon>
                <path d={mdiTrashCan} />
              </SvgIcon>
            }
          />
          <Switch value={result}>
            <Switch.Case value="self">
              <Paper.Content scrollable={false}>
                <Result type="fail">
                  <BodyText>You cannot deactivate yourself.</BodyText>
                </Result>
              </Paper.Content>
              <Paper.Footer></Paper.Footer>
            </Switch.Case>
            <Switch.Case value="not-admin">
              <Paper.Content scrollable={false}>
                <Result type="fail">
                  <BodyText>Only admin users can deactivate other users.</BodyText>
                </Result>
              </Paper.Content>
              <Paper.Footer></Paper.Footer>
            </Switch.Case>
            <Switch.Case value="attempt">
              <Paper.Content scrollable={false}>
                <Result type="fail">
                  <BodyText>
                    Are you sure you want to deactivate user {user.name}? This could affect the way
                    the user will be able to interact with the app.
                  </BodyText>
                </Result>
              </Paper.Content>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    color="primary"
                    variant="contained"
                    onClick={() => {
                      setResult('deactivating');
                      deactivate();
                    }}
                  >
                    Yes
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="deactivating">
              <Paper.Content scrollable={false}>
                <Center>
                  <Loader>Deactivating...</Loader>
                </Center>
              </Paper.Content>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
};

export const ConnectedUserView = connect(
  state => ({
    user: state.usersManager.users.selected,
    deactivating: state.usersManager.flags.user.removing,
    loggedUser: currentUserSelector(state),
    userDetails: getCurrentUserDetails(state)
  }),
  dispatch => ({
    deactivateUser: userName => dispatch(deactivateUserThunk(userName))
  })
)(UserView);

function BodyText({ children }) {
  return (
    <h2
      style={{
        fontFamily: 'Open Sans',
        fontStyle: 'normal',
        fontWeight: 'bold',
        fontSize: '14px',
        lineHeight: '20px',
        letterSpacing: '0.25px',
        marginTop: '24px'
      }}
    >
      {children}
    </h2>
  );
}
