import React, { useState, useEffect, useRef } from 'react';
import { connect } from 'react-redux';

import { Modal, ModalHeader } from 'shared/components/overlays';

import { Paper, Center } from 'shared/components/layout';

import { loadAllAccountsThunk } from '../../store/thunks';

import { DatabaseForm } from '../Forms/DatabaseForm';

import { mdiSettings, mdiDatabase } from '@mdi/js';
import IconButton from '@material-ui/core/IconButton';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { Switch } from 'shared/components';

import { Loader } from 'shared/components/loaders';

import { fetchProtocols } from 'shared/api/protocols';
import { fetchDataTypes } from 'shared/api/datatypes';

export const DatabaseSettings = ({ database, disabled }) => {
  const [open, setOpen] = useState(false);

  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };

  if (disabled) {
    return (
      <SvgIcon style={{ color: 'rgba(0, 0, 0, 0.26)' }}>
        <Icon path={mdiSettings} />
      </SvgIcon>
    );
  }

  return (
    <>
      <IconButton onClick={modalActions.open}>
        <SvgIcon>
          <Icon path={mdiSettings} />
        </SvgIcon>
      </IconButton>

      <Modal open={open} actions={modalActions}>
        <EditDatabaseContainer database={database} />
      </Modal>
    </>
  );
};

export const EditDatabase = ({ database, reloadAccounts }) => {
  const [state, setState] = useState({ loading: true, protocols: [], modes: [] });
  const formRef = useRef();
  useEffect(() => {
    // result and protocols provided by fetch
    Promise.all([
      fetchProtocols(),
      fetchDataTypes('mas0300.sys.account.privilege_mode', true)
    ]).then(([pr, mr]) => {
      if (pr.result === 'success' && mr.result === 'success') {
        setState({
          loading: false,
          result: 'success',
          protocols: pr.protocols,
          modes: mr.data_types[0]?.enumerations
        });
      } else {
        setState({
          loading: false,
          result: 'fail',
          protocols: [],
          modes: []
        });
      }
    });
  }, []);

  return (
    <Paper
      style={{
        width: '75vw',
        height: '75vh'
      }}
    >
      <ModalHeader
        icon={
          <SvgIcon>
            <path d={mdiDatabase} />
          </SvgIcon>
        }
        title="Edit Database"
        desc="Edit the database connection information"
        formikRef={formRef}
      />
      <Switch value={state.loading}>
        <Switch.Case value>
          <Center>
            <Loader />
          </Center>
        </Switch.Case>
        <Switch.Case value={false}>
          <DatabaseForm
            account={database}
            protocols={state.protocols}
            modes={state.modes}
            handleSubmission={reloadAccounts}
            editing
            formRef={formRef}
          />
        </Switch.Case>
      </Switch>
    </Paper>
  );
};

export const EditDatabaseContainer = connect(null, dispatch => ({
  reloadAccounts: () => dispatch(loadAllAccountsThunk())
}))(EditDatabase);
