import { actionStates } from 'shared/lib';

export class ActionCreatorHandler {
  constructor(action) {
    this.action = action;
  }

  getEventAction() {
    return this.action;
  }

  getLoadingAction() {
    return `${this.action}__${actionStates.LOADING}`;
  }

  getFailAction() {
    return `${this.action}__${actionStates.FAIL}`;
  }

  getSuccessAction() {
    return `${this.action}__${actionStates.SUCCESS}`;
  }

  dispatchLoading(payload) {
    return {
      type: this.getLoadingAction(),
      payload,
    };
  }

  dispatchFail(payload) {
    return {
      type: this.getFailAction(),
      payload,
    };
  }

  dispatchSuccess(payload) {
    return {
      type: this.getSuccessAction(),
      payload,
    };
  }

  dispatchEvent(payload) {
    return {
      type: this.action,
      payload,
    };
  }
}
