import React from 'react';
import { connect } from 'react-redux';
import { bool, func, array, object } from 'prop-types';
import classnames from 'classnames';

import { Loader } from 'shared/components/loaders';
import { Center } from 'shared/components';
import { getLicenseReportThunk } from '../../../store/thunks';
import { FeaturesStatus } from '../../../components/LicenseReporting/FeaturesStatus/FeaturesStatus';
import { FeaturesUsage } from '../../../components/LicenseReporting/FeaturesUsage/FeaturesUsage';
import { UnusedFeatures } from '../../../components/LicenseReporting/UnusedFeatures/UnusedFeatures';

import './Report.local.scss';

export class Report extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      scrolled: false
    };
  }

  async componentDidMount() {
    const { license, getLicenseReport, licenses } = this.props;
    if (license) {
      await getLicenseReport(license);
    } else {
      await Promise.all([getLicenseReport(licenses[0]), getLicenseReport(licenses[1])]);
    }
  }

  render() {
    const { scrolled } = this.state;
    const { isLoading, reportOne, reportTwo } = this.props;
    return isLoading ? (
      <Center>
        <Loader>Loading report details</Loader>
      </Center>
    ) : (
      <div
        className={classnames('searchable-list-slot', {
          scrolled
        })}
      >
        <div className="container" onWheel={event => this.onWheel(event)}>
          <FeaturesStatus reportOne={reportOne} reportTwo={reportTwo} />
          <FeaturesUsage reportOne={reportOne} reportTwo={reportTwo} />
          <UnusedFeatures reportOne={reportOne} reportTwo={reportTwo} />
        </div>
      </div>
    );
  }

  onWheel = event => {
    if (event.currentTarget.scrollTop) {
      if (!this.state.scrolled) {
        this.setState({ scrolled: true });
      }
    } else if (this.state.scrolled) {
      this.setState({ scrolled: false });
    }

    scroller(event);
  };
}

Report.propTypes = {
  getLicenseReport: func,
  isLoading: bool,
  license: object,
  licenses: array,
  reportOne: object,
  reportTwo: object
};

const mapStateToProps = (state, ownProps) => {
  let reportOne;
  let reportTwo;
  if (ownProps.license) {
    // need to display details for only one report so get its details from the store
    if (state.explorer.reports && state.explorer.reports[ownProps.license.uuid]) {
      reportOne = state.explorer.reports[ownProps.license.uuid];
    }
  } else if (
    // we have two reports to compare

    state.explorer.reports &&
    state.explorer.reports[ownProps.licenses[0].uuid] &&
    state.explorer.reports[ownProps.licenses[1].uuid]
  ) {
    if (ownProps.licenses[0].date_invoke > ownProps.licenses[1].date_invoke) {
      reportOne = state.explorer.reports[ownProps.licenses[1].uuid];
      reportTwo = state.explorer.reports[ownProps.licenses[0].uuid];
    } else {
      reportOne = state.explorer.reports[ownProps.licenses[0].uuid];
      reportTwo = state.explorer.reports[ownProps.licenses[1].uuid];
    }
  }

  return {
    isLoading: state.explorer.flags.reports.loading,
    reportOne,
    reportTwo
  };
};

export const ConnectedReport = connect(mapStateToProps, dispatch => ({
  getLicenseReport: license => dispatch(getLicenseReportThunk(license))
}))(Report);

function scroller(event) {
  const target = event.currentTarget;

  target.scrollBy({
    top: event.deltaY,
    left: event.deltaX
  });
}
