import { put, takeEvery } from '@redux-saga/core/effects';

import { actions } from 'session/store/actions';
import { RestClient } from 'shared/lib/rest';
import { Login } from 'session/pages';

function loginPath() {
  return process.env.NODE_ENV === 'development'
    ? window.location.replace(Login.path)
    : window.location.replace(`/ui/#${Login.path}`);
}

function* logout() {
  localStorage.removeItem('username');
  RestClient.removeBasicAuth();
  loginPath();
  yield put(actions.logout.dispatchSuccess());
}

export function* LogoutEffect() {
  yield takeEvery(actions.logout.getLoadingAction(), logout);
}
