import React, { useContext, useState } from 'react';
import { string, node, func, shape, bool } from 'prop-types';

import CloseIcon from '@material-ui/icons/Close';
import Avatar from '@material-ui/core/Avatar';
import WarningIcon from '@material-ui/icons/Warning';

import { ModalContext } from '../Modal/Modal';

import { Modal } from 'shared/components/overlays';
import { Result } from 'shared/components/overlays/Result/Result';
import { Paper } from 'shared/components/layout';
import { Button } from 'shared/components/controls';

import './ModalHeader.local.scss';

export const ModalHeader = ({
  avatar,
  icon,
  title,
  desc,
  closeable,
  onClose,
  aria: providedAria,
  formikRef,
  changedForm = false
}) => {
  const context = useContext(ModalContext);
  const aria = context.aria || providedAria;
  const actions = context.actions || { close: onClose };
  const [open, setOpen] = useState(false);

  const close = () => {
    if (changedForm || (formikRef?.current?.dirty && fieldChanged(formikRef?.current?.touched))) {
      setOpen(true);
    } else {
      actions.close();
    }
  };

  const fieldChanged = fields => {
    let changed = false;
    const fieldsArr = Object.keys(fields);

    fieldsArr.map(field => {
      if (fields[field]) {
        changed = true;
      }

      return null;
    });

    return changed;
  };

  return (
    <header className="modal-header">
      {avatar && <div className="icon-slot">{avatar}</div>}
      {/** TODO: this should actually be avatar :((( ... don't have time to refactor now need to patch through */}
      {icon && (
        <div className="icon-slot">
          <Avatar className="generic-avatar" alt="header-icon">
            {icon}
          </Avatar>
        </div>
      )}
      <div className="modal-title-slot" id={aria['aria-labelledby']}>
        <h1 className="modal-title">{title}</h1>
        {desc && (
          <p className="modal-desc" id={aria['aria-describedby']}>
            {desc}
          </p>
        )}
      </div>
      {closeable && actions.close && (
        <>
          <div className="modal-close-button-slot">
            <button className="modal-close-button" onClick={close}>
              <CloseIcon fontSize="large" />
            </button>
          </div>

          <Modal open={open}>
            <Paper
              style={{
                minWidth: '350px',
                maxHeight: '368px',
                maxWidth: '65vw'
              }}
            >
              <Paper.Header>
                <div className="modal-header">
                  <div className="icon-slot">
                    <Avatar className="generic-avatar" alt="header-icon">
                      <WarningIcon />
                    </Avatar>
                  </div>
                  <div className="modal-title-slot">
                    <h1 className="modal-title">Warning</h1>
                  </div>
                  <div className="modal-close-button-slot">
                    <button
                      className="modal-close-button"
                      onClick={() => {
                        setOpen(false);
                      }}
                    >
                      <CloseIcon fontSize="large" />
                    </button>
                  </div>
                </div>
              </Paper.Header>
              <Paper.Content scrollable={false}>
                <Result type="fail">
                  <BodyText>Exit without saving?</BodyText>
                </Result>
              </Paper.Content>
              <Paper.Footer>
                <Paper.Footer.Left>
                  <Button
                    onClick={() => {
                      setOpen(false);
                    }}
                  >
                    No
                  </Button>
                </Paper.Footer.Left>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      setOpen(false);
                      actions.close();
                    }}
                  >
                    Yes
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Paper>
          </Modal>
        </>
      )}
    </header>
  );
};

ModalHeader.propTypes = {
  title: node,
  desc: node,
  onClose: func,
  closeable: bool,
  avatar: node,
  icon: node,
  aria: shape({
    'aria-labelledby': string,
    'aria-describedby': string
  })
};

ModalHeader.defaultProps = {
  closeable: true
};

function BodyText({ children }) {
  return (
    <h2
      style={{
        fontFamily: 'Open Sans',
        fontStyle: 'normal',
        fontWeight: 'bold',
        fontSize: '14px',
        lineHeight: '20px',
        letterSpacing: '0.25px',
        marginTop: '24px'
      }}
    >
      {children}
    </h2>
  );
}
