import React, { useState } from 'react';
import * as PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import AccountCircleOutlinedIcon from '@material-ui/icons/AccountCircleOutlined';
import { SmallInput } from 'shared/components';

export const UserFilterEditor = ({ value: defaultValue, setFilterValue }) => {
  const [value, setValue] = useState(defaultValue || '');
  const IconComp = UserFilterEditor.TypeDescriptor.icon;
  const onChange = (event) => { setValue(event.target.value); };
  const onKeyDown = (event) => { if (event.key === 'Enter') { setFilterValue({ value, type: UserFilterEditor.TypeDescriptor.value }); } };

  return (
    <SmallInput
      autoFocus
      value={value}
      placeholder="Search"
      onChange={onChange}
      onKeyDown={onKeyDown}
      icon={<IconComp />}
    />
  );
};

UserFilterEditor.propTypes = {
  value: PropTypes.string,
  setFilterValue: PropTypes.func.isRequired,
};

UserFilterEditor.defaultProps = {
  value: '',
};

UserFilterEditor.TypeDescriptor = {
  icon: () => <AccountCircleOutlinedIcon />,
  name: () => <FormattedMessage id="dashboard.user" />,
  value: 'user',
};
