import React from 'react';
import { array, string, func } from 'prop-types';

import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import './NavigationTab.local.scss';

export class NavigationTab extends React.PureComponent {
  render() {
    const { active, onClick, tabs } = this.props;
    return (
      <div className="navigation-tab">
        <Tabs
          value={tabs.map(e => e.value).indexOf(active)}
          indicatorColor="primary"
          textColor="primary"
        >
          {tabs.map(tab => (
            <Tab
              key={tab.label}
              label={tab.label}
              onClick={() => onClick(tab.value)}
              data-qa={`navigate-to-${tab.value}`}
            />
          ))}
        </Tabs>
      </div>
    );
  }
}

NavigationTab.propTypes = {
  active: string,
  onClick: func,
  tabs: array
};

NavigationTab.defaultProps = {
  tabs: []
};

export default NavigationTab;
