import { ActionCreator } from 'shared/lib';

const actionsTypes = {
  CHECK_AUTHORIZATION: '[session] CHECK_AUTHORIZATION',
  LOGIN: '[session] LOGIN',
  LOGOUT: '[session] LOGOUT',
  BE_VERSION: '[session] BE_VERSION'
};

export const sessionActions = {
  checkAuthorization: new ActionCreator(actionsTypes.CHECK_AUTHORIZATION),
  login: new ActionCreator(actionsTypes.LOGIN),
  logout: new ActionCreator(actionsTypes.LOGOUT),
  getBeVersion: new ActionCreator(actionsTypes.BE_VERSION)
};
