import React from 'react';

import { LightTooltip } from 'shared/components/tooltip/Tooltip';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiChartDonut } from '@mdi/js';

import './RefreshDashboard.local.scss';

export const RefreshDashboard = ({ active, onClickCallback }) => {
  return (
    <div className="refresh-container" onClick={() => onClickCallback()} role="presentation">
      {active && <div className="indicator" />}
      <LightTooltip placement="bottom" title="Refresh">
        <SvgIcon className="refresh-icon">
          <Icon path={mdiChartDonut} />
        </SvgIcon>
      </LightTooltip>
    </div>
  );
};
