import React from 'react';
import { Route, Link, Switch, useRouteMatch, useLocation } from 'react-router-dom';

import { Header } from 'core/components/Header/Header';

import { Button } from 'shared/components/controls';

import { HeaderPageSwitch } from './components/HeaderPageSwitch/HeaderPageSwitch';

import { UsersPage } from './users';

import { GroupsPage } from './groups';

export const UsersAndGroupsPage = () => {
  return (
    <>
      <Header>
        <UserManagementHeaderLinks />
        <RightSideControls />
      </Header>
      <UserManagementSwitch />
    </>
  );
};

export const UserManagementHeaderLinks = () => {
  const { url } = useRouteMatch();
  const location = useLocation();

  const routes = [
    { to: `${url}/users`, label: 'Users' },
    { to: `${url}/groups`, label: 'Groups' }
  ];

  return <HeaderPageSwitch active={location.pathname} routes={routes} />;
};

export const RightSideControls = () => {
  const location = useLocation();
  const { url } = useRouteMatch();

  if (location.pathname.includes('create-new')) {
    return null;
  }

  if (location.pathname.includes('users')) {
    return (
      <div className="side-controls" style={{ margin: '0 0 0 auto' }}>
        <Button color="primary" to={`${url}/users/create-new`} component={Link}>
          Create User
        </Button>
      </div>
    );
  }

  if (location.pathname.includes('groups')) {
    return (
      <div className="side-controls" style={{ margin: '0 0 0 auto' }}>
        <Button color="primary" to={`${url}/groups/create-new`} component={Link}>
          Create Group
        </Button>
      </div>
    );
  }

  return null;
};

export const UserManagementSwitch = () => {
  const { url } = useRouteMatch();

  return (
    <Switch>
      <Route exact path={`${url}`}>
        <UsersPage />
      </Route>
      <Route path={`${url}/groups`}>
        <GroupsPage />
      </Route>
      <Route path={`${url}/users`}>
        <UsersPage />
      </Route>
    </Switch>
  );
};

export default UsersAndGroupsPage;
