export default deflt => {
  if (typeof deflt !== 'string') return null;
  deflt = deflt.trim();
  const regex = /^([a-z][a-z0-9]*)\((.*)\)$/i;
  const matches = regex.exec(deflt);
  // const json = `[${matches[2]}]`;
  switch (deflt) {
    case 'bool(True)':
      return true;
    case 'bool(False)':
      return false;
    case 'dict()':
      return {};
    case 'list()':
      return [];
    default:
      if (Array.isArray(matches)) {
        switch (matches[1]) {
          case 'int':
            return parseInt(matches[2], 10);
          case 'str':
            // Python's escaping is very close to JavaScript's, so
            // we'll take a chance here.
            try {
              // return JSON.parse(json)[0];
              return matches[2].replace(/'/g, '');
            } catch (e) {
              // Sorry, no dice
              return null;
            }
          default:
            return null;
        }
      } else if (/^[a-z][a-z0-9]*$/i.test(deflt)) {
        // Enumeration literal
        return deflt;
      }
  }
  return null;
};
