import { actions } from 'shared/store/actions';
import { actions as sessionActions } from 'session/store/actions';
import { fromJS } from 'immutable';
import moment from 'moment';

let sessionGlobalFilters = {
  startDate: moment()
    .subtract(7, 'd')
    .startOf('day')
    .unix(),
  endDate: moment()
    .add(1, 'days')
    .startOf('day')
    .unix()
};

sessionGlobalFilters = JSON.parse(localStorage.getItem('global-filters')) || sessionGlobalFilters;

export const initialState = {
  globalFilters: sessionGlobalFilters,
  jobs: {
    content: [],
    page_count: 1,
    total_count: 0,
    fetching: false,
    callFailed: false,
    reason: ''
  },
  connections: {
    content: [],
    page_count: 1,
    total_count: 0,
    fetching: false
  },
  searchResults: {
    jobs: {
      content: [],
      total_count: 0,
      fetching: false
    },
    processes: {
      content: [],
      total_count: 0,
      fetching: false
    }
  }
};

export const reducerActions = {
  [actions.setGlobalFilters.getSuccessAction()]: (state, payload) => {
    const nextState = state
      .setIn(['globalFilters', 'startDate'], payload.startDate.unix())
      .setIn(['globalFilters', 'endDate'], payload.endDate.unix());

    const globalFilters = nextState.get('globalFilters').toJS();
    localStorage.setItem('global-filters', JSON.stringify(globalFilters));

    return nextState;
  },
  [sessionActions.login.getSuccessAction()]: state => {
    const nextState = state
      .setIn(
        ['globalFilters', 'startDate'],
        moment()
          .subtract(7, 'd')
          .startOf('day')
          .unix()
      )
      .setIn(
        ['globalFilters', 'endDate'],
        moment()
          .add(1, 'days')
          .startOf('day')
          .unix()
      );

    const globalFilters = nextState.get('globalFilters').toJS();
    localStorage.setItem('global-filters', JSON.stringify(globalFilters));

    return nextState;
  },
  [sessionActions.logout.getSuccessAction()]: state => {
    localStorage.removeItem('global-filters');
    return state;
  },
  [actions.getJobs.getSuccessAction()]: (state, payload) =>
    state.set(
      'jobs',
      fromJS({
        content: payload.data.invocations,
        page_count: payload.data.page_count,
        total_count: payload.data.total_count,
        fetching: false,
        callFailed: false,
        reason: ''
      })
    ),
  [actions.getJobs.getFailAction()]: (state, payload) =>
    state.set(
      'jobs',
      fromJS({
        callFailed: true,
        fetching: false,
        page_count: 1,
        content: [],
        total_count: 0,
        reason: payload.reason
      })
    ),
  [actions.getJobs.getLoadingAction()]: state => state.setIn(['jobs', 'fetching'], true),
  [actions.getConnections.getSuccessAction()]: (state, payload) =>
    state.set(
      'connections',
      fromJS({
        content: payload.data.accounts,
        page_count: payload.data.page_count,
        total_count: payload.data.total_count,
        fetching: false
      })
    ),
  [actions.getConnections.getLoadingAction()]: state =>
    state.setIn(['connections', 'fetching'], true),
  [actions.searchJobs.getSuccessAction()]: (state, payload) =>
    state.setIn(
      ['searchResults', 'jobs'],
      fromJS({
        content: payload.data.invocations,
        total_count: payload.data.total_count,
        fetching: false
      })
    ),
  [actions.searchJobs.getLoadingAction()]: state =>
    state.setIn(['searchResults', 'jobs', 'fetching'], true),
  [actions.searchJobs.getFailAction()]: state =>
    state.setIn(['searchResults', 'jobs', 'fetching'], false),
  [actions.searchProcesses.getSuccessAction()]: (state, payload) =>
    state.setIn(
      ['searchResults', 'processes'],
      fromJS({
        content: payload.data.processes,
        total_count: payload.data.total_count,
        fetching: false
      })
    ),
  [actions.searchProcesses.getLoadingAction()]: state =>
    state.setIn(['searchResults', 'processes', 'fetching'], true),
  [actions.searchProcesses.getFailAction()]: state =>
    state.setIn(['searchResults', 'processes', 'fetching'], false)
};
