import React from 'react';

import { NavigationTab } from 'shared/components/layout/NavigationTab/NavigationTab';

export function ViewTabs({ active, changeTab, invocation }) {
  const tabs = [{ label: 'Schedule', value: 'schedule', active: true }];

  if (invocation) {
    tabs.push({ label: 'Documentation', value: 'documentation' });
  }

  return <NavigationTab active={active} onClick={changeTab} tabs={tabs} />;
}
