import React from 'react';
import { object, node, any } from 'prop-types';
import moment from 'moment';

import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiCheckboxMarkedCircle, mdiCloseCircle } from '@mdi/js';

import './FeaturesStatus.local.scss';

export class FeaturesStatus extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      selectedIndex: 0
    };
  }

  render() {
    const { reportOne, reportTwo } = this.props;

    if (!reportOne) return <></>;

    return (
      <div className="features-status-container">
        <header>
          <h1>License Features Status</h1>
        </header>

        {!reportTwo && <div className="feature-status">{this.renderSingleReport()}</div>}
        {reportTwo && <div className="feature-status">{this.renderComparableReports()}</div>}
      </div>
    );
  }

  setSelectedIndex = (event, newIndex) => {
    this.setState({ selectedIndex: newIndex });
  };

  renderSingleReport = () => {
    const columns = Object.entries(this.props?.reportOne?.FEATURES ?? {});

    return (
      <>
        <div className="feature-name">
          <Tabs
            orientation="vertical"
            value={this.state.selectedIndex}
            onChange={(event, newIndex) => this.setSelectedIndex(event, newIndex)}
          >
            {columns.map((column, index) => (
              <Tab key={column[0]} label={column[0]} data-qa={`feature-column-${index}`} />
            ))}
          </Tabs>
        </div>
        <div className="feature-details">
          <header className="row header">
            <div className="feature">Feature</div>
            <div className="status-container">
              <div className="date-time-container">
                <div className="date">
                  {moment(this.props.reportOne.date_invoke).format('MMM DD YYYY')}
                </div>
                <div className="time">
                  {moment(this.props.reportOne.date_invoke).format('HH:mm:ss')}
                </div>
              </div>
            </div>
          </header>
          {columns.map((column, index) => {
            const values = Object.keys(column[1]);
            return (
              <TabPanel key={index} value={this.state.selectedIndex} index={index}>
                {values.map((value, valIndex) => {
                  return (
                    <div key={`${index}-${valIndex}`} className="row">
                      <div className="feature-name">{values[valIndex]}</div>
                      <div className="status-container">
                        {column[1][value].toString() === 'true' ? (
                          <div className="status available">
                            <SvgIcon>
                              <Icon path={mdiCheckboxMarkedCircle} />
                            </SvgIcon>
                            Available
                          </div>
                        ) : (
                          <div className="status unavailable">
                            <SvgIcon>
                              <Icon path={mdiCloseCircle} />
                            </SvgIcon>
                            Unavailable
                          </div>
                        )}
                      </div>
                    </div>
                  );
                })}
              </TabPanel>
            );
          })}
        </div>
      </>
    );
  };

  renderComparableReports = () => {
    const comparableFeatures = {};
    const { reportOne, reportTwo } = this.props;
    if (reportOne.FEATURES && reportTwo && reportTwo.FEATURES) {
      const reportOneEntries = Object.entries(reportTwo.FEATURES);

      reportOneEntries.map(feature => {
        comparableFeatures[feature[0]] = {};
        const options = Object.keys(feature[1]);

        options.map((value, index) => {
          comparableFeatures[feature[0]][options[index]] = [
            feature[1][value].toString() === 'true' ? 'true' : 'false'
          ];
          comparableFeatures[feature[0]][options[index]][1] = 'false';

          return null;
        });

        return null;
      });

      const reportTwoEntries = Object.entries(reportOne.FEATURES);
      reportTwoEntries.map(feature => {
        const options = Object.keys(feature[1]);

        if (feature[0] in comparableFeatures) {
          options.map((value, index) => {
            comparableFeatures[feature[0]][options[index]][1] =
              feature[1][value].toString() === 'true' ? 'true' : 'false';
            return null;
          });
        } else {
          // current featured category was not present in previous report so we add it
          comparableFeatures[feature[0]] = {};

          options.map((value, index) => {
            // add it as false for the previous report
            comparableFeatures[feature[0]][options[index]] = ['false'];
            // add it with current value for the current report
            comparableFeatures[feature[0]][options[index]][1] =
              feature[1][value].toString() === 'true' ? 'true' : 'false';

            return null;
          });
        }
        return null;
      });
    }

    const columns = Object.entries(comparableFeatures);

    return (
      <>
        <div className="feature-name">
          <Tabs
            orientation="vertical"
            value={this.state.selectedIndex}
            onChange={(event, newIndex) => this.setSelectedIndex(event, newIndex)}
          >
            {columns.map((column, index) => (
              <Tab key={column[0]} label={column[0]} data-qa={`feature-column-${index}`} />
            ))}
          </Tabs>
        </div>

        <div className="feature-details">
          <header className="row header">
            <div className="feature">Feature</div>
            <div className="status-container compare">
              <div className="date-time-container">
                <div className="date">
                  {moment(this.props.reportOne.date_invoke).format('MMM DD YYYY')}
                </div>
                <div className="time">
                  {moment(this.props.reportOne.date_invoke).format('HH:mm:ss')}
                </div>
              </div>
              <div className="date-time-container">
                <div className="date">
                  {moment(this.props.reportTwo.date_invoke).format('MMM DD YYYY')}
                </div>
                <div className="time">
                  {moment(this.props.reportTwo.date_invoke).format('HH:mm:ss')}
                </div>
              </div>
            </div>
          </header>

          {columns.map((column, index) => {
            const values = Object.keys(column[1]);
            return (
              <TabPanel key={index} value={this.state.selectedIndex} index={index}>
                {values.map((value, valIndex) => {
                  return (
                    <div key={`${index}-${valIndex}`} className="row">
                      <div className="feature-name">{values[valIndex]}</div>
                      <div className="status-container compare">
                        {column[1][value][0].toString() === 'true' ? (
                          <div className="status available">
                            <SvgIcon>
                              <Icon path={mdiCheckboxMarkedCircle} />
                            </SvgIcon>
                            Available
                          </div>
                        ) : (
                          <div className="status unavailable">
                            <SvgIcon>
                              <Icon path={mdiCloseCircle} />
                            </SvgIcon>
                            Unavailable
                          </div>
                        )}
                        {column[1][value][1].toString() === 'true' ? (
                          <div className="status available">
                            <SvgIcon>
                              <Icon path={mdiCheckboxMarkedCircle} />
                            </SvgIcon>
                            Available
                          </div>
                        ) : (
                          <div className="status unavailable">
                            <SvgIcon>
                              <Icon path={mdiCloseCircle} />
                            </SvgIcon>
                            Unavailable
                          </div>
                        )}
                      </div>
                    </div>
                  );
                })}
              </TabPanel>
            );
          })}
        </div>
      </>
    );
  };
}

FeaturesStatus.propTypes = {
  reportOne: object,
  reportTwo: object
};

export default FeaturesStatus;

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

TabPanel.propTypes = {
  children: node,
  index: any.isRequired,
  value: any.isRequired
};
