import React from 'react';
import { object } from 'prop-types';
import moment from 'moment';
import classnames from 'classnames';

import Button from '@material-ui/core/Button';
import Tooltip from '@material-ui/core/Tooltip';

import { withStyles } from '@material-ui/core/styles';

import './FeaturesUsage.local.scss';

export class FeaturesUsage extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      isExpanded: false,
      needsExpansion: false
    };

    this.listRef = React.createRef();
  }

  componentDidMount() {
    if (
      this.listRef.current &&
      this.listRef.current.scrollHeight > this.listRef.current.offsetHeight
    ) {
      this.setState({ needsExpansion: true });
    }
  }

  /* eslint-disable react/no-did-update-set-state */
  componentDidUpdate() {
    if (
      this.listRef.current &&
      this.listRef.current.scrollHeight > this.listRef.current.offsetHeight &&
      !this.state.needsExpansion
    ) {
      this.setState({ needsExpansion: true });
    }
  }

  render() {
    const { reportOne, reportTwo } = this.props;
    const { isExpanded, needsExpansion } = this.state;

    if (!reportOne) return <></>;
    return (
      <div className="features-usage-container">
        <header>
          <h1>License Features Usage</h1>
        </header>

        {!reportTwo && (
          <div ref={this.listRef} className={classnames('features-container', { all: isExpanded })}>
            {this.renderSingleReport()}
          </div>
        )}
        {reportTwo && (
          <div ref={this.listRef} className={classnames('features-container', { all: isExpanded })}>
            {this.renderComparableReports()}
          </div>
        )}
        {needsExpansion && !isExpanded && (
          <div className="btn-container">
            <Button
              className="view-all-btn"
              onClick={() => this.setState({ isExpanded: true })}
              data-qa="view-all"
            >
              View all
            </Button>
          </div>
        )}
      </div>
    );
  }

  renderSingleReport = () => {
    const { reportOne } = this.props;
    const features = Object.entries(reportOne?.USED ?? {});
    return features.map(feature => {
      return (
        <div key={feature[0]} className="feature">
          <div className="value single">{feature[1].USAGES}</div>
          <div className="label">{feature[0]}</div>
        </div>
      );
    });
  };

  renderComparableReports = () => {
    const { reportOne, reportTwo } = this.props;
    const comparableFeatures = {};
    if (reportOne.USED && reportOne && reportOne.USED) {
      const reportOneEntries = Object.entries(reportOne.USED);

      reportOneEntries.map(feature => {
        comparableFeatures[feature[0]] = {};
        comparableFeatures[feature[0]].usage = [parseInt(feature[1].USAGES, 10)];
        comparableFeatures[feature[0]].usage[1] = 0;

        return null;
      });

      const reportTwoEntries = Object.entries(reportTwo.USED);

      reportTwoEntries.map(feature => {
        if (feature[0] in comparableFeatures) {
          comparableFeatures[feature[0]].usage[1] = parseInt(feature[1].USAGES, 10);
        } else {
          comparableFeatures[feature[0]] = {};
          comparableFeatures[feature[0]].usage = [0];
          comparableFeatures[feature[0]].usage[1] = parseInt(feature[1].USAGES, 10);
        }

        return null;
      });
    }

    const features = Object.entries(comparableFeatures);
    return features.map(feature => {
      return (
        <LightTooltip
          key={feature[0]}
          placement="top"
          title={
            <>
              <div className="row">
                <div className="date">
                  {moment(reportOne.date_invoke).format('MMM DD YYYY (HH:mm:ss)')}
                </div>
                <div className="value">{comparableFeatures[feature[0]].usage[0]}</div>
              </div>
              <div className="row">
                <div className="date">
                  {moment(reportTwo.date_invoke).format('MMM DD YYYY (HH:mm:ss)')}
                </div>
                <div className="value">{comparableFeatures[feature[0]].usage[1]}</div>
              </div>
            </>
          }
        >
          <div className="feature">
            {this.getFeatureValue(
              comparableFeatures[feature[0]].usage[0],
              comparableFeatures[feature[0]].usage[1]
            )}
            <div className="label">{feature[0]}</div>
          </div>
        </LightTooltip>
      );
    });
  };

  getFeatureValue = (rep1, rep2) => {
    let valueNode = <div className="value lower">{rep1 - rep2}</div>;

    if (rep1 === rep2) {
      valueNode = <div className="value equal">=</div>;
    } else if (rep2 > rep1) {
      valueNode = <div className="value higher">{rep2 - rep1}</div>;
    }

    return valueNode;
  };
}

FeaturesUsage.propTypes = {
  reportOne: object,
  reportTwo: object
};

export default FeaturesUsage;

const LightTooltip = withStyles(() => ({
  tooltip: {
    display: 'flex',
    flexDirection: 'column',
    color: '#fff',
    backgroundColor: '#787E89',
    letterSpacing: '0.4px',
    fontSize: '12px',
    fontWeight: 'normal',
    fontStyle: 'normal',
    lineHeight: '16px',
    fontFamily: 'Open Sans',

    '& .row': {
      display: 'flex',

      '& .value': {
        paddingLeft: '15px',
        marginLeft: 'auto',
        fontWeight: '600'
      }
    }
  }
}))(Tooltip);
