import React from 'react';
import { bool, node, string, array, func } from 'prop-types';
import classnames from 'classnames';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiContentSave } from '@mdi/js';

import { LightTooltip } from 'shared/components/tooltip/Tooltip';
import { capitalizeFirstLetter } from 'shared/lib/capitalize-first-letter';

import './ActionCard.local.scss';

export class ActionCard extends React.PureComponent {
  render() {
    const {
      settings,
      status,
      icon,
      label,
      addOnly,
      options,
      onClick,
      readOnlyOptions,
      lastBackup,
      type = '',
      statusInfo = ''
    } = this.props;

    return (
      <div className={classnames('action-card', { addable: addOnly })}>
        <header>
          {lastBackup && (
            <div className="last-backup">
              <span className="icon">
                <SvgIcon>
                  <Icon path={mdiContentSave} />
                </SvgIcon>
              </span>
              <span className="value">{lastBackup}</span>
            </div>
          )}
          {settings && <div className="settings">{settings}</div>}
        </header>
        <div className="main-icon" onClick={onClick} role="presentation">
          {status && (
            <LightTooltip placement="bottom" title={capitalizeFirstLetter(statusInfo)}>
              <div className={`status ${status} ${type}`} />
            </LightTooltip>
          )}
          {icon}
        </div>
        <div className="main-label">{label}</div>
        {options &&
          options.map((option, index) => (
            <div
              className={classnames('option', { disabled: option.disabled })}
              key={`${option.label}-${index}`}
              onClick={option.onClick}
              role="presentation"
            >
              <div className="icon">{option.icon}</div>
              <div className="label">{option.label}</div>
            </div>
          ))}
        {readOnlyOptions &&
          readOnlyOptions.map(option => (
            <div
              className="option presentation"
              key={option.label}
              onClick={option.onClick}
              role="presentation"
            >
              {option.fillable && option.percentage && (
                <div className="filled" style={{ width: `${option.percentage}%` }} />
              )}
              <div className="icon">{option.icon}</div>
              <div className="label">{option.label}</div>
              {option.percentage && <div className="end-icon">{option.percentage}%</div>}
            </div>
          ))}
      </div>
    );
  }
}

ActionCard.propTypes = {
  settings: node,
  icon: node,
  label: string,
  addOnly: bool,
  options: array,
  onClick: func,
  status: string,
  readOnlyOptions: array
};

export default ActionCard;
