import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { mapSortInsensitive } from 'users-manager/store/mappers';
import { getJobsFiltersOptions } from 'shared/store/selectors';

import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiHexagonMultipleOutline, mdiDatabase, mdiServer, mdiCircleSlice5 } from '@mdi/js';
import Autocomplete from '@material-ui/lab/Autocomplete';
import AccountCircleOutlinedIcon from '@material-ui/icons/AccountCircleOutlined';

import { Input } from 'shared/components/controls';

import './filters.scss';

export const PLATFORM = [
  { label: 'MariaDB', name: 'mariadb', dbms: 'mysql', value: { name: 'mariadb', dbms: 'mysql' } },
  {
    label: 'MySQL',
    name: 'mysql',
    dbms: 'mysql',
    value: { name: 'mysql', dbms: 'mysql' }
  },
  { label: 'Oracle', name: 'oracle', dbms: 'oracle', value: { name: 'oracle', dbms: 'oracle' } },
  { label: 'PostgreSQL', name: 'pgsql', dbms: 'pgsql', value: { name: 'pgsql', dbms: 'pgsql' } },
  {
    label: 'Microsoft SQL Server',
    name: 'microsoft.sqlserver',
    dbms: 'mssql',
    value: { name: 'microsoft.sqlserver', dbms: 'mssql' }
  },
  {
    label: 'Oracle RDS',
    name: 'rds.oracle',
    dbms: 'oracle',
    value: { name: 'rds.oracle', dbms: 'oracle' }
  }
];

const Filters = props => {
  const { filters, selections, onChange, excludeStatus, allUsers, currentTab } = props;
  const {
    shell_host_address_filter,
    platform_filter,
    status,
    database_name_filter,
    user_filter
  } = selections;

  const users = [
    { value: '', label: 'All' },
    ...allUsers.map(user => ({ label: user.name, value: user.name }))
  ];
  const selectedUser = users.find(u => u.value && u.value === user_filter) ?? '';
  const platforms = [
    { value: { name: '', dbms: '' }, label: 'All' },
    ...PLATFORM.map(el => ({ value: el.value, label: el.label }))
  ];
  const selectedPlatform =
    platforms.find(p => p?.value?.name && p.value.name === platform_filter?.name) ?? '';
  const servers = [
    { value: '', label: 'All' },
    ...filters.address.map(el => ({ value: el, label: el }))
  ];
  const selectedServer =
    servers.find(sv => sv.value && sv.value === shell_host_address_filter) ?? '';
  const databases = [
    { value: '', label: 'All' },
    ...filters.database.map(el => ({ value: el, label: el }))
  ];
  const selectedDb = databases.find(db => db.value && db.value === database_name_filter) ?? '';
  let statuses = [
    { value: '', label: 'All' },
    ...filters.status.map(el => ({ value: el, label: el }))
  ];

  if (currentTab === 'current-jobs') {
    statuses = statuses.filter(s => s.value !== 'SCHEDULED');
  }

  const selectedStatus = statuses.find(s => s.value && s.value === status) ?? '';

  return (
    <div className="job-filters">
      <Autocomplete
        disableClearable
        id="users-filter"
        options={users}
        value={selectedUser}
        getOptionLabel={opt => opt.label || ''}
        onChange={(e, option) => {
          onChange('user_filter', option?.value || '');
        }}
        renderInput={params => (
          <Input
            {...params}
            size="small"
            label={
              <span className="job-filter-select-label-wrap">
                <AccountCircleOutlinedIcon />
                <span className="job-filter-select-label">Users</span>
              </span>
            }
            variant="outlined"
          />
        )}
      />

      <Autocomplete
        disableClearable
        id="platform-filter"
        options={platforms}
        value={selectedPlatform}
        getOptionLabel={opt => opt.label || ''}
        onChange={(e, option) => {
          onChange('platform_filter', option?.value || '');
        }}
        renderInput={params => (
          <Input
            {...params}
            size="small"
            label={
              <span className="job-filter-select-label-wrap">
                <SvgIcon>
                  <path d={mdiHexagonMultipleOutline} />
                </SvgIcon>
                <span className="job-filter-select-label">Platform</span>
              </span>
            }
            variant="outlined"
          />
        )}
      />

      <Autocomplete
        disableClearable
        id="server-filter"
        options={servers}
        value={selectedServer}
        getOptionLabel={opt => opt.label || ''}
        onChange={(e, option) => {
          onChange('shell_host_address_filter', option?.value || '');
        }}
        renderInput={params => (
          <Input
            {...params}
            size="small"
            label={
              <span className="job-filter-select-label-wrap">
                <SvgIcon>
                  <path d={mdiServer} />
                </SvgIcon>
                <span className="job-filter-select-label">Server</span>
              </span>
            }
            variant="outlined"
          />
        )}
      />

      <Autocomplete
        disableClearable
        id="database-filter"
        options={databases}
        value={selectedDb}
        getOptionLabel={opt => opt.label || ''}
        onChange={(e, option) => {
          onChange('database_name_filter', option?.value || '');
        }}
        renderInput={params => (
          <Input
            {...params}
            size="small"
            label={
              <span className="job-filter-select-label-wrap">
                <SvgIcon>
                  <path d={mdiDatabase} />
                </SvgIcon>
                <span className="job-filter-select-label">Database</span>
              </span>
            }
            variant="outlined"
          />
        )}
      />

      {!excludeStatus && (
        <Autocomplete
          disableClearable
          id="status-filter"
          options={statuses}
          value={selectedStatus}
          getOptionLabel={opt => opt.label || ''}
          onChange={(e, option) => {
            onChange('status', option?.value || '');
          }}
          style={{ color: 'red' }}
          renderInput={params => (
            <Input
              {...params}
              size="small"
              label={
                <span className="job-filter-select-label-wrap">
                  <SvgIcon>
                    <path d={mdiCircleSlice5} />
                  </SvgIcon>
                  <span className="job-filter-select-label">Status</span>
                </span>
              }
              variant="outlined"
            />
          )}
        />
      )}
    </div>
  );
};
Filters.propTypes = {
  filters: PropTypes.object.isRequired,
  selections: PropTypes.object.isRequired,
  onChange: PropTypes.func.isRequired,
  excludeStatus: PropTypes.bool
};

export const mapStateToProps = state => ({
  filters: getJobsFiltersOptions(state),
  allUsers: mapSortInsensitive(state.usersManager.users.all)
});

export default connect(mapStateToProps)(Filters);
