import * as Yup from 'yup';

export function getInitialValues(parameters) {
  const initialValues = {};

  parameters.map(parameter => {
    initialValues[parameter.name] = parameter.deflt !== null ? parameter.deflt : '';
    return null;
  });

  return initialValues;
}

export function getValidationSchema(parameters) {
  const validationSchema = {};

  parameters.map(parameter => {
    validationSchema[parameter.name] = Yup.string();

    if (parameter.is_required) {
      validationSchema[parameter.name].required('A value is required');
    }

    return null;
  });
}

export function formatParameters(values) {
  const parameters = {};

  Object.keys(values).map(value => {
    if (typeof values[value] === 'string') {
      if (values[value].length > 0 || values[value].trim()) {
        parameters[value] = values[value];
      }
    } else if (typeof values[value] === 'object') {
      if (values[value].name && (values[value].name.length > 0 || values[value].name.trim())) {
        parameters[value] = values[value].name;
      }
    }

    return null;
  });

  return parameters;
}
