import React, { useState, useEffect } from 'react';
import { object, array, func } from 'prop-types';
import { produce } from 'immer';

import GroupAddIcon from '@material-ui/icons/GroupAdd';
import GroupIcon from '@material-ui/icons/Group';
import Modal from '@material-ui/core/Modal';
import Avatar from '@material-ui/core/Avatar';
import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiAccountGroup } from '@mdi/js';

import { CONSTANTS } from 'config';
import { RestClient } from 'shared/lib/rest';

import { Button } from 'shared/components/controls';

import { Paper } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';
import { ModalHeader, Result } from 'shared/components/overlays';

import { AvatarCard } from '../../../../components/AvatarCard/AvatarCard';

import { SearchableList } from '../../../../components/SearchableList/SearchableList';

import './GroupsSelection.local.scss';

export const GroupsSelection = ({ selected, helpers, retrieveGroupDescription }) => {
  const [open, setOpen] = useState(false);

  const openModal = () => {
    setOpen(true);
  };

  const closeModal = () => {
    setOpen(false);
    setIsFormChanged(false);
  };

  const [isFormChanged, setIsFormChanged] = useState(false);

  /* eslint-disable jsx-a11y/click-events-have-key-events */
  return (
    <>
      <div tabIndex={0} role="button" className="edit-user-groups" onClick={openModal}>
        <AvatarCard
          label="Add to Group"
          avatar={{
            fallback: (
              <SvgIcon>
                <path d={mdiAccountGroup} />
              </SvgIcon>
            )
          }}
        />
      </div>
      <Modal
        aria-labelledby="edit-user-groups-modal-title"
        aria-describedby="edit-user-groups-modal-description"
        open={open}
        onClose={closeModal}
        disableBackdropClick
      >
        <div
          style={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            width: 'calc(75vw)',
            height: 'calc(75vh)'
          }}
        >
          <Paper>
            <ModalHeader
              avatar={
                <Avatar className="generic-avatar" alt="edit-user-groups">
                  <GroupAddIcon />
                </Avatar>
              }
              title="Add to Group"
              desc="Please select the groups you want to add this user to"
              aria={{
                title: 'edit-user-groups-modal-title',
                desc: 'edit-user-groups-modal-description'
              }}
              onClose={closeModal}
              changedForm={isFormChanged}
            />

            <Paper.Content scrollable={false}>
              <GroupsList
                selected={selected}
                helpers={helpers}
                closeModal={closeModal}
                setIsFormChanged={setIsFormChanged}
                retrieveGroupDescription={retrieveGroupDescription}
              />
            </Paper.Content>
          </Paper>
        </div>
      </Modal>
    </>
  );
};

GroupsSelection.propTypes = {
  selected: array,
  helpers: object
};

const GroupsList = ({
  selected,
  helpers,
  closeModal,
  setIsFormChanged,
  retrieveGroupDescription
}) => {
  const [state, setState] = useState({
    all: [],
    loading: true,
    result: undefined,
    done: false
  });

  const filtered = state.all
    .filter(g => g.name !== 'user')
    .sort((a, b) => a.name.toLowerCase().localeCompare(b.name.toLowerCase()));

  useEffect(() => {
    RestClient.get(CONSTANTS.BASE.ENDPOINTS.GROUPS, { page_size: 2147483645 })
      .then(response => {
        const data = response.data.data.groups;

        setState(prev =>
          produce(prev, draft => {
            draft.all = data;
            draft.result = response.result;
            draft.done = true;
            draft.loading = false;
          })
        );
      })
      .catch(error => {
        setState(prev =>
          produce(prev, draft => {
            draft.all = [];
            draft.result = error.result;
            draft.done = true;
            draft.loading = false;
          })
        );
      });
  }, []);

  if (state.loading) {
    return <Loader />;
  }

  if (state.done && !state.all.length) {
    return (
      <>
        <Paper.Content>
          <Result type="fail">
            <Result.Text>Could not load groups list</Result.Text>
          </Result>
        </Paper.Content>

        <Paper.Footer>
          <Button variant="contained" color="primary" onClick={closeModal}>
            Close
          </Button>
        </Paper.Footer>
      </>
    );
  }

  return (
    <>
      <Paper.Content>
        <div className="seachable-list-container">
          <SearchableList
            items={filtered}
            filter={(item, query) => item.name.toLowerCase().includes(query.toLowerCase())}
            variant="row"
            visibleScroll
          >
            {entry => {
              return (
                <div className="card-wrap" style={{ width: '178px' }}>
                  <AvatarCard
                    onClick={() => {
                      const existing = selected.indexOf(entry.name);

                      if (existing !== -1) {
                        helpers.remove(existing);
                      } else {
                        helpers.push(entry.name);
                      }
                      setIsFormChanged(true);
                    }}
                    selected={selected.includes(entry.name)}
                    label={entry.name}
                    avatar={{ fallback: <GroupIcon /> }}
                    description={retrieveGroupDescription(entry.name)}
                  />
                </div>
              );
            }}
          </SearchableList>
        </div>
      </Paper.Content>
      <Paper.Footer>
        <Button variant="contained" color="primary" onClick={closeModal}>
          Confirm
        </Button>
      </Paper.Footer>
    </>
  );
};

GroupsList.propTypes = {
  selected: array,
  helpers: object,
  closeModal: func
};
