import React from 'react';
import { connect } from 'react-redux';
import * as PropTypes from 'prop-types';

import Paper from '@material-ui/core/Paper';

import { Center } from 'shared/components';
import { Loader } from 'shared/components/loaders';
import { jobsSelector } from 'shared/store/state';
import { filterStatusSelector } from '../../store/state';

import { Daemon, JobStatus } from './components';

import './status.local.scss';

export class StatusContainer extends React.PureComponent {
  render() {
    const { className, jobs } = this.props;
    return (
      <Paper className={className}>
        <Daemon />
        <div className="jobs-status-container">
          {jobs.get('fetching') ? (
            <Center>
              <Loader inline>Loading jobs</Loader>
            </Center>
          ) : (
            this.filterJobs(jobs.toJS().content)
          )}
        </div>
      </Paper>
    );
  }

  filterJobs = jobs => {
    const { active } = this.props;
    const count = {
      executing: 0,
      succeeded: 0,
      scheduled: 0,
      aborted: 0,
      failed: 0
    };
    count.executing = jobs.filter(job => job.status === 'EXECUTING')?.length;
    count.succeeded = jobs.filter(job => job.status === 'SUCCEEDED')?.length;
    count.scheduled = jobs.filter(job => job.status === 'SCHEDULED')?.length;
    count.aborted = jobs.filter(job => job.status === 'ABORTED')?.length;
    count.failed = jobs.filter(job => job.status === 'FAILED')?.length;

    return (
      <>
        <JobStatus count={count.executing} status="EXECUTING" active={active} />
        <JobStatus count={count.succeeded} status="SUCCEEDED" active={active} />
        <JobStatus count={count.scheduled} status="SCHEDULED" active={active} />
        <JobStatus count={count.aborted} status="ABORTED" active={active} />
        <JobStatus count={count.failed} status="FAILED" active={active} />
      </>
    );
  };
}

StatusContainer.propTypes = {
  className: PropTypes.string,
  jobs: PropTypes.object.isRequired
};

StatusContainer.defaultProps = {
  className: 'status-container'
};

export const mapStateToProps = state => ({
  jobs: jobsSelector(state),
  active: filterStatusSelector(state)
});

export default connect(mapStateToProps)(StatusContainer);
