import React from 'react';
import { useSelector } from 'react-redux';

import { want } from 'shared/lib/want';

import SvgIcon from '@material-ui/core/SvgIcon';
import {
  mdiProgressClock,
  mdiCheckCircleOutline,
  mdiCalendarArrowRight,
  mdiTimelineClock,
  mdiCancel,
  mdiAlertRhombusOutline,
  mdiStopCircleOutline
} from '@mdi/js';

import { getInvocationStatus } from '../../store/selectors';
import {
  InvocationStatus,
  CannotShowStatusFor,
  FAILURES,
  SUCESSES,
  WARNINGS
} from '../../store/constants';

import './JobStatus.local.scss';

export const JobStatus = () => {
  const STATUS = useSelector(getInvocationStatus);
  const ignore = CannotShowStatusFor.includes(STATUS);

  if (ignore) {
    return null;
  }

  return (
    <div className="status-label" style={{ background: wantStatusColor(STATUS) }}>
      <SvgIcon fontSize="inherit" color="inherit">
        <path d={wantStatusIcon(STATUS)} />
      </SvgIcon>
      <span className="status-label-text">{STATUS}</span>
    </div>
  );
};

const wantStatusIcon = want(
  {
    [InvocationStatus.EXECUTING]: mdiProgressClock,
    [InvocationStatus.SUCCEEDED]: mdiCheckCircleOutline,
    [InvocationStatus.DELAYED]: mdiTimelineClock,
    [InvocationStatus.SCHEDULED]: mdiCalendarArrowRight,
    [InvocationStatus.KILLED]: mdiCancel,
    [InvocationStatus.FAILED]: mdiAlertRhombusOutline,
    [InvocationStatus.ABORTED]: mdiStopCircleOutline
  },
  '',
  status => status
);

const wantStatusColor = want(
  {
    fail: '#ED3A4A',
    warning: '#F1BD5C',
    success: '#34CC88'
  },
  '#3765AF', // progress blue color for unresolved statuses
  status => {
    if (FAILURES.includes(status)) {
      return 'fail';
    }

    if (SUCESSES.includes(status)) {
      return 'success';
    }

    if (WARNINGS.includes(status)) {
      return 'warning';
    }
  }
);
