import React from 'react';
import { string, func } from 'prop-types';

import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import './NavigationTab.local.scss';

export class NavigationTab extends React.PureComponent {
  render() {
    const { active, onClick, protocol } = this.props;
    const data = [
      { label: 'Workload', value: 'workload' },
      { label: 'Sessions', value: 'sessions' },
      { label: 'Objects', value: 'objects' },
      { label: 'Storage', value: 'storage' }
    ];

    if (protocol === 'ORACLE') {
      data.push({ label: 'License reporting', value: 'license' });
    }
    return (
      <div className="navigation-tab">
        <Tabs
          value={data.map(e => e.value).indexOf(active)}
          indicatorColor="primary"
          textColor="primary"
        >
          {data.map(tab => (
            <Tab
              key={tab.label}
              label={tab.label}
              onClick={() => onClick(tab.value)}
              data-qa={`navigate-to-${tab.value}`}
            />
          ))}
        </Tabs>
      </div>
    );
  }
}

NavigationTab.propTypes = {
  active: string,
  onClick: func
};

export default NavigationTab;
