export function mapAccountsTree(data) {
  // data is split into 3 levels,
  // first level is defined by `protocol`
  // we find root nodes by matching protocols.
  const roots = data.reduce(splitIntoRootsByProtocol, []);
  roots.forEach(root => {
    // define the servers map
    // all server and database objects for this protocol
    root.servers = root.nodes.reduce(splitIntoRootsByAddress, {});
    const servers = Object.keys(root.servers);
    servers.forEach(name => {
      const server = root.servers[name];
      server.databases = server.nodes.reduce(splitIntoRootsByDatabase, {});
    });
  });
  return roots;
}
export function splitIntoRootsByProtocol(acc, item) {
  const protocolBlacklist = ['SSH'];
  const { protocol } = item;
  if (protocolBlacklist.includes(protocol)) {
    return acc;
  }
  if (!(item.special && item.special.database)) {
    return acc;
  }
  const root = acc.find(r => r.name === protocol);
  if (root) {
    root.nodes = [...root.nodes, item];
  } else {
    acc.push({
      name: item.protocol,
      path: item.protocol,
      type: 'dbms',
      nodes: [item],
      servers: []
    });
  }
  return acc;
}
function splitIntoRootsByAddress(serversMap, item) {
  const { address } = item;
  const existingEntry = serversMap[address];
  if (existingEntry) {
    existingEntry.nodes = [...existingEntry.nodes, item];
  } else {
    serversMap[address] = {
      name: item.address,
      path: `${item.protocol}--${item.address}`,
      type: 'server',
      nodes: [item]
    };
  }
  return serversMap;
}
function splitIntoRootsByDatabase(databaseMap, item) {
  const {
    special: { database: name }
  } = item;
  databaseMap[name] = {
    name,
    path: `${item.protocol}--${item.address}--${name}`,
    node: item
  };
  return databaseMap;
}

/**
 *
 * @param {*} content // /account call data
 *
 * This will resturcture the list of accounts into a list of dbms entries with servers and databases.
 */

export function mapAccountsNewStructure(content, protocols) {
  const accounts = { raw: content };

  let protocolsArr = protocols.map(p => p.name);

  if (protocolsArr.length === 0) {
    protocolsArr = ['CREDENTIAL', 'ILOM', 'SSH'];
  }

  accounts.databases = content.filter(account => account.special.database);
  accounts.dbms = [];

  /* eslint-disable */
  accounts.databases.forEach(function resolver(entry) {
    /**
     * Add a path def to the database entry
     * path descriptions are made here, to mimic somewhat the old mapping.
     * Keep other properties.
     */
    const database = {
      ...entry,
      path: `${entry.protocol}--${entry.address}--${entry.special.database}`
    };

    let dbms = accounts.dbms.find(dbms => dbms.name === database.protocol);

    const dbServer = accounts.raw.find(
      account => protocolsArr.includes(account.protocol) && account.address === database.address
    );

    let server;

    // a server entry exists
    if (dbServer) {
      server = {
        path: `${database.protocol}--${database.address}`,
        ...dbServer,
        databases: [database]
      };
    } else {
      // db does not have an associated server connection so create a 'placeholder' for server
      server = {
        path: `${database.protocol}--${database.address}`,
        address: database.address,
        databases: [database]
      };
    }

    if (!dbms) {
      /**
       * Add a new dbms entry
       *
       * path descriptions are made here, to mimic somewhat the old mapping.
       */
      accounts.dbms.push({
        name: database.protocol,
        protocol: database.protocol,
        path: database.protocol,
        databases: [database],
        servers: [server]
      });

      return;
    }

    /**
     * DBMS exists. Update it
     */
    dbms.databases.push(database);

    // find server entr
    let dbmsServerEntry = dbms.servers.find(sv => sv.address === database.address);

    if (!dbmsServerEntry) {
      dbms.servers.push({ ...server, databases: [database] });
    } else {
      if (dbmsServerEntry.databases) {
        dbmsServerEntry.databases.push(database);
      } else {
        dbmsServerEntry.databases = [database];
      }
    }
  }, accounts);

  // find all existing servers
  const allServers = content.filter(entry => protocolsArr.includes(entry.protocol));

  // check if servers have a database
  // if they don't add them in a DBMS called "OTHER".
  // will be removed from there as soon as they get a database
  allServers.forEach(server => {
    const exists = accounts.databases.find(db => db.address === server.address);

    if (!exists) {
      const otherIndex = accounts.dbms.findIndex(d => d.name === 'OTHER');

      if (otherIndex > -1) {
        accounts.dbms[otherIndex].servers.push({
          path: `OTHER--${server.address}`,
          ...server,
          databases: []
        });
      } else {
        accounts.dbms.push({
          name: 'OTHER',
          path: 'OTHER',
          protocol: 'OTHER',
          servers: [{ path: `OTHER--${server.address}`, ...server, databases: [] }]
        });
      }
    }
  });

  const otherIndex = accounts.dbms.findIndex(d => d.name === 'OTHER');
  if (otherIndex > -1 && accounts.dbms[otherIndex].servers.length === 0) {
    accounts.dbms.splice(otherIndex, 1);
  }
  return accounts;
}

export function mapProtocols(protocols) {
  const allowedProtocols = protocols.filter(p => {
    if (p.is_abstract) {
      return false;
    }

    if (p?.special_required?.includes?.('database')) {
      return false;
    }

    return true;
  });

  return allowedProtocols;
}

export function mapUpdateDBStatus(existingDb, databases, isLoading) {
  let statuses = [...existingDb];

  databases.forEach(function(db) {
    const index = existingDb.findIndex(d => d.name === db.name);
    if (index > -1) {
      statuses[index] = { ...db, loading: isLoading };
    } else {
      statuses = [...statuses, { ...db, loading: isLoading }];
    }
  });

  return statuses;
}
