import React from 'react';
import * as PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { FormattedMessage } from 'react-intl';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiPlaySpeed } from '@mdi/js';
import CircularProgress from '@material-ui/core/CircularProgress';
import Button from '@material-ui/core/Button';

import { JobResults } from 'shared/components/JobResults';
import { createUrl } from 'shared/lib';

import './search-results.local.scss';

export class SearchResultsComponent extends React.PureComponent {
  render() {
    const {
      className,
      category,
      totalResults,
      isLoading,
      displayedResults,
      content,
      searchedTerm = '',
      setFilterSearch,
      setFilterType
    } = this.props;
    return (
      <div className={className}>
        {isLoading ? (
          <div className="header-area">
            <div className="category-name">{category}</div>
            <div className="loading">
              <CircularProgress size="20px" />
            </div>
          </div>
        ) : (
          <>
            <div className="header-area">
              <div className="category-name">{category}</div>
              <div className="separator">-</div>
              <div className="total-results">
                {totalResults} <FormattedMessage id="dashboard.results" />
              </div>
              {totalResults > displayedResults && (
                <Button className="all-results qa-view-all-results">
                  {category === 'jobs' && (
                    <Link to={() => this.createURLLink()}>
                      <FormattedMessage id="dashboard.view-all-results" />
                    </Link>
                  )}
                  {category === 'processes' && (
                    <div
                      role="presentation"
                      onClick={() => {
                        setFilterSearch(searchedTerm);
                        setFilterType('process');
                      }}
                    >
                      <Link to="/system/entities">
                        <FormattedMessage id="dashboard.view-all-results" />
                      </Link>
                    </div>
                  )}
                </Button>
              )}
            </div>
            <div className="content-area">
              {category === 'jobs' && <JobResults jobs={content} fromSearch />}
              {category === 'processes' && this.renderProcessesResults()}
            </div>
          </>
        )}
      </div>
    );
  }

  renderProcessesResults = () =>
    this.props.content.map((process, index) => (
      <div key={index} className="result-process-row">
        <div className="icon-container">
          <SvgIcon>
            <Icon path={mdiPlaySpeed} />
          </SvgIcon>
        </div>
        <div className="process-container">{process.name}</div>
      </div>
    ));

  createURLLink = () => {
    let url = '/jobs/search-results';
    const params = {};

    if (this.props.filters.nameFilter) {
      params.name_filter = this.props.filters.nameFilter;
    }

    if (this.props.filters.customFilter) {
      params[this.props.filters.customFilter.type] = this.props.filters.customFilter.value;
    }

    url = createUrl(url, params);
    return url;
  };
}

export default SearchResultsComponent;

SearchResultsComponent.propTypes = {
  className: PropTypes.string,
  content: PropTypes.oneOfType([PropTypes.array, PropTypes.object]),
  category: PropTypes.string,
  totalResults: PropTypes.number,
  isLoading: PropTypes.bool,
  displayedResults: PropTypes.number
};

SearchResultsComponent.defaultProps = {
  className: 'results-container',
  content: [],
  category: '',
  totalResults: 0,
  isLoading: false,
  displayedResults: 3
};
