import React from 'react';
import { Switch } from 'shared/components';
import { Ensure } from 'core/components/Ensure/Ensure';

import { useFormikContext } from 'formik';
import { Parameter } from 'shared/components/form/parameters';

import normalize from 'shared/components/form/parameters/normalizeParameter';

import { Section, Row, Column } from '../shared/components/layout';

import { chunk } from 'lodash';

/** accepts recived parameters for invocations that are already scheduled */
export const ProcessParameters = ({ recieved = {}, editable }) => {
  const { values } = useFormikContext();

  /**
   * Returns a parameter list from a given list of parameters, and a Process name
   * This could be turned into a connected component that can take the processes from entities for itself.
   * */
  function selectParametersForProcess(processName, processes) {
    const process = processes.find(item => item.name === processName);

    if (process) {
      return process.parameters.map(p => ({ ...p, value: recieved[p.name], disabled: !editable }));
    }
    return [];
  }

  function currentValue(allValues, parameter) {
    if (allValues[parameter.name] !== undefined) {
      return allValues[parameter.name];
    }

    if (allValues?.parameters && allValues?.parameters[parameter.name] !== undefined) {
      return allValues?.parameters[parameter.name];
    }

    return undefined;
  }

  return (
    <Section>
      <Section.Header>
        <Section.Title>Parameters</Section.Title>
      </Section.Header>
      <Section.Content>
        <Switch value={!!values.process}>
          <Switch.Case value={false}>
            <Column xs={12}>
              <p>Select a process to edit parameters</p>
            </Column>
          </Switch.Case>
          <Switch.Case value>
            <Ensure type="form">
              {({ data: forms }) => (
                <Ensure type="type">
                  {({ data: datatypes }) => (
                    <Ensure type="prototype">
                      {({ data: prototypes }) => (
                        <Ensure type="process">
                          {({ data }) => {
                            const parameters = selectParametersForProcess(values.process, data);

                            const normalized = parameters.map(parameter => {
                              return normalize(parameter, datatypes, prototypes, forms);
                            });

                            /*  << +- 1 of '/' to toggle
                            const notSupported = false;
                            /* /

                            // In old app, if at least one normalized parameter exists that is not supported,
                            // the whole process is disregarded.

                            const notSupported = normalized.some(p => !p.is_supported);
                            // */

                            const filtered = normalized
                              .filter(p => p.editor.type !== 'hidden')
                              .sort((a, b) => a.position - b.position); // sort parameters based on the meta.position

                            // if (notSupported || filtered.length === 0) {
                            if (filtered.length === 0) {
                              return (
                                <Row>
                                  <Column>
                                    <p>
                                      This process contains unsuported parameters with no defaults,
                                      and cannot be run from the web interface.
                                    </p>
                                  </Column>
                                </Row>
                              );
                            }

                            const chunks = chunk(filtered, 2);

                            return chunks.map((params, index) => (
                              <Row key={index} spacing={3}>
                                {params.map(parameter => (
                                  <Column key={parameter.name} xs={6}>
                                    <Parameter
                                      normalized={parameter}
                                      value={currentValue(values, parameter)}
                                      disabled={!editable}
                                      recieved={recieved}
                                    />
                                  </Column>
                                ))}
                              </Row>
                            ));
                          }}
                        </Ensure>
                      )}
                    </Ensure>
                  )}
                </Ensure>
              )}
            </Ensure>
          </Switch.Case>
        </Switch>
      </Section.Content>
    </Section>
  );
};
