import React from 'react';
import * as PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Cookie from 'js-cookie';

import { actionStates } from 'shared/lib';
import { loginSelector } from 'session/store/state';
import { actions } from 'session/store/actions';
import { Container } from 'session/components';

import { Form } from './components';

export class LoginPage extends React.PureComponent {
  componentDidMount() {
    Cookie.remove('Authorization');
  }

  componentDidUpdate() {
    if (this.props.status === actionStates.SUCCESS) {
      this.props.history.push('/dashboard');
    }
  }

  render() {
    return (
      <Container
        title="session.login.title"
        isLoading={this.props.status === actionStates.LOADING}
      >
        <Form
          isLoading={this.props.status === actionStates.LOADING}
          errors={this.props.errors}
          submit={this.props.login}
          resetErrors={this.props.resetErrors}
        />
      </Container>
    );
  }

  static path = '/login';
}

LoginPage.propTypes = {
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  errors: PropTypes.object.isRequired,
  status: PropTypes.oneOf([actionStates.LOADING, actionStates.SUCCESS, actionStates.FAIL, '']).isRequired,
  login: PropTypes.func.isRequired,
  resetErrors: PropTypes.func.isRequired,
};

export const mapStateToProps = (state) => ({
  errors: loginSelector(state).get('errors'),
  status: loginSelector(state).get('status'),
});

export const mapDispatchToProps = (dispatch) => ({
  login: (data) => dispatch(actions.login.dispatchLoading(data)),
  resetErrors: () => dispatch(actions.login.dispatchFail([])),
});

export default connect(mapStateToProps, mapDispatchToProps)(LoginPage);
