import React, { useState, useCallback } from 'react';
import { connect } from 'react-redux';

import { fetchEntity } from '../../actions';

import { Switch } from 'shared/components';
import { Button } from 'shared/components/controls';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Result } from 'shared/components/overlays/Result/Result';
import { Paper } from 'shared/components/layout';

import { deleteEntity } from 'shared/api/delete';

import { namespaceChildrenListSelector } from '../../selectors';

import SvgIcon from '@material-ui/core/SvgIcon';

import { mdiTrashCan } from '@mdi/js';

export const ConnectedDeleteNamespace = connect(
  state => ({
    nestedNames: namespaceChildrenListSelector(state)
  }),
  {
    refresh: fetchEntity
  }
)(DeleteNamespace);

function DeleteNamespace({ disabled, entity, refresh, nestedNames }) {
  const canDelete = nestedNames.length === 0;
  const [result, setResult] = useState('attempt');
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: useCallback(() => {
      setOpen(true);
    }, []),
    close: useCallback(() => {
      setResult('attempt');
      setOpen(false);
    }, [])
  };

  const type = entity.entity;
  const { name } = entity;

  const terminator = useCallback(() => {
    deleteEntity(name, type).then(response => {
      setResult(response.result);
    });
  }, [type, name]);

  return (
    <>
      <Button disabled={disabled} classes={{ root: 'entity-button' }} onClick={modalActions.open}>
        Delete
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            minWidth: '400px',
            maxHeight: '368px',
            maxWidth: '65vw'
          }}
        >
          <ModalHeader
            title={`Delete ${type}`}
            icon={
              <SvgIcon>
                <path d={mdiTrashCan} />
              </SvgIcon>
            }
          />
          <Switch value={canDelete}>
            <Switch.Case value>
              <Switch value={result}>
                <Switch.Case value="attempt">
                  <Paper.Content scrollable={false}>
                    <Result type="fail">
                      <BodyText>
                        Are you sure you want to remove this {type} ? This could affect the way the
                        user will be able to interact with the app.
                      </BodyText>
                    </Result>
                  </Paper.Content>
                  <Paper.Footer>
                    <Paper.Footer.Right>
                      <Button
                        color="primary"
                        variant="contained"
                        onClick={() => {
                          terminator();
                        }}
                      >
                        Delete
                      </Button>
                    </Paper.Footer.Right>
                  </Paper.Footer>
                </Switch.Case>
                <Switch.Case value="success">
                  <Result type="success">
                    <BodyText>{name} successfully deleted!</BodyText>
                  </Result>
                  <Paper.Footer>
                    <Paper.Footer.Right>
                      <Button
                        onClick={() => {
                          modalActions.close();
                          refresh(type);
                        }}
                      >
                        Done
                      </Button>
                    </Paper.Footer.Right>
                  </Paper.Footer>
                </Switch.Case>
                <Switch.Case value="fail">
                  <Result type="fail">
                    <BodyText>You do not have permissions to delete this entity.</BodyText>
                  </Result>
                  <Paper.Footer>
                    <Paper.Footer.Right>
                      <Button
                        onClick={() => {
                          modalActions.close();
                        }}
                      >
                        Close
                      </Button>
                    </Paper.Footer.Right>
                  </Paper.Footer>
                </Switch.Case>
              </Switch>
            </Switch.Case>
            <Switch.Case value={false}>
              <Result type="fail">
                <BodyText>To delete a namespace it must be empty!</BodyText>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                    }}
                  >
                    Close
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
}

function BodyText({ children }) {
  return (
    <h2
      style={{
        fontFamily: 'Open Sans',
        fontStyle: 'normal',
        fontWeight: 'bold',
        fontSize: '14px',
        lineHeight: '20px',
        letterSpacing: '0.25px',
        marginTop: '24px'
      }}
    >
      {children}
    </h2>
  );
}
