import { RestClient } from 'shared/lib/rest';

export function getPrivileges(name, isAlias) {
  return RestClient.get(`/entity/${name}/privileges`, {
    ...(isAlias && { cls: 'alias' }),
    page_size: 2147483645
  });
}

/**
    privilege: type,
    enitity_name: entityName,
    alias: isAlias
    role: user_or_group.role,
    role_type: user_or_group.is_group ? 'group' : 'user',
    explicit_grant: user_or_group.granted.includes(type),
    explicit_denied: user_or_group.denied.includes(type),
    action: granted ? 'revoke' : 'grant'
  }}
 */
export async function setPrivileges(data) {
  const { granted, alias } = data;

  const actions = granted ? ['revoke', 'deny'] : ['grant', 'allow'];

  try {
    await RestClient.patch(
      `/${data.role_type}/${data.role}/${actions[0]}/${data.privilege}/${data.enitity_name}`,
      alias ? { cls: 'alias' } : undefined,
      {
        page_size: 2147483645
      }
    );

    await RestClient.patch(
      `/${data.role_type}/${data.role}/${actions[1]}/${data.privilege}/${data.enitity_name}`,
      alias ? { cls: 'alias' } : undefined,
      {
        page_size: 2147483645
      }
    );

    return getPrivileges(data.enitity_name, data.alias);
  } catch (e) {
    throw new Error('failed');
  }
}
