import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';

import { connect } from 'react-redux';

import { getWorkloadData } from 'shared/lib/workload';

import { actions } from 'shared/store/actions';
import { jobsSelector, globalFiltersSelector } from 'shared/store/state';
import { Button } from 'shared/components/controls';
import { Loader } from 'shared/components/loaders';
import { loadAllUsersThunk } from 'users-manager/store/thunks';

import { Header } from 'core/components/Header';
import {
  WorkloadGraph,
  Center,
  RangeDatePicker,
  SimpleSearchInput,
  Switch
} from 'shared/components';
import { Main, Paper } from 'shared/components/layout';
import { NavigationTab } from 'shared/components/layout/NavigationTab/NavigationTab';
import { Result } from 'shared/components/overlays/Result/Result';

import { JobItem } from './components/job-item';
import { JobList } from './components/job-list';

import { Filters } from './containers/filters';
import { JobsInvocations } from './containers/jobs-invocations';

import { Schedule } from './containers/Schedule/Schedule';
import { TotalJobsCount } from './components/total-jobs-count/TotalJobsCount';

import './jobs.local.scss';

function renderJobItem(item, index, filters) {
  return <JobItem extended key={index} job={item} filters={filters} />;
}

class RootModule extends React.PureComponent {
  constructor(props) {
    super(props);

    const query = new URLSearchParams(this.props.location.search);

    this.state = {
      range: {
        startDate: moment.unix(props.globalFilters.get('startDate')),
        endDate: moment.unix(props.globalFilters.get('endDate'))
      },
      shell_host_address_filter: query.get('shell_host_address_filter') || '',
      platform_filter: {
        dbms: query.get('platform_filter_dbms') || '',
        name: query.get('platform_filter_name') || ''
      }, // will have the platform_name_filter and platform_dbms_filter
      status: query.get('status') || '',
      database_name_filter: query.get('database_name_filter') || '',
      name_filter: '',
      user_filter: query.get('user_filter') || '',
      selectedTab: 'all',
      isWorkloadDisplayed: false,
      pageFilters: {}
    };
  }

  componentDidMount() {
    if (this.props.allUsers.length === 0) {
      this.props.loadAllUsers();
    }
  }

  render() {
    const {
      shell_host_address_filter,
      platform_filter,
      status,
      database_name_filter,
      user_filter,
      selectedTab,
      isWorkloadDisplayed
    } = this.state;

    const callFailed = this.props.jobs.get('callFailed');
    const reason = this.props.jobs.get('reason');

    if (callFailed && reason === 'rights' && !this.props.jobs.get('fetching')) {
      return (
        <>
          <Header title="Jobs" />
          <Main>
            <Paper>
              <Paper.Content>
                <Result type="fail">
                  <BodyText>
                    You don&apos;t have sufficient privileges to access invocation data. Please
                    contact your administrator.
                  </BodyText>
                </Result>
              </Paper.Content>
            </Paper>
          </Main>
        </>
      );
    }

    return (
      <JobsInvocations>
        <Header title="Jobs">
          <RangeDatePicker
            onDatesChange={this.onChangeDatesHandler}
            startDate={this.state.range.startDate}
            endDate={this.state.range.endDate}
          />
        </Header>
        <Main>
          <Paper>
            <Paper.Header>
              <NavigationTab
                tabs={[
                  { label: 'All jobs', value: 'all' },
                  { label: 'Current jobs', value: 'current-jobs' },
                  { label: 'Scheduled', value: 'scheduled' }
                ]}
                active={selectedTab}
                onClick={this.handleOnTabChange}
              />
              {(selectedTab === 'all' || selectedTab === 'current-jobs') && (
                <Button
                  style={{
                    whiteSpace: 'nowrap',
                    lineHeight: '20px',
                    height: '20px',
                    paddingTop: '0'
                  }}
                  onClick={this.toggleWorkloadDisplay}
                >
                  {isWorkloadDisplayed ? 'Hide Workload' : 'Display workload'}
                </Button>
              )}
            </Paper.Header>
            <Switch value={selectedTab}>
              <Switch.Case value="all">
                {!this.props.jobs.get('fetching') && (
                  <TotalJobsCount jobs={this.props.jobs.get('content').toJS()} status={status} />
                )}
                {isWorkloadDisplayed && (
                  <Paper.Header>
                    <div style={{ height: 220, width: '100%' }}>
                      {this.props.jobs.get('fetching') ? (
                        <Center>
                          <Loader>Computing workload</Loader>
                        </Center>
                      ) : (
                        <WorkloadGraph
                          data={getWorkloadData(this.props.jobs.get('content').toJS(), {
                            currentDate: moment.unix(
                              this.props.globalFilters.get('startDate'),
                              'YYYY-MM-DD'
                            ),
                            endDate: moment.unix(
                              this.props.globalFilters.get('endDate'),
                              'YYYY-MM-DD'
                            )
                          })}
                          isSingleDay={
                            moment
                              .unix(this.props.globalFilters.get('startDate'), 'YYYY-MM-DD')
                              .format('YYYY-MM-DD') ===
                            moment
                              .unix(this.props.globalFilters.get('endDate'), 'YYYY-MM-DD')
                              .format('YYYY-MM-DD')
                          }
                          setSingleDay={this.onChangeDatesHandler}
                        />
                      )}
                    </div>
                  </Paper.Header>
                )}
                {!isWorkloadDisplayed && <></>}
              </Switch.Case>
              <Switch.Case value="current-jobs">
                {!this.props.jobs.get('fetching') && (
                  <TotalJobsCount
                    jobs={this.props.jobs
                      .get('content')
                      .filter(item => item.get('status') !== 'SCHEDULED')
                      .toJS()}
                    status={status}
                  />
                )}
                {isWorkloadDisplayed && (
                  <Paper.Header>
                    <div style={{ height: 220, width: '100%' }}>
                      {this.props.jobs.get('fetching') ? (
                        <Center>
                          <Loader>Computing workload</Loader>
                        </Center>
                      ) : (
                        <WorkloadGraph
                          data={getWorkloadData(this.props.jobs.get('content').toJS(), {
                            currentDate: moment.unix(
                              this.props.globalFilters.get('startDate'),
                              'YYYY-MM-DD'
                            ),
                            endDate: moment.unix(
                              this.props.globalFilters.get('endDate'),
                              'YYYY-MM-DD'
                            )
                          })}
                          isSingleDay={
                            moment
                              .unix(this.props.globalFilters.get('startDate'), 'YYYY-MM-DD')
                              .format('YYYY-MM-DD') ===
                            moment
                              .unix(this.props.globalFilters.get('endDate'), 'YYYY-MM-DD')
                              .format('YYYY-MM-DD')
                          }
                          setSingleDay={this.onChangeDatesHandler}
                        />
                      )}
                    </div>
                  </Paper.Header>
                )}
                {!isWorkloadDisplayed && <></>}
              </Switch.Case>
              <Switch.Case value="scheduled">
                <TotalJobsCount
                  jobs={this.props.jobs
                    .get('content')
                    .filter(item => item.get('status') === 'SCHEDULED')
                    .toJS()}
                  status="SCHEDULED"
                />
              </Switch.Case>
            </Switch>
            <Paper.Content scrollable={false}>
              <div className="jobs-filters-controllers">
                <div className="job-search">
                  <SimpleSearchInput
                    value={this.state.name_filter}
                    onChange={this.onSearchInputChange}
                    onKeyDown={this.onKeyDown}
                  />
                </div>
                <Filters
                  selections={{
                    shell_host_address_filter,
                    platform_filter,
                    status,
                    database_name_filter,
                    user_filter
                  }}
                  onChange={this.handleOnFilterChange}
                  excludeStatus={selectedTab === 'scheduled'}
                  currentTab={selectedTab}
                />
              </div>
              {this.props.jobs.get('fetching') ? (
                <Center>
                  <Loader>Loading jobs list</Loader>
                </Center>
              ) : (
                <JobList items={this.returnListBasedOnTab()} filters={this.state.pageFilters}>
                  {renderJobItem}
                </JobList>
              )}
            </Paper.Content>
            <Paper.Footer>
              <Schedule />
              <Button to="/jobs/job">New Job</Button>
            </Paper.Footer>
          </Paper>
        </Main>
      </JobsInvocations>
    );
  }

  toggleWorkloadDisplay = () => {
    this.setState(prevState => ({
      isWorkloadDisplayed: !prevState.isWorkloadDisplayed
    }));
  };

  updateQSP = (filter, value) => {
    const query = new URLSearchParams(this.props.location.search);

    if (typeof value === 'object') {
      const keys = Object.keys(value);

      keys.forEach(key => {
        if (!value[key]) {
          query.delete(`${filter}_${key}`);
        } else if (query.has(`${filter}_${key}`) && query.get(`${filter}_${key}`) !== value[key]) {
          query.set(`${filter}_${key}`, value[key]);
        } else {
          query.append(`${filter}_${key}`, value[key]);
        }
      });
    } else if (!value) {
      query.delete(filter);
    } else if (query.has(filter) && query.get(filter) !== value) {
      query.set(filter, value);
    } else {
      query.append(filter, value);
    }

    query.toString();

    this.props.history.push({ search: `?${query}` });
  };

  handleOnTabChange = tab => {
    const query = new URLSearchParams(this.props.location.search);

    if (query.get('status')) {
      query.delete('status');
      query.toString();

      this.props.history.push({ search: `?${query}` });
      this.setState({ status: '', selectedTab: tab }, () => {
        this.performFilter(this.state);
      });
    } else {
      this.setState({ selectedTab: tab });
    }
  };

  handleOnFilterChange = (filter, value) => {
    this.updateQSP(filter, value);

    this.setState(
      {
        [filter]: value
      },
      () => {
        this.performFilter(this.state);
      }
    );
  };

  returnListBasedOnTab = () => {
    const { selectedTab, status } = this.state;
    const list = this.props.jobs.get('content');

    if (status) {
      return list.filter(item => item.get('status') === status);
    }

    if (selectedTab === 'scheduled') {
      return list.filter(item => item.get('status') === 'SCHEDULED');
    }

    if (selectedTab === 'current-jobs') {
      return list.filter(item => item.get('status') !== 'SCHEDULED');
    }

    return list;
  };

  performFilter = ({
    shell_host_address_filter,
    platform_filter,
    status,
    database_name_filter,
    user_filter,
    name_filter,
    range
  }) => {
    // const range = {
    //   startDate: moment.unix(this.props.globalFilters.get('startDate')),
    //   endDate: moment.unix(this.props.globalFilters.get('endDate'))
    // };
    if (range.startDate === null || range.endDate === null) return;

    const start = moment(range.startDate.format('YYYY-MM-DD'));
    const end = moment(range.endDate.format('YYYY-MM-DD'));
    const days = parseInt(moment.duration(end.diff(start)).asDays(), 10);

    const payload = {
      date_invoke: range.startDate.format('YYYY-MM-DD'),
      period: days + 1,
      page_size: 2147483645
    };

    if (shell_host_address_filter) {
      payload.shell_host_address_filter = shell_host_address_filter;
    }

    if (platform_filter) {
      payload.platform_name_filter = platform_filter.name;
      payload.platform_dbms_filter = platform_filter.dbms;
    }

    if (status) {
      payload.status = status;
    }

    if (database_name_filter) {
      payload.database_name_filter = database_name_filter;
    }

    if (name_filter) {
      payload.name_filter = name_filter;
    }

    if (user_filter) {
      payload.owner = user_filter;
    }

    this.props.getJobs(payload);
    this.setState({ pageFilters: payload });
  };

  onSearchInputChange = event => {
    this.setState({
      name_filter: event.target.value
    });
  };

  onKeyDown = event => {
    if (event.key === 'Enter' && !this.props.jobs.get('fetching')) {
      this.performFilter(this.state);
    }
  };

  onChangeDatesHandler = range => {
    this.setState(
      {
        range: {
          startDate: range.startDate,
          endDate: range.endDate
        }
      },
      () => {
        this.props.setGlobalFilters(range);
        this.performFilter(this.state);
      }
    );
  };
}
RootModule.propTypes = {
  globalFilters: PropTypes.object.isRequired,
  setGlobalFilters: PropTypes.func.isRequired,
  jobs: PropTypes.object.isRequired,
  getJobs: PropTypes.func.isRequired
};

export const mapStateToProps = state => ({
  globalFilters: globalFiltersSelector(state),
  jobs: jobsSelector(state),
  allUsers: state.usersManager.users.all
});

export const mapDispatchToProps = dispatch => ({
  getJobs: data => {
    dispatch(actions.getJobs.dispatchLoading(data));
  },
  setGlobalFilters: data => dispatch(actions.setGlobalFilters.dispatchLoading(data)),
  loadAllUsers: () => dispatch(loadAllUsersThunk())
});

export default connect(mapStateToProps, mapDispatchToProps)(RootModule);

function BodyText({ children }) {
  return (
    <h2
      style={{
        fontFamily: 'Open Sans',
        fontStyle: 'normal',
        fontWeight: 'bold',
        fontSize: '14px',
        lineHeight: '20px',
        letterSpacing: '0.25px',
        marginTop: '24px'
      }}
    >
      {children}
    </h2>
  );
}
