import React from 'react';
import { connect } from 'react-redux';
import { object, func } from 'prop-types';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiDatabase } from '@mdi/js';

import { changeStateThunk } from '../../../../../store/thunks';

import './Database.local.scss';

export class Database extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      isExpanded: false
    };
  }

  static getDerivedStateFromProps(props) {
    if (props.storeState.path === props.data.path) {
      return { isExpanded: true };
    }

    return null;
  }

  render() {
    const {
      data,
      storeState: { path }
    } = this.props;

    const name = data.special.database;

    return (
      <li className="database-item">
        <header className={`${data.path === path ? 'active' : ''}`}>
          <div
            role="presentation"
            data-qa={`select-${name}-database`}
            onClick={() => this.setPath(data.path)}
          >
            <div className="icon">
              <SvgIcon>
                <Icon path={mdiDatabase} />
              </SvgIcon>
            </div>
            <div className="value" title={name}>
              {name}
            </div>
          </div>
        </header>
      </li>
    );
  }

  toggleExpanded = () => {
    this.setState(prevState => ({
      isExpanded: !prevState.isExpanded
    }));
  };

  setPath = path => {
    this.props.changeState({ path, level: 'workload' });
  };
}

Database.propTypes = {
  data: object,
  changeState: func,
  storeState: object
};

export const ConnectedDatabase = connect(
  state => ({
    storeState: state.explorer.state
  }),
  dispatch => ({
    changeState: newState => dispatch(changeStateThunk(newState))
  })
)(Database);
