import _ from 'lodash';
import { createSelector } from 'reselect';

function pipe(...fns) {
  return arg => fns.reduce((prev, fn) => fn(prev), arg);
}

function filterBy(selector, equalityFn, value) {
  return data => {
    if (value) {
      return data.filter(el => equalityFn(selector(el), value));
    }

    return data;
  };
}

export function retrieveFilteredAccounts(protocol, server) {
  return state => {
    const { accounts } = state.explorer;

    const filterByProtocol = filterBy(
      el => (el.nodes[0] ? el.nodes[0].protocol : ''),
      (stateValue, currentValue) => stateValue === currentValue,
      protocol
    );

    const filterByServer = filterBy(
      el => Object.keys(el.servers),
      (servers, currentServer) => servers.includes(currentServer),
      server
    );

    return pipe(filterByProtocol, filterByServer)(accounts);
  };
}

export function retrieveServerEntry({ protocol, address }, structure) {
  const dbms = structure.dbms.find(entry => entry.protocol === protocol);
  return dbms && dbms.servers.find(server => server.address === address);
}

export function retrieveDatabaseEntry({ database, protocol, address }, structure) {
  const server = retrieveServerEntry({ protocol, address }, structure);

  return server.databases.find(db => db.special.database === database);
}

const getStatePath = state => state.explorer.state.path;
const getStructured = state => state.explorer.structured.dbms;
const getSessions = state => state.explorer.sessions;
const getSessionsFlags = state => state.explorer.flags.sessions;
const getStoragePointsLoadingFlag = state => state.explorer.flags.storage.loadingPoints;
const getStorageDetailsLoadingFlag = state => state.explorer.flags.storage.loadingDetails;
const getSelectedStorage = state => state.explorer.storage.selected;
const getStoredEntries = state => state.explorer.storage.storedEntries;
const getSelectedObject = state => state.explorer.objects.selectedObject;
const getSelectedOwner = state => state.explorer.objects.selectedOwner;
const getObjectsEntries = state => state.explorer.objects.storedEntries;
const getObjectsLoadingFlag = state => state.explorer.flags.objects.loading;

export const getFullPath = createSelector([getStatePath, getStructured], (fullPath, dbms) => {
  const pathArr = fullPath.split('--');
  return dbms
    .find(({ path }) => path === pathArr[0])
    ?.servers.find(({ address }) => address === pathArr[1])
    ?.databases.find(({ special }) => special.database === pathArr[2])?.name;
});

export const haveStorageData = createSelector(
  [getFullPath, getStoredEntries],
  (fullPath, storedEntries) => {
    const selectedStorage = storedEntries[fullPath] || {};

    return Object.keys(selectedStorage).length > 0;
  }
);

export const getFormattedStorage = createSelector(
  [getFullPath, getStoredEntries, getStoragePointsLoadingFlag],
  (fullPath, storedEntries, isLoading) => {
    const points = storedEntries[fullPath]?.points;
    if (isLoading || !_.isArray(points)) {
      return [];
    }
    const factor = 1024;
    return points.map(point => {
      let usage = (point.Used_MB / point.Total_MB) * 100;
      if (usage < 0) {
        usage = 0;
      }
      return {
        ns: point.Tablespace,
        freePerc: point['Pct._Free'],
        freeGB: point.Free_MB / factor,
        totalGB: point.Total_MB / factor,
        usedGB: point.Used_MB / factor,
        usage
      };
    });
  }
);

export const haveObjectsData = createSelector(
  [getFullPath, getObjectsEntries],
  (fullPath, storedEntries) => {
    const selectedObjects = storedEntries[fullPath] || {};

    return Object.keys(selectedObjects).length > 0;
  }
);

export const getStorageDetailsData = createSelector(
  [
    getFullPath,
    getStoredEntries,
    getFormattedStorage,
    getStorageDetailsLoadingFlag,
    getSelectedStorage
  ],
  (fullPath, storedEntries, list, isLoading, selectedNS) => {
    const details = storedEntries[fullPath]?.details;
    if (isLoading || !_.isArray(details)) {
      return {
        tablespace: {},
        files: []
      };
    }
    return {
      tablespace: list.find(({ ns }) => ns === selectedNS),
      files: details.filter(({ TABLESPACE_NAME }) => TABLESPACE_NAME === selectedNS)
    };
  }
);

export const getStructuredObjects = createSelector(
  [getFullPath, getObjectsEntries, getObjectsLoadingFlag],
  (fullPath, storedEntries, isLoading) => {
    if (isLoading) {
      return [];
    }

    const objects = storedEntries[fullPath] || {};
    const tree = Object.keys(objects).reduce((acc, key) => {
      _.isArray(objects[key]) &&
        objects[key].forEach(object => {
          if (acc[object.OWNER] && !acc[object.OWNER].includes(key)) {
            acc[object.OWNER].push(key);
          } else {
            acc[object.OWNER] = [key];
          }
        });
      return acc;
    }, []);
    return Object.keys(tree).map(key => ({ owner: key, objects: tree[key] }));
  }
);

export const getInvalidObjects = createSelector(
  [getFullPath, getObjectsEntries, getObjectsLoadingFlag],
  (fullPath, storedEntries, isLoading) => {
    if (isLoading) {
      return [];
    }
    const invalidObjects = [];
    const objects = storedEntries[fullPath] || {};
    Object.keys(objects).forEach(key => {
      _.isArray(objects[key]) &&
        objects[key].forEach(object => {
          const included = invalidObjects.findIndex(
            ({ owner, object: name }) => owner === object?.OWNER && name === key
          );
          if (object && object.STATUS !== 'VALID' && included === -1) {
            invalidObjects.push({ owner: object.OWNER, object: key });
          }
        });
    });
    return invalidObjects;
  }
);

export const getSelectedObjectData = createSelector(
  [getSelectedObject, getSelectedOwner, getFullPath, getObjectsEntries],
  (object, owner, fullPath, storedEntries) => {
    const objects = storedEntries[fullPath];
    if (!object || !owner || !_.isObject(objects) || !_.isArray(objects[object])) {
      return [];
    }
    return objects[object].filter(({ OWNER }) => OWNER === owner);
  }
);

export const getSortedSessions = createSelector(
  [getSessions, getSessionsFlags],
  (sessions, { loading, sort }) => {
    if (loading || !_.isArray(sessions)) {
      return [];
    }
    if (sort === undefined) {
      return sessions;
    }
    let sortFunction;
    // Backend doesn't support sorting at this time. Consider moving sort to backend though
    if (sort.order === 'asc') {
      sortFunction = (a, b) => {
        return new Date(a[sort.sortKey]).getTime() - new Date(b[sort.sortKey]).getTime();
      };
    } else {
      sortFunction = (a, b) => {
        return new Date(b[sort.sortKey]).getTime() - new Date(a[sort.sortKey]).getTime();
      };
    }
    return sessions.slice().sort(sortFunction);
  }
);

export const getBackup = (database, backups) => {
  const backup = backups.find(db => db.name === database.name)?.backup;

  return backup;
};

export const getDBStatus = (database, statuses) => {
  const status = statuses.find(db => db.name === database.name)?.status || 'not-connected';

  return status;
};

export const getDBStatusInfo = (database, statuses) => {
  const status = statuses.find(db => db.name === database.name)?.status || 'not-connected';
  const info = statuses.find(db => db.name === database.name)?.message;

  return dbMessage(status, info);
};

const STATUS_TYPES = {
  down: 'down',
  up: 'up',
  error: 'error'
};

export const getDBMSStatus = (dbms, statuses) => {
  if (statuses.length === 0) return 'not-connected';

  const allStatuses = {};

  statuses
    .filter(d => d.dbms === dbms.name)
    .forEach(function findStatus(entry) {
      allStatuses[entry.status] = true;
    });

  if (allStatuses[STATUS_TYPES.down]) return 'down';
  if (allStatuses[STATUS_TYPES.error]) return 'error';
  if (allStatuses[STATUS_TYPES.up]) return 'up';

  return 'not-connected';
};

export const getDBMSStatusInfo = (dbms, statuses) => {
  if (statuses.length === 0) {
    return '';
  }

  const dbmsStatus = {
    down: [],
    error: [],
    up: [],
    'not-connected': []
  };

  statuses
    .filter(d => d.dbms === dbms.name)
    .forEach(function findStatus(entry) {
      dbmsStatus[entry.status].push({
        server: entry.server,
        message: dbMessage(entry.status, entry.message)
      });
    });

  let message = '';

  if (dbmsStatus.down.length > 0) {
    if (dbmsStatus.down.length === 1) {
      message += `${dbmsStatus.down[0].server} has a database down. `;
    } else {
      message += `The following servers: ${dbmsStatus.down.map((sv, index) => {
        if (index < dbmsStatus.down.length - 1) {
          return sv.server;
        }
        return ` ${sv.server}`;
      })} have at least one database down. `;
    }
  }

  if (dbmsStatus.error.length > 0) {
    if (dbmsStatus.error.length === 1) {
      message += `${dbmsStatus.error[0].server} has a database with error. `;
    } else {
      message += `\n The following servers: ${dbmsStatus.error.map((sv, index) => {
        if (index < dbmsStatus.down.length - 1) {
          return sv.server;
        }
        return ` ${sv.server}`;
      })} have at least one database with error. `;
    }
  }

  if (dbmsStatus['not-connected'].length > 0) {
    if (dbmsStatus['not-connected'].length === 1) {
      message += `${dbmsStatus['not-connected'][0].server} server has a database which status could not be determined. Please refresh. `;
    } else {
      message += `Unknown status for databases from servers: (${dbmsStatus['not-connected'].map(
        sv => sv.dbName
      )}). Please refresh. `;
    }
  }

  if (
    dbmsStatus.down.length === 0 &&
    dbmsStatus.error.length === 0 &&
    dbmsStatus['not-connected'].length === 0 &&
    dbmsStatus.up.length > 0
  ) {
    message = 'All servers are up.';
  }

  return message;
};

export const getServerStatus = (server, statuses) => {
  if (statuses.length === 0) return 'not-connected';
  const allStatuses = {};

  statuses
    .filter(sv => sv.server === server.address)
    .forEach(function findStatus(entry) {
      allStatuses[entry.status] = true;
    });

  if (allStatuses[STATUS_TYPES.down]) return 'down';
  if (allStatuses[STATUS_TYPES.error]) return 'error';
  if (allStatuses[STATUS_TYPES.up]) return 'up';

  return 'not-connected';
};

export const getServerStatusInfo = (server, statuses) => {
  if (statuses.length === 0) {
    return '';
  }

  const serverStatus = {
    down: [],
    error: [],
    up: [],
    'not-connected': []
  };

  statuses
    .filter(sv => sv.server === server.address)
    .forEach(function findStatus(entry) {
      serverStatus[entry.status].push({
        dbName: entry.dbName,
        message: dbMessage(entry.status, entry.message)
      });
    });

  let message = '';

  if (serverStatus.down.length > 0) {
    if (serverStatus.down.length === 1) {
      message += `${serverStatus.down[0].dbName} database is down. `;
    } else {
      message += `The following databases: ${serverStatus.down.map(sv => sv.dbName)} are down. `;
    }
  }

  if (serverStatus.error.length > 0) {
    if (serverStatus.error.length === 1) {
      message += `${serverStatus.error[0].dbName} database has errors. `;
    } else {
      message += `Errors reported in databases: (${serverStatus.error.map(sv => sv.dbName)}). `;
    }
  }

  if (serverStatus['not-connected'].length > 0) {
    if (serverStatus['not-connected'].length === 1) {
      message += `${serverStatus['not-connected'][0].dbName} database status could not be determined. Please refresh. `;
    } else {
      message += `Unknown status for databases: (${serverStatus['not-connected'].map(
        sv => sv.dbName
      )}). Please refresh. `;
    }
  }

  if (
    serverStatus.down.length === 0 &&
    serverStatus.error.length === 0 &&
    serverStatus['not-connected'].length === 0 &&
    serverStatus.up.length > 0
  ) {
    message = 'All databases are up.';
  }

  return message;
};

export const getEntityPendingStatus = (entity, statuses, type) => {
  const pathArray = entity.path.split('--');
  const dbms = pathArray[0];
  if (statuses.length === 0) return { label: `Loading ${type} status...`, loading: true };

  if (type === 'server') {
    return statuses
      .filter(en => en.dbms === dbms && en.server === entity.address)
      .find(e => e.loading)
      ? { label: 'Refreshing...', loading: true }
      : { label: 'Refresh dbs status', loading: false };
  }

  if (type === 'db') {
    return statuses.find(db => db.name === entity.name)?.loading
      ? { label: 'Refreshing...', loading: true }
      : { label: 'Refresh db status', loading: false };
  }

  return { label: 'Refresh', type: false };
};

export const dbMessage = (status, message) => {
  if (!message) {
    if (status === 'up') {
      return 'Instance is up.';
    }

    if (status === 'error') {
      return 'An unknown error has occurred on this instance.';
    }

    if (status === 'down') {
      return 'Instance is down.';
    }

    if (status === 'not-connected') {
      return 'Please refresh in order to get the instance status.';
    }
  }

  return message;
};
