export const getOutputText = (output, invocation) => {
  if (!invocation || (!output && invocation.status !== 'FAILED')) return '';

  let outputText = '';

  if (invocation.status === 'FAILED') {
    outputText = `The following error(s) occurred:
      ${invocation.errors.map(error => ` ${error}`).join('\n')}\n ${
      output && output.length > 0 ? `Output: \n${output.map(row => row.data.text).join('\n')}` : ''
    }`;
  } else {
    outputText = output.map(row => row.data.text).join('\n');
  }

  return outputText;
};
