import React, { useState } from 'react';

import { Result } from 'shared/components/overlays';

import { Switch } from 'shared/components';

import { Formik, Form } from 'formik';

import { Button, Checkbox } from 'shared/components/controls';
import { Text, Password } from 'shared/components/form/editors/Generic';

import { Paper, Center, Section, Row, Column } from 'shared/components/layout';

import { postAccount, patchAccount } from 'shared/api/accounts';
import { SubmitButton } from 'shared/components/form/SubmitButton';

export const ServerForm = ({
  server = {},
  protocols,
  handleSubmission,
  editing,
  isNew,
  formRef
}) => {
  const [submission, setSubmission] = useState({});
  const [passwordEditable, setPasswordEditable] = useState(!editing);
  const [showRSA, setShowRSA] = useState(false);
  const handleAccountCreation = getCreateAccountHandler({
    showRSA,
    passwordEditable,
    setSubmission,
    isNew,
    server
  });

  const allowedProtocols = protocols.filter(p => {
    if (p.is_abstract) {
      return false;
    }

    if (p?.special_required?.includes?.('database')) {
      return false;
    }

    return true;
  });

  const availablePorts = allowedProtocols.reduce((map, p) => {
    map[p.name] = p.default_port;

    return map;
  }, {});

  function mapServerValues(entry) {
    return {
      name: entry.name || '',
      description: entry.description || '',
      host: entry.address || '',
      port: entry.port || availablePorts[entry.protocol || 'SSH'],
      user: entry.user || '',
      server_password: entry.user_key || '',
      confirm_server_password: '',
      rsa_key: entry.user_key || '',
      protocol: entry.protocol || 'SSH'
    };
  }

  const initialValues = mapServerValues(server);

  const extraValidations = values => {
    const errors = {};
    if (passwordEditable && !showRSA) {
      if (values.server_password !== values.confirm_server_password) {
        errors.confirm_server_password = 'Passwords do not match';
      }
    }

    if (values.port) {
      if (!/^\d+$/.test(values.port)) {
        errors.port = 'Only numeric values are allowed';
      }
    }

    return errors;
  };

  return (
    <Switch value={submission?.result}>
      <Switch.Case value="success">
        <Center>
          <Result type="success">
            <Result.Text>{isNew ? 'Added new' : 'Updated'} account.</Result.Text>
          </Result>
        </Center>
        <Paper.Footer>
          <Paper.Footer.Right>
            <Button onClick={loadNewAccountHandler(submission.accounts, handleSubmission)}>
              Done
            </Button>
          </Paper.Footer.Right>
        </Paper.Footer>
      </Switch.Case>
      <Switch.Case value="fail">
        <Center>
          <Result type="fail">
            <Result.Text>Could not {isNew ? 'create new' : 'update'} account.</Result.Text>
            <Result.Messages messages={submission.messages} />
          </Result>
        </Center>
        <Paper.Footer></Paper.Footer>
      </Switch.Case>
      <Switch.Case value={undefined}>
        <Formik
          initialValues={initialValues}
          onSubmit={handleAccountCreation}
          validate={extraValidations}
          validateOnBlur
          // validateOnChange={false}
          validateOnMount={false}
          innerRef={formRef}
        >
          {() => {
            return (
              <>
                <Paper.Content>
                  <Form>
                    <Section>
                      <Section.Header>
                        <Section.Title>Details:</Section.Title>
                      </Section.Header>
                      <Row>
                        <Column>
                          <Row>
                            <Text name="name" required label="Name" value={initialValues.name} />
                          </Row>
                          <Row>
                            <Column>
                              <Text
                                name="protocol"
                                required
                                label="Protocol"
                                disabled={!isNew}
                                options={allowedProtocols.map(p => ({
                                  value: p.name,
                                  label: p.name
                                }))}
                                value={initialValues.protocol}
                                reactToChange={(value, formik) => {
                                  formik.setFieldValue('port', availablePorts[value]);
                                }}
                              />
                            </Column>
                          </Row>
                        </Column>
                        <Column>
                          <Text
                            name="description"
                            multiline
                            required
                            label="Description"
                            value={initialValues.description}
                          />
                        </Column>
                      </Row>
                    </Section>
                    <Section>
                      <Section.Header>
                        <Section.Title>Connection Info:</Section.Title>
                      </Section.Header>
                      <Row>
                        <Column style={{ maxWidth: '80%', width: '80%' }}>
                          <Text
                            name="host"
                            label="Host"
                            required
                            value={initialValues.host}
                            disabled={editing}
                          />
                        </Column>
                        <Column style={{ maxWidth: '20%', width: '20%' }}>
                          <Text name="port" required label="Port" value={initialValues.port} />
                        </Column>
                      </Row>
                      <Row>
                        <Column>
                          <Text
                            name="server_user"
                            label="User"
                            required
                            value={initialValues.user}
                          />
                        </Column>
                        {passwordEditable && <PasswordOrRSA allowed showRSA={showRSA} />}
                      </Row>
                    </Section>
                  </Form>
                </Paper.Content>

                <Paper.Footer>
                  {editing && (
                    <Paper.Footer.Left>
                      {!passwordEditable && (
                        <Button onClick={() => setPasswordEditable(true)}>
                          Change existing password
                        </Button>
                      )}
                      {passwordEditable && (
                        <Checkbox
                          checked={showRSA}
                          onInput={() => setShowRSA(prev => !prev)}
                          label="Use RSA key"
                        />
                      )}
                    </Paper.Footer.Left>
                  )}
                  {!editing && (
                    <Paper.Footer.Left>
                      <Checkbox
                        checked={showRSA}
                        onInput={() => setShowRSA(prev => !prev)}
                        label="Use RSA key"
                      />
                    </Paper.Footer.Left>
                  )}
                  <Paper.Footer.Right>
                    <SubmitButton confirmation>Submit</SubmitButton>
                  </Paper.Footer.Right>
                </Paper.Footer>
              </>
            );
          }}
        </Formik>
      </Switch.Case>
    </Switch>
  );
};

const PasswordOrRSA = ({ allowed, showRSA }) => (
  <Column>
    <Switch value={allowed && showRSA}>
      <Switch.Case value>
        <Text multiline name="rsa_key" required label="RSA Key" />
      </Switch.Case>
      <Switch.Case value={false}>
        <Row>
          <Password name="server_password" label="Password" required />
        </Row>
        <Row>
          <Password name="confirm_server_password" label="Confirm Password" required />
        </Row>
      </Switch.Case>
    </Switch>
  </Column>
);

function getCreateAccountHandler({ showRSA, passwordEditable, setSubmission, isNew, server }) {
  return function handleAccountCreation(values) {
    const request_body = {
      protocol: values.protocol,
      user: values.server_user,
      description: values.description,
      address: values.host,
      ...(passwordEditable && {
        user_key: showRSA ? values.rsa_key : values.server_password
      }),
      port: Number.parseInt(values.port, 10)
    };

    if (isNew) {
      request_body.name = values.name;
    } else {
      request_body.rename = values.name;
    }

    if (isNew) {
      postAccount(request_body).then(response => {
        setSubmission({
          ...response
        });
      });
    } else {
      patchAccount(server.name, request_body).then(response => {
        setSubmission({
          ...response
        });
      });
    }
  };
}

function loadNewAccountHandler(accounts, handleSubmission) {
  return function loadNewAccountFromResult() {
    // const newAccount = accounts[0];

    // add this entry to redux structure
    // addNewAccount(newAccount);
    handleSubmission();
  };
}
