import React from 'react';
import * as PropTypes from 'prop-types';
import SvgIcon from '@material-ui/core/SvgIcon';

import { LightTooltip } from 'shared/components/tooltip/Tooltip';

import {
  mdiProgressClock,
  mdiCheckCircleOutline,
  mdiCalendarArrowRight,
  mdiStopCircleOutline,
  mdiCancel,
  mdiTimelineClock,
  mdiAlertRhombusOutline
} from '@mdi/js';

import './job-status.local.scss';

const statusMap = {
  EXECUTING: mdiProgressClock,
  SUCCEEDED: mdiCheckCircleOutline,
  SCHEDULED: mdiCalendarArrowRight,
  DELAYED: mdiTimelineClock,
  ABORTED: mdiStopCircleOutline,
  FAILED: mdiAlertRhombusOutline,
  KILLED: mdiCancel
};

export class JobStatus extends React.PureComponent {
  render() {
    return (
      <LightTooltip placement="bottom-start" title={this.props.status.toUpperCase()}>
        <div className="daemon qa-daemon-status">
          <div className={`status ${this.props.status.toLowerCase()}`}>
            <SvgIcon>
              <path d={statusMap[this.props.status]} />
            </SvgIcon>
          </div>
        </div>
      </LightTooltip>
    );
  }
}

JobStatus.propTypes = {
  status: PropTypes.string
};
JobStatus.defaultProps = {
  status: ''
};
