import { actions } from 'core/store/actions';

export const initialState = {
  notifications: [],
};

export const reducerActions = {
  [actions.addNotifications.getEventAction()]: (state, payload) => state
    .merge('notifications', payload),
  [actions.removeNotification.getEventAction()]: (state, notificationIndex) => state
    .removeIn(['notifications', notificationIndex]),
};
