import React, { useState } from 'react';
import * as PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';

import TimelapseIcon from '@material-ui/icons/Timelapse';
import { Dropdown, SmallInput } from 'shared/components';

export const StatusFilterEditor = ({ value: defaultValue, setFilterValue }) => {
  const [value, setValue] = useState(defaultValue || '');
  const IconComp = StatusFilterEditor.TypeDescriptor.icon;
  const saveFilter = (index, status) => { setValue(status.value); setFilterValue({ type: 'status', value: status.value }); };

  const items = [
    { value: 'ABORTED', name: 'Aborted' },
    { value: 'DELAYED', name: 'Delayed' },
    { value: 'EXECUTING', name: 'Executing' },
    { value: 'FAILED', name: 'Failed' },
    { value: 'KILLED', name: 'Killed' },
    { value: 'SCHEDULED', name: 'Scheduled' },
    { value: 'SUCCEEDED', name: 'Succeeded' },
  ];

  return (
    <Dropdown
      target={(meta, actions) => (
        <SmallInput
          ref={meta.ref}
          value={value}
          placeholder="Select status"
          disabled
          readOnly
          onClick={actions.toggle}
          icon={<IconComp />}
        />
      )}
      onSelect={saveFilter}
      items={items}
      isDropdownOpen
    />
  );
};

StatusFilterEditor.propTypes = {
  value: PropTypes.string,
  setFilterValue: PropTypes.func.isRequired,
};

StatusFilterEditor.defaultProps = {
  value: '',
};

StatusFilterEditor.TypeDescriptor = {
  icon: () => <TimelapseIcon />,
  name: () => <FormattedMessage id="dashboard.status" />,
  value: 'status',
};
