import React from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import GroupIcon from '@material-ui/icons/Group';

import { mdiChevronLeft, mdiChevronRight } from '@mdi/js';
import SvgIcon from '@material-ui/core/SvgIcon';

import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';

const Groups = ({ groups, onSelect, eventTypeIcons = [] }) => {
  const renderGroupsList = () => {
    if (Array.isArray(groups) && groups.length) {
      return groups.map(item => {
        return (
          <ListItem
            onClick={() => onSelect(item)}
            selected={item.selected}
            key={item.data.name}
            button
          >
            <ListItemIcon>
              <GroupIcon size="small" style={{ color: 'var(--primary-800)' }} />
            </ListItemIcon>
            <ListItemText primary={item.data.name} />
            <ListItemSecondaryAction>
              {item.displayIcons && eventTypeIcons.map(icon => icon)}
              <SvgIcon size="small" edge="end" color="primary">
                <path d={item.selected ? mdiChevronLeft : mdiChevronRight} />
              </SvgIcon>
            </ListItemSecondaryAction>
          </ListItem>
        );
      });
    }
    return null;
  };

  return (
    <div className="selector">
      <div className="notifications-title">
        <div className="heading">Groups:</div>
      </div>
      <List dense disablePadding>
        {renderGroupsList()}
      </List>
    </div>
  );
};

export default Groups;
