import moment from 'moment';

import { getOutputText } from './getOutputText';

export const sendToEmail = (invocation, output, processName) => {
  const text = getOutputText(output, invocation);
  const date = moment(invocation.date_invoke).format('MMM DD YYYY, H:mm:ss');
  const subject = processName ? `${processName} output` : 'Job output';
  const body = `PROCESS: ${processName}\nDATE: ${date}\nSTATUS: ${invocation.status}\n\nOUTPUT:\n\n${text}`;
  window.location.href = `mailto:?subject=${subject}&body=${encodeURIComponent(body)}`;
};
