import React from 'react';

import Tooltip from '@material-ui/core/Tooltip';
import { withStyles } from '@material-ui/core/styles';

import './TotalJobsCount.local.scss';

export const TotalJobsCount = ({ jobs, status }) => {
  const jobsTotal = () => {
    return (
      <div className="tooltip-container">
        <div className="row executing">
          <span className="label"> EXECUTING:</span>{' '}
          <span className="value">{jobs.filter(job => job.status === 'EXECUTING').length}</span>
        </div>
        <div className="row succeeded">
          <span className="label"> SUCCEEDED:</span>{' '}
          <span className="value">{jobs.filter(job => job.status === 'SUCCEEDED').length}</span>
        </div>
        <div className="row scheduled">
          <span className="label"> SCHEDULED:</span>{' '}
          <span className="value">{jobs.filter(job => job.status === 'SCHEDULED').length}</span>
        </div>
        <div className="row delayed">
          <span className="label">DELAYED:</span>{' '}
          <span className="value">{jobs.filter(job => job.status === 'DELAYED').length}</span>
        </div>
        <div className="row aborted">
          <span className="label">ABORTED:</span>{' '}
          <span className="value">{jobs.filter(job => job.status === 'ABORTED').length}</span>
        </div>
        <div className="row failed">
          <span className="label">FAILED: </span>{' '}
          <span className="value">{jobs.filter(job => job.status === 'FAILED').length}</span>
        </div>
        <div className="row killed">
          <span className="label">KILLED:</span>{' '}
          <span className="value">{jobs.filter(job => job.status === 'KILLED').length}</span>
        </div>
      </div>
    );
  };

  if (status) {
    return (
      <div className="count-jobs">
        {jobs.length} {status} {jobs.length === 1 ? 'job' : 'jobs'}
      </div>
    );
  }

  return (
    <div className="count-jobs">
      <LightTooltip placement="bottom" title={jobsTotal()}>
        <span>{jobs.length}</span>
      </LightTooltip>{' '}
      total jobs
    </div>
  );
};

const LightTooltip = withStyles(() => ({
  tooltip: {
    backgroundColor: '#fff',
    letterSpacing: '0.4px',
    fontSize: '12px',
    lineHeight: '16px',
    fontWeight: '700',
    fontFamily: 'Open Sans',
    boxShadow: '0px 4px 8px rgba(4, 17, 37, 0.1)',
    borderRadius: '8px'
  }
}))(Tooltip);
