import React, { useState } from 'react';

import { Center } from 'shared/components';
import { SimpleSearchInput } from 'shared/components/simple-search-input/simple-search-input';
import { ServerCard } from '../ServerCard/ServerCard';

import './VirtualDashboard.local.scss';

export const VirtualDashboard = ({ servers }) => {
  const [query, setQuery] = useState('');
  const filtered = servers.filter(item => filterServerByQuery(item, query));

  function filterServerByQuery(server) {
    let found = false;

    Object.values(server).map(val => {
      if (
        val
          .toString()
          .toLowerCase()
          .includes(query.toLowerCase())
      ) {
        found = true;
      }

      return null;
    });

    return found;
  }

  return (
    <div className="container">
      {servers.length === 0 && <Center>Currently there are no virtual servers</Center>}
      {servers.length > 0 && (
        <>
          <div className="search-input-slot">
            <SimpleSearchInput
              data-qa="users-search-input"
              value={query}
              onChange={e => setQuery(e.target.value)}
            />
          </div>
          <div className="servers-container">
            {filtered.map(server => (
              <ServerCard key={server.vcenterservername} server={server} type="virtual" />
            ))}
          </div>
        </>
      )}
    </div>
  );
};
