import React, { useEffect } from 'react';
import { useLocation, Link as RouterLink } from 'react-router-dom';

import { Header } from 'core/components/Header';

import { Main, Paper, Center } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';
import { LimitlessTooltip } from 'shared/components/tooltip/Tooltip';

import './job-process.local.scss';
import { Breadcrumbs, Link } from '@material-ui/core';

import ConnectedJobView from './job-view';
import { useDispatch, useSelector } from 'react-redux';

import { getQuery, getInvocationStatus, getInvocation } from './store/selectors';
import { processQuery } from './store/actions';
import { InvocationStatus } from './store/constants';

import './root.local.scss';

function useQuery() {
  return new URLSearchParams(useLocation().search);
}

const LinkRouter = props => <Link {...props} component={RouterLink} />;

const JobProcess = () => {
  const query = useQuery();

  const uuid = query.get('invocation');
  const processName = query.get('process');
  const redirected = query.get('redirected');

  return (
    <>
      <Header>
        <Breadcrumbs classes={{ root: 'job-main-breadcrumbs' }} aria-label="breadcrumb">
          <LinkRouter to="/jobs">Jobs</LinkRouter>
          <OtherMainLinks />
        </Breadcrumbs>
      </Header>
      <Main>
        <Paper>
          <PrepareFlow uuid={uuid} process={processName}>
            <ConnectedJobView redirected={redirected === 'true'} />
          </PrepareFlow>
        </Paper>
      </Main>
    </>
  );
};

export default JobProcess;

function PrepareFlow({ children, uuid, process }) {
  const query = useSelector(getQuery);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(processQuery(uuid, process));
  }, [uuid]);

  if (query.uuid !== uuid) {
    return (
      <>
        <Paper.Content scrollable={false}>
          <Center>
            <Loader>Setting up invocation viewer</Loader>
          </Center>
        </Paper.Content>
      </>
    );
  }

  return children;
}

function OtherMainLinks() {
  const status = useSelector(getInvocationStatus);
  const invocation = useSelector(getInvocation);

  if (status === InvocationStatus.NEW) {
    return <div className="current-job">New Job</div>;
  }

  if (invocation && invocation.designation) {
    return (
      <LimitlessTooltip
        placement="bottom-start"
        title={
          <>
            <p>Invocation: {invocation.designation}</p>
            {invocation.schedule && <p>Schedule: {invocation.schedule}</p>}
          </>
        }
      >
        <div className="current-job">{invocation.designation}</div>
      </LimitlessTooltip>
    );
  }

  return null;
}
