import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import { Loader } from 'shared/components/loaders';
import { Center, Switch } from 'shared/components';

import { loadAllAccountsThunk } from 'explorer/store/thunks';

import { UsageListing } from './components/UsageListing/UsageListing';

export const ServerUsage = ({ loading, usage, getAllAccounts }) => {
  useEffect(() => {
    getAllAccounts();
  }, []);

  return (
    <Switch value={loading}>
      <Switch.Case value>
        <Center>
          <Loader>Loading Usage Parameters</Loader>
        </Center>
      </Switch.Case>
      <Switch.Case value={false}>
        <UsageListing usage={usage} />
      </Switch.Case>
    </Switch>
  );
};

export const ConnectedServerUsage = connect(
  state => {
    return {
      loading: state.explorer.flags.serversUsage.loading,
      usage: state.explorer.serversUsage
    };
  },
  dispatch => ({
    getAllAccounts: () => dispatch(loadAllAccountsThunk({ from: 'dashboard' }))
  })
)(ServerUsage);
