import React, { useState, useRef } from 'react';
import { connect } from 'react-redux';
import { Formik, Form } from 'formik';

import { fetchEntity } from '../actions';

import { getAlias, patchAlias, postAlias } from 'shared/api/alias';
import { Switch } from 'shared/components';
import { Button } from 'shared/components/controls';
import { NamespaceSelect } from 'shared/components/form/editors/Custom';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Result } from 'shared/components/overlays/Result/Result';
import { SubmitButton } from 'shared/components/form/SubmitButton';
import { Paper, Section, Row, Column } from 'shared/components/layout';

import { Text } from 'shared/components/form/editors/Generic';

import SvgIcon from '@material-ui/core/SvgIcon';
import FolderIcon from '@material-ui/icons/Folder';
import { ConnectedDeleteEntity } from './common-actions/DeleteEntity';
import { PrivilegesModal } from './common-actions/privileges/PrivilegesModal';

import { hasWritePrivileges } from '../utils/privileges';

function isEntityDeletable(entity) {
  return (
    entity.owner !== 'postgres' &&
    (entity.entity === 'form' ||
      entity.entity === 'process' ||
      entity.entity === 'schedule' ||
      entity.entity === 'account')
  );
}

const ActionsCommon = ({ entity }) => {
  const displayActions = entity.entity !== 'root';
  if (displayActions) {
    const displayDelete = isEntityDeletable(entity);
    const isAlias = entity.alias;
    const disableActions = entity?.meta?.appearance === 'Greyed';
    const canWrite = hasWritePrivileges(entity);

    return (
      <>
        {canWrite && (
          <PrivilegesModal entity={entity} isAlias={isAlias} disabled={disableActions} />
        )}
        {!isAlias && <ConnectedNewAlias entity={entity} />}
        {isAlias && canWrite && <ConnectedEditAlias entity={entity} />}
        {canWrite && displayDelete && (
          <ConnectedDeleteEntity entity={entity} disabled={disableActions} />
        )}
      </>
    );
  }
  return null;
};

export default ActionsCommon;

//* These should be moved to the common-actions folder */

export const ConnectedNewAlias = connect(null, {
  request: fetchEntity
})(NewAlias);

function NewAlias({ entity, request }) {
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };
  const [result, setResult] = useState('');
  const isDisabled = entity?.meta?.appearance === 'Greyed';
  const formRef = useRef();

  return (
    <>
      <Button
        disabled={isDisabled}
        classes={{ root: 'entity-button' }}
        onClick={() => {
          setOpen(true);
        }}
      >
        New Alias
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            minWidth: '600px',
            minHeight: '600px'
          }}
        >
          <ModalHeader
            title="New Alias"
            icon={
              <SvgIcon>
                <FolderIcon />
              </SvgIcon>
            }
            formikRef={formRef}
          />
          <Switch value={result}>
            <Switch.Case value="success">
              <Result type="success">
                <Result.Text>Alias was added successfully!</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                      setResult('');
                    }}
                  >
                    Done
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="fail">
              <Result type="fail">
                <Result.Text>An error occurred and the alias could not be added.</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                      setResult('');
                    }}
                  >
                    Close
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="">
              <Formik
                initialValues={{
                  name: '',
                  namespace: '',
                  original: entity ? entity.name : '',
                  description: ''
                }}
                onSubmit={values => {
                  handleSubmitNew(setResult, request, values);
                }}
                innerRef={formRef}
              >
                {({ values }) => {
                  return (
                    <>
                      <Paper.Content>
                        <Form>
                          <Section>
                            <Section.Header />
                            <Row>
                              <Text
                                disabled
                                name="original"
                                label="Original"
                                value={values.original}
                              />
                            </Row>
                            <Row>
                              <Text name="name" label="Name" value={values.name} required />
                            </Row>
                            <Row>
                              <Column xs={12}>
                                <NamespaceSelect
                                  name="namespace"
                                  label="Parent namespace"
                                  value={values.namespace}
                                />
                              </Column>
                            </Row>
                            <Row>
                              <Text
                                name="description"
                                label="Description"
                                value={values.description}
                                multiline
                                required
                              />
                            </Row>
                          </Section>
                        </Form>
                      </Paper.Content>
                      <Paper.Footer>
                        <Paper.Footer.Right>
                          <SubmitButton variant="contained">Save</SubmitButton>
                        </Paper.Footer.Right>
                      </Paper.Footer>
                    </>
                  );
                }}
              </Formik>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
}

async function handleSubmitNew(setResult, request, values) {
  const alias = values.namespace ? `${values.namespace}.${values.name}` : values.name;
  const body = {
    name: values.original,
    alias,
    description: values.description
  };

  postAlias(body)
    .then(() => {
      setResult('success');
      request('alias');
    })
    .catch(error => {
      console.log('[edit alias] patch alias error: ', error); // eslint-disable-line
      setResult('fail');
    });
}

export const ConnectedEditAlias = connect(null, {
  request: fetchEntity
})(EditAlias);

function EditAlias({ entity, request }) {
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };
  const [result, setResult] = useState('');
  const [description, setDescription] = useState('');
  const formRef = useRef();
  const getInitialValues = () => {
    const values = {};
    const splittedNamespace = entity.name.split('.');

    values.name = splittedNamespace[splittedNamespace.length - 1] || '';
    values.namespace = splittedNamespace.slice(0, splittedNamespace.length - 1).join('.');
    values.original = entity ? entity.alias : '';
    values.description = description;

    return values;
  };

  const getAliasDetails = name => {
    getAlias(name)
      .then(data => {
        if (
          data.data.data &&
          data.data.data.aliases &&
          data.data.data.aliases[0] &&
          data.data.data.aliases[0].description
        ) {
          setDescription(data.data.data.aliases[0].description);
          setOpen(true);
        }
      })
      .catch(error => {
        console.log('[get alias] error getting alias details: ', error); // eslint-disable-line
      });
  };
  const isDisabled = entity?.meta?.appearance === 'Greyed';

  return (
    <>
      <Button
        disabled={isDisabled}
        classes={{ root: 'entity-button' }}
        onClick={() => {
          getAliasDetails(entity.name);
        }}
      >
        Edit Alias
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            minWidth: '600px',
            minHeight: '600px'
          }}
        >
          <ModalHeader
            title="Edit Alias"
            icon={
              <SvgIcon>
                <FolderIcon />
              </SvgIcon>
            }
            formikRef={formRef}
          />
          <Switch value={result}>
            <Switch.Case value="success">
              <Result type="success">
                <Result.Text>Alias was edited successfully!</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                      setResult('');
                    }}
                  >
                    Done
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="fail">
              <Result type="fail">
                <Result.Text>An error occurred and the alias could not be edited.</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                      setResult('');
                    }}
                  >
                    Close
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="">
              <Formik
                initialValues={getInitialValues()}
                onSubmit={values => {
                  handleEditSubmit(setResult, request, entity.name, values);
                }}
                innerRef={formRef}
              >
                {({ values }) => {
                  return (
                    <>
                      <Paper.Content>
                        <Form>
                          <Section>
                            <Section.Header />
                            <Row>
                              <Text
                                disabled
                                name="original"
                                label="Original"
                                value={values.original}
                              />
                            </Row>
                            <Row>
                              <Text name="name" label="Name" value={values.name} required />
                            </Row>
                            <Row>
                              <Column xs={12}>
                                <NamespaceSelect
                                  name="namespace"
                                  label="Parent namespace"
                                  value={values.namespace}
                                />
                              </Column>
                            </Row>
                            <Row>
                              <Text
                                name="description"
                                label="Description"
                                value={values.description}
                                multiline
                                required
                              />
                            </Row>
                          </Section>
                        </Form>
                      </Paper.Content>
                      <Paper.Footer>
                        <Paper.Footer.Right>
                          <SubmitButton variant="contained">Save</SubmitButton>
                        </Paper.Footer.Right>
                      </Paper.Footer>
                    </>
                  );
                }}
              </Formik>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
}

async function handleEditSubmit(setResult, request, originalName, values) {
  const name = values.namespace ? `${values.namespace}.${values.name}` : values.name;
  const body = {
    rename: name,
    description: values.description
  };

  patchAlias(originalName, body)
    .then(() => {
      setResult('success');
      request('alias');
    })
    .catch(error => {
      console.log('[edit alias] patch alias error: ', error); // eslint-disable-line
      setResult('fail');
    });
}
