import React, { useState, useRef } from 'react';
import { connect } from 'react-redux';

import { Formik, Form } from 'formik';

import { fetchEntity } from '../actions';

import { patchConstant } from 'shared/api/constant';
import { Switch } from 'shared/components';
import { Button } from 'shared/components/controls';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Result } from 'shared/components/overlays/Result/Result';
import { SubmitButton } from 'shared/components/form/SubmitButton';
import { Paper, Section, Row } from 'shared/components/layout';

import { Text } from 'shared/components/form/editors/Generic';

import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiImageFilterCenterFocus } from '@mdi/js';
import { hasWritePrivileges } from '../utils/privileges';

const ActionsConstant = ({ entity }) => {
  const canWrite = hasWritePrivileges(entity);

  if (!canWrite) {
    return null;
  }

  return <ConnectedEditConstant constant={entity} />;
};

export default ActionsConstant;

export const ConnectedEditConstant = connect(null, {
  request: fetchEntity
})(EditConstant);

function EditConstant({ constant, request }) {
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };
  const formRef = useRef();
  const [result, setResult] = useState('');
  const disableActions = constant?.meta?.appearance === 'Greyed';

  return (
    <>
      <Button
        disabled={disableActions}
        classes={{ root: 'entity-button' }}
        onClick={() => {
          setOpen(true);
        }}
      >
        Edit
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            minWidth: '600px',
            minHeight: '368px'
          }}
        >
          <ModalHeader
            title="Edit Constant"
            icon={
              <SvgIcon>
                <path d={mdiImageFilterCenterFocus} />
              </SvgIcon>
            }
            formikRef={formRef}
          />
          <Switch value={result}>
            <Switch.Case value="success">
              <Result type="success">
                <Result.Text>Constant was edited successfully!</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                      setResult('');
                    }}
                  >
                    Done
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="fail">
              <Result type="fail">
                <Result.Text>An error occurred and the constant could not be saved.</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                      setResult('');
                    }}
                  >
                    Close
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="">
              <Formik
                initialValues={{ name: constant.name, value: constant.value }}
                onSubmit={values => {
                  handleSubmit(setResult, request, values);
                }}
                innerRef={formRef}
              >
                {() => {
                  return (
                    <>
                      <Paper.Content>
                        <Form>
                          <Section>
                            <Section.Header />

                            <Row>
                              <Text name="name" label="Name" disabled value={constant.name} />
                            </Row>
                            <Row>
                              <Text
                                name="value"
                                label="Value"
                                value={constant.value}
                                description={constant.description}
                              />
                            </Row>
                          </Section>
                        </Form>
                      </Paper.Content>

                      <Paper.Footer>
                        <Paper.Footer.Right>
                          <SubmitButton variant="contained">Save</SubmitButton>
                        </Paper.Footer.Right>
                      </Paper.Footer>
                    </>
                  );
                }}
              </Formik>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
}

async function handleSubmit(setResult, request, values) {
  const body = {
    value: values.value
  };

  patchConstant(values.name, body)
    .then(() => {
      setResult('success');
      request('constant');
    })
    .catch(error => {
      console.log('[edit constant] patch constant error: ', error); // eslint-disable-line
      setResult('fail');
    });
}
