import React from 'react';
import { array } from 'prop-types';

import { ConnectedDatabase } from './containers/Database/Database';

export class DatabaseList extends React.PureComponent {
  render() {
    const { databases } = this.props;

    if (!databases) return null;

    return databases.map(database => <ConnectedDatabase key={database.name} data={database} />);
  }
}

DatabaseList.propTypes = {
  databases: array
};

export default DatabaseList;
