import React from 'react';
import { connect } from 'react-redux';

import { SearchableList } from 'users-manager/components/SearchableList/SearchableList';
import { ServerCard } from '../../components/ServerCard/ServerCard';

import { toggleServerThunk } from '../../store/thunks';

import './SearchSection.local.scss';

function SearchSection({ allServers, selectedServers, toggleServer }) {
  function filterServerByQuery(server, query) {
    return server.name.toLowerCase().includes(query.toLowerCase());
  }

  function isServerSelected(server) {
    if (selectedServers.find(sv => sv.name === server.name)) return true;
    return false;
  }

  function toggleSelected(server) {
    toggleServer(server);
  }

  return (
    <section className="servers-search-section">
      <SearchableList items={allServers} filter={filterServerByQuery}>
        {server => (
          <ServerCard
            selected={isServerSelected(server)}
            toggleSelectedCallback={toggleSelected}
            server={server}
          />
        )}
      </SearchableList>
    </section>
  );
}

export const ConnectedSearchSection = connect(
  state => {
    return {
      allServers: state.executiveDashboard.allServers,
      selectedServers: state.executiveDashboard.selectedServers
    };
  },
  dispatch => ({
    toggleServer: name => dispatch(toggleServerThunk(name))
  })
)(SearchSection);
