import React from 'react';
import * as PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { actions } from 'session/store/actions';
import { Container } from 'session/components';

export class LogoutPage extends React.PureComponent {
  componentDidMount() {
    this.props.logout();
  }

  render() {
    return (
      <Container
        title="session.logout.title"
        isLoading
      >
        <div />
      </Container>
    );
  }

  static path = '/logout';
}

LogoutPage.propTypes = {
  logout: PropTypes.func.isRequired,
};

export const mapDispatchToProps = (dispatch) => ({
  logout: (data) => dispatch(actions.logout.dispatchLoading(data)),
});

export default connect(null, mapDispatchToProps)(LogoutPage);
