import React from 'react';
import Chip from '@material-ui/core/Chip';
// import Button from '@material-ui/core/Button';
import PersonIcon from '@material-ui/icons/Person';
import NotificationsNoneIcon from '@material-ui/icons/NotificationsNone';
import EditIcon from '@material-ui/icons/Edit';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import { Center } from 'shared/components/layout';

import { Button } from 'shared/components/controls';

import './notification-list.scss';
import { withStyles } from '@material-ui/core';

const UserChip = withStyles({
  root: {
    background: 'rgba(55, 101, 175, 0.08)',
    borderRadius: '4px'
  },
  label: {
    display: 'flex',
    justifyContent: 'flex-start',
    alignItems: 'center',
    '& > svg': {
      marginLeft: '8px'
    }
  }
})(Chip);

const NotificationList = ({ users, eventTypeIcons, toggleApplyButton }) => {
  const filteredUsers = users.filter(user => user.selected);
  const renderUserList = () => {
    if (!filteredUsers.length) {
      return <Center>No notifications applied</Center>;
    }
    return filteredUsers.map(user => {
      return (
        <UserChip
          classes={{ root: 'notification-list-item' }}
          label={
            <>
              <span>{user.data.name}</span>
              {eventTypeIcons}
            </>
          }
          color="default"
          icon={<PersonIcon size="small" />}
        />
      );
    });
  };

  let buttonLabel = 'Add';
  let buttonIcon = <NotificationsNoneIcon />;
  if (filteredUsers.length) {
    buttonLabel = 'Edit';
    buttonIcon = <EditIcon />;
  }

  return (
    <div className="notification-list-wrapper">
      <div>{renderUserList()}</div>
      <div className="notification-list-edit-section">
        <Button
          variant="contained"
          color="primary"
          size="small"
          onClick={toggleApplyButton}
          startIcon={buttonIcon}
          endIcon={<ArrowDropDownIcon />}
          classes={{ root: 'notifications-add-edit' }}
        >
          {buttonLabel}
        </Button>
      </div>
    </div>
  );
};

export default NotificationList;
