import React, { forwardRef, useMemo, useEffect, useState } from 'react';

import moment from 'moment';
import MomentUtils from '@date-io/moment';

import { useField, useFormikContext } from 'formik';

import FormControl from '@material-ui/core/FormControl';

import { ParameterLoader } from 'shared/components/loaders';

import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
  KeyboardTimePicker
} from '@material-ui/pickers';

import { Input } from 'shared/components/controls';

import Autocomplete from '@material-ui/lab/Autocomplete';

import check from './field-level-validations';

export const Text = forwardRef(function Text(
  { options, validate, defaultValue = '', value, required, reactToChange, ...props },
  ref
) {
  const [shouldValidate, setShouldValidate] = useState(false);
  const validation = useMemo(() => {
    if (!shouldValidate) {
      return null;
    }
    if (validate) {
      return validate;
    }

    if (required) {
      return check.required;
    }
  }, [validate, required, shouldValidate]);

  const [field, meta, helpers] = useField({
    touched: false,
    name: props.name,
    validate: validation
  });

  const formikCtx = useFormikContext();

  useEffect(() => {
    // used to allow the "" (empty string) value
    if (value === undefined || value === null) {
      helpers.setValue(defaultValue);
    } else {
      helpers.setValue(value);
    }
    helpers.setTouched(false);
    setShouldValidate(true);
  }, [value]);

  useEffect(() => {
    /**
     * accepts a reaction function, ideally called when setValue is called.
     * It should be used to update other known field names in reaction to this one changing.
     */
    reactToChange && reactToChange(value, formikCtx);
  }, [value]);

  useEffect(() => {
    /** Unmount cleanup ? */
    return () => {
      if (!props.multiline || !props.isPopup) {
        helpers.setValue(undefined);
        // helpers.setTouched(false);
        helpers.setError(undefined);
      }
    };
  }, []);

  if (field.value === undefined) {
    return <ParameterLoader>Mounting Field</ParameterLoader>;
  }

  if (options && options.length) {
    const selected = options.find(o => o.value === field.value) ?? null;
    const description = options.find(o => o.value === field.value)?.description || undefined;
    return (
      <Autocomplete
        disableClearable
        id={`autocomplete-${props.name}`}
        options={options}
        value={selected}
        getOptionLabel={option => option.label || field.value}
        getOptionDisabled={option => option.disabled}
        onChange={(e, option) => {
          helpers.setValue(option.value);

          /**
            only on Select option changes for now. Can be modified for generic text,
            but should be done when needed.
            onChange from '...field' can be decorated.
           */
          reactToChange && reactToChange(option.value, formikCtx);
        }}
        disabled={props.disabled}
        renderInput={params => {
          return (
            <Input
              {...props}
              {...params}
              ref={ref}
              type="text"
              error={meta.error && meta.touched}
              helperText={meta.touched ? meta.error : ''}
              description={description}
            />
          );
        }}
      />
    );
  }

  return (
    <Input
      {...props}
      {...field}
      ref={ref}
      error={meta.error && meta.touched}
      helperText={meta.touched ? meta.error : ''}
    />
  );
});

export function Email(props) {
  const validations = useMemo(() => check.merge(check.email, props.required ?? check.required), [
    props.required
  ]);

  return <Text validate={validations} {...props} type="email" />;
}

export function PhoneNumber(props) {
  const validations = useMemo(() => check.merge(check.phone, props.required ?? check.required), [
    props.required
  ]);

  return <Text validate={validations} {...props} />;
}
/* TODO: fix Integer required validation checks. Investigate how Yup returns the validateSync for this particular instance */
export function Integer({ validate, ...props }) {
  const validations = useMemo(() => check.merge(props.required ?? check.required_not_null), [
    props.required,
    validate
  ]); // validate is given by the parameter normalizer.
  // it should be a list of check keys. TODO: map to validation functions to merge

  return <Text {...props} validate={validations} type="number" />;
}

export function Password(props) {
  return <Text {...props} type="password" />;
}

const BOOL_OPTIONS = [
  { label: 'True', value: true },
  { label: 'False', value: false }
];

export const BooleanSelect = forwardRef(function Boolean({ options, ...props }, ref) {
  return <Text type="text" value defaultValue {...props} ref={ref} options={BOOL_OPTIONS} />;
});

export const Date = forwardRef(function Date(props, ref) {
  const [field, meta, helpers] = useField({
    name: props.name
  });

  useEffect(() => {
    helpers.setValue(props.value);
    helpers.setTouched(false);

    /** Unmount cleanup ? */
    return () => {
      helpers.setValue(undefined);
      helpers.setTouched(false);
      helpers.setError(undefined);
    };
  }, [props.value]);

  const value = field.value ? moment(field.value).format('MM/DD/YYYY') : moment().utc();

  return (
    <FormControl fullWidth error={meta.error && meta.touched}>
      <MuiPickersUtilsProvider utils={MomentUtils}>
        <KeyboardDatePicker
          ref={ref}
          disableToolbar
          inputVariant="outlined"
          format="MM/DD/YYYY"
          label={props.label}
          disabled={props.disabled}
          KeyboardButtonProps={{
            'aria-label': 'change date'
          }}
          error={meta.error && meta.touched}
          value={value}
          onChange={date => helpers.setValue(date?.format('MM/DD/YYYY') || ' ')}
        />
      </MuiPickersUtilsProvider>
    </FormControl>
  );
});

export const Time = forwardRef(function Time(props, ref) {
  const [field, meta, helpers] = useField({
    name: props.name
  });

  useEffect(() => {
    helpers.setValue(props.value);
    helpers.setTouched(false);

    /** Unmount cleanup ? */
    return () => {
      helpers.setValue(undefined);
      helpers.setTouched(false);
      helpers.setError(undefined);
    };
  }, [props.value]);

  const value = field.value
    ? moment(field.value, 'HH:mm')
    : moment()
        .utc()
        .format('HH:mm');
  return (
    <FormControl fullWidth error={meta.error && meta.touched}>
      <MuiPickersUtilsProvider utils={MomentUtils}>
        <KeyboardTimePicker
          ampm={false}
          ref={ref}
          disableToolbar
          inputVariant="outlined"
          format="HH:mm"
          label={props.label}
          disabled={props.disabled}
          KeyboardButtonProps={{
            'aria-label': 'change date'
          }}
          error={meta.error && meta.touched}
          value={value}
          onChange={date => {
            helpers.setValue(date?.format('HH:mm') || ' ');
          }}
          mask="__:__"
        />
      </MuiPickersUtilsProvider>
    </FormControl>
  );
});

export const Select = forwardRef(function Select(
  { options, validate, defaultValue, value, required, isNamespace, ...props },
  ref
) {
  const validation = useMemo(() => {
    if (validate) {
      return validate;
    }

    if (required) {
      return check.required;
    }
  }, [validate, required]);

  const [field, meta, helpers] = useField({
    touched: false,
    name: props.name,
    validate: validation
  });

  useEffect(() => {
    helpers.setValue(value || defaultValue || '');
    helpers.setTouched(false);

    /** Unmount cleanup ? */
    return () => {
      helpers.setValue(undefined);
      helpers.setTouched(false);
      helpers.setError(undefined);
    };
  }, []);

  /**
   * This should be moved to it's own thing.
   * For now, it satisfies... :(((
   */
  if (options && options.length) {
    return (
      <Autocomplete
        disableClearable
        id={`autocomplete-${props.name}`}
        options={options}
        getOptionSelected={option => option.value === field.value}
        getOptionLabel={option => option.label || value || ''}
        value={value}
        onChange={(e, option) => {
          helpers.setValue(option.value);
        }}
        renderInput={params => {
          return (
            <Input
              {...props}
              {...params}
              ref={ref}
              type="text"
              error={meta.error && meta.touched}
              helperText={meta.touched ? meta.error : ''}
              desription={undefined}
            />
          );
        }}
      />
    );
  }

  return (
    <Input
      {...props}
      {...field}
      ref={ref}
      type="text"
      error={meta.error && meta.touched}
      helperText={meta.touched ? meta.error : ''}
    />
  );
});
