import React, { useState, useEffect, useMemo } from 'react';
import { object, func } from 'prop-types';
import { connect } from 'react-redux';
import { produce } from 'immer';
import SvgIcon from '@material-ui/core/SvgIcon';
import Avatar from '@material-ui/core/Avatar';

import { mdiAccountGroup, mdiAccountMultiplePlus } from '@mdi/js';

import { CONSTANTS } from 'config';
import { RestClient } from 'shared/lib/rest';
import { creators } from 'shared/lib/redux-utils';

import { Button, Checkbox } from 'shared/components/controls';

import { Paper } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';
import { Modal, ModalHeader, Result } from 'shared/components/overlays';

import { SearchableList } from '../../../../components/SearchableList/SearchableList';
import { AvatarCard } from '../../../../components/AvatarCard/AvatarCard';
import { getGroupDescription } from '../../../../store/selectors';

import { PATCH_SELECTED_GROUP } from '../../../store/actions';

import './EditSubGroups.local.scss';

export const EditSubGroups = ({ group, updateGroup, retrieveGroupDescription }) => {
  const [open, setOpen] = useState(false);
  // could use memo and callback
  const openModal = () => {
    setOpen(true);
  };

  const closeModal = () => {
    setOpen(false);
    setIsFormChanged(false);
  };

  const modalActions = {
    close: closeModal,
    open: openModal
  };

  const [isFormChanged, setIsFormChanged] = useState(false);
  /* eslint-disable jsx-a11y/click-events-have-key-events */
  return (
    <>
      <div tabIndex={0} role="button" className="edit-user-groups" onClick={openModal}>
        <AvatarCard
          label="Add to group"
          avatar={{
            fallback: (
              <SvgIcon>
                <path d={mdiAccountMultiplePlus} />
              </SvgIcon>
            )
          }}
        />
      </div>
      <Modal open={open} actions={modalActions}>
        {({ aria, actions }) => (
          <div
            style={{
              position: 'absolute',
              top: '50%',
              left: '50%',
              transform: 'translate(-50%, -50%)',
              width: 'calc(75vw)',
              height: 'calc(75vh)'
            }}
          >
            <Paper>
              <ModalHeader
                avatar={
                  <Avatar className="generic-avatar" alt="edit-user-groups">
                    <SvgIcon>
                      <path d={mdiAccountGroup} />
                    </SvgIcon>
                  </Avatar>
                }
                title="Add to Group"
                desc="Please select which groups this one should be a part of"
                aria={aria}
                onClose={actions.close}
                changedForm={isFormChanged}
              />
              <Paper.Content scrollable={false}>
                <GroupSelection
                  group={group}
                  updateGroup={updateGroup}
                  closeModal={actions.close}
                  setIsFormChanged={setIsFormChanged}
                  retrieveGroupDescription={retrieveGroupDescription}
                />
              </Paper.Content>
            </Paper>
          </div>
        )}
      </Modal>
    </>
  );
};

EditSubGroups.propTypes = {
  group: object,
  updateGroup: func
};

export const EditSubGroupsContainer = connect(
  state => {
    const group = state.groups.selected;

    return {
      group,
      retrieveGroupDescription: name => getGroupDescription(name, state.groups.all)
    };
  },
  dispatch => ({
    updateGroup: group => dispatch(creators.action(PATCH_SELECTED_GROUP, group))
  })
)(EditSubGroups);

const GroupSelection = ({
  group,
  closeModal,
  updateGroup,
  setIsFormChanged,
  retrieveGroupDescription
}) => {
  const [state, setState] = useState({
    selections: [],
    groupsFetched: false,
    subgroupsSubmitted: false,
    loading: true,
    result: undefined,
    all: [],
    showAll: false
  });

  const filtered = useMemo(() => {
    return state.showAll
      ? state.all
          .filter(g => g.name !== 'user')
          .sort((a, b) => a.name.toLowerCase().localeCompare(b.name.toLowerCase()))
      : state.all
          .filter(g => !group.member_of.includes(g.name) && g.name !== 'user')
          .sort((a, b) => a.name.toLowerCase().localeCompare(b.name.toLowerCase()));
  }, [state.all, state.showAll, group]);

  const isDisabled = name => {
    if (group.name === name) {
      return true;
    }

    if (state.showAll) {
      return group.member_of.includes(name);
    }

    return false;
  };

  useEffect(() => {
    RestClient.get(`${CONSTANTS.BASE.ENDPOINTS.GROUPS}`, { page_size: 2147483645 })
      .then(({ data: resolved }) => {
        setState(prev =>
          produce(prev, draft => {
            draft.all = resolved.data.groups;
            draft.groupsFetched = true;
            draft.result = resolved.result;
            draft.loading = false;
          })
        );
      })
      .catch(({ data: rejected }) => {
        setState(prev =>
          produce(prev, draft => {
            draft.all = [];
            draft.error = rejected.messages;
            draft.groupsFetched = true;
            draft.result = rejected.result;
            draft.loading = false;
          })
        );
      });
  }, []);

  if (state.loading) {
    return <Loader>Loading...</Loader>;
  }

  if (
    state.groupsFetched &&
    (!state.all.length || (state.result === 'fail' && !state.subgroupError))
  ) {
    return (
      <>
        <Paper.Content>
          <Result type="fail">
            <Result.Text>Could not retrieve groups list!</Result.Text>
          </Result>
        </Paper.Content>
        <Paper.Footer>
          <Button
            variant="contained"
            color="primary"
            onClick={() => {
              closeModal();
            }}
          >
            Close
          </Button>
        </Paper.Footer>
      </>
    );
  }

  if (state.groupsFetched && state.subgroupsSubmitted && state.result === 'success') {
    return (
      <>
        <Paper.Content>
          <Result type="success">
            <Result.Text>Added selected groups succesfully!</Result.Text>
          </Result>
        </Paper.Content>
        <Paper.Footer>
          <Button
            variant="contained"
            color="primary"
            onClick={() => {
              closeModal();
            }}
          >
            Confirm
          </Button>
        </Paper.Footer>
      </>
    );
  }

  if (state.groupsFetched && state.subgroupsSubmitted && state.result === 'fail') {
    let errorMessage = 'Could not add selected groups!';

    if (
      state.error &&
      state.error[0] &&
      state.error[0].text &&
      state.error[0].text.split('\n').length > 0
    ) {
      [errorMessage] = state.error[0].text.split('\n');
    }

    return (
      <>
        <Paper.Content>
          <Result type="fail">
            <Result.Text>{errorMessage}</Result.Text>
          </Result>
        </Paper.Content>
        <Paper.Footer>
          <Button
            variant="contained"
            color="primary"
            onClick={() => {
              closeModal();
            }}
          >
            Close
          </Button>
        </Paper.Footer>
      </>
    );
  }

  return (
    <>
      <Paper.Content>
        <div className="seachable-list-container">
          <SearchableList
            items={filtered}
            filter={(item, query) => item.name.toLowerCase().includes(query.toLowerCase())}
            variant="row"
            visibleScroll
          >
            {entry => {
              return (
                <div className="card-wrap" style={{ width: '178px' }}>
                  <AvatarCard
                    onClick={() => {
                      setState(prev =>
                        produce(prev, draft => {
                          const selections = new Set(prev.selections);

                          if (selections.has(entry.name)) {
                            selections.delete(entry.name);
                          } else {
                            selections.add(entry.name);
                          }

                          draft.selections = [...selections];
                        })
                      );
                      setIsFormChanged(true);
                    }}
                    disabled={isDisabled(entry.name)}
                    selected={state.selections.includes(entry.name)}
                    label={entry.name}
                    avatar={{
                      fallback: (
                        <SvgIcon>
                          <path d={mdiAccountGroup} />
                        </SvgIcon>
                      )
                    }}
                    description={retrieveGroupDescription(entry.name)}
                  />
                </div>
              );
            }}
          </SearchableList>
        </div>
      </Paper.Content>
      <Paper.Footer>
        <Paper.Footer.Left>
          <Checkbox
            checked={state.showAll}
            onInput={() => setState(prev => ({ ...prev, showAll: !prev.showAll }))}
            label="Show groups that this one already is a member of"
          />
        </Paper.Footer.Left>
        <Paper.Footer.Right>
          <Button
            disabled={!state.selections.length}
            variant="contained"
            color="primary"
            onClick={() => {
              setState(prev =>
                produce(prev, draft => {
                  draft.loading = true;
                })
              );

              RestClient.patch(`${CONSTANTS.BASE.ENDPOINTS.GROUPS}/${group.name}/grant`, {
                groups: state.selections
              })
                .then(({ data: resolved }) => {
                  setState(prev =>
                    produce(prev, draft => {
                      draft.loading = false;
                      draft.subgroupsSubmitted = true;
                      draft.result = resolved.result;
                      draft.subgroupError = false;
                    })
                  );

                  updateGroup(resolved.data.groups[0]);
                })
                .catch(error => {
                  setState(prev =>
                    produce(prev, draft => {
                      draft.loading = false;
                      draft.subgroupsSubmitted = true;
                      draft.result = 'fail';
                      draft.error = error?.response?.data?.messages;
                      draft.subgroupError = true;
                    })
                  );
                });
            }}
          >
            Add
          </Button>
        </Paper.Footer.Right>
      </Paper.Footer>
    </>
  );
};

GroupSelection.propTypes = {
  group: object,
  updateGroup: func,
  closeModal: func
};
