import React, { useState, useCallback } from 'react';
import { array, func } from 'prop-types';
import moment from 'moment';
import classnames from 'classnames';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiLicense, mdiFileCompare } from '@mdi/js';
import Tooltip from '@material-ui/core/Tooltip';
import CheckIcon from '@material-ui/icons/Check';
import { withStyles } from '@material-ui/core/styles';

import { Center } from 'shared/components';

import './LicenseList.local.scss';

const LightTooltip = withStyles(() => ({
  tooltip: {
    color: '#fff',
    backgroundColor: '#787E89',
    letterSpacing: '0.4px',
    fontSize: '12px',
    lineHeight: '16px',
    fontFamily: 'Open Sans'
  }
}))(Tooltip);

export const LicenseList = ({
  data,
  selectedLicenses,
  setSelectedLicensesCallback,
  compareWithLatestCallback,
  licenseReportCallback
}) => {
  const [hoveredLicenseUuid, setHoveredLicenseUuid] = useState(null);
  const [scrolled, setScrolled] = useState(false);
  const onWheel = useCallback(
    event => {
      if (event.currentTarget.scrollTop) {
        if (!scrolled) {
          setScrolled(true);
        }
      } else if (scrolled) {
        setScrolled(false);
      }

      scroller(event);
    },
    [scrolled]
  );

  const updateHoveredLicenseUuid = licenseUuid => {
    setHoveredLicenseUuid(licenseUuid);
  };

  const renderIcon = licenseUuid => {
    let icon = (
      <SvgIcon>
        <Icon path={mdiLicense} />
      </SvgIcon>
    );

    // license is selected
    if (selectedLicenses.findIndex(l => l.uuid === licenseUuid) > -1) {
      icon = <CheckIcon />;
    } else if (hoveredLicenseUuid === licenseUuid) {
      icon = <CheckIcon className="hovered-icon" />;
    }

    return icon;
  };

  return (
    <div
      className={classnames('searchable-list-slot', {
        scrolled
      })}
    >
      {data.length > 0 ? (
        <div className="grid" onWheel={onWheel}>
          {data.map((license, index) => {
            return (
              <div
                data-qa={`report-${data.length - index}-details`}
                className="row"
                key={license.uuid}
                role="presentation"
                onMouseEnter={() => updateHoveredLicenseUuid(license.uuid)}
                onMouseLeave={() => updateHoveredLicenseUuid(null)}
                onClick={() => licenseReportCallback(license)}
              >
                <div className="column">
                  <div
                    data-qa={`toggle-selected-report-${data.length - index}`}
                    className={`icon-container ${
                      selectedLicenses.findIndex(l => l.uuid === license.uuid) > -1
                        ? 'selected'
                        : ''
                    }${hoveredLicenseUuid === license.uuid ? ' hovered' : ''}`}
                    onClick={event => {
                      event.stopPropagation();
                      setSelectedLicensesCallback(license);
                    }}
                    role="presentation"
                  >
                    {renderIcon(license.uuid)}
                  </div>
                </div>
                <div className="column name">
                  {`License feature usage report ${data.length - index}`}
                </div>
                <div className="column">
                  <div className="two-rows-container">
                    <div className="label">Date:</div>
                    <div className="value">{moment(license.date_invoke).format('MMM DD YYYY')}</div>
                  </div>
                </div>
                <div className="column">
                  <div className="two-rows-container">
                    <div className="label">Ran at:</div>
                    <div className="value">{moment(license.date_invoke).format('HH:mm:ss')}</div>
                  </div>
                </div>
                <div className="column">
                  <div className="two-rows-container">
                    <div className="label">Ran by:</div>
                    <div className="value">{license.user_owner}</div>
                  </div>
                </div>
                <div className="column">
                  {index > 0 && selectedLicenses.length === 0 && (
                    <div
                      data-qa={`compare-report${data.length - index}-to-latest`}
                      className="icon"
                      onClick={event => {
                        event.stopPropagation();
                        compareWithLatestCallback(data[0], license);
                      }}
                      role="presentation"
                    >
                      <LightTooltip placement="bottom" title="Compare to latest">
                        <SvgIcon>
                          <Icon path={mdiFileCompare} />
                        </SvgIcon>
                      </LightTooltip>
                    </div>
                  )}
                </div>
              </div>
            );
          })}
        </div>
      ) : (
        <Center>Currently there are no license reporting available.</Center>
      )}
    </div>
  );
};

LicenseList.propTypes = {
  data: array,
  selectedLicenses: array,
  setSelectedLicensesCallback: func,
  compareWithLatestCallback: func,
  licenseReportCallback: func
};

function scroller(event) {
  const target = event.currentTarget;

  target.scrollBy({
    top: event.deltaY,
    left: event.deltaX
  });
}
