import React from 'react';
import classnames from 'classnames';
import './Entity.scss';
import GroupIcon from '@material-ui/icons/Group';
import FilterNoneIcon from '@material-ui/icons/FilterNone';
import { LightTooltip } from 'shared/components/tooltip/Tooltip';

/* eslint-disable */
const Entity = ({ entity, selectEntity, columns, entityIcons }) => {
  const cls = classnames('entity', {
    '--isSelected': entity.isSelected
  });
  const user = entity.user_owner;
  const aliasStr = entity.alias;

  return (
    <div
      role="button"
      tabIndex="0"
      className={cls}
      onClick={event => {
        selectEntity(entity, columns.level);

        const target = event.currentTarget;
        const box = target.getBoundingClientRect();
        const parent = target.parentNode.parentNode;

        setTimeout(function() {
          parent.scrollTo({ left: box.right, behavior: 'smooth' });
        }, 200);
      }}
    >
      <LightTooltip placement="bottom" title={entity.entity || ''}>
        <div className="entity-icon-wrap">{entityIcons[entity.entity]}</div>
      </LightTooltip>
      <div className="entity-details">
        <div className="entity-name">
          {!!aliasStr && (
            <div title={`Alias of ${aliasStr}`}>
              <FilterNoneIcon />
            </div>
          )}
          <LightTooltip placement="bottom" title={entity.name}>
            <span>{entity.name}</span>
          </LightTooltip>
        </div>
        <div className="entity-user">
          <GroupIcon />
          <span>{user}</span>
        </div>
      </div>
    </div>
  );
};

/* eslint-enable */

export default Entity;
