import { RestClient } from 'shared/lib/rest';

export function postSchedule(body) {
  return RestClient.post('schedule', body, {
    page_size: 2147483645
  });
}

export function getSchedule(name) {
  return RestClient.get(`schedule/${name}`, {
    page_size: 2147483645
  });
}

export function patchSchedule(name, body) {
  return RestClient.patch(`schedule/${name}`, body, {
    page_size: 2147483645
  });
}
