import React from 'react';

export function want(map, Unresolved = () => null, keygen = key => key) {
  function settler(props) {
    const key = keygen(props);
    return map[key] ?? Unresolved;
  }

  return settler;
}

export function wantComponent(settler) {
  function Picker(props) {
    const Picked = settler(props);

    return <Picked {...props} />;
  }

  return Picker;
}
