import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

import { Dashboard } from 'dashboard';
import { Jobs } from 'jobs';
import { Entities } from 'entities';
import { UsersAndGroups } from 'users-manager';
import { Explorer } from 'explorer';
import { Sidebar } from './containers';
import { JobProcess } from '../job-process';
import { ConnectedJobsSearchResults } from 'jobs-search-results/JobsSearchResults';
import { ConnectedExecutiveDashboard } from 'executive-dashboard/ExecutiveDashboard';
import { LicenseCollection } from 'license-collection/LicenseCollection';

import './root.local.scss';

export class RootLayout extends React.PureComponent {
  render() {
    return (
      <>
        <Sidebar />
        <div className="app-container">
          <Switch>
            <Route path="/dashboard">
              <Dashboard />
            </Route>
            <Route exact path="/jobs" component={Jobs} />
            <Route exact path="/jobs/search-results" component={ConnectedJobsSearchResults} />
            <Route exact path="/jobs/job" component={JobProcess} />
            <Route exact path="/system/entities" component={Entities} />
            <Route path="/system/mas-accounts" component={UsersAndGroups} />
            <Route exact path="/system/explorer" component={Explorer} />
            <Route
              exact
              path="/compliance/executive-dashboard"
              component={ConnectedExecutiveDashboard}
            />
            <Route exact path="/compliance/license-collection" component={LicenseCollection} />
            <Redirect to="/dashboard" />
          </Switch>
        </div>
      </>
    );
  }
}

RootLayout.propTypes = {};
