import React, { useEffect } from 'react';
import { func, bool } from 'prop-types';
import { connect } from 'react-redux';
import { useParams } from 'react-router-dom';

import { Loader } from 'shared/components/loaders';

import { loadUserThunk } from '../../store/thunks';

import { ConnectedUserView } from '../pages/UserView/UserView';

export const UserLoader = ({ loadUser, loading }) => {
  const { userName } = useParams();
  useEffect(() => {
    if (userName) {
      loadUser(userName);
    }
  }, [userName]);

  return (
    <>
      {loading ? (
        <Loader>
          Loading User <b>{userName}</b>
        </Loader>
      ) : (
        <ConnectedUserView />
      )}
    </>
  );
};

UserLoader.propTypes = {
  loadUser: func,
  loading: bool
};

export const ConnectedUserLoader = connect(
  state => ({
    loading: state.usersManager.flags.user.loading
  }),
  dispatch => ({ loadUser: name => dispatch(loadUserThunk(name)) })
)(UserLoader);
