import React, { useState, useCallback } from 'react';
import { array, string, func } from 'prop-types';
import { connect } from 'react-redux';
import GroupIcon from '@material-ui/icons/Group';
import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiDeleteCircleOutline, mdiTrashCan } from '@mdi/js';

import { ScrollableContainer, Paper } from 'shared/components/layout';
import { Result } from 'shared/components/overlays/Result/Result';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Switch, Center } from 'shared/components';
import { Button } from 'shared/components/controls';
import { Loader } from 'shared/components/loaders';
import { capitalizeFirstLetter } from 'shared/lib/capitalize-first-letter';

import { AvatarCard } from '../../../../components/AvatarCard/AvatarCard';
import { EditUserGroupsContainer } from './EditUserGroups';
import { getGroupDescription } from '../../../../store/selectors';

import './UserGroups.local.scss';
import { removeUserFromGroupsThunk } from '../../../../store/thunks';

export const UserGroups = ({ user, groups, removeFromGroups, retrieveGroupDescription }) => {
  const [selectedGroup, setSelectedGroup] = useState('');
  const [errorMessage, setErrorMessage] = useState('An error occurred.');
  const [result, setResult] = useState('attempt');
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: useCallback(() => {
      setOpen(true);
    }, []),
    close: useCallback(() => {
      setResult('attempt');
      setErrorMessage('An error occurred.');
      setOpen(false);
    }, [])
  };

  const remove = useCallback(() => {
    removeFromGroups(user, [selectedGroup])
      .then(response => {
        if (typeof response === 'object' && response.type === 'fail') {
          setResult('error');
          setErrorMessage(response.message);
        } else {
          setResult('attempt');
          setErrorMessage('An error occurred.');
          setOpen(false);
        }
      })
      .catch(() => {
        setResult('attempt');
        setOpen(false);
        console.log('Could not remove user from group!'); // eslint-disable-line
      });
  }, [user, selectedGroup]);

  const isOnlySelect = groups?.length === 1 && groups[0] === 'select';
  const questionMessage = isOnlySelect
    ? 'If a user is removed from the select group they will lose all visibility to MAS features and output.  Are you sure you want to continue?'
    : `Are you sure you want to remove user ${user} from group ${selectedGroup}? This
  could affect the way the user will be able to interact with the app`;

  return (
    <>
      <div className="user-groups-block">
        <ScrollableContainer variant="row white">
          {passed => (
            <ul {...passed}>
              {groups &&
                groups.map(group_name => (
                  <li className="group-card-item" key={group_name}>
                    <AvatarCard
                      label={group_name}
                      disabled={user === 'mas'}
                      avatar={{ fallback: <GroupIcon /> }}
                      action={
                        <SvgIcon
                          onClick={e => {
                            e.preventDefault();
                            e.stopPropagation();
                            setSelectedGroup(group_name);
                            modalActions.open();
                          }}
                        >
                          <path d={mdiDeleteCircleOutline} />
                        </SvgIcon>
                      }
                      description={retrieveGroupDescription(group_name)}
                    />
                  </li>
                ))}
              <li className="group-card-item" key="add-user-to-groups">
                <EditUserGroupsContainer />
              </li>
            </ul>
          )}
        </ScrollableContainer>
      </div>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            width: '50vw',
            height: '50vh'
          }}
        >
          <ModalHeader
            title="Remove user from group"
            icon={
              <SvgIcon>
                <path d={mdiTrashCan} />
              </SvgIcon>
            }
          />
          <Switch value={result}>
            <Switch.Case value="attempt">
              <Paper.Content scrollable={false}>
                <Result type="fail">
                  <BodyText>{questionMessage}</BodyText>
                </Result>
              </Paper.Content>
              <Paper.Footer>
                {isOnlySelect && (
                  <Paper.Footer.Left>
                    <Button
                      color="primary"
                      onClick={() => {
                        setResult('attempt');
                        setErrorMessage('An error occurred.');
                        setOpen(false);
                      }}
                    >
                      Cancel
                    </Button>
                  </Paper.Footer.Left>
                )}
                <Paper.Footer.Right>
                  <Button
                    color="primary"
                    variant="contained"
                    onClick={() => {
                      setResult('removing');
                      remove();
                    }}
                  >
                    {isOnlySelect ? 'Continue' : 'Yes'}
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="removing">
              <Paper.Content scrollable={false}>
                <Center>
                  <Loader>Removing...</Loader>
                </Center>
              </Paper.Content>
            </Switch.Case>
            <Switch.Case value="error">
              <Paper.Content scrollable={false}>
                <Result type="fail">
                  <BodyText>{capitalizeFirstLetter(errorMessage)}</BodyText>
                </Result>

                <Paper.Footer>
                  <Button
                    color="primary"
                    variant="contained"
                    onClick={() => {
                      setResult('attempt');
                      setErrorMessage('An error occurred.');
                      setOpen(false);
                    }}
                  >
                    Close
                  </Button>
                </Paper.Footer>
              </Paper.Content>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
};

UserGroups.propTypes = {
  user: string,
  groups: array,
  removeFromGroups: func
};

export const UserGroupsContainer = connect(
  state => {
    const user = selectedUser(state);

    const visibleGroups = user.member_of?.filter(group => group !== 'user');

    return {
      user: user.name,
      groups: visibleGroups,
      retrieveGroupDescription: name => getGroupDescription(name, state.groups.all)
    };
  },
  dispatch => ({
    removeFromGroups: (user, groups) => dispatch(removeUserFromGroupsThunk({ user, groups }))
  })
)(UserGroups);

function selectedUser(state) {
  return state.usersManager.users.selected;
}

function BodyText({ children }) {
  return (
    <h2
      style={{
        fontFamily: 'Open Sans',
        fontStyle: 'normal',
        fontWeight: 'bold',
        fontSize: '14px',
        lineHeight: '20px',
        letterSpacing: '0.25px',
        marginTop: '24px'
      }}
    >
      {children}
    </h2>
  );
}
