import React from 'react';
import { Field } from 'formik';
import { array, object } from 'prop-types';

import InputAdornment from '@material-ui/core/InputAdornment';
import { TextField as MUITextField } from '@material-ui/core';
import SvgIcon from '@material-ui/core/SvgIcon';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import { mdiLanConnect } from '@mdi/js';
import Icon from '@mdi/react';

import { Dropdown } from 'shared/components';

import './DropdownField.local.scss';

export const DropdownField = ({
  parameter,
  datatypes,
  rawAccounts,
  forms,
  prototypes,
  disabled
}) => {
  const dataType = datatypes.find(datatype => datatype.name === parameter.data_type);
  let protocols = null;

  if (dataType) {
    if (
      dataType.name.startsWith('mas0000.sys.account.conn.') ||
      dataType.name === 'mas0000.sys.account.vsphere'
    ) {
      if (Array.isArray(dataType.protocol) && dataType.protocol.length > 0) {
        protocols = [];
        dataType.protocol.forEach(protocol => {
          if (protocol === 'DBMS') {
            // This is quick and dirty until we have a configuration-driven solution
            // in the UI.
            protocols = [...protocols, 'MSSQL', 'MYSQL', 'ORACLE', 'PGSQL'];
          } else {
            protocols.push(protocol);
          }
        });
        const options = rawAccounts
          .map(account => ({ name: account.name, value: account }))
          .filter(option => protocols === null || protocols.includes(option.value.protocol));

        const validate = value => {
          if (parameter.is_required) {
            if (!value) {
              return 'This Field is Required';
            }
          }
        };

        return (
          <Field name={parameter.name} validate={validate}>
            {({ field, meta }) => {
              return (
                <Dropdown
                  onSelect={(index, opt) => {
                    field.onChange({ target: { name: parameter.name, value: opt.value } });
                  }}
                  disabled={disabled}
                  target={(meta2, actions) => (
                    <MUITextField
                      className={disabled ? 'disabled' : ''}
                      type="text"
                      readOnly
                      disabled
                      variant="outlined"
                      fullWidth
                      label={parameter.label}
                      placeholder={parameter.label}
                      {...field}
                      value={field.value ? field.value.name : ''}
                      onChange={() => {}}
                      onClick={actions.toggle}
                      ref={meta2.ref}
                      error={meta.error && meta.touched}
                      helperText={meta.touched && meta.error ? meta.error : undefined}
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            {parameter.icon ? (
                              parameter.icon
                            ) : (
                              <SvgIcon>
                                <Icon path={mdiLanConnect} />
                              </SvgIcon>
                            )}
                          </InputAdornment>
                        ),

                        endAdornment: (
                          <InputAdornment position="end">
                            <ArrowDropDownIcon />
                          </InputAdornment>
                        )
                      }}
                    />
                  )}
                  items={options}
                />
              );
            }}
          </Field>
        );
      }
    }
  } else {
    const prototype = prototypes.find(p => p.name === parameter.data_type);

    if (prototype) {
      const options = forms
        .filter(form => form.prototype === prototype.name)
        .map(form => ({ name: form.name, value: form.name }));

      const validate = value => {
        if (parameter.is_required) {
          if (!value) {
            return 'This Field is Required';
          }
        }
      };

      return (
        <Field name={parameter.name} validate={validate}>
          {({ field, meta }) => {
            return (
              <Dropdown
                onSelect={(index, opt) => {
                  field.onChange({ target: { name: parameter.name, value: opt.value } });
                }}
                disabled={disabled}
                target={(meta2, actions) => (
                  <MUITextField
                    className={disabled ? 'disabled' : ''}
                    type="text"
                    readOnly
                    disabled
                    variant="outlined"
                    fullWidth
                    label={parameter.label}
                    placeholder={parameter.label}
                    {...field}
                    value={field.value ? field.value : ''}
                    onChange={() => {}}
                    onClick={actions.toggle}
                    ref={meta2.ref}
                    error={meta.error && meta.touched}
                    helperText={meta.touched && meta.error ? meta.error : undefined}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          {parameter.icon ? (
                            parameter.icon
                          ) : (
                            <SvgIcon>
                              <Icon path={mdiLanConnect} />
                            </SvgIcon>
                          )}
                        </InputAdornment>
                      ),

                      endAdornment: (
                        <InputAdornment position="end">
                          <ArrowDropDownIcon />
                        </InputAdornment>
                      )
                    }}
                  />
                )}
                items={options}
              />
            );
          }}
        </Field>
      );
    }
  }

  return <div>Currently not supported input</div>;
};

DropdownField.propTypes = {
  parameter: object,
  datatypes: array,
  rawAccounts: array,
  forms: array,
  prototypes: array
};
