import React from 'react';
import { connect } from 'react-redux';
import { array, func } from 'prop-types';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiHexagonMultipleOutline, mdiChartTimelineVariant } from '@mdi/js';

import { Paper } from 'shared/components/layout';
import { changeStateThunk } from '../../store/thunks';
import { getDBMSStatus, getDBMSStatusInfo } from '../../store/selectors';

import { ActionCard } from '../../components/ActionCard/ActionCard';
import { CardsLayout } from '../../components/CardsLayout/CardsLayout';

export class AllDbms extends React.PureComponent {
  render() {
    const { all, retrieveDBMSStatus, retrieveDBMSStatusInfo } = this.props;

    return (
      <Paper.Content>
        <CardsLayout>
          {all.map(dbms => (
            <ActionCard
              key={dbms.name}
              icon={
                <SvgIcon>
                  <Icon path={mdiHexagonMultipleOutline} />
                </SvgIcon>
              }
              status={dbms.name !== 'OTHER' ? retrieveDBMSStatus(dbms) : ''}
              statusInfo={retrieveDBMSStatusInfo(dbms)}
              type="dbms"
              label={dbms.name}
              onClick={() => this.setPath(dbms.path)}
              options={[
                {
                  icon: (
                    <SvgIcon>
                      <Icon path={mdiChartTimelineVariant} />
                    </SvgIcon>
                  ),
                  label: 'Workload',
                  onClick: () => this.props.changeState({ path: dbms.path, level: 'workload' })
                }
              ]}
            />
          ))}
        </CardsLayout>
      </Paper.Content>
    );
  }

  setPath = path => {
    this.props.changeState({ path, level: 'all-servers' });
  };
}

AllDbms.propTypes = {
  all: array,
  changeState: func
};

export const ConnectedAllDbms = connect(
  state => ({
    all: state.explorer?.structured?.dbms || [],
    allDbms: state.explorer.accounts,
    retrieveDBMSStatus: dbms => getDBMSStatus(dbms, state.explorer.dbStatus),
    retrieveDBMSStatusInfo: dbms => getDBMSStatusInfo(dbms, state.explorer.dbStatus)
  }),
  dispatch => ({
    changeState: newState => dispatch(changeStateThunk(newState))
  })
)(AllDbms);
