import React, { useState, useCallback } from 'react';

import { SimpleSearchInput } from 'shared/components/simple-search-input/simple-search-input';
import { array, func, oneOf } from 'prop-types';
import classnames from 'classnames';

import './SearchableList.local.scss';

export const SearchableList = ({ items, children, filter, variant, visibleScroll }) => {
  const [scrolled, setScrolled] = useState(false);
  const onWheel = useCallback(
    event => {
      if (event.currentTarget.scrollTop) {
        if (!scrolled) {
          setScrolled(true);
        }
      } else if (scrolled) {
        setScrolled(false);
      }

      scroller(event);
    },
    [scrolled]
  );

  const [query, setQuery] = useState('');
  const filtered = items.filter(item => filter(item, query));

  return (
    <>
      <div className="search-input-slot">
        <SimpleSearchInput
          data-qa="users-search-input"
          value={query}
          onChange={e => setQuery(e.target.value)}
        />
      </div>
      <div
        className={classnames('searchable-list-slot', {
          visibleScroll,
          scrolled,
          [variant]: variant
        })}
      >
        <ul
          className={classnames('list', {
            [variant]: variant
          })}
          onWheel={onWheel}
        >
          {filtered.map((item, index, list) => (
            <li key={index} className="list-item">
              {children(item, index, list)}
            </li>
          ))}
        </ul>
      </div>
    </>
  );
};

SearchableList.propTypes = {
  items: array,
  children: func,
  filter: func,
  variant: oneOf(['row', 'white', 'row white'])
};

SearchableList.defaultProps = {
  items: [],
  filter: (item, query) => item === query
};

function scroller(event) {
  const target = event.currentTarget;

  target.scrollBy({
    top: event.deltaY,
    left: event.deltaX
  });
}
