import React, { useState } from 'react';
import * as PropTypes from 'prop-types';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Chip from '@material-ui/core/Chip';
import { DayFilterEditor } from '../day-filter-editor/day-filter-editor';
import { UserFilterEditor } from '../user-filter-editor/user-filter-editor';
import { StatusFilterEditor } from '../status-filter-editor/status-filter-editor';
import { FilterLoader } from '../filter-loader/filter-loader';

import './filter-chip.local.scss';

export const FilterChip = ({ value, type, save }) => {
  const [editMode, setEditMode] = useState(false);
  const FILTERS_MAP = {
    status: StatusFilterEditor,
    day: DayFilterEditor,
    user: UserFilterEditor,
  };
  const IconComp = FILTERS_MAP[type].TypeDescriptor.icon;

  return (
    <>
      {editMode
        ? (
          <ClickAwayListener onClickAway={() => { setEditMode(false); }}>
            <div>
              <FilterLoader value={value} type={type} setFilterValue={(newFilter) => { save(newFilter); setEditMode(false); }} />
            </div>
          </ClickAwayListener>
        )
        : (
          <Chip
            className="filter-chip"
            icon={<IconComp />}
            label={value}
            onClick={() => { setEditMode(true); }}
            onDelete={() => { save(null); }}
          />
        )}
    </>
  );
};

FilterChip.propTypes = {
  value: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
  save: PropTypes.func,
};

FilterChip.defaultProps = {
  save: () => {},
};
