import React from 'react';
import { hot } from 'react-hot-loader/root';
import { Route, Switch } from 'react-router-dom';
import { connect } from 'react-redux';
import * as PropTypes from 'prop-types';
import Cookie from 'js-cookie';

import CircularProgress from '@material-ui/core/CircularProgress';
import { Center } from 'shared/components';

import { Login, Logout } from 'session';
import { actionStates } from 'shared/lib/action-creator';
import { authorizationSelector } from 'session/store/state';
import { actions } from 'session/store/actions';
import { Layout } from './core';

import './scoped-css-mui-compatibility';

export class RootModule extends React.PureComponent {
  componentDidMount() {
    if (!Cookie.get('Authorization')) {
      this.props.history.listen((newLocation, action) => {
        if (action === 'PUSH') {
          if (
            newLocation.pathname !== this.currentPathname ||
            newLocation.search !== this.currentSearch
          ) {
            this.currentPathname = newLocation.pathname;
            this.currentSearch = newLocation.search;
            this.props.history.push({
              pathname: newLocation.pathname,
              search: newLocation.search
            });
          }
        } else {
          this.props.history.go(1);
        }
      });
      this.props.history.push('/login');
      this.props.setAuthorization();
      return;
    }

    const username = JSON.parse(localStorage.getItem('username')) || 'mas';

    this.props.checkAuthorization(username);
  }

  componentDidUpdate(prevProps) {
    if (
      this.props.authorizationStatus !== prevProps.authorizationStatus &&
      this.props.authorizationStatus === actionStates.FAIL
    ) {
      this.props.history.push('/login');
    }
  }

  render() {
    if (this.props.authorizationStatus === actionStates.LOADING) {
      return (
        <Center>
          <CircularProgress />
        </Center>
      );
    }

    return (
      <Switch>
        <Route exact path={Login.path} component={Login} />
        <Route exact path={Logout.path} component={Logout} />
        <Route path="/" component={Layout} />
      </Switch>
    );
  }
}

RootModule.propTypes = {
  history: PropTypes.shape({
    push: PropTypes.func.isRequired
  }).isRequired,
  authorizationStatus: PropTypes.oneOf([
    '',
    actionStates.SUCCESS,
    actionStates.LOADING,
    actionStates.FAIL
  ]).isRequired,
  setAuthorization: PropTypes.func.isRequired,
  checkAuthorization: PropTypes.func.isRequired
};

export const mapStateToProps = state => ({
  authorizationStatus: authorizationSelector(state).get('status')
});

export const mapDispatchToProps = dispatch => ({
  checkAuthorization: user => dispatch(actions.checkAuthorization.dispatchLoading(user)),
  setAuthorization: () => dispatch(actions.checkAuthorization.dispatchFail())
});

export default hot(connect(mapStateToProps, mapDispatchToProps)(RootModule));
