import React, { useState } from 'react';
import { connect } from 'react-redux';
import { func } from 'prop-types';
import { useHistory, useLocation } from 'react-router-dom';
import * as Yup from 'yup';

import { Button, Input } from 'shared/components/controls';

import { Center } from 'shared/components';
import { Paper, Section, Column, Row } from 'shared/components/layout';
import { Result } from 'shared/components/overlays';

import { Formik, Form, Field, FieldArray } from 'formik';

import { createGroupThunk } from '../../store/thunks';

import { SubGroups } from './components/SubGroups';
import { getGroupDescription } from '../../../store/selectors';

import './CreateNewGroup.local.scss';

const validationSchema = Yup.object().shape({
  groupname: Yup.string()
    .min(3, 'Invalid length')
    .required('Group name is required'),
  groups: Yup.array()
});

const CreateNewGroup = ({ submitGroupValues, allGroups, retrieveGroupDescription }) => {
  const history = useHistory();
  const location = useLocation();

  const [operation, setOperation] = useState({ done: false });

  const extraValidations = values => {
    const errors = {};

    if (
      !/^(?:[a-z_][a-z0-9_.]*)$/i.test(values.groupname) ||
      (values.groupname && values.groupname.substr(-1) === '.')
    ) {
      errors.groupname = 'A valid name is required';
    }

    let exists = false;

    allGroups.map(group => {
      if (exists) return;

      if (group.name === values.groupname) {
        exists = true;
      }
      return null;
    });

    if (exists) {
      errors.groupname = 'A group with this name already exists';
    }

    return errors;
  };

  if (operation.done) {
    /**
     * Failed results are the ony onnes that are shown.
     * For successfull creation, the user is navigated to the new entry
     */
    let errorMessage = 'Could not create new group';

    if (
      operation.result === 'fail' &&
      operation.error &&
      operation.error[0] &&
      operation.error[0].text &&
      operation.error[0].text.split('\n').length > 0
    ) {
      [errorMessage] = operation.error[0].text.split('\n');
    }

    return (
      <>
        <Paper>
          <Paper.Content scrollable={false}>
            <Center>
              <Result type="fail">
                <Result.Text>{errorMessage}</Result.Text>
              </Result>
            </Center>
          </Paper.Content>

          <Paper.Footer>
            <Button
              color="primary"
              onClick={() => {
                setOperation({ done: false, result: undefined, error: undefined });
              }}
            >
              Retry
            </Button>
          </Paper.Footer>
        </Paper>
      </>
    );
  }

  return (
    <Formik
      initialValues={{
        groupname: '',
        description: '',
        groups: []
      }}
      validationSchema={validationSchema}
      validate={extraValidations}
      onSubmit={async values => {
        try {
          const result = await submitGroupValues(values);

          if (result.result === 'success') {
            history.push(location.pathname.replace('create-new', values.groupname));
          }

          if (result.result === 'fail') {
            setOperation({
              done: true,
              result: 'fail',
              error: result.messages
            });
          }
        } catch (error) {
          setOperation({
            done: true,
            result: 'fail',
            error: error?.messages
          });
        }
      }}
    >
      {({ values, isSubmitting, isValid, submitForm }) => (
        <Paper>
          <Paper.Title>New Group</Paper.Title>
          <Paper.Content>
            <Form className="create-new-group-form">
              <Section>
                <Section.Title>Group Details</Section.Title>
                <Row>
                  <Column style={{ maxWidth: '220px', padding: '0', marginRight: '24px' }}>
                    <Field name="groupname">
                      {({ field, meta }) => (
                        <Input
                          required
                          {...field}
                          label="Group Name"
                          placeholder="Group Name"
                          error={!!meta.error}
                          helperText={meta.error}
                        />
                      )}
                    </Field>
                  </Column>
                  <Column style={{ maxWidth: '220px' }}>
                    <Field name="description">
                      {({ field, meta }) => (
                        <Input
                          {...field}
                          label="Short Description"
                          placeholder="Short Description"
                          error={!!meta.error}
                          helperText={meta.error}
                        />
                      )}
                    </Field>
                  </Column>
                </Row>
              </Section>
              <Section full>
                <Section.Title>Member of groups</Section.Title>
                <FieldArray name="groups">
                  {helpers => (
                    <SubGroups
                      selections={values.groups}
                      helpers={helpers}
                      retrieveGroupDescription={retrieveGroupDescription}
                    />
                  )}
                </FieldArray>
              </Section>
            </Form>
          </Paper.Content>

          <Paper.Footer>
            <Paper.Footer.Left>
              <Button to="/system/mas-accounts/groups" color="primary">
                Cancel
              </Button>
            </Paper.Footer.Left>

            <Paper.Footer.Right>
              <Button
                type="submit"
                onClick={submitForm}
                variant="contained"
                disabled={isSubmitting || !isValid}
              >
                Save
              </Button>
            </Paper.Footer.Right>
          </Paper.Footer>
        </Paper>
      )}
    </Formik>
  );
};

CreateNewGroup.propTypes = { submitGroupValues: func };

export const CreateNewGroupPage = connect(
  state => ({
    allGroups: state.groups.all.filter(g => g.name !== 'user'),
    retrieveGroupDescription: name => getGroupDescription(name, state.groups.all)
  }),
  dispatch => ({
    submitGroupValues: values => dispatch(createGroupThunk(values))
  })
)(CreateNewGroup);
