import React, { useState, useRef } from 'react';
import { object } from 'prop-types';
import { connect } from 'react-redux';
import _ from 'lodash';

import { Formik, Field, Form } from 'formik';

import GroupAddIcon from '@material-ui/icons/GroupAdd';
import Avatar from '@material-ui/core/Avatar';
import { CONSTANTS } from 'config';
import { RestClient } from 'shared/lib/rest';

import { Center } from 'shared/components';
import { Button, Input } from 'shared/components/controls';

import { Paper, Row } from 'shared/components/layout';
import { Modal, ModalHeader, Result } from 'shared/components/overlays';
import { ChangePasswordContainer } from './ChangePassword';
import { UserDatesBox } from '../components/UserDatesBox';

// import './EditUserDetails.local.scss';

export const EditUserDetails = ({ user }) => {
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };

  const formRef = useRef();

  return (
    <>
      <Button color="primary" onClick={modalActions.open}>
        User Details
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            width: '392px',
            maxWidth: '392px',
            minHeight: '370px'
          }}
        >
          <ModalHeader
            avatar={
              <Avatar className="generic-avatar" alt="edit-user-details">
                <GroupAddIcon />
              </Avatar>
            }
            title="User Details"
            formikRef={formRef}
          />
          <UserDetailsForm user={user} modalActions={modalActions} formRef={formRef} />
        </Paper>
      </Modal>
    </>
  );
};

EditUserDetails.propTypes = {
  user: object
};

export const EditUserDetailsContainer = connect(
  state => {
    const user = selectedUser(state);

    return { user };
  },
  () => ({})
)(EditUserDetails);

function selectedUser(state) {
  return state.usersManager.users.selected;
}

export const UserDetailsForm = ({ user, modalActions, formRef }) => {
  const [state, setState] = useState({ submitResult: undefined });

  if (state.submitResult === 'success') {
    return (
      <>
        <Center>
          <Result type="success">
            <Result.Text>Updated user details!</Result.Text>
          </Result>
        </Center>
        <Paper.Footer>
          <Paper.Footer.Left>
            <ChangePasswordContainer />
          </Paper.Footer.Left>
          <Paper.Footer.Right>
            <Button variant="contained" color="primary" onClick={modalActions.close}>
              Done
            </Button>
          </Paper.Footer.Right>
        </Paper.Footer>
      </>
    );
  }

  if (state.submitResult === 'fail') {
    return (
      <>
        <Center>
          <Result type="fail">
            <Result.Text>Could not update user details!</Result.Text>
          </Result>
        </Center>
        <Paper.Footer>
          <Paper.Footer.Left>
            <ChangePasswordContainer />
          </Paper.Footer.Left>
          <Paper.Footer.Right>
            <Button
              variant="contained"
              color="primary"
              onClick={() => {
                setState(prev => ({ ...prev, submitResult: undefined }));
              }}
            >
              Retry
            </Button>
          </Paper.Footer.Right>
        </Paper.Footer>
      </>
    );
  }

  const initialValues = {
    description: user.description
  };

  return (
    <>
      <Formik
        validateOnChange={false}
        initialValues={initialValues}
        onSubmit={(values, actions) => {
          if (_.isEqual(initialValues, values)) {
            actions.setSubmitting(false);
            modalActions.close();
          }

          // parse and add user information form data.

          /**
            patch user description
            */
          RestClient.patch(`${CONSTANTS.BASE.ENDPOINTS.USERS}/${user.name}`, {
            description: values.description
          })
            .then(() => {
              setState(prev => ({ ...prev, submitResult: 'success' }));
            })
            .catch(() => {
              actions.setSubmitting(false);

              setState(prev => ({ ...prev, submitResult: 'fail' }));
            });
        }}
        innerRef={formRef}
      >
        {({ dirty, isSubmitting, submitForm }) => (
          <>
            <Paper.Content scrollable>
              <Form>
                <UserDatesBox created={user.date_created} updated={user.date_updated} />
                <Row>
                  <Field name="description">
                    {({ field, meta }) => {
                      return (
                        <Input
                          {...field}
                          type="text"
                          error={meta.error && meta.touched}
                          helperText={meta.touched ? meta.error : undefined}
                          label="Short Description"
                        />
                      );
                    }}
                  </Field>
                </Row>
              </Form>
            </Paper.Content>
            <Paper.Footer>
              <Paper.Footer.Left>
                <ChangePasswordContainer />
              </Paper.Footer.Left>
              <Paper.Footer.Right>
                <Button
                  variant="contained"
                  color="primary"
                  disabled={!dirty || isSubmitting}
                  onClick={submitForm}
                >
                  Save
                </Button>
              </Paper.Footer.Right>
            </Paper.Footer>
          </>
        )}
      </Formik>
    </>
  );
};

UserDetailsForm.propTypes = {
  user: object,
  modalActions: object
};
