import React from 'react';

import { Button } from '@material-ui/core';
import { Paper } from 'shared/components/layout';
import CircularProgress from '@material-ui/core/CircularProgress';

import { Center } from 'shared/components';
import './output.scss';
import { useSelector } from 'react-redux';
import { getInvocation } from '../../store/selectors';
import { InvocationStatus } from '../../store/constants';

import { ListOutput } from '../../components/ListOutput/ListOutput';

import { fetchInvocationOutput } from 'shared/api/invocation';

import { sendToEmail } from 'license-collection/utils/sendToEmail';
import { copyToClipboard } from 'license-collection/utils/copyToClipboard';

function noOutput(invocation = {}) {
  return [
    {
      data: {
        text: 'No output to display.\n'
      }
    },
    {
      data: {
        text: `Status: ${invocation.status}\n`
      }
    },
    {
      data: {
        text: `Process: ${invocation.process}\n`
      }
    },
    {
      data: {
        text: `Invocation: ${invocation.uuid}\n`
      }
    }
  ];
}

export const JobOutput = () => {
  const invocation = useSelector(getInvocation);

  const [loading, setLoading] = React.useState(true);
  const [output, setOutput] = React.useState(noOutput(invocation));

  React.useEffect(() => {
    fetchInvocationOutput(invocation.uuid)
      .then(({ data }) => {
        const recieved = data.data.outputs;

        if (recieved && recieved.length) {
          setOutput(recieved);
        } else {
          setOutput(noOutput(invocation));
        }

        setLoading(false);
      })
      .catch(() => {
        setOutput(noOutput(invocation));
        setLoading(false);
      });
  }, []);

  if (loading) {
    return (
      <Center>
        <CircularProgress />
      </Center>
    );
  }

  const outputType = invocation.status === InvocationStatus.FAILED ? 'fail' : 'normal';

  const monitoring = output?.[0]?.data?.text?.match?.(
    /{"process": {"uuid": "([0-9a-f]{8}\b-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-\b[0-9a-f]{12})",/
  )?.[1];

  console.log(monitoring); // eslint-disable-line

  return (
    <>
      <Paper.Content scrollable>
        <ListOutput
          type={outputType}
          output={output}
          errors={invocation.errors}
          monitoring={monitoring}
        />
      </Paper.Content>
      <Paper.Footer>
        <Paper.Footer.Left>
          <Button
            color="primary"
            onClick={() => sendToEmail(invocation, output, invocation.process)}
          >
            Send to email
          </Button>
          <Button color="primary" onClick={() => copyToClipboard(output, invocation)}>
            Copy to clipboard
          </Button>
        </Paper.Footer.Left>
      </Paper.Footer>
    </>
  );
};
