import React from 'react';

import './ListOutput.local.scss';

export const ListOutput = ({ output, type, errors }) => {
  let content;

  if (type === 'fail') {
    content = (
      <>
        <p className="category error">The following error(s) occurred:</p>
        <div className="list error">
          {errors.map((error, index) => {
            return <p key={index}> {error}</p>;
          })}
        </div>
        {output && output.length > 0 && (
          <>
            <p className="category">Output:</p>
            <div className="list">
              {output.map((row, index) => {
                return (
                  <p className="row" key={`${row.data.text}-${index}`}>
                    {row.data.text}
                  </p>
                );
              })}
            </div>
          </>
        )}
      </>
    );
  } else {
    content = (
      <div className="list">
        {output.map((row, index) => {
          return (
            <p className="row" key={index}>
              {row.data.text}
            </p>
          );
        })}
      </div>
    );
  }

  return content;
};
