import { actions } from 'session/store/actions';
import { actionStates } from 'shared/lib';
import { fromJS } from 'immutable';

export const initialState = {
  login: {
    status: '',
    errors: []
  },
  authorization: {
    status: actionStates.LOADING
  },
  currentUser: {
    name: '',
    details: {}
  },
  beVersion: ''
};

export const reducerActions = {
  // authorization Check
  [actions.checkAuthorization.getLoadingAction()]: state =>
    state.setIn(['authorization', 'status'], actionStates.LOADING),
  [actions.checkAuthorization.getSuccessAction()]: (state, payload) =>
    state
      .set(
        'currentUser',
        fromJS({
          name: payload.name,
          details: payload.details
        })
      )
      .setIn(['authorization', 'status'], actionStates.SUCCESS),
  [actions.checkAuthorization.getFailAction()]: state =>
    state.setIn(['authorization', 'status'], actionStates.FAIL),

  // login
  [actions.login.getLoadingAction()]: state =>
    state.setIn(['login', 'status'], actionStates.LOADING),
  [actions.login.getSuccessAction()]: state =>
    state
      .setIn(['login', 'status'], actionStates.SUCCESS)
      .setIn(['authorization', 'status'], actionStates.SUCCESS),
  [actions.login.getFailAction()]: (state, errors) =>
    state.setIn(['login', 'status'], actionStates.FAIL).setIn(['login', 'errors'], fromJS(errors)),
  [actions.getBeVersion.getSuccessAction()]: (state, payload) => {
    const nextState = state.set('beVersion', payload.data);
    return nextState;
  }
};
