import React from 'react';
import { string } from 'prop-types';

import { ConnectedAllDbms } from '../../containers/AllDbms/AllDbms';
import { ConnectedAllServers } from '../../containers/AllServers/AllServers';
import { ConnectedAllDatabases } from '../../containers/AllDatabases/AllDatabases';
import { ConnectedWorkload } from '../../containers/Workload/Workload';
import { ConnectedLicense } from '../../containers/License/License';
import { ConnectedStorage } from '../../containers/Storage/Storage';
import { ConnectedObjects } from '../../containers/Objects/Objects';
import { ConnectedSessions } from '../../containers/Sessions/Sessions';

export class ContentSwitch extends React.PureComponent {
  render() {
    const { activePage } = this.props;

    return this.renderPage(activePage);
  }

  renderPage = activePage => {
    switch (activePage) {
      case 'all-dbms':
        return <ConnectedAllDbms />;
      case 'all-servers':
        return <ConnectedAllServers />;
      case 'all-databases':
        return <ConnectedAllDatabases />;
      case 'workload':
        return <ConnectedWorkload />;
      case 'license':
        return <ConnectedLicense />;
      case 'storage':
        return <ConnectedStorage />;
      case 'objects':
        return <ConnectedObjects />;
      case 'sessions':
        return <ConnectedSessions />;
      default:
        return <></>;
    }
  };
}

ContentSwitch.propTypes = {
  activePage: string
};

export default ContentSwitch;
