import React, { useState, useCallback } from 'react';
import { object, func } from 'prop-types';
import { connect } from 'react-redux';
import { Section, Paper } from 'shared/components/layout';

import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiDeleteCircleOutline, mdiAccountGroup, mdiTrashCan } from '@mdi/js';

import { Result } from 'shared/components/overlays/Result/Result';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Switch, Center } from 'shared/components';
import { Button } from 'shared/components/controls';
import { Loader } from 'shared/components/loaders';
import { capitalizeFirstLetter } from 'shared/lib/capitalize-first-letter';

import { AvatarCard } from '../../../../components/AvatarCard/AvatarCard';
import { EditSubGroupsContainer } from './EditSubGroups';
import { removeSubGroupsThunk } from '../../../store/thunks';
import { getGroupDescription } from '../../../../store/selectors';

import './SubGroups.local.scss';

export const SubGroups = ({ group, removeSubGroups, retrieveGroupDescription }) => {
  const [selectedGroup, setSelectedGroup] = useState('');
  const [errorMessage, setErrorMessage] = useState('An error occurred.');
  const [result, setResult] = useState('attempt');
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: useCallback(() => {
      setOpen(true);
    }, []),
    close: useCallback(() => {
      setResult('attempt');
      setErrorMessage('An error occurred.');
      setOpen(false);
    }, [])
  };

  const remove = useCallback(() => {
    removeSubGroups(group.name, [selectedGroup])
      .then(response => {
        if (typeof response === 'object' && response.result === 'fail') {
          setResult('error');
          setErrorMessage(response.messages);
        } else {
          setResult('attempt');
          setErrorMessage('An error occurred.');
          setOpen(false);
        }
      })
      .catch(() => {
        setResult('attempt');
        setOpen(false);
        console.log('Could not remove group from group!'); // eslint-disable-line
      });
  }, [group, selectedGroup]);

  const isOnlySelect = group?.member_of?.length === 1 && group?.member_of[0] === 'select';
  const questionMessage = isOnlySelect
    ? 'If a user is removed from the select group they will lose all visibility to MAS features and output.  Are you sure you want to continue?'
    : `Are you sure you want to remove group ${group.name} from group ${selectedGroup}?
      This could affect the way the user will be able to interact with the app.`;

  return (
    <>
      <Section>
        <Section.Header>
          <Section.Title>Member of groups</Section.Title>
        </Section.Header>
        <div>
          <ul className="card-list">
            {group.member_of
              .filter(g => g !== 'user')
              .sort((a, b) => a.toLowerCase().localeCompare(b.toLowerCase()))
              .map(subgroup => (
                <li className="card-item" key={subgroup}>
                  <AvatarCard
                    label={subgroup}
                    avatar={{
                      fallback: (
                        <SvgIcon>
                          <path d={mdiAccountGroup} />
                        </SvgIcon>
                      )
                    }}
                    action={
                      <SvgIcon
                        onClick={e => {
                          e.preventDefault();
                          e.stopPropagation();
                          setSelectedGroup(subgroup);
                          modalActions.open();
                        }}
                      >
                        <path d={mdiDeleteCircleOutline} />
                      </SvgIcon>
                    }
                    description={retrieveGroupDescription(subgroup)}
                  />
                </li>
              ))}
            <li className="card-item" key="add-sub-group">
              <EditSubGroupsContainer />
            </li>
          </ul>
        </div>
      </Section>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            width: '50vw',
            height: '50vh'
          }}
        >
          <ModalHeader
            title="Remove group from group"
            icon={
              <SvgIcon>
                <path d={mdiTrashCan} />
              </SvgIcon>
            }
          />
          <Switch value={result}>
            <Switch.Case value="attempt">
              <Paper.Content scrollable={false}>
                <Result type="fail">
                  <BodyText>{questionMessage}</BodyText>
                </Result>
              </Paper.Content>
              <Paper.Footer>
                {isOnlySelect && (
                  <Paper.Footer.Left>
                    <Button
                      color="primary"
                      onClick={() => {
                        setResult('attempt');
                        setOpen(false);
                      }}
                    >
                      Cancel
                    </Button>
                  </Paper.Footer.Left>
                )}
                <Paper.Footer.Right>
                  <Button
                    color="primary"
                    variant="contained"
                    onClick={() => {
                      setResult('removing');
                      remove();
                    }}
                  >
                    {isOnlySelect ? 'Continue' : 'Yes'}
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="removing">
              <Paper.Content scrollable={false}>
                <Center>
                  <Loader>Removing...</Loader>
                </Center>
              </Paper.Content>
            </Switch.Case>
            <Switch.Case value="error">
              <Paper.Content scrollable={false}>
                <Result type="fail">
                  <BodyText>{capitalizeFirstLetter(errorMessage)}</BodyText>
                </Result>
                <Paper.Footer>
                  <Button
                    color="primary"
                    variant="contained"
                    onClick={() => {
                      setResult('attempt');
                      setErrorMessage('An error occurred.');
                      setOpen(false);
                    }}
                  >
                    Close
                  </Button>
                </Paper.Footer>
              </Paper.Content>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
};

SubGroups.propTypes = {
  group: object,
  removeSubGroups: func
};

export const SubGroupsContainer = connect(
  state => ({
    group: state.groups.selected,
    retrieveGroupDescription: name => getGroupDescription(name, state.groups.all)
  }),
  dispatch => ({
    removeSubGroups: (group, groups) => dispatch(removeSubGroupsThunk({ group, groups }))
  })
)(SubGroups);

function BodyText({ children }) {
  return (
    <h2
      style={{
        fontFamily: 'Open Sans',
        fontStyle: 'normal',
        fontWeight: 'bold',
        fontSize: '14px',
        lineHeight: '20px',
        letterSpacing: '0.25px',
        marginTop: '24px'
      }}
    >
      {children}
    </h2>
  );
}
