import { CONSTANTS } from 'config';

import { RestClient } from 'shared/lib/rest';
import { creators } from 'shared/lib/redux-utils';

import {
  PROVIDE_EXECUTIVE_DASHBOARD,
  PROVIDE_VIRTUAL_EXECUTIVE_DASHBOARD,
  SELECT_ALL,
  DESELECT_ALL,
  TOGGLE_SERVER
} from '../actions';

export function loadExecutiveDashboardThunk() {
  return async dispatch => {
    dispatch(creators.loading(PROVIDE_EXECUTIVE_DASHBOARD));

    try {
      const response = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.EXECUTIVE_DASHBOARD, {
        page_size: 2147483645
      });

      dispatch(creators.success(PROVIDE_EXECUTIVE_DASHBOARD, response.data.data));
    } catch (error) {
      dispatch(creators.fail(PROVIDE_EXECUTIVE_DASHBOARD, error));
    }
  };
}

export function deselectAllThunk() {
  return dispatch => {
    dispatch(creators.action(DESELECT_ALL));

    return 'done';
  };
}

export function selectAllThunk() {
  return function selectAll(dispatch, getState) {
    const { rawEntries } = getState().executiveDashboard;

    dispatch(creators.action(SELECT_ALL, rawEntries));

    return 'done';
  };
}

export function toggleServerThunk(server) {
  return function toggleServer(dispatch, getState) {
    const { allServers } = getState().executiveDashboard;
    let { selectedServers } = getState().executiveDashboard;

    const svIndex = allServers.findIndex(sv => sv.name === server.name);

    if (selectedServers.find(sv => sv.name === server.name)) {
      selectedServers = selectedServers.filter(sv => sv.name !== server.name);
    } else {
      selectedServers = [...selectedServers, allServers[svIndex]];
    }

    selectedServers.sort((a, b) => a.nrCores - b.nrCores);

    dispatch(creators.action(TOGGLE_SERVER, selectedServers));
    return 'done';
  };
}

export function loadVirtualExecutiveDashboardThunk() {
  return async dispatch => {
    dispatch(creators.loading(PROVIDE_VIRTUAL_EXECUTIVE_DASHBOARD));

    try {
      const response = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.VIRTUAL_EXECUTIVE_DASHBOARD, {
        page_size: 2147483645
      });

      dispatch(creators.success(PROVIDE_VIRTUAL_EXECUTIVE_DASHBOARD, response.data.data));
    } catch (error) {
      dispatch(creators.fail(PROVIDE_VIRTUAL_EXECUTIVE_DASHBOARD, error));
    }
  };
}
