import React from 'react';
import { useFormikContext } from 'formik';

import { Button } from 'shared/components/controls';

export const SubmitButtonNoDirty = ({ children = 'Submit', ...props }) => {
  const { values, submitForm, isSubmitting, isValidating } = useFormikContext();

  return (
    <Button
      type="submit"
      disabled={isSubmitting || isValidating}
      onClick={() => {
        console.log('submitting', values); // eslint-disable-line
        submitForm();
      }}
      {...props}
    >
      {children}
    </Button>
  );
};
