import React from 'react';
import { object, func } from 'prop-types';
import { connect } from 'react-redux';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiDns } from '@mdi/js';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';

import { DatabaseList } from '../../../DatabaseList/DatabaseList';
import { changeStateThunk } from '../../../../../store/thunks';

import './Server.local.scss';

export class Server extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      isExpanded: false,
      autoExpanded: false
    };
  }

  static getDerivedStateFromProps(props, state) {
    if (props.storeState.path === props.data.path && !state.autoExpanded) {
      return { isExpanded: true, autoExpanded: true };
    }

    return null;
  }

  render() {
    const {
      data,
      storeState: { path }
    } = this.props;
    const { isExpanded } = this.state;

    return (
      <li className="server-item">
        <header className={this.isActive(path, data.path)}>
          <div
            role="presentation"
            data-qa={`select-${data.address}-server`}
            onClick={() => this.setPath(data.path)}
          >
            <div className="icon">
              <SvgIcon>
                <Icon path={mdiDns} />
              </SvgIcon>
            </div>
            <div className="value" title={data.address}>
              {data.address}
            </div>
          </div>
          <div
            className="status"
            role="presentation"
            data-qa={`toggle-${data.address}-server`}
            onClick={this.toggleExpanded}
          >
            {isExpanded ? <ArrowDropUpIcon /> : <ArrowDropDownIcon />}
          </div>
        </header>
        <ul className="server-content">
          {isExpanded && <DatabaseList databases={data.databases} />}
        </ul>
      </li>
    );
  }

  isActive = (path, current) => {
    const activePath = current.split('--');
    const storedPath = path.split('--');

    if (activePath[0] === storedPath[0] && activePath[1] === storedPath[1]) {
      return 'active';
    }

    return '';
  };

  toggleExpanded = () => {
    this.setState(prevState => ({
      isExpanded: !prevState.isExpanded
    }));
  };

  setPath = path => {
    this.props.changeState({ path, level: 'all-databases' });
    this.setState({ isExpanded: true });
  };
}

Server.propTypes = {
  data: object,
  storeState: object,
  changeState: func
};

export const ConnectedServer = connect(
  state => ({
    storeState: state.explorer.state
  }),
  dispatch => ({
    changeState: newState => dispatch(changeStateThunk(newState))
  })
)(Server);
