import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';

import { Switch } from 'shared/components';
import { Paper, Center } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';

import { NavigationTab } from 'shared/components/layout/NavigationTab/NavigationTab';

import './job-process.local.scss';

import { getInvocation } from './store/selectors';
import { invocationPoller } from './store/tasks';
import { AllowOutputFor } from './store/constants';

import JobForm from './job-form';

import { JobOutput } from './containers/output/output';
import { JobStatus } from './components/JobStatus/JobStatus';

export const JobView = ({ invocation, redirected }) => {
  const [tab, setTab] = useState('details');

  useEffect(() => {
    invocationPoller.start();

    return function stopPolling() {
      invocationPoller.stop();
    };
  }, [invocation.status]);

  if (!invocation || invocation.status === 'NOT_FETCHED') {
    return (
      <>
        <Paper.Content scrollable={false}>
          <Center>
            <Loader>Fetching invocation</Loader>
          </Center>
        </Paper.Content>
      </>
    );
  }

  return (
    <>
      <Paper.Header>
        <ViewTabs invocation={invocation} active={tab} changeTab={setTab} />
        <JobStatus />
      </Paper.Header>
      <Switch value={tab}>
        <Switch.Case value="details">
          <JobForm redirected={redirected} />
        </Switch.Case>
        <Switch.Case value="output">
          <JobOutput />
        </Switch.Case>
      </Switch>
    </>
  );
};

export default connect(state => {
  return {
    invocation: getInvocation(state)
  };
}, {})(JobView);

function ViewTabs({ active, changeTab, invocation }) {
  const withOutput = AllowOutputFor.includes(invocation.status);

  const tabs = [
    { label: 'Job Details', value: 'details', active: true },
    { label: 'Output', value: 'output', disabled: !withOutput }
  ];

  return <NavigationTab active={active} onClick={changeTab} tabs={tabs} />;
}
