import React, { useEffect, useState, useCallback, useRef } from 'react';

import { string, node, oneOf } from 'prop-types';
import classnames from 'classnames';

import './Overflow.scss';

export const Overflow = ({ className, component, children, variant }) => {
  const nodeRef = useRef(null);
  const [scrollableX, setScrollableX] = useState(false);
  const [scrollableY, setScrollableY] = useState(false);
  const [scrolledX, setScrolledX] = useState(false);
  const [scrolledY, setScrolledY] = useState(false);

  const Tag = component || 'div';

  const onScroll = useCallback(
    event => {
      if (event.currentTarget.scrollTop) {
        if (!scrolledY) {
          setScrolledY(true);
        }
      } else if (scrolledY) {
        setScrolledY(false);
      }

      if (event.currentTarget.scrollLeft) {
        if (!scrolledX) {
          setScrolledX(true);
        }
      } else if (scrolledX) {
        setScrolledX(false);
      }
    },
    [scrollableX, scrollableY, scrolledX, scrolledY]
  );

  useEffect(() => {
    if (nodeRef.current) {
      const element = nodeRef.current;
      const box = {
        width: element.clientWidth,
        height: element.clientHeight,
        scrollHeight: element.scrollHeight,
        scrollWidth: element.scrollWidth
      };

      if (box.width < box.scrollWidth && !scrollableX) {
        setScrollableX(true);
      }

      if (box.height < box.scrollHeight && !scrollableY) {
        setScrollableY(true);
      }
    }
  }, [children]);

  return (
    <div
      className={classnames(
        'overflow-container',

        {
          [variant]: variant,
          scrollableX,
          scrollableY,
          scrolledX,
          scrolledY
        },
        className
      )}
    >
      <Tag ref={nodeRef} className={classnames('overflow-scroller')} onScroll={onScroll}>
        {children}
      </Tag>
    </div>
  );
};

Overflow.propTypes = {
  children: node,
  className: string,
  variant: oneOf(['white'])
};

Overflow.defaultProps = {};
