import React from 'react';

import { CircleImg, NumberLabel, StringLabel } from 'shared/components';
import { Paper } from 'shared/components/layout';
import HorizontalBar from '../../utils/HorizontalStackedBar/HorizontalStackedBar';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiDns } from '@mdi/js';

import './ServersTile.local.scss';

const colors = {
  1: '#6DF0D2',
  2: '#1DE4BD',
  4: '#1AC9E6',
  6: '#7D3AC1',
  8: '#EA7369',
  16: '#EF7E32',
  24: '#EABD3B'
};

export const ServersTile = ({ servers, isSingle }) => {
  const total = servers.reduce((acc, sv) => {
    return acc + sv.nrCores;
  }, 0);

  const cores = [];
  servers.map(sv => {
    const index = cores.findIndex(core => core.nrCores === sv.nrCores);
    if (index > -1) {
      cores[index].count += 1;
    } else {
      cores.push({ count: 1, nrCores: sv.nrCores });
    }

    return null;
  });

  function getData() {
    const data = [];

    cores.map(core => {
      data.push({
        value: core.count,
        description: `${core.nrCores}C`,
        color: colors[core.nrCores]
      });

      return null;
    });

    return data;
  }

  return (
    <Paper style={{ height: '160px', padding: '16px' }}>
      <Paper.Header style={{ alignItems: 'center', marginBottom: '24px' }}>
        <CircleImg
          image={
            <SvgIcon>
              <Icon path={mdiDns} />
            </SvgIcon>
          }
        />
        <NumberLabel number={servers.length} />
        <StringLabel text={isSingle ? 'Server' : 'Total servers'} />
      </Paper.Header>
      <div className="content">
        <div className="first-column">
          <div className="value">{total}</div>
          <div className="label">Cores</div>
        </div>

        <div className="second-column">
          <div className="bar-chart">
            <HorizontalBar
              height={36}
              showTextIn
              fontColor="#fff"
              fontWeight="bold"
              data={getData()}
            />
          </div>
          <div className="label">Server Cores Distribution</div>
        </div>
      </div>
    </Paper>
  );
};
