import React from 'react';
import { connect } from 'react-redux';

import { Button } from 'shared/components/controls';

import { deselectAllThunk, selectAllThunk } from '../../store/thunks';

function ToggleSelection({ servers, selected, deselectAll, selectAll }) {
  if (servers === selected) {
    return (
      <Button style={{ color: '#03A9F4' }} onClick={() => deselectAll()}>
        Deselect All
      </Button>
    );
  }
  return (
    <Button style={{ color: '#03A9F4' }} onClick={() => selectAll()}>
      Select all
    </Button>
  );
}

export const ConnectedToggleSelection = connect(
  state => {
    return {
      servers: state.executiveDashboard.allServers.length,
      selected: state.executiveDashboard.selectedServers.length
    };
  },
  dispatch => ({
    deselectAll: () => dispatch(deselectAllThunk()),
    selectAll: () => dispatch(selectAllThunk())
  })
)(ToggleSelection);
