import React, { useState, useCallback } from 'react';
import { connect } from 'react-redux';
import moment from 'moment';

import { Switch, Center } from 'shared/components';
import { Button } from 'shared/components/controls';
import { Modal, ModalHeader, Result } from 'shared/components/overlays';
import { Paper } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';

import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiTrashCan } from '@mdi/js';

import { deleteInvocation } from 'shared/api/delete';
import { globalFiltersSelector } from 'shared/store/state';
import { actions as sharedActions } from 'shared/store/actions';
import './DeleteJobModal.local.scss';

export const ConnectedDeleteJobModal = connect(
  state => {
    return {
      globalFilters: globalFiltersSelector(state)
    };
  },
  dispatch => {
    return {
      getJobs: data => {
        dispatch(sharedActions.getJobs.dispatchLoading(data));
      }
    };
  }
)(DeleteJobModal);

function DeleteJobModal({ open, actions, uuid, filters, globalFilters, getJobs }) {
  const [result, setResult] = useState('attempt');
  const getQSP = () => {
    if (!filters || !Object.keys(filters).length) {
      const startDate = moment.unix(globalFilters.get('startDate'));
      const endDate = moment.unix(globalFilters.get('endDate'));
      const start = moment(startDate.format('YYYY-MM-DD'));
      const end = moment(endDate.format('YYYY-MM-DD'));
      const days = parseInt(moment.duration(end.diff(start)).asDays(), 10);
      const payload = {
        date_invoke: startDate.format('YYYY-MM-DD'),
        period: days + 1,
        page_size: 2147483645
      };

      return payload;
    }
    return filters;
  };

  const terminator = useCallback(() => {
    deleteInvocation(uuid)
      .then(() => {
        setResult('success');
        if (filters) {
          getJobs(getQSP());
        }
      })
      .catch(() => {
        setResult('fail');
      });
  }, uuid);
  return (
    <>
      <Modal open={open} actions={actions}>
        <Paper
          style={{
            width: '50vw',
            height: '50vh'
          }}
        >
          <ModalHeader
            title="Delete"
            icon={
              <SvgIcon>
                <path d={mdiTrashCan} />
              </SvgIcon>
            }
          />
          <Switch value={result}>
            <Switch.Case value="attempt">
              <Paper.Content scrollable={false}>
                <Result type="fail">
                  <div className="container">
                    <p>Are you sure you want to delete invocation with uuid:</p>
                    <p className="uuid">{uuid}</p>
                  </div>
                </Result>
              </Paper.Content>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    color="primary"
                    variant="contained"
                    onClick={() => {
                      setResult('loading');
                      terminator();
                    }}
                  >
                    Delete
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="success">
              <Result type="success">
                <h2 className="container">Invocation successfully deleted!</h2>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      actions.close();
                      setResult('attempt');
                    }}
                  >
                    Done
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="fail">
              <Result type="fail">
                <h2 className="container">Invocation could not be deleted.</h2>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      actions.close();
                      setResult('attempt');
                    }}
                  >
                    Close
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="loading">
              <Paper.Content scrollable={false}>
                <Center>
                  <Loader>Removing...</Loader>
                </Center>
              </Paper.Content>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
}
