import React from 'react';

import { ConnectedEditConnection } from './ConnectedEditConnection';

import { hasWritePrivileges } from '../utils/privileges';

const ActionsAccount = ({ entity }) => {
  const isDisabled = entity?.meta?.appearance === 'Greyed';
  const type = getEntityType(entity);
  const canWrite = hasWritePrivileges(entity);

  if (!canWrite) {
    return null;
  }

  return <ConnectedEditConnection connection={entity} type={type} isDisabled={isDisabled} />;
};

export default ActionsAccount;

const CONNECTION_TYPES = {
  SERVER: 'server',
  DATABASE: 'database'
};

const getEntityType = entity => {
  if (entity.protocol !== 'SSH' && entity.special && entity.special.database) {
    return CONNECTION_TYPES.DATABASE;
  }

  return CONNECTION_TYPES.SERVER;
};
