import React, { useState, useEffect } from 'react';
import classnames from 'classnames';

import { Modal, ModalHeader } from 'shared/components/overlays';
import { Paper } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';
import { Center } from 'shared/components';

import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';

import { ListOutput } from '../ListOutput/ListOutput';
import { sendToEmail } from '../../utils/sendToEmail';
import { copyToClipboard } from '../../utils/copyToClipboard';

import './OutputModal.local.scss';

export const OutputModal = ({
  open,
  actions,
  output,
  invocation,
  modalDetails,
  processName,
  messages
}) => {
  let content;

  const [message, setMessage] = useState(messages[0]);

  let interval;

  useEffect(() => {
    if (messages && messages.length > 1) {
      if (!open) {
        clearInterval(interval);
      } else {
        let index = 1;
        interval = setInterval(() => {
          setMessage(messages[index]);
          if (index + 1 < messages.length) {
            index += 1;
          }
        }, 4000);

        return () => clearInterval(interval);
      }
    }
  }, [open]);

  if (!output && invocation.status !== 'SUCCEEDED' && invocation.status !== 'FAILED') {
    content = (
      <>
        <Center>
          <Loader>{message}</Loader>
        </Center>
        <Paper.Footer></Paper.Footer>
      </>
    );
  }

  if (invocation.status === 'FAILED') {
    content = (
      <>
        <Paper.Content scrollable={false}>
          <div className="container">
            <ListOutput type="fail" output={output} errors={invocation.errors} />
          </div>
        </Paper.Content>
        <Paper.Footer>
          <Paper.Footer.Left>
            <Button
              className="btn"
              onClick={() => sendToEmail(invocation, output, processName)}
              data-qa="send-in-email"
            >
              Send in email
            </Button>
            <Button
              className="btn"
              onClick={() => copyToClipboard(output, invocation)}
              data-qa="copy-to-clipboard"
            >
              Copy to clipboard
            </Button>
          </Paper.Footer.Left>
        </Paper.Footer>
      </>
    );
  }

  if (invocation.status === 'SUCCEEDED') {
    content = (
      <>
        <Paper.Content scrollable={false}>
          <div className="container">
            <ListOutput output={output} />
          </div>
        </Paper.Content>
        <Paper.Footer>
          <Paper.Footer.Left>
            <Button
              className="btn"
              onClick={() => sendToEmail(invocation, output, processName)}
              data-qa="send-in-email"
            >
              Send in email
            </Button>
            <Button
              className="btn"
              onClick={() => copyToClipboard(output, invocation)}
              data-qa="copy-to-clipboard"
            >
              Copy to clipboard
            </Button>
          </Paper.Footer.Left>
        </Paper.Footer>
      </>
    );
  }

  return (
    <Modal open={open} actions={actions}>
      <Paper
        style={{
          minWidth: '75vh',
          minHeight: '75vh',
          width: '75vh',
          height: '75vh'
        }}
      >
        <ModalHeader
          title={modalDetails.title}
          desc={invocation.status === 'FAILED' ? 'An error orccured!' : 'Output'}
          avatar={
            <Avatar
              className={classnames('output-icon', { error: invocation.status === 'FAILED' })}
            >
              {modalDetails.icon}
            </Avatar>
          }
        />
        {content}
      </Paper>
    </Modal>
  );
};
