import React from 'react';
import { array } from 'prop-types';

import { ConnectedServer } from './containers/Server/Server';

export class ServerList extends React.PureComponent {
  render() {
    const { servers } = this.props;

    return servers.map(server => <ConnectedServer key={server.address} data={server} />);
  }
}

ServerList.propTypes = {
  servers: array
};

export default ServerList;
