import React from 'react';
import Switch from '../components/Switch';
import ActionsNamespace from '../components/ActionsNamespace';
import ActionsConstant from '../components/ActionsConstant';
import ActionsProcess from '../components/ActionsProcess';
import ActionsForm from '../components/ActionsForm';
import ActionsAccount from '../components/ActionsAccount';
import ActionsRoot from '../components/ActionsRoot';
import ActionsPrototype from '../components/ActionsPrototype';
import ActionsSchedule from '../components/ActionsSchedule';
import ActionsCommon from '../components/ActionsCommon';
import GroupIcon from '@material-ui/icons/Group';
import './SelectedEntity.scss';

const SelectedEntity = ({ selectedEntity, entityIcons }) => {
  if (!selectedEntity) {
    return null;
  }

  return (
    <div className="selected-entity">
      <div className="selected-entity-title">
        <div className="selected-entity-title-icon">{entityIcons[selectedEntity.entity]}</div>
        <div className="selected-entity-title-details">
          {selectedEntity.name}
          <div className="selected-entity-title-user">
            <GroupIcon />
            <span>{selectedEntity.user_owner}</span>
          </div>
        </div>
      </div>
      <div className="entity-detail-wrap">
        <div className="entity-description-wrap">{selectedEntity.description}</div>

        <Switch value={selectedEntity.entity}>
          <Switch.Case value="root">
            <ActionsRoot />
          </Switch.Case>

          <Switch.Case value="namespace">
            <ActionsNamespace entity={selectedEntity} />
          </Switch.Case>

          <Switch.Case value="constant">
            <ActionsConstant entity={selectedEntity} />
          </Switch.Case>

          <Switch.Case value="form">
            <ActionsForm entity={selectedEntity} />
          </Switch.Case>

          <Switch.Case value="process">
            <ActionsProcess entity={selectedEntity} />
          </Switch.Case>

          <Switch.Case value="prototype">
            <ActionsPrototype entity={selectedEntity} />
          </Switch.Case>

          <Switch.Case value="schedule">
            <ActionsSchedule entity={selectedEntity} />
          </Switch.Case>

          <Switch.Case value="account">
            <ActionsAccount entity={selectedEntity} />
          </Switch.Case>
        </Switch>
        <ActionsCommon entity={selectedEntity} />
      </div>
    </div>
  );
};

export default SelectedEntity;
