import React, { useState, useCallback } from 'react';

import { Button } from 'shared/components/controls';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Paper } from 'shared/components/layout';

import SvgIcon from '@material-ui/core/SvgIcon';

import { mdiImageFilterCenterFocus } from '@mdi/js';

import { PrivilegesEditor } from './PrivilegesEditor';

export const PrivilegesModal = ({ disabled, entity, isAlias }) => {
  const [open, setOpen] = useState(false);

  const modalActions = {
    open: useCallback(() => {
      setOpen(true);
    }, []),
    close: useCallback(() => {
      // refresh(entity.type);
      setOpen(false);
    }, [])
  };
  return (
    <>
      <Button disabled={disabled} classes={{ root: 'entity-button' }} onClick={modalActions.open}>
        Privileges
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            minWidth: '50vw',
            minHeight: '648px',
            maxHeight: '648px'
          }}
        >
          <ModalHeader
            title="Privileges"
            icon={
              <SvgIcon>
                <path d={mdiImageFilterCenterFocus} />
              </SvgIcon>
            }
          />
          <PrivilegesEditor entity={entity} isAlias={isAlias} />
        </Paper>
      </Modal>
    </>
  );
};
