import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { func, array, object } from 'prop-types';

import { Switch, Route, useRouteMatch } from 'react-router-dom';

import { Main, Center } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';

import { loadAllUsersThunk } from '../store/thunks';
import { mapSortInsensitive } from '../store/mappers';
import { loadAllGroupsThunk } from '../groups/store/thunks';

import './root.scss';

import { ConnectedUserLoader } from './containers/UserLoader';

import { SearchableList } from '../components/SearchableList/SearchableList';
import { UserCard } from '../components/UserCard/UserCard';

import { NoUserSelectedPage } from './pages/NoUsersSelected';
import { CreateNewUserPage } from './pages/CreateNewUser/CreateNewUser';

export const UsersPage = ({
  allUsers,
  selectedUser,
  loadAllUsers,
  allGroups,
  loadAllGroups,
  isLoading
}) => {
  useEffect(() => {
    loadAllUsers();
    if (allGroups.length === 0) {
      loadAllGroups();
    }
  }, []);

  function filterUserByQuery(user, query) {
    return user.name.toLowerCase().includes(query.toLowerCase());
  }

  const match = useRouteMatch();

  const userLink = user => {
    return `${match.url}/${user.name}`;
  };

  if (isLoading) {
    return (
      <Main>
        <Center>
          <Loader>Loading users...</Loader>
        </Center>
      </Main>
    );
  }

  return (
    <Main>
      <section className="users-search-section">
        <SearchableList items={allUsers} filter={filterUserByQuery}>
          {user => <UserCard selected={selectedUser} user={user} href={userLink(user)} />}
        </SearchableList>
      </section>
      <section className="details-view-section">
        <UserSwitch />
      </section>
    </Main>
  );
};

UsersPage.propTypes = {
  allUsers: array,
  selectedUser: object,
  loadAllUsers: func
};

export const UserSwitch = () => {
  const { path } = useRouteMatch();

  return (
    <Switch>
      <Route exact path={`${path}/create-new`}>
        <CreateNewUserPage />
      </Route>
      <Route path={`${path}/:userName`}>
        <ConnectedUserLoader />
      </Route>
      <Route exact path={`${path}`}>
        <NoUserSelectedPage />
      </Route>
    </Switch>
  );
};

export default connect(
  state => ({
    allUsers: mapSortInsensitive(state.usersManager.users.all),
    selectedUser: state.usersManager.users.selected,
    allGroups: state.groups.all,
    isLoading: state.usersManager.flags.users.loading
  }),
  dispatch => ({
    loadAllUsers: () => dispatch(loadAllUsersThunk()),
    loadAllGroups: () => dispatch(loadAllGroupsThunk())
  })
)(UsersPage);
