import React from 'react';
import { object, string, array, bool, func, shape } from 'prop-types';
import { connect } from 'react-redux';

import './Privileges.local.scss';

import { Checkbox } from 'shared/components/controls';
import { Section } from 'shared/components/layout';
import { grantPrivilegeThunk, revokePrivilegeThunk } from '../../../store/thunks';

export const Privileges = ({ group, privileges, disabled, grant, revoke }) => {
  const actions = { grant, revoke };

  return (
    <Section>
      <Section.Header>
        <Section.Title>Class privileges:</Section.Title>
        <Section.Info>
          Effective class privileges are calculated based on explicit grants and denies in this
          group and in groups of which this group may be a member
        </Section.Info>
      </Section.Header>
      <div className="user-privileges">
        <ul className="privileges-list">
          {privileges.map(entry => {
            return (
              <li key={entry.key} className="privileges-item">
                <Privilege group={group} privilege={entry} actions={actions} disabled={disabled} />
              </li>
            );
          })}
        </ul>
      </div>
    </Section>
  );
};

Privileges.propTypes = {
  group: object,
  privileges: array,
  disabled: bool,
  grant: func,
  revoke: func
};

export const Privilege = ({ group, privilege, disabled, actions }) => {
  const { key, granted, type } = privilege;
  const handler = () => {
    const thunk = granted ? actions.revoke : actions.grant;

    thunk(group.name, key, type);
  };

  return (
    <Checkbox
      data-qa={`privilege-${key}`}
      disabled={disabled}
      label={capitalize(key)}
      checked={granted}
      onChange={handler}
    />
  );
};

Privilege.propTypes = {
  group: object,
  privilege: shape({
    key: string,
    type: string,
    granted: bool
  }),
  disabled: bool,
  actions: shape({
    grant: func,
    revoke: func
  })
};

export const PrivilegesContainer = connect(
  state => {
    const group = state.groups.selected;

    return {
      group,
      disabled: state.groups.ops.privileges.loading,
      privileges: mapPrivileges(group.class_privileges)
    };
  },
  dispatch => ({
    grant: (group, key, type) =>
      dispatch(
        grantPrivilegeThunk({
          group,
          type,
          key
        })
      ),
    revoke: (group, key, type) =>
      dispatch(
        revokePrivilegeThunk({
          group,
          type,
          key
        })
      )
  })
)(Privileges);

function mapPrivileges(data) {
  const {
    effective,
    actual: { granted }
  } = data;

  const keys = Object.keys(effective).sort();

  return keys.reduce((list, key) => {
    const privilege = {
      key,
      /**
       * Fix: create is given as implicit type for grant/revoke.
       */
      type: effective[key][0] || 'create',
      granted: !!granted[key].length
    };

    list.push(privilege);

    return list;
  }, []);
}

function capitalize(str) {
  return str.charAt(0).toUpperCase() + str.slice(1);
}
