import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import * as PropTypes from 'prop-types';
import moment from 'moment';
import { FormattedMessage } from 'react-intl';

import Button from '@material-ui/core/Button';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiChartTimelineVariant } from '@mdi/js';

import {
  JobItem,
  WorkloadGraph,
  CircleImg,
  NumberLabel,
  StringLabel,
  Center,
  RangeDatePicker
} from 'shared/components';
import { Loader } from 'shared/components/loaders';
import { actions } from 'shared/store/actions';
import { jobsSelector, globalFiltersSelector } from 'shared/store/state';
import { Result } from 'shared/components/overlays/Result/Result';
import { Paper, Main } from 'shared/components/layout';
import { filterStatusSelector } from './store/state';
import { Header } from 'core/components/Header';
import { getWorkloadData } from 'shared/lib/workload';
import { fetchEntity } from 'entities/actions';
import { StatusContainer, Search } from './containers';
import { ConnectedServerUsage } from './containers/ServerUsage/ServerUsage';

import './root.module.scss';

export class RootModule extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      range: {
        startDate: moment.unix(props.globalFilters.get('startDate')),
        endDate: moment.unix(props.globalFilters.get('endDate'))
      }
    };

    this.onChangeDatesHandler = this.onChangeDatesHandler.bind(this);
  }

  componentDidMount() {
    const days = moment
      .unix(this.props.globalFilters.get('endDate'), 'YYYY-MM-DD')
      .diff(moment.unix(this.props.globalFilters.get('startDate'), 'YYYY-MM-DD'), 'days');

    this.props.getJobs({
      date_invoke: moment.unix(this.props.globalFilters.get('startDate')).format('YYYY-MM-DD'),
      period: days + 1,
      page_size: 2147483645
    });

    if (
      this.props.entitiesStatus.process === 'not-fetched' ||
      this.props.entitiesStatus.process === 'failed'
    ) {
      this.props.request('process');
    }
  }

  render() {
    const callFailed = this.props.jobs.get('callFailed');
    const reason = this.props.jobs.get('reason');

    if (callFailed && reason === 'rights' && !this.props.jobs.get('fetching')) {
      return (
        <>
          <Header title="Dashboard" />
          <Main>
            <Paper>
              <Paper.Content>
                <Result type="fail">
                  <BodyText>
                    You don&apos;t have sufficient privileges to access invocation data. Please
                    contact your administrator.
                  </BodyText>
                </Result>
              </Paper.Content>
            </Paper>
          </Main>
        </>
      );
    }

    const days = moment
      .unix(this.props.globalFilters.get('endDate'), 'YYYY-MM-DD')
      .diff(moment.unix(this.props.globalFilters.get('startDate'), 'YYYY-MM-DD'), 'days');

    const averageJobs = Math.round(parseInt(this.props.jobs.get('total_count'), 10) / (days + 1));

    return (
      <div className={this.props.className}>
        <Header>
          <h1 className="header-title">Dashboard</h1>
          <RangeDatePicker
            onDatesChange={this.onChangeDatesHandler}
            startDate={this.state.range.startDate}
            endDate={this.state.range.endDate}
          />
        </Header>
        <Search />
        <StatusContainer />
        <div className="summary-container">
          <div className="jobs-container">
            {this.props.jobs.get('fetching') ? (
              <Center>
                <Loader>Loading jobs</Loader>
              </Center>
            ) : (
              <>
                <div className="jobs-content" id="jobs-content">
                  {this.filteredJobs(this.props.storeFilterStatus)}
                </div>
              </>
            )}
          </div>

          <div className="right-column">
            <div className="workload-container">
              {this.props.jobs.get('fetching') ? (
                <Center>
                  <Loader>Generating Workload Chart</Loader>
                </Center>
              ) : (
                <>
                  <div className="workload-header">
                    <CircleImg
                      image={
                        <SvgIcon>
                          <Icon path={mdiChartTimelineVariant} />
                        </SvgIcon>
                      }
                    />
                    <NumberLabel number={averageJobs} />
                    <StringLabel
                      text={
                        <>
                          <FormattedMessage id="dashboard.jobs" />/
                          <FormattedMessage id="dashboard.day" />
                        </>
                      }
                    />
                  </div>
                  <div className="workload-content">
                    <WorkloadGraph
                      data={getWorkloadData(this.props.jobs.get('content').toJS(), {
                        currentDate: moment.unix(
                          this.props.globalFilters.get('startDate'),
                          'YYYY-MM-DD'
                        ),
                        endDate: moment.unix(this.props.globalFilters.get('endDate'), 'YYYY-MM-DD')
                      })}
                      isSingleDay={
                        moment
                          .unix(this.props.globalFilters.get('startDate'), 'YYYY-MM-DD')
                          .format('YYYY-MM-DD') ===
                        moment
                          .unix(this.props.globalFilters.get('endDate'), 'YYYY-MM-DD')
                          .format('YYYY-MM-DD')
                      }
                      setSingleDay={this.onChangeDatesHandler}
                    />
                  </div>
                </>
              )}
            </div>
            <div className="servers-usage-container">
              <ConnectedServerUsage />
            </div>
          </div>
        </div>
      </div>
    );
  }

  filteredJobs = status => {
    const { jobs } = this.props;

    if (status === 'ALL') {
      if (jobs.get('content').size === 0) {
        return <Center>There are no jobs available with the selected filters</Center>;
      }

      return (
        <>
          {jobs
            .get('content')
            .slice(0, 20)
            .map((item, index) => (
              <JobItem key={index} job={item} />
            ))}
          {jobs.get('total_count') > 20 && (
            <Button className="full-log center qa-view-full-log">
              <Link to="/jobs">
                <FormattedMessage id="dashboard.view-full-log" />
              </Link>
            </Button>
          )}
        </>
      );
    }

    const totalFilteredJobs = jobs.get('content').filter(job => job.get('status') === status).size;

    if (totalFilteredJobs === 0) {
      return <Center>There are no jobs available with the selected filters</Center>;
    }

    return (
      <>
        {jobs
          .get('content')
          .filter(job => job.get('status') === status)
          .slice(0, 20)
          .map((item, index) => (
            <JobItem key={index} job={item} />
          ))}
        {totalFilteredJobs > 20 && (
          <Button className="full-log center qa-view-full-log">
            <Link to={`/jobs?status=${status}`}>
              <FormattedMessage id="dashboard.view-full-log" />
            </Link>
          </Button>
        )}
      </>
    );
  };

  onChangeDatesHandler(range) {
    this.setState({
      range: {
        startDate: range.startDate,
        endDate: range.endDate
      }
    });

    if (range.startDate && range.endDate) {
      this.props.setGlobalFilters(range);

      const start = moment(range.startDate.format('YYYY-MM-DD'));
      const end = moment(range.endDate.format('YYYY-MM-DD'));
      const days = parseInt(moment.duration(end.diff(start)).asDays(), 10);

      this.props.getJobs({
        date_invoke: moment(range.startDate).format('YYYY-MM-DD'),
        period: days + 1,
        page_size: 2147483645
      });
    }
  }
}

RootModule.propTypes = {
  className: PropTypes.string,
  globalFilters: PropTypes.object.isRequired,
  setGlobalFilters: PropTypes.func.isRequired,
  jobs: PropTypes.object.isRequired,
  getJobs: PropTypes.func.isRequired
};

RootModule.defaultProps = {
  className: 'dashboard'
};

export const mapStateToProps = state => ({
  globalFilters: globalFiltersSelector(state),
  jobs: jobsSelector(state),
  storeFilterStatus: filterStatusSelector(state),
  entitiesStatus: state.entities.fetch_status
});

export const mapDispatchToProps = dispatch => ({
  getJobs: data => dispatch(actions.getJobs.dispatchLoading(data)),
  setGlobalFilters: data => dispatch(actions.setGlobalFilters.dispatchLoading(data)),
  request: data => dispatch(fetchEntity(data))
});

export default connect(mapStateToProps, mapDispatchToProps)(RootModule);

function BodyText({ children }) {
  return (
    <h2
      style={{
        fontFamily: 'Open Sans',
        fontStyle: 'normal',
        fontWeight: 'bold',
        fontSize: '14px',
        lineHeight: '20px',
        letterSpacing: '0.25px',
        marginTop: '24px'
      }}
    >
      {children}
    </h2>
  );
}
