import React from 'react';
import { connect } from 'react-redux';
import { func } from 'prop-types';

import RefreshIcon from '@material-ui/icons/Refresh';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import {
  mdiChartTimelineVariant,
  mdiDatabase,
  mdiCheckboxMultipleBlankOutline,
  mdiShape,
  mdiLicense,
  mdiHexagonSlice2
} from '@mdi/js';

import { getCurrentUserDetails } from 'session/store/selectors';
import { isOnlySelectGroup } from 'entities/utils/privileges';
import { Paper } from 'shared/components/layout';
import { changeStateThunk, refreshDBStatusThunk } from '../../store/thunks';
import { ActionCard } from '../../components/ActionCard/ActionCard';
import { CardsLayout } from '../../components/CardsLayout/CardsLayout';

import { DatabaseSettings } from '../Settings/DatabaseSettings';

import {
  retrieveServerEntry,
  getBackup,
  getDBStatus,
  getDBStatusInfo,
  getEntityPendingStatus
} from '../../store/selectors';

export class AllDatabases extends React.PureComponent {
  render() {
    const {
      databases,
      retrieveDbBackup,
      retrieveDbStatus,
      retrieveDbStatusInfo,
      userDetails
    } = this.props;
    const isDisabled = isOnlySelectGroup(userDetails);

    return (
      <>
        <Paper.Content>
          <CardsLayout>
            {databases.map(database => (
              <ActionCard
                key={database.name}
                lastBackup={retrieveDbBackup(database)}
                settings={<DatabaseSettings database={database} disabled={isDisabled} />}
                status={retrieveDbStatus(database)}
                statusInfo={retrieveDbStatusInfo(database)}
                type="database"
                icon={
                  <SvgIcon>
                    <Icon path={mdiDatabase} />
                  </SvgIcon>
                }
                label={database.special.database}
                onClick={() => this.setPath(database.path)}
                options={this.renderOptions(database)}
              />
            ))}
          </CardsLayout>
        </Paper.Content>
      </>
    );
  }

  setPath = path => {
    this.props.changeState({ path, level: 'workload' });
  };

  refreshDBStatus = db => {
    if (!this.props.retrievePendingStatus(db).loading) {
      this.props.refreshDatabaseStatus(db);
    }
  };

  renderOptions = database => {
    const [protocol] = database.path.split('--');
    const options = [
      {
        icon: (
          <SvgIcon>
            <Icon path={mdiChartTimelineVariant} />
          </SvgIcon>
        ),
        label: 'Workload',
        onClick: () => this.props.changeState({ path: database.path, level: 'workload' })
      },
      {
        icon: (
          <SvgIcon>
            <Icon path={mdiCheckboxMultipleBlankOutline} />
          </SvgIcon>
        ),
        label: 'Sessions',
        onClick: () => this.props.changeState({ path: database.path, level: 'sessions' })
      },
      {
        icon: (
          <SvgIcon>
            <Icon path={mdiShape} />
          </SvgIcon>
        ),
        label: 'Objects',
        onClick: () => this.props.changeState({ path: database.path, level: 'objects' })
      },
      {
        icon: (
          <SvgIcon>
            <Icon path={mdiHexagonSlice2} />
          </SvgIcon>
        ),
        label: 'Storage',
        onClick: () => this.props.changeState({ path: database.path, level: 'storage' })
      },
      {
        icon: <RefreshIcon />,
        label: this.props.retrievePendingStatus(database).label,
        disabled: this.props.retrievePendingStatus(database).loading,
        onClick: () => this.refreshDBStatus(database)
      }
    ];

    if (protocol === 'ORACLE') {
      options.splice(options.length - 1, 0, {
        icon: (
          <SvgIcon>
            <Icon path={mdiLicense} />
          </SvgIcon>
        ),
        label: 'License reporting',
        onClick: () => this.props.changeState({ path: database.path, level: 'license' })
      });
    }

    return options;
  };
}

AllDatabases.propTypes = {
  changeState: func
};

export const ConnectedAllDatabases = connect(
  state => {
    const {
      structured,
      state: { path }
    } = state.explorer;

    const [protocol, address] = path.split('--');
    const server = retrieveServerEntry({ protocol, address }, structured);
    const backups = state.explorer.dbLastBackup;
    const statuses = state.explorer.dbStatus;

    return {
      databases: (server && server.databases) || [],
      userDetails: getCurrentUserDetails(state),
      retrieveDbBackup: database => getBackup(database, backups),
      retrieveDbStatus: database => getDBStatus(database, statuses),
      retrieveDbStatusInfo: database => getDBStatusInfo(database, statuses),
      retrievePendingStatus: database =>
        getEntityPendingStatus(database, state.explorer.dbStatus, 'db')
    };
  },
  dispatch => ({
    changeState: newState => dispatch(changeStateThunk(newState)),
    refreshDatabaseStatus: db => dispatch(refreshDBStatusThunk(db))
  })
)(AllDatabases);
