import React from 'react';
import { node, bool } from 'prop-types';
import classnames from 'classnames';
import './Main.local.scss';

export const Main = ({ flex, children }) => (
  <main
    className={classnames('page-main', {
      flex
    })}
  >
    {children}
  </main>
);

Main.propTypes = {
  children: node,
  flex: bool
};

Main.defaultProps = {
  flex: true
};
