import { RestClient } from 'shared/lib/rest';

export async function postInvocation(body) {
  try {
    const { data } = await RestClient.post('/invocation', body);

    return { result: data.result, invocations: data.data.invocations };
  } catch (e) {
    return { result: 'fail', invocations: [], messages: e.messages };
  }
}

export function requestInvocation(uuid) {
  return RestClient.get(`/invocation/${uuid}`);
}

export async function fetchInvocation(uuid) {
  try {
    const response = await RestClient.get(`/invocation/${uuid}`);

    return {
      result: 'success',
      invocation: response.data.data.invocations[0]
    };
  } catch (e) {
    console.log(e); // eslint-disable-line
    return { result: 'fail' };
  }
}

export async function fetchInvocationOutput(uuid) {
  return RestClient.get(`/invocation/${uuid}/display`, {
    page_size: 2147483645
  });
}
