import { createSelector } from 'reselect';

const localState = state => state.shared;

export const jobsSelector = createSelector(localState, state => state.get('jobs'));

export const connectionsSelector = createSelector(localState, state => state.get('connections'));

export const jobsSearchedSelector = createSelector(localState, state =>
  state.get('searchResults').get('jobs')
); // eslint-disable-line

export const processesSearchedSelector = createSelector(localState, state =>
  state.get('searchResults').get('processes')
); // eslint-disable-line

export const globalFiltersSelector = createSelector(localState, state =>
  state.get('globalFilters')
); // eslint-disable-line
