import React from 'react';
import { connect } from 'react-redux';
import { object, func } from 'prop-types';

import { mdiHexagonMultipleOutline } from '@mdi/js';
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';

import { changeStateThunk } from '../../../store/thunks';

import { ServerList } from '../ServerList/ServerList';

import './Dbms.local.scss';

export class DBMS extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      isExpanded: false,
      autoExpanded: false
    };
  }

  static getDerivedStateFromProps(props, state) {
    if (props.storeState.path === props.data.path && !state.autoExpanded) {
      return { isExpanded: true, autoExpanded: true };
    }

    return null;
  }

  render() {
    const {
      data,
      storeState: { path }
    } = this.props;
    const { isExpanded } = this.state;

    return (
      <li className="dbms-item">
        <header>
          <div
            role="presentation"
            data-qa={`select-${data.protocol}-dbms`}
            onClick={() => this.setPath(data.path)}
          >
            <div className="icon">
              <SvgIcon>
                <Icon path={mdiHexagonMultipleOutline} />
              </SvgIcon>
            </div>
            <div
              className={`value ${path.startsWith(data.path) ? 'active' : ''}`}
              title={data.protocol}
            >
              {data.protocol}
            </div>
          </div>
          <div
            role="presentation"
            data-qa={`toggle-${data.protocol}-dbms`}
            className={`status ${path.startsWith(data.path) ? 'active' : ''}`}
            onClick={this.toggleExpanded}
          >
            {isExpanded ? <RemoveIcon /> : <AddIcon />}
          </div>
        </header>
        <ul className="dbms-content">{isExpanded && <ServerList servers={data.servers} />}</ul>
      </li>
    );
  }

  toggleExpanded = () => {
    this.setState(prevState => ({
      isExpanded: !prevState.isExpanded
    }));
  };

  setPath = path => {
    this.props.changeState({ path, level: 'all-servers' });
    this.setState({ isExpanded: true });
  };
}

DBMS.propTypes = {
  data: object,
  changeState: func,
  storeState: object
};

export const ConnectedDBMS = connect(
  state => ({
    storeState: state.explorer.state
  }),
  dispatch => ({
    changeState: newState => dispatch(changeStateThunk(newState))
  })
)(DBMS);
