import React from 'react';

import { Section } from 'shared/components/layout';

export const Documentation = ({ process }) => {
  if (!process) {
    return <Section>Select a process in order to see its documentation</Section>;
  }
  return (
    <Section>
      <Section.Header>
        <Section.Title>{process.name}</Section.Title>
        <Section.Info>{process.description}</Section.Info>
      </Section.Header>
      {process.parameters && process.parameters.length > 0 && (
        <Section.Content>
          <h2>
            <b>Parameters:</b>
          </h2>
          <div>
            {process.parameters.map(parameter => {
              return (
                <React.Fragment key={parameter.name}>
                  <h3>
                    {parameter.name} ({parameter.data_type})
                  </h3>
                  <Section.Info>{parameter.description}</Section.Info>
                </React.Fragment>
              );
            })}
          </div>
        </Section.Content>
      )}
    </Section>
  );
};
