import React, { useState, useEffect, useRef } from 'react';

import SvgIcon from '@material-ui/core/SvgIcon';
import SlowMotionVideoIcon from '@material-ui/icons/SlowMotionVideo';

import { Switch } from 'shared/components';
import { Button } from 'shared/components/controls';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Result } from 'shared/components/overlays/Result/Result';
import { Paper } from 'shared/components/layout';

import { ViewTabs } from '../ViewTabs/ViewTabs';
import { Documentation } from '../Documentation/Documentation';
import { ConnectedScheduleForm } from '../../containers/ScheduleForm/ScheduleForm';
import { getProcess } from 'shared/api/process';
import { getSchedule } from 'shared/api/schedule';

export const ScheduleModal = ({
  open,
  actions,
  invocation,
  isScheduled,
  result,
  setResult,
  selectedOption,
  filledValues,
  setFilledValues
}) => {
  const [tab, setTab] = useState('schedule');
  const [processDetails, setProcessDetails] = useState({});
  const [scheduleDetails, setScheduleDetails] = useState({});
  const newSchedule = selectedOption === 'edit-schedule';
  const formRef = useRef();
  useEffect(() => {
    if (isScheduled && invocation?.schedule) {
      getSchedule(invocation.schedule)
        .then(data => {
          if (data.data.data && data.data.data.schedules && data.data.data.schedules[0]) {
            setScheduleDetails(data.data.data.schedules[0]);
          }
        })
        .catch(error => {
          console.log('[get process] error getting process details: ', error); // eslint-disable-line
        });
    }
    if (invocation && invocation.process) {
      getProcess(invocation.process)
        .then(data => {
          if (data.data.data && data.data.data.processes && data.data.data.processes[0]) {
            setProcessDetails(data.data.data.processes[0]);
          }
        })
        .catch(error => {
          console.log('[get process] error getting process details: ', error); // eslint-disable-line
        });
    }
  }, []);

  return (
    <Modal open={open} actions={actions}>
      <Paper
        style={{
          minWidth: '85vw',
          minHeight: '85vh',
          maxWidth: '85vw',
          maxHeight: '85vh'
        }}
      >
        <ModalHeader
          title="New Schedule"
          icon={
            <SvgIcon>
              <SlowMotionVideoIcon />
            </SvgIcon>
          }
          formikRef={formRef}
        />
        <Switch value={result}>
          <Switch.Case value="success">
            <Result type="success">
              <Result.Text>Schedule was added successfully!</Result.Text>
            </Result>
            <Paper.Footer>
              <Paper.Footer.Right>
                <Button
                  onClick={() => {
                    actions.close();
                    setResult('');
                  }}
                >
                  Done
                </Button>
              </Paper.Footer.Right>
            </Paper.Footer>
          </Switch.Case>
          <Switch.Case value="fail">
            <Result type="fail">
              <Result.Text>
                <p>An error occurred and the new schedule could not be added.</p>{' '}
                <p style={{ color: '#3765af', textDecoration: 'underline', cursor: 'pointer' }}>
                  <a
                    role="presentation"
                    onClick={() => {
                      setResult('');
                    }}
                  >
                    Go back to form
                  </a>
                </p>
              </Result.Text>
            </Result>
            <Paper.Footer>
              <Paper.Footer.Right>
                <Button
                  onClick={() => {
                    actions.close();
                    setResult('');
                  }}
                >
                  Close
                </Button>
              </Paper.Footer.Right>
            </Paper.Footer>
          </Switch.Case>
          <Switch.Case value="">
            <Paper.Header>
              <ViewTabs active={tab} changeTab={setTab} invocation={invocation} />
            </Paper.Header>
            <Switch value={tab}>
              <Switch.Case value="schedule">
                <ConnectedScheduleForm
                  setResult={setResult}
                  invocation={newSchedule ? scheduleDetails : invocation}
                  isScheduled={isScheduled}
                  filledValues={filledValues}
                  setFilledValues={setFilledValues}
                  formRef={formRef}
                  selectedOption={selectedOption}
                />
              </Switch.Case>
              <Switch.Case value="documentation">
                <Paper.Content>
                  <Documentation process={processDetails} />
                </Paper.Content>
              </Switch.Case>
            </Switch>
          </Switch.Case>
        </Switch>
      </Paper>
    </Modal>
  );
};
