import React from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import PersonIcon from '@material-ui/icons/Person';
import PermIdentityIcon from '@material-ui/icons/PermIdentity';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import Checkbox from '@material-ui/core/Checkbox';

import { Button } from 'shared/components/controls';

import './users.scss';

const Users = ({
  users,
  onSelect,
  onSelectUserAction,
  isClearActionAvailable,
  isGroupSelected,
  eventTypeIcons
}) => {
  const renderUsersList = () => {
    if (Array.isArray(users) && users.length) {
      let emptyListMessage = '';
      if (!isGroupSelected) {
        emptyListMessage = 'Please select a group to view its users.';
      } else if (!users.find(item => item.display)) {
        emptyListMessage = 'No users available in the group.';
      }
      if (emptyListMessage) {
        return (
          <div className="notifications-users-empty-list">
            <PermIdentityIcon size="small" />
            <span>{emptyListMessage}</span>
          </div>
        );
      }
      return users
        .filter(user => user.display)
        .map(user => {
          return (
            <ListItem
              onClick={() => onSelect(user)}
              selected={user.selected}
              key={user.data.name}
              button
            >
              <ListItemIcon>
                <PersonIcon size="small" style={{ color: 'var(--primary-800)' }} />
              </ListItemIcon>
              <ListItemText primary={user.data.name} />
              <ListItemSecondaryAction classes={{ root: 'notifications-users-check-wrap' }}>
                {user.selected && eventTypeIcons.map(icon => icon)}
                <Checkbox
                  onChange={() => onSelect(user)}
                  edge="end"
                  color="inherit"
                  size="small"
                  checked={user.selected}
                  classes={{ root: 'checkbox-custom' }}
                />
              </ListItemSecondaryAction>
            </ListItem>
          );
        });
    }
    return null;
  };

  const currentActionLabel = isClearActionAvailable ? 'Clear' : 'Select all';
  const shouldDisplayAction = isGroupSelected && users.find(item => item.display);

  return (
    <div className="selector">
      <div className="notifications-title">
        <div className="heading">Users:</div>
        {shouldDisplayAction && (
          <Button role="presentation" onClick={() => onSelectUserAction(isClearActionAvailable)}>
            {currentActionLabel}
          </Button>
        )}
      </div>
      <List dense disablePadding>
        {renderUsersList()}
      </List>
    </div>
  );
};

export default Users;
