import React from 'react';

import { WantParameter } from './wishes';

export function RepeatableParameter({ normalized, ...others }) {
  return <WantParameter parameter={normalized} {...others} />;
}

export const Parameter = ({ normalized, ...others }) => {
  if (normalized.is_repeatable) {
    return <RepeatableParameter {...others} normalized={normalized} />;
  }

  return <WantParameter {...others} parameter={normalized} />;
};
