import React from 'react';
import { connect } from 'react-redux';
import { func } from 'prop-types';

import RefreshIcon from '@material-ui/icons/Refresh';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiDns, mdiChartTimelineVariant, mdiPercent } from '@mdi/js';

import { getCurrentUserDetails } from 'session/store/selectors';
import { isOnlySelectGroup } from 'entities/utils/privileges';
import { Paper } from 'shared/components/layout';
import { changeStateThunk, refreshServerDbsStatusThunk } from '../../store/thunks';
import {
  getServerStatus,
  getServerStatusInfo,
  getEntityPendingStatus
} from '../../store/selectors';

import { ActionCard } from '../../components/ActionCard/ActionCard';
import { CardsLayout } from '../../components/CardsLayout/CardsLayout';

import { ServerSettings } from '../Settings/ServerSettings';

export class AllServers extends React.PureComponent {
  render() {
    const {
      all,
      serversUsage,
      retrieveServerStatus,
      userDetails,
      retrieveServerStatusInfo
    } = this.props;
    const servers = all;
    const isDisabled = isOnlySelectGroup(userDetails);

    return (
      <Paper.Content>
        <CardsLayout>
          {servers.map((server, index) => (
            <ActionCard
              key={`${server.name}-${index}`}
              settings={<ServerSettings server={server} disabled={isDisabled} />}
              status={server.databases.length > 0 ? retrieveServerStatus(server) : ''}
              statusInfo={retrieveServerStatusInfo(server)}
              type="server"
              icon={
                <SvgIcon>
                  <Icon path={mdiDns} />
                </SvgIcon>
              }
              label={server.address}
              onClick={() => this.setPath(server.path)}
              options={this.renderOptions(server)}
              readOnlyOptions={[
                {
                  icon: (
                    <SvgIcon>
                      <Icon path={mdiPercent} />
                    </SvgIcon>
                  ),
                  label: 'CPU Load',
                  fillable: true,
                  percentage:
                    serversUsage.find(sv => sv.address === server.address)?.usage || undefined
                }
              ]}
            />
          ))}
        </CardsLayout>
      </Paper.Content>
    );
  }

  setPath = path => {
    this.props.changeState({ path, level: 'all-databases' });
  };

  renderOptions = server => {
    const options = [
      {
        icon: (
          <SvgIcon>
            <Icon path={mdiChartTimelineVariant} />
          </SvgIcon>
        ),
        label: 'Workload',
        onClick: () => this.props.changeState({ path: server.path, level: 'workload' })
      }
    ];

    if (server.databases.length > 0) {
      options.push({
        icon: <RefreshIcon />,
        label: this.props.retrievePendingStatus(server).label,
        disabled: this.props.retrievePendingStatus(server).loading,
        onClick: () => this.refreshServerDBs(server)
      });
    }

    return options;
  };

  refreshServerDBs = server => {
    if (!this.props.retrievePendingStatus(server).loading) {
      this.props.refreshServerDbsStatus(server);
    }
  };
}

AllServers.propTypes = {
  changeState: func
};

export const ConnectedAllServers = connect(
  state => {
    const {
      structured,
      state: { path }
    } = state.explorer;

    const dbms = structured.dbms.find(entry => entry.protocol === path);

    return {
      all: dbms?.servers || [],
      allDbms: state.explorer.accounts,
      storeState: state.explorer.state,
      serversUsage: state.explorer.serversUsage?.servers || [],
      userDetails: getCurrentUserDetails(state),
      retrieveServerStatus: server => getServerStatus(server, state.explorer.dbStatus),
      retrieveServerStatusInfo: server => getServerStatusInfo(server, state.explorer.dbStatus),
      retrievePendingStatus: server =>
        getEntityPendingStatus(server, state.explorer.dbStatus, 'server')
    };
  },
  dispatch => ({
    changeState: newState => dispatch(changeStateThunk(newState)),
    refreshServerDbsStatus: server => dispatch(refreshServerDbsStatusThunk(server))
  })
)(AllServers);
