export function loadAllAccountsThunk(payload) {
  return async (dispatch, getState) => {
    dispatch(creators.loading(LOAD_ALL_ACCOUNTS));

    try {
      const response = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.ACCOUNTS, {
        page_size: 2147483645
      });

      response.data.protocols = getState().explorer.protocols;

      dispatch(creators.success(LOAD_ALL_ACCOUNTS, response.data));
    } catch (error) {
      dispatch(creators.fail(LOAD_ALL_ACCOUNTS, error));
      return;
    }

    if (payload?.from === 'dashboard') {
      dispatch(provideServersUsageThunk());
    } else {
      dispatch(provideDBLastBackupThunks());
      dispatch(provideDBStatusThunks());
    }
  };
}

