export function loadObjectsThunk(filters) {
  return async dispatch => {
    dispatch(creators.loading(LOAD_OBJECT_DETAILS));
    const objectApis = [
      { name: 'triggers', path: '/object_trigger' },
      { name: 'tables', path: '/object_table' },
      { name: 'indexes', path: '/object_index' },
      { name: 'procedures', path: '/object_procedure' },
      { name: 'packages', path: '/object_package' },
      { name: 'packageBodies', path: '/object_package_body' }
    ];
    try {
      let allData = await Promise.allSettled(
        objectApis.map(api =>
          RestClient.get(`/system/explorer${api.path}`, {
            dbms: filters.fullPath,
            page_size: 2147483645
          })
        )
      );
      allData = objectApis.map((api, index) => {
        return {
          res: allData[index]?.value?.data,
          name: api.name
        };
      });
      dispatch(creators.success(LOAD_OBJECT_DETAILS, { data: allData, key: filters.fullPath }));
    } catch (error) {
      dispatch(creators.fail(LOAD_OBJECT_DETAILS, error));
    }
  };
}

