import React from 'react';
import { bool, node, oneOf } from 'prop-types';
import classnames from 'classnames';

import { Overflow } from '../Overflow/Overflow';

import './Paper.local.scss';

export const Paper = ({ padded, flex, children, ...rest }) => (
  <div className={classnames('paper', { padded, [flex]: flex })} {...rest}>
    {children}
  </div>
);

Paper.propTypes = {
  children: node,
  padded: bool,
  flex: oneOf(['column', 'row'])
};

Paper.defaultProps = {
  padded: true,
  flex: 'column'
};

export const PaperHeader = ({ children, ...rest }) => (
  <header className="paper-header" {...rest}>
    {children}
  </header>
);

PaperHeader.propTypes = {
  children: node
};

export const PaperContent = ({ children, scrollable }) => {
  if (scrollable) {
    return (
      <Overflow className="paper-content" variant="white">
        {children}
      </Overflow>
    );
  }

  return <main className="paper-content">{children}</main>;
};

PaperContent.propTypes = {
  scrollable: bool,
  children: node
};

PaperContent.defaultProps = {
  scrollable: true
};

export const PaperFooter = ({ children }) => <footer className="paper-footer">{children}</footer>;

PaperFooter.propTypes = {
  children: node
};

export const LeftSideSlot = ({ children }) => {
  return <div className="slot-left">{children}</div>;
};

LeftSideSlot.propTypes = {
  children: node
};

export const RightSideSlot = ({ children }) => {
  return <div className="slot-right">{children}</div>;
};

RightSideSlot.propTypes = {
  children: node
};

PaperFooter.Left = LeftSideSlot;
PaperFooter.Right = RightSideSlot;

export const PaperTitle = ({ children, ...rest }) => (
  <h2 className="paper-title" {...rest}>
    {children}
  </h2>
);

PaperTitle.propTypes = {
  children: node
};

Paper.Header = PaperHeader;
Paper.Title = PaperTitle;
Paper.Content = PaperContent;
Paper.Footer = PaperFooter;
