import React, { useState, useCallback } from 'react';
import { connect } from 'react-redux';

import { useHistory } from 'react-router-dom';

import { isEmpty, pickBy } from 'lodash';

import moment from 'moment';

import { Switch } from 'shared/components';
import { Paper } from 'shared/components/layout';

import { Notifications } from './containers/notifications/notifications';

import { Formik, Form } from 'formik';

import { Text, Date, Time } from 'shared/components/form/editors/Generic';
import { PlatformSelect, ProcessSelect, PLATFORMS } from 'shared/components/form/editors/Custom';

import { Button } from 'shared/components/controls';

import { SubmitButton } from 'shared/components/form/SubmitButton';

import './job-process.local.scss';
import { Section, Row, Column } from '../shared/components/layout';

import { getCurrentUserName } from '../session/store/selectors';

import { getInvocation } from './store/selectors';
import { AllowGeneralEditingFor, InvocationStatus } from './store/constants';

import { postInvocation } from 'shared/api/invocation';

import { ProcessParameters } from './job-parameters';

import { Schedule } from 'jobs/containers/Schedule/Schedule';
import { ConnectedDeleteJobModal } from 'jobs/actions/delete/DeleteJobModal';

const JobForm = ({ loggedInAs, invocation, redirected }) => {
  const history = useHistory();

  const [editable, setEditable] = useState(AllowGeneralEditingFor.includes(invocation.status));

  const derivedMonitor = {
    enabled: !!invocation?.parameters?.events,
    events: invocation?.parameters?.events || 0,
    users: invocation?.parameters?.users || []
  };

  const [monitor, setMonitor] = useState(derivedMonitor);

  const { initialValues, disabledFields } = deriveFormState(invocation, editable);

  const availableProcess = (platformVal, processVal) => {
    const selectedPlatform = PLATFORMS.find(platform => platform.value === platformVal);

    if (selectedPlatform.tester.test(processVal)) return processVal;
    return '';
  };

  const userValue = editable ? loggedInAs : invocation.user_owner;
  const dateValue = editable
    ? moment()
        .utc()
        .format('MM/DD/YYYY')
    : moment(invocation.date_invoke).format('MM/DD/YYYY');
  const timeValue = editable
    ? moment()
        .utc()
        .format('HH:mm')
    : moment(invocation.date_invoke).format('HH:mm');

  const [open, setOpen] = useState(false);
  const modalActions = {
    open: useCallback(() => {
      setOpen(true);
    }, []),
    close: useCallback(() => {
      setOpen(false);
    }, [])
  };

  const extraValidations = values => {
    const errors = {};
    const { date_invoke, time_invoke } = values;

    if (!moment(date_invoke).isValid()) {
      errors.date_invoke = 'Invalid date format';
    }

    if (!/^(2[0-3]|[01]?[0-9]):([0-5]?[0-9])$/.test(time_invoke)) {
      errors.time_invoke = 'Invalid time format';
    }

    if (
      values['fqname--name'] &&
      (!/^(?:[a-z_][a-z0-9_.]*)$/i.test(values['fqname--name']) ||
        values['fqname--name'].substr(-1) === '.')
    ) {
      errors['fqname--name'] = 'A valid MAS relative name is required.';
    }

    return errors;
  };

  return (
    <>
      <Formik
        initialValues={initialValues}
        validate={extraValidations}
        onSubmit={async (values, actions) => {
          actions.setSubmitting(true);
          handleJobCreation(values, monitor).then(({ result, invocations }) => {
            if (result === 'success') {
              const { uuid } = invocations[0];

              history.push(`/jobs/job?invocation=${uuid}`);
            }
          });
        }}
      >
        {() => {
          return (
            <>
              <Paper.Content scrollable>
                <Form>
                  <Section>
                    <Section.Header>
                      <Section.Title>Details</Section.Title>
                    </Section.Header>
                    <Section.Content>
                      <Row>
                        <Column xs={3}>
                          <PlatformSelect
                            name="platform"
                            label="Platform"
                            description="Select a platform to filter out the processes"
                            reactToChange={(value, formik) => {
                              formik.setFieldValue(
                                'process',
                                availableProcess(value, formik.values.process)
                              );
                            }}
                            disabled={disabledFields.platform || !editable || redirected}
                          />
                        </Column>
                        <Column xs={9}>
                          <ProcessSelect
                            name="process"
                            label="Process"
                            value={invocation.process}
                            required
                            disabled={disabledFields.process || !editable || redirected}
                          />
                        </Column>
                      </Row>
                      <Row>
                        <Column xs={3}>
                          <Date
                            name="date_invoke"
                            label="Date"
                            disabled={!editable}
                            value={dateValue}
                          />
                        </Column>
                        <Column xs={3}>
                          <Time
                            name="time_invoke"
                            label="MAS system time (UTC)"
                            disabled={!editable}
                            value={timeValue}
                          />
                        </Column>
                        <Column xs={3}>
                          <Text name="job_user" label="User" required disabled value={userValue} />
                        </Column>
                      </Row>
                    </Section.Content>
                  </Section>
                  <ProcessParameters recieved={invocation.parameters} editable={editable} />
                  <Section>
                    <Section.Header>
                      <Section.Title>Notifications</Section.Title>
                    </Section.Header>
                    <Section.Content>
                      <Row spacing={3}>
                        <Column xs={12}>
                          <Notifications
                            name="notifications"
                            key={invocation.uuid}
                            disabled={!editable}
                            recievedData={derivedMonitor}
                            onApply={n_state => {
                              if (n_state.events === 0 || isEmpty(n_state.users)) {
                                setMonitor({
                                  enabled: false
                                });
                              }
                              setMonitor({
                                enabled: true,
                                ...n_state
                              });
                            }}
                          />
                        </Column>
                      </Row>
                    </Section.Content>
                  </Section>
                </Form>
              </Paper.Content>
              <Paper.Footer>
                <Paper.Footer.Left>
                  <Button onClick={() => history.goBack()}>Cancel</Button>
                  {invocation.status === 'DELAYED' && (
                    <Button
                      color="secondary"
                      onClick={() => {
                        setOpen(true);
                      }}
                    >
                      Delete
                    </Button>
                  )}
                </Paper.Footer.Left>
                <Paper.Footer.Right>
                  <FormVerbs
                    status={invocation.status}
                    editable={editable}
                    setEditable={setEditable}
                    monitor={monitor}
                    invocation={invocation}
                  />
                </Paper.Footer.Right>
              </Paper.Footer>
            </>
          );
        }}
      </Formik>
      {open && (
        <ConnectedDeleteJobModal open={open} actions={modalActions} uuid={invocation.uuid} />
      )}
    </>
  );
};

export default connect(state => {
  return {
    loggedInAs: getCurrentUserName(state),
    invocation: getInvocation(state)
  };
}, {})(JobForm);

/* eslint-disable no-unused-vars */
function FormVerbs({ status, editable, setEditable, monitor, invocation }) {
  const makeEditable = () => setEditable(true);

  return (
    <Switch value={editable}>
      <Switch.Case value>
        <SubmitButton>{monitor.enabled ? 'Monitor' : 'Run'}</SubmitButton>
      </Switch.Case>
      <Switch.Case value={false}>
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'flex-end' }}>
          <Schedule invocation={invocation} />
          <Button onClick={makeEditable}>Edit</Button>
        </div>
      </Switch.Case>
    </Switch>
  );
}

function handleJobCreation(values, monitor) {
  const {
    process,
    platform,
    date_invoke,
    time_invoke,
    job_user,
    ...params_with_maybe_replvars
  } = values;
  const sendable = {};

  const { parameters, replvars } = extractReplVars(params_with_maybe_replvars);

  function rejector(value, key) {
    if (key === 'parameters') {
      return false;
    }

    if (!value && typeof value !== 'boolean') {
      return false;
    }

    if (value === 'None') {
      return false;
    }

    if (typeof value === 'object' && isEmpty(value)) {
      return false;
    }

    return true;
  }

  if (date_invoke) {
    const dateMoment = moment(date_invoke, 'MM/DD/YYYY');

    const timeMoment = time_invoke ? moment(time_invoke, 'hh:mm') : moment();
    const currentDate = moment()
      .utc()
      .set('second', 0)
      .format('YYYY-MM-DDTHH:mm:ss');

    const date = dateMoment.format('YYYY-MM-DD');
    const time = timeMoment.format('HH:mm');
    const dateTime = moment(date + ' ' + time, 'YYYY-MM-DD HH:mm').format('YYYY-MM-DDTHH:mm:ss'); // eslint-disable-line prefer-template

    const duration = moment.duration(
      moment(dateTime, 'YYYY-MM-DD HH:mm').diff(moment(currentDate, 'YYYY-MM-DD HH:mm'))
    );
    const minutes = duration.asMinutes();

    if (minutes > 0) {
      sendable.timestamp = dateTime;
    } else {
      sendable.timestamp = null;
    }
  }

  if (monitor.enabled) {
    /**
     * Is monitoring a process
     */
    sendable.process = 'mas.sys.invocation.notify.create';

    sendable.parameters = {
      name: process,
      parameters: pickBy(parameters, rejector),
      when: sendable.timestamp,
      events: monitor.events,
      users: monitor.users
    };

    sendable.timestamp = null;
  } else {
    sendable.process = process;

    sendable.parameters = pickBy(parameters, rejector);
  }

  return postInvocation(sendable);
}

function deriveFormState(invocation) {
  const values = {
    process: invocation.process,
    parameters: { ...invocation.parameters }
  };
  const disabledFields = {
    ...(invocation.status !== InvocationStatus.NEW && {
      platform: true,
      process: true,
      user: true,
      parameters: true
    })
  };

  return { initialValues: values, disabledFields };
}

function extractReplVars(rawValues) {
  // find the name + namespace and combine those 2 into a single value
  const values = extractFQName(rawValues);
  const keys = Object.keys(values);
  const result = keys.reduce(
    (extracted, key) => {
      if (key.startsWith('replvar--')) {
        const [index, field_name] = key.replace('replvar--', '').split('--');

        const entry = extracted.replvars[index];

        // ignore deflt propery if is an empty string
        if (field_name === 'deflt' && values[key] === '') {
          return extracted;
        }

        if (entry) {
          if (field_name === 'deflt') {
            entry[field_name] = `str('"'${values[key]}'"')`;
          } else {
            entry[field_name] = values[key];
          }
        } else if (field_name === 'deflt') {
          extracted.replvars.push({ [field_name]: `str('"'${values[key]}'"')` });
        } else {
          extracted.replvars.push({ [field_name]: values[key] });
        }

        return extracted;
      }

      if (key.startsWith('replvalue')) {
        const [index] = key.replace('replvalue--', '').split('--');

        extracted.replvalues[values[`replvar--${index}--name`]] = values[key];

        return extracted;
      }

      extracted.parameters[key] = values[key];

      return extracted;
    },
    { replvars: [], replvalues: {}, parameters: {} }
  );

  return {
    parameters: {
      ...result.parameters,
      replvars: cleanUpReplVars(result.replvars),
      replvalues: result.replvalues
    }
  };
}

function cleanUpReplVars(replvars) {
  return replvars.filter(p => true).map((p, index) => ({ ...p, meta: { position: index + 3 } }));
}

function extractFQName(values) {
  if (values['fqname--name']) {
    if (values['fqname--namespace']) {
      values.name = `${values['fqname--namespace']}.${values['fqname--name']}`;
    } else {
      values.name = values['fqname--name'];
    }
    delete values['fqname--namespace'];
    delete values['fqname--name'];
  } else if (values['fqname--fqname']) {
    if (values['fqname--namespace']) {
      values.fqname = `${values['fqname--namespace']}.${values['fqname--fqname']}`;
    } else {
      values.fqname = values['fqname--fqname'];
    }
    delete values['fqname--namespace'];
    delete values['fqname--fqname'];
  }
  return values;
}
