import { requestInvocation } from 'shared/api/invocation';

export function processQuery(uuid, process) {
  return {
    type: 'PROCESS_QUERY',
    uuid,
    process
  };
}

export function fetchInvocation(uuid, process) {
  return function thunk(dispatch) {
    dispatch({
      type: 'FETCH_INVOCATION',
      uuid,
      process,
      promise: requestInvocation(uuid)
    });
  };
}

export function updateInvocation(invocation) {
  return {
    type: 'UPDATE_INVOCATION',
    invocation
  };
}
