import React from 'react';
import { object } from 'prop-types';
import { Field } from 'formik';

import InputAdornment from '@material-ui/core/InputAdornment';
import { TextField as MUITextField } from '@material-ui/core';
import SvgIcon from '@material-ui/core/SvgIcon';
import Tooltip from '@material-ui/core/Tooltip';
import { withStyles } from '@material-ui/core/styles';
import Icon from '@mdi/react';
import { mdiHelpCircleOutline, mdiLanConnect } from '@mdi/js';

import './TextField.local.scss';

export const TextField = ({ parameter, disabled }) => {
  const validate = value => {
    if (parameter.is_required) {
      if (!value) {
        return 'This Field is Required';
      }
    }
  };

  return (
    <Field name={parameter.name} validate={validate}>
      {({ field, meta }) => {
        return (
          <MUITextField
            className={disabled ? 'disabled' : ''}
            {...field}
            variant="outlined"
            type="text"
            fullWidth
            disabled={disabled}
            error={meta.error && meta.touched}
            helperText={meta.touched && meta.error ? meta.error : undefined}
            label={parameter.label}
            placeholder={parameter.label}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  {parameter.icon ? (
                    parameter.icon
                  ) : (
                    <SvgIcon>
                      <Icon path={mdiLanConnect} />
                    </SvgIcon>
                  )}
                </InputAdornment>
              ),
              endAdornment: (
                <LightTooltip placement="bottom-end" title={parameter.description}>
                  <InputAdornment position="end">
                    <SvgIcon>
                      <Icon path={mdiHelpCircleOutline} />
                    </SvgIcon>
                  </InputAdornment>
                </LightTooltip>
              )
            }}
          />
        );
      }}
    </Field>
  );
};

TextField.propTypes = {
  parameter: object
};

const LightTooltip = withStyles(() => ({
  tooltip: {
    color: '#fff',
    backgroundColor: '#787E89',
    letterSpacing: '0.4px',
    fontSize: '12px',
    lineHeight: '16px',
    fontFamily: 'Open Sans'
  }
}))(Tooltip);
