import React from 'react';
import { object } from 'prop-types';
import classnames from 'classnames';

import Button from '@material-ui/core/Button';

import './UnusedFeatures.local.scss';

export class UnusedFeatures extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      isExpanded: false,
      needsExpansion: false
    };

    this.listRef = React.createRef();
  }

  componentDidMount() {
    if (
      this.listRef.current &&
      this.listRef.current.scrollHeight > this.listRef.current.offsetHeight
    ) {
      this.setState({ needsExpansion: true });
    }
  }

  /* eslint-disable react/no-did-update-set-state */
  componentDidUpdate() {
    if (
      this.listRef.current &&
      this.listRef.current.scrollHeight > this.listRef.current.offsetHeight &&
      !this.state.needsExpansion
    ) {
      this.setState({ needsExpansion: true });
    }
  }

  render() {
    const { reportOne, reportTwo } = this.props;
    const { isExpanded, needsExpansion } = this.state;

    if (!reportOne) return <></>;

    return (
      <div className="unused-features-container">
        <header>
          <h1>Unused features</h1>
        </header>

        {!reportTwo && (
          <div ref={this.listRef} className={classnames('features-container', { all: isExpanded })}>
            {this.renderSingleReport()}
          </div>
        )}
        {reportTwo && (
          <div ref={this.listRef} className={classnames('features-container', { all: isExpanded })}>
            {this.renderComparableReports()}
          </div>
        )}
        {needsExpansion && !isExpanded && (
          <div className="btn-container">
            <Button
              className="view-all-btn"
              onClick={() => this.setState({ isExpanded: true })}
              data-qa="view-all"
            >
              View all
            </Button>
          </div>
        )}
      </div>
    );
  }

  renderSingleReport = () => {
    const { reportOne } = this.props;
    const features = Object.entries(reportOne?.UNUSED ?? {});

    return features.map(feature => {
      return (
        <div key={feature[0]} className="feature">
          {feature[0]}
        </div>
      );
    });
  };

  renderComparableReports = () => {
    const { reportOne, reportTwo } = this.props;
    const features = Object.keys(reportTwo.UNUSED);

    const comparableFeatures = features.filter(feature => {
      return reportOne.UNUSED[feature] !== undefined;
    });

    return comparableFeatures.map(feature => {
      return (
        <div key={feature} className="feature">
          {feature}
        </div>
      );
    });
  };
}

UnusedFeatures.propTypes = {
  reportOne: object,
  reportTwo: object
};

export default UnusedFeatures;
