import { put, takeEvery } from '@redux-saga/core/effects';

import { CONSTANTS } from 'config';
import { actions } from 'session/store/actions';
import { RestClient } from 'shared/lib/rest';

function* checkAuthorization({ payload }) {
  RestClient.setBasicAuth();
  try {
    const response = yield RestClient.get(CONSTANTS.BASE.ENDPOINTS.CURRENT_USER(payload), {
      page_size: 2147483645
    });

    const userDetails = {
      name: response.data.data.source.user
    };

    if (response.data.data.users && response.data.data.users[0]) {
      [userDetails.details] = response.data.data.users;
    }

    yield put(actions.checkAuthorization.dispatchSuccess(userDetails));
  } catch (errors) {
    yield put(actions.checkAuthorization.dispatchFail());
  }
}

export function* CheckAuthorizationEffect() {
  yield takeEvery(actions.checkAuthorization.getLoadingAction(), checkAuthorization);
}
