export const createUrl = (url, params = {}) => {
  if (typeof params !== 'object' || !params) {
    return url;
  }
  let path = url;
  const query = { ...params };
  Object.keys(query).forEach((key) => {
    if (typeof query[key] !== 'undefined' && path.includes(`:${key}`)) {
      path = path.replace(`:${key}`, query[key]);
      delete query[key];
    }
  });

  if (!Object.keys(query).length || !Object.keys(query).find(((key) => typeof query[key] !== 'undefined'))) {
    return path;
  }

  const paramsUrl = Object.keys(query).filter((key) => query[key])
    .map((key) => `${key}=${encodeURIComponent(query[key])}`)
    .join('&');
  return `${path}${'?'.concat(...paramsUrl)}`;
};
