export function mapAllServers(entries) {
  const serversInfo = entries.reduce((acc, entry) => {
    const index = acc.findIndex(sv => sv.name === entry.srvname);
    if (index > -1) {
      acc[index].totalDBs += 1;
      acc[index].eelic_count += entry.eelic_count;
      acc[index].selic_count += entry.selic_count;
      acc[index].sids.push(entry);
      acc[index].installedOptions.aa_count +=
        parseInt(entry.aa_count, 10) === 1 ? parseInt(entry.aa_count, 10) : 0;
      acc[index].installedOptions.ac_count +=
        parseInt(entry.ac_count, 10) === 1 ? parseInt(entry.ac_count, 10) : 0;
      acc[index].installedOptions.as_count +=
        parseInt(entry.as_count, 10) === 1 ? parseInt(entry.as_count, 10) : 0;
      acc[index].installedOptions.dv_count +=
        parseInt(entry.dv_count, 10) === 1 ? parseInt(entry.dv_count, 10) : 0;
      acc[index].installedOptions.mt_count +=
        parseInt(entry.mt_count, 10) === 1 ? parseInt(entry.mt_count, 10) : 0;
      acc[index].installedOptions.adg_count +=
        parseInt(entry.adg_count, 10) === 1 ? parseInt(entry.adg_count, 10) : 0;
      acc[index].installedOptions.rac_count +=
        parseInt(entry.rac_count, 10) === 1 ? parseInt(entry.rac_count, 10) : 0;
      acc[index].installedOptions.olap_count +=
        parseInt(entry.olap_count, 10) === 1 ? parseInt(entry.olap_count, 10) : 0;
      acc[index].installedOptions.part_count +=
        parseInt(entry.part_count, 10) === 1 ? parseInt(entry.part_count, 10) : 0;
      acc[index].installedOptions.in_mem_db_count +=
        parseInt(entry.in_mem_db_count, 10) === 1 ? parseInt(entry.in_mem_db_count, 10) : 0;
    } else {
      acc.push({
        name: entry.srvname,
        collDate: entry.collDate,
        nrCores: entry.Ncore,
        totalDBs: 1,
        eelic_count: entry.eelic_count,
        selic_count: entry.selic_count,
        sids: [entry],
        installedOptions: {
          aa_count: parseInt(entry.aa_count, 10) === 1 ? 1 : 0,
          ac_count: parseInt(entry.ac_count, 10) === 1 ? 1 : 0,
          as_count: parseInt(entry.as_count, 10) === 1 ? 1 : 0,
          dv_count: parseInt(entry.dv_count, 10) === 1 ? 1 : 0,
          mt_count: parseInt(entry.mt_count, 10) === 1 ? 1 : 0,
          adg_count: parseInt(entry.adg_count, 10) === 1 ? 1 : 0,
          rac_count: parseInt(entry.rac_count, 10) === 1 ? 1 : 0,
          olap_count: parseInt(entry.olap_count, 10) === 1 ? 1 : 0,
          part_count: parseInt(entry.part_count, 10) === 1 ? 1 : 0,
          in_mem_db_count: parseInt(entry.in_mem_db_count, 10) === 1 ? 1 : 0
        }
      });
    }
    return acc;
  }, []);

  serversInfo.map(sv => {
    const total = Object.values(sv.installedOptions).reduce((a, b) => a + b);
    sv.totalInstalledOptions = total;

    return null;
  });

  serversInfo.sort((a, b) => a.nrCores - b.nrCores);

  return serversInfo;
}

export function mapVirtualServers(entries) {
  const serversInfo = entries.reduce((acc, entry) => {
    const index = acc.findIndex(sv => sv.vcenterservername === entry.vcenterservername);

    if (index > -1) {
      if (
        entry.vmname !== null ||
        entry.vmguesthostname !== null ||
        entry.vmguestos !== null ||
        entry.vmipaddress !== null
      ) {
        acc[index].hosts_nr += 1;
      }
    } else {
      acc.push({
        vcenterservername: entry.vcenterservername,
        cpu: entry.cpu,
        cpucores: entry.cpucores,
        hosts_nr:
          entry.vmname !== null ||
          entry.vmguesthostname !== null ||
          entry.vmguestos !== null ||
          entry.vmipaddress !== null
            ? 1
            : 0
      });
    }
    return acc;
  }, []);

  return serversInfo;
}
