import { types, createReducer } from 'shared/lib/redux-utils';

import {
  LOAD_ALL_GROUPS,
  LOAD_GROUP,
  GRANT_PRIVILEGE,
  REVOKE_PRIVILEGE,
  REMOVE_USERS_FROM_GROUP,
  REMOVE_GROUPS_FROM_GROUP,
  ADD_USERS_TO_GROUP,
  PATCH_SELECTED_GROUP,
  CREATE_GROUP,
  DELETE_GROUP
} from './actions';

export const initialState = {
  all: [],
  ops: {
    all: { done: false, loading: false },
    selected: { done: false, loading: false },
    privileges: { done: false, loading: false },
    remove: { loading: false },
    create: { loading: false }
  }
};

export const producers = {
  [types.loading(LOAD_ALL_GROUPS)]: draft => {
    draft.ops.all = {
      loading: true
    };
  },
  [types.success(LOAD_ALL_GROUPS)]: (draft, { payload }) => {
    const response = payload;
    draft.all = response.data.groups;
    draft.ops.all = {
      loading: false,
      result: response.result,
      errors: response.messages
    };
  },
  [types.fail(LOAD_ALL_GROUPS)]: (draft, { payload }) => {
    const response = payload;
    draft.all = response.data.groups;
    draft.ops.all = {
      loading: false,
      result: response.result,
      errors: response.messages
    };
  },
  [types.loading(LOAD_GROUP)]: draft => {
    draft.ops.selected = {
      loading: true
    };
  },
  [types.success(LOAD_GROUP)]: (draft, { payload }) => {
    const response = payload;
    draft.selected = response.data.groups[0]; //eslint-disable-line
    draft.ops.selected = {
      loading: false,
      result: response.result,
      errors: response.messages
    };
  },
  [types.fail(LOAD_GROUP)]: (draft, { payload }) => {
    draft.selected = null;
    draft.ops.selected = {
      loading: false,
      result: payload.result,
      errors: payload.messages
    };
  },
  [types.action(PATCH_SELECTED_GROUP)]: (draft, { payload }) => {
    draft.selected = payload;
  },
  [types.loading(GRANT_PRIVILEGE)]: draft => {
    draft.ops.privileges = { loading: true };
  },
  [types.success(GRANT_PRIVILEGE)]: (draft, { payload }) => {
    draft.ops.privileges = {
      loading: false,
      result: payload.result,
      errors: undefined
    };

    const [group] = payload.data.groups;

    draft.selected = group;
  },
  [types.fail(GRANT_PRIVILEGE)]: (draft, { payload }) => {
    draft.ops.privileges = {
      loading: false,
      result: payload.result,
      errors: payload.messages
    };
  },
  [types.loading(REVOKE_PRIVILEGE)]: draft => {
    draft.ops.privileges = { loading: true };
  },
  [types.success(REVOKE_PRIVILEGE)]: (draft, { payload }) => {
    draft.ops.privileges = {
      loading: false,
      result: payload.result,
      errors: undefined
    };

    const [group] = payload.data.groups;

    draft.selected = group;
  },
  [types.fail(REVOKE_PRIVILEGE)]: (draft, { payload }) => {
    draft.ops.privileges = {
      loading: false,
      result: payload.result,
      errors: payload.messages
    };
  },
  [types.loading(REMOVE_USERS_FROM_GROUP)]: draft => {
    draft.ops.group_users = { loading: true };
  },
  [types.success(REMOVE_USERS_FROM_GROUP)]: (draft, { payload }) => {
    draft.ops.group_users = {
      loading: false,
      result: payload.result,
      errors: undefined
    };

    const [group] = payload.data.groups;

    draft.selected = group;
  },
  [types.fail(REMOVE_USERS_FROM_GROUP)]: (draft, { payload }) => {
    draft.ops.group_users = {
      loading: false,
      result: payload.result,
      errors: payload.messages
    };
  },

  [types.loading(REMOVE_GROUPS_FROM_GROUP)]: draft => {
    draft.ops.group_subgroups = { loading: true };
  },
  [types.success(REMOVE_GROUPS_FROM_GROUP)]: (draft, { payload }) => {
    draft.ops.group_subgroups = {
      loading: false,
      result: payload.result,
      errors: undefined
    };

    const [group] = payload.data.groups;

    draft.selected = group;
  },
  [types.fail(REMOVE_GROUPS_FROM_GROUP)]: (draft, { payload }) => {
    draft.ops.group_subgroups = {
      loading: false,
      result: payload.result,
      errors: payload.messages
    };
  },

  [types.loading(ADD_USERS_TO_GROUP)]: draft => {
    draft.ops.group_users = { loading: true };
  },
  [types.success(ADD_USERS_TO_GROUP)]: (draft, { payload }) => {
    draft.ops.group_users = {
      loading: false,
      result: payload.result,
      errors: undefined
    };

    const [group] = payload.data.groups;

    draft.selected = group;
  },
  [types.fail(ADD_USERS_TO_GROUP)]: (draft, { payload }) => {
    draft.ops.group_users = {
      loading: false,
      result: payload.result,
      errors: payload.messages
    };
  },
  [types.loading(CREATE_GROUP)]: draft => {
    draft.ops.create = { loading: true };
  },
  [types.success(CREATE_GROUP)]: draft => {
    draft.ops.create = { loading: false };
  },
  [types.fail(CREATE_GROUP)]: (draft, { payload }) => {
    draft.ops.create = {
      loading: false,
      errors: payload
    };
  },
  [types.loading(DELETE_GROUP)]: draft => {
    draft.ops.remove = { loading: true };
  },
  [types.success(DELETE_GROUP)]: draft => {
    draft.selected = null;
    draft.ops.remove = { loading: false };
  },
  [types.fail(DELETE_GROUP)]: (draft, { payload }) => {
    draft.ops.remove = { loading: false, errors: payload };
  }
};

export default createReducer(producers, initialState);
