import React from 'react';
import classnames from 'classnames';

import { LightTooltip } from 'shared/components/tooltip/Tooltip';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiCubeOutline, mdiDns } from '@mdi/js';

import './ToggleDashboard.local.scss';

export const ToggleDashboard = ({ active, onClickCallback }) => {
  return (
    <div className="toggle-container">
      <LightTooltip placement="bottom" title="Virtual servers">
        <div
          className={classnames('option', {
            active: active === 'virtual'
          })}
          onClick={() => onClickCallback('virtual')}
          role="presentation"
        >
          <SvgIcon>
            <Icon path={mdiCubeOutline} />
          </SvgIcon>
        </div>
      </LightTooltip>
      <LightTooltip placement="bottom" title="Physical servers">
        <div
          className={classnames('option', {
            active: active === 'physical'
          })}
          onClick={() => onClickCallback('physical')}
          role="presentation"
        >
          <SvgIcon>
            <Icon path={mdiDns} />
          </SvgIcon>
        </div>
      </LightTooltip>
    </div>
  );
};
