import React from 'react';
import * as PropTypes from 'prop-types';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import InputLabel from '@material-ui/core/InputLabel';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormControl from '@material-ui/core/FormControl';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';

import { FormattedMessage } from 'react-intl';

import { RestErrors } from 'shared/components';

import './form.local.scss';

export class FormComponent extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      username: '',
      password: '',
      showPassword: false
    };

    this.submitHandler = this.submitHandler.bind(this);
  }

  render() {
    const { className } = this.props;
    const restErrors = this.getRestErrors();

    return (
      <div className={className}>
        <form onSubmit={this.submitHandler} noValidate autoComplete="off">
          <div className="flex direction-column">
            <TextField
              autoFocus
              className={`${className}__field`}
              helperText={this.getError('username')}
              onChange={this.valueChangeHandler('username')}
              variant="outlined"
              label={<FormattedMessage id="general.form.username" />}
              inputProps={{
                className: 'qa-login-username',
                style: { fontSize: '14px', fontFamily: 'Lato' }
              }}
              InputLabelProps={{
                style: { fontSize: '14px', fontFamily: 'Lato' }
              }}
            />

            <FormControl className={`${className}__field`} variant="outlined">
              <InputLabel htmlFor="outlined-adornment-password">Password</InputLabel>
              <OutlinedInput
                id="outlined-adornment-password"
                type={this.state.showPassword ? 'text' : 'password'}
                value={this.state.password}
                onChange={this.valueChangeHandler('password')}
                inputProps={{
                  className: 'qa-login-password',
                  style: { fontSize: '14px', fontFamily: 'Lato' }
                }}
                endAdornment={
                  <InputAdornment position="end">
                    <div className="password-icon">
                      <IconButton
                        className="qa-login-toggle-password-visibility"
                        aria-label="toggle password visibility"
                        onClick={this.setShowPassword}
                        onMouseDown={this.handleMouseDownPassword}
                        edge="end"
                        color="inherit"
                      >
                        {this.state.showPassword ? <Visibility /> : <VisibilityOff />}
                      </IconButton>
                    </div>
                  </InputAdornment>
                }
                labelWidth={70}
              />
            </FormControl>
          </div>
          {restErrors.size > 0 && (
            <div className={`${className}__errors`}>
              <RestErrors errors={restErrors} />
            </div>
          )}
          <div className={`${className}__actions flex`}>
            <Button
              className="qa-login-btn"
              size="large"
              type="submit"
              variant="contained"
              color="primary"
              disabled={
                this.props.isLoading || this.state.username === '' || this.state.password === ''
              }
            >
              <FormattedMessage id="session.login.button" />
            </Button>
            {/* <div className="forgot-password qa-forgot-password">
              <FormattedMessage id="session.login.forgot-password" />
            </div> */}
          </div>
        </form>
      </div>
    );
  }

  setShowPassword = () => {
    this.setState(prevState => ({
      showPassword: !prevState.showPassword
    }));
  };

  handleMouseDownPassword = event => {
    event.preventDefault();
  };

  getError(field) {
    const error = this.props.errors.find(item => item.get('path') === `.${field}`);
    if (!error) {
      return '';
    }

    return error.get('message') || 'This field is invalid';
  }

  getRestErrors() {
    return this.props.errors.filter(
      item => item.get('path') !== '.username' && item.get('path') !== '.password'
    );
  }

  resetErrors() {
    if (this.props.errors.size) {
      this.props.resetErrors();
    }
  }

  submitHandler(event) {
    event.preventDefault();

    this.props.submit({
      username: this.state.username,
      password: this.state.password
    });
  }

  valueChangeHandler(name) {
    return event => {
      this.resetErrors();
      this.setState({
        [name]: event.target.value
      });
    };
  }
}

FormComponent.propTypes = {
  errors: PropTypes.object.isRequired,
  isLoading: PropTypes.bool.isRequired,
  resetErrors: PropTypes.func.isRequired,
  submit: PropTypes.func.isRequired,
  className: PropTypes.string
};

FormComponent.defaultProps = {
  className: 'form'
};
