import { getQuery, getInvocationStatus } from './selectors';

import { InvocationStatus } from './constants';

import { fetchInvocation } from './actions';

import withPolling from 'shared/lib/polling';

import store from 'core/store';

async function task() {
  const { uuid, process } = getQuery(store.getState());

  if (uuid) {
    await store.dispatch(fetchInvocation(uuid, process));
  }

  const STATUS = getInvocationStatus(store.getState());

  switch (STATUS) {
    case InvocationStatus.EXECUTING:
    case InvocationStatus.SCHEDULED:
    case InvocationStatus.DELAYED:
    case 'NOT_FETCHED':
      console.log('[invocation-poller] - polling', STATUS); // eslint-disable-line
      throw new Error('need to poll invocation...');
    default:
      break;
  }

  console.log('[invocation-poller] - done'); // eslint-disable-line

  return Promise.resolve();
}

export const invocationPoller = withPolling(task, 5000);
