import React, { useState } from 'react';

import { Button } from 'shared/components/controls';
import { ScheduleModal } from './components/ScheduleModal/ScheduleModal';

export const Schedule = ({ invocation }) => {
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setFilledValues({});
      setResult('');
      setOpen(false);
    }
  };
  const [filledValues, setFilledValues] = useState({});
  const [result, setResult] = useState('');
  const [selectedOption, setSelectedOption] = useState('');
  const onClickHandler = option => {
    setSelectedOption(option);
    setOpen(true);
  };

  const btnLabel = invocation ? 'Schedule' : 'New Schedule';
  const isScheduled = invocation?.status === 'SCHEDULED' && invocation?.schedule;

  return (
    <>
      {isScheduled && (
        <Button style={{ minWidth: '140px' }} onClick={() => onClickHandler('edit-schedule')}>
          Edit Schedule
        </Button>
      )}
      <Button
        onClick={() => {
          onClickHandler('schedule');
        }}
      >
        {btnLabel}
      </Button>
      <ScheduleModal
        open={open}
        actions={modalActions}
        invocation={invocation}
        isScheduled={isScheduled}
        selectedOption={selectedOption}
        result={result}
        setResult={setResult}
        filledValues={filledValues}
        setFilledValues={setFilledValues}
      />
    </>
  );
};
