import React from 'react';
import classnames from 'classnames';
import { mdiCheckDecagram, mdiAlertDecagram } from '@mdi/js';

import SvgIcon from '@material-ui/core/SvgIcon';
import { node, oneOf, bool } from 'prop-types';

import './Result.local.scss';

const icons_map = {
  success: mdiCheckDecagram,
  fail: mdiAlertDecagram
};

export const Result = ({ children, type }) => {
  return (
    <div
      className={classnames('result-display', {
        [type]: type
      })}
    >
      <div className="result-icon">
        <SvgIcon fontSize="inherit">
          <path d={icons_map[type] || mdiAlertDecagram} />
        </SvgIcon>
      </div>
      <div>{children}</div>
    </div>
  );
};

Result.propTypes = {
  type: oneOf(['success', 'fail']),
  children: node
};

export const ResultText = ({ children, compact }) => (
  <h2 className={classnames('result-text', { compact })}>{children}</h2>
);

ResultText.propTypes = {
  compact: bool,
  children: node
};

export const ResultMessages = ({ messages = [] }) => (
  <div className="result-messages">
    {messages.map((msg, index) => (
      <div className="message-entry" key={index}>
        <p>{msg?.text?.split?.('\n')?.[0]}</p>
      </div>
    ))}
  </div>
);

ResultText.propTypes = {
  children: node
};

Result.Text = ResultText;
Result.Messages = ResultMessages;
