import React from 'react';
import MUIButton from '@material-ui/core/Button';

import './Button.local.scss';

import { Link } from 'react-router-dom';

const LinkBehavior = React.forwardRef((props, ref) => <Link ref={ref} {...props} />);

export const Button = ({ to, href, ...props }) => {
  const link = to || href;

  if (link) {
    return <MUIButton color="primary" {...props} component={LinkBehavior} to={link} />;
  }

  return <MUIButton color="primary" {...props} />;
};
