import { RestClient } from 'shared/lib/rest';
import { ENTITY_TYPES_FETCH } from './selectors';

export function fetchEntities() {
  return function thunk(dispatch) {
    const promises = ENTITY_TYPES_FETCH.map(({ type, query }) => {
      let url = `${type}`;
      if (query) {
        url = url.concat(`/${encodeURIComponent(query)}`);
      }
      const promise = RestClient.get(url, { page_size: 2147483645 });
      dispatch({
        type: 'FETCH_ENTITIES',
        promise,
        entityType: type
      });
      return promise;
    });

    dispatch({
      type: 'FETCH_ENTITIES_ALL',
      promise: promises
    });
  };
}

export function fetchEntity(eType) {
  const definition = ENTITY_TYPES_FETCH.find(desc => eType === desc.type);

  if (!definition) {
    return function notFound() {
      console.warn('[fetchEntity] invalid entity type:', eType); // eslint-disable-line
    };
  }

  return function thunk(dispatch) {
    const promise = RestClient.get(
      `${definition.type}${definition.query ? `/${definition.query}` : ''}?page_size=2147483645`
    );

    dispatch({
      type: 'FETCH_ENTITIES',
      promise,
      entityType: definition.type
    });

    return promise;
  };
}

export function selectEntity(entity, index) {
  return {
    entity,
    index,
    type: 'SELECT_ENTITY'
  };
}

export function clearSelectedEntity(entity, index) {
  return {
    entity,
    index,
    type: 'CLEAR_SELECTED_ENTITY'
  };
}

export function setFilterType(value) {
  return {
    name: 'type',
    value,
    type: 'UPDATE_FILTERS'
  };
}

export function setFilterSearch(value) {
  return {
    name: 'search',
    value,
    type: 'UPDATE_FILTERS'
  };
}

export function openModal(name) {
  return {
    type: 'OPEN_ENTITIES_MODAL',
    name
  };
}
