import React from 'react';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiPlayCircle, mdiCheck } from '@mdi/js';
import Tooltip from '@material-ui/core/Tooltip';
import { withStyles } from '@material-ui/core/styles';

import { CircleImg, NumberLabel, StringLabel } from 'shared/components';
import { Paper } from 'shared/components/layout';

import './UsedOptions.local.scss';

export const UsedOptions = ({ servers }) => {
  function optionType(type) {
    if (type.value === 1) {
      return (
        <LightTooltip
          placement="top"
          title={
            <>
              <span style={{ fontWeight: '600' }}>{type.name}:&nbsp;</span>
              <span>Installed</span>
            </>
          }
        >
          <SvgIcon className="installed">
            <Icon path={mdiCheck} />
          </SvgIcon>
        </LightTooltip>
      );
    }

    return null;
  }

  return (
    <Paper style={{ height: 'calc(100% - 192px)', padding: '16px' }}>
      <Paper.Header style={{ alignItems: 'center', marginBottom: '24px' }}>
        <CircleImg
          image={
            <SvgIcon>
              <Icon path={mdiPlayCircle} />
            </SvgIcon>
          }
        />
        <NumberLabel number={servers[0].totalInstalledOptions} />
        <StringLabel text="Options installed" />
      </Paper.Header>
      <Paper.Content scrollable={false}>
        <div className="grid">
          <div className="column header left" />
          <div className="column header aa">AA</div>
          <div className="column header ac">AC</div>
          <div className="column header adg">ADG</div>
          <div className="column header as">AS</div>
          <div className="column header dv">DV</div>
          <div className="column header imd">IMD</div>
          <div className="column header mt">MT</div>
          <div className="column header ol">OL</div>
          <div className="column header p">P</div>
          <div className="column header rac">RAC</div>
          {servers[0].sids.map((sid, index) => {
            return (
              <div key={`${sid.sid}-${index}`} className="row">
                <div className="column left">{sid.sid}</div>
                <div className="column">
                  {optionType({ name: 'Advanced Analytics', value: sid.aa_count })}
                </div>
                <div className="column">
                  {optionType({ name: 'Advanced Compression', value: sid.ac_count })}
                </div>
                <div className="column">
                  {optionType({ name: 'Active Data Guard', value: sid.adg_count })}
                </div>
                <div className="column">
                  {optionType({ name: 'Advanced Security', value: sid.as_count })}
                </div>
                <div className="column">
                  {optionType({ name: 'Database Vault', value: sid.dv_count })}
                </div>
                <div className="column">
                  {optionType({ name: 'In-Memory Database', value: sid.in_mem_db_count })}
                </div>
                <div className="column">
                  {optionType({ name: 'Multitenant', value: sid.mt_count })}
                </div>
                <div className="column">{optionType({ name: 'OLAP', value: sid.olap_count })}</div>
                <div className="column">
                  {optionType({ name: 'Partitioning (User)', value: sid.part_count })}
                </div>
                <div className="column">
                  {optionType({ name: 'Real Application Clusters', value: sid.rac_count })}
                </div>
              </div>
            );
          })}
        </div>
      </Paper.Content>
    </Paper>
  );
};

const LightTooltip = withStyles(() => ({
  tooltip: {
    color: '#fff',
    backgroundColor: '#787E89',
    letterSpacing: '0.4px',
    fontSize: '12px',
    lineHeight: '16px',
    fontFamily: 'Open Sans'
  }
}))(Tooltip);
