import { createSelector } from 'reselect';

//
export const getCurrentUserName = createSelector(
  state => state.session,
  session => {
    /** ImmutableJs object :( */
    return session.get('currentUser').get('name');
  }
);

export const getCurrentUserDetails = createSelector(
  state => state.session,
  session => {
    return session.get('currentUser').get('details');
  }
);
