import React from 'react';
import * as PropTypes from 'prop-types';

import { FormattedMessage } from 'react-intl';
import { mdiFilter } from '@mdi/js';
import Icon from '@mdi/react';
import SvgIcon from '@material-ui/core/SvgIcon';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';
import Button from '@material-ui/core/Button';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';

import { Dropdown } from 'shared/components';
import { DayFilterEditor } from './components/day-filter-editor/day-filter-editor';
import { UserFilterEditor } from './components/user-filter-editor/user-filter-editor';
import { StatusFilterEditor } from './components/status-filter-editor/status-filter-editor';
import { FilterChip } from './components/filter-chip/filter-chip';
import { FilterLoader } from './components/filter-loader/filter-loader';

import './add-filter.local.scss';

/**
 * TODO: @Razvan, @Constantin
 *
 * Review component structure / split smaller compoenents.
 * Add support for filters array. For now linked to search-area-container.jsx
 */

function checkFilters(filters) {
  if (!filters) {
    return false;
  }

  if (filters.length) {
    return true;
  }

  return true;
}

export class AddFilter extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      /* draft: any filter value type */
    };
  }

  filterRef = React.createRef();

  render() {
    const { filters, hasSearchTerm } = this.props;
    const { draft } = this.state;
    const hasFilters = checkFilters(filters);

    return (
      <ClickAwayListener onClickAway={this.handleClickOutside}>
        <div>
          {(!draft && !hasFilters && !hasSearchTerm) && this.renderDropdown()}
          {draft && <FilterLoader type={draft.type} setFilterValue={this.saveDraftFilter} />}
          {hasFilters && <FilterChip value={filters.value} type={filters.type} save={this.saveDraftFilter} />}
        </div>
      </ClickAwayListener>
    );
  }

  handleClickOutside = () => {
    this.setState({ draft: null });
  }

  saveDraftFilter = (filter) => {
    this.props.updateFilters(filter);
    // clear draft
    this.setState({ draft: null });
  }

  loadFilter = (index, filter) => {
    this.setState({ draft: { type: filter.value, editing: true } });
  }

  mapDropdownItem = (Item) => (
    ({ ...Item, icon: <Item.icon />, name: <Item.name /> })
  )

  renderDropdown() {
    return (
      <Dropdown
        target={(meta, actions) => (
          <Button
            size="small"
            className={`add-filter qa-add-filter ${(meta.isOpen ? 'expanded' : '')}`}
            startIcon={<SvgIcon><Icon path={mdiFilter} /></SvgIcon>}
            endIcon={meta.isOpen ? <ArrowDropUpIcon /> : <ArrowDropDownIcon />}
            onClick={actions.toggle}
            ref={meta.ref}
          >
            <FormattedMessage id="dashboard.add-filter" />
          </Button>
        )}
        onSelect={this.loadFilter}
        items={[
          DayFilterEditor.TypeDescriptor,
          StatusFilterEditor.TypeDescriptor,
          UserFilterEditor.TypeDescriptor,
        ].map(this.mapDropdownItem, this)}
      />
    );
  }
}

AddFilter.propTypes = {
  filters: PropTypes.object,
  updateFilters: PropTypes.func.isRequired,
  hasSearchTerm: PropTypes.bool,
};

AddFilter.defaultProps = {
  filters: {},
  hasSearchTerm: false,
};
