import React, { forwardRef } from 'react';
import classnames from 'classnames';
import { bool } from 'prop-types';

import InputAdornment from '@material-ui/core/InputAdornment';
import MUITextField from '@material-ui/core/TextField';
import SvgIcon from '@material-ui/core/SvgIcon';

import { mdiInformationOutline } from '@mdi/js';

import { LightTooltip } from '../../LightTooltip/LightTooltip';

import './Input.local.scss';

export const Input = forwardRef(function InputFw(
  { inline, error, size, children, description, multiline, variant, ...rest },
  ref
) {
  const InputProps = { ...(rest.InputProps && rest.InputProps) };

  const nrRows = rest?.isPopup ? 9999 : 7;

  if (description) {
    InputProps.endAdornment = (
      <>
        <InputAdornment position="end">
          <LightTooltip title={description}>
            <SvgIcon>
              <path d={mdiInformationOutline} />
            </SvgIcon>
          </LightTooltip>
        </InputAdornment>
        {InputProps.endAdornment}
      </>
    );
  }

  return (
    <MUITextField
      ref={ref}
      fullWidth
      className={classnames('input-field', { inline, error })}
      variant={variant || 'outlined'}
      error={error}
      {...(multiline && {
        multiline: true,
        rows: nrRows
      })}
      {...rest}
      InputProps={InputProps} // eslint-disable-line
      size={size}
    >
      {children}
    </MUITextField>
  );
});

Input.propTypes = {
  inline: bool,
  error: bool
};
