import React, { useEffect } from 'react';
import { func, array } from 'prop-types';

import { Switch, Route, useRouteMatch } from 'react-router-dom';
import { connect } from 'react-redux';

import { Main, Center } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';

import { loadAllGroupsThunk } from './store/thunks';
import { mapSortInsensitive } from '../store/mappers';

import { SearchableList } from '../components/SearchableList/SearchableList';
import { GroupCard } from './components/GroupCard/GroupCard';

import { ConnectedGroupLoader } from './pages/GroupView/GroupView';
import { CreateNewGroupPage } from './pages/CreateNewGroup/CreateNewGroup';
import { NoGroupSelectedPage } from './pages/NoGroupSelected';

import './root.local.scss';

export const GroupsPage = ({ allGroups, loadAllGroups, isLoading }) => {
  useEffect(() => {
    loadAllGroups();
  }, []);

  function filterGroupByQuery(group, query) {
    return group.name.toLowerCase().includes(query.toLowerCase());
  }

  const match = useRouteMatch();

  const groupLink = group => {
    return `${match.url}/${group.name}`;
  };

  if (isLoading) {
    return (
      <Main>
        <Center>
          <Loader>Loading groups...</Loader>
        </Center>
      </Main>
    );
  }

  return (
    <Main>
      <section className="search-list-section">
        <SearchableList items={allGroups} filter={filterGroupByQuery}>
          {group => <GroupCard group={group} href={groupLink(group)} />}
        </SearchableList>
      </section>
      <section className="details-view-section">
        <GroupsPagesSwitch />
      </section>
    </Main>
  );
};

GroupsPage.propTypes = {
  allGroups: array,
  loadAllGroups: func
};

export const GroupsPagesSwitch = () => {
  const { path } = useRouteMatch();

  return (
    <Switch>
      <Route exact path={`${path}/create-new`}>
        <CreateNewGroupPage />
      </Route>
      <Route path={`${path}/:groupName`}>
        <ConnectedGroupLoader />
      </Route>
      <Route exact path={`${path}`}>
        <NoGroupSelectedPage />
      </Route>
    </Switch>
  );
};

export default connect(
  state => ({
    allGroups: mapSortInsensitive(state.groups.all.filter(g => g.name !== 'user')),
    isLoading: state.groups.ops.all.loading
  }),
  dispatch => ({
    loadAllGroups: () => dispatch(loadAllGroupsThunk())
  })
)(GroupsPage);
