import { RestClient } from 'shared/lib/rest';

export function getNamespace(name) {
  return RestClient.get(`/namespace/${name}`, {
    page_size: 2147483645
  });
}

export function patchNamespace(name, body) {
  return RestClient.patch(`/namespace/${name}`, body, {
    page_size: 2147483645
  });
}

export function postNamespace(body) {
  return RestClient.post('/namespace', body, {
    page_size: 2147483645
  });
}
