import React from 'react';
import { node } from 'prop-types';

import './ModalFooter.local.scss';

export const ModalFooter = ({ children }) => {
  return <footer className="modal-footer">{children}</footer>;
};

ModalFooter.propTypes = {
  children: node
};

export const LeftSideSlot = ({ children }) => {
  return <div className="slot-left">{children}</div>;
};

LeftSideSlot.propTypes = {
  children: node
};

export const RightSideSlot = ({ children }) => {
  return <div className="slot-right">{children}</div>;
};

RightSideSlot.propTypes = {
  children: node
};

ModalFooter.Left = LeftSideSlot;
ModalFooter.Right = RightSideSlot;
