import React from 'react';
import * as PropTypes from 'prop-types';
import moment from 'moment';

import { AreaChart, Area, XAxis, Tooltip, ResponsiveContainer } from 'recharts';
import { Center } from 'shared/components';

import { CustomTooltip, CustomAxisTick } from './components';

import './workload-graph.local.scss';

export class WorkloadGraphComponent extends React.PureComponent {
  render() {
    if (this.props.data.length === 0) {
      return <Center>There are no jobs available with the selected filters</Center>;
    }

    return (
      <div className={this.props.className}>
        <ResponsiveContainer>
          <AreaChart
            data={this.props.data}
            margin={{ top: 16, right: 5, left: 5, bottom: 16 }}
            onClick={this.setSingleDay}
          >
            <defs>
              <linearGradient id="gradient" x1="0" y1="0" x2="0" y2="1">
                <stop offset="0%" stopColor="#3765AF" stopOpacity={0.16} />
                <stop offset="100%" stopColor="#3765AF" stopOpacity={0} />
              </linearGradient>
            </defs>
            <defs>
              <linearGradient id="chartStroke" x1="0" y1="0" x2="1" y2="0">
                <stop offset="0%" stopColor="#3765AF" stopOpacity={0} />
                <stop offset="5%" stopColor="#3765AF" stopOpacity={1} />
                <stop offset="95%" stopColor="#3765AF" stopOpacity={1} />
                <stop offset="100%" stopColor="#3765AF" stopOpacity={0} />
              </linearGradient>
            </defs>
            <XAxis
              dataKey="name"
              tickSize={false}
              axisLine={false}
              interval="preserveStartEnd"
              tick={<CustomAxisTick />}
            />
            <Tooltip
              content={<CustomTooltip isSingleDay={this.props.isSingleDay} />}
              offset={0}
              cursor={false}
              viewBox={{ width: 750, height: 144 }}
              position={{ x: 'auto', y: 0 }}
            />
            <Area
              type="monotone"
              dataKey="total"
              stroke="url(#chartStroke)"
              strokeWidth={2}
              fillOpacity={1}
              fill="url(#gradient)"
            />
          </AreaChart>
        </ResponsiveContainer>
      </div>
    );
  }

  setSingleDay = e => {
    if (!this.props.isSingleDay && e && this.props.setSingleDay) {
      this.props.setSingleDay({
        startDate: moment(e.activeLabel),
        endDate: moment(e.activeLabel)
      });
    }
  };
}

export default WorkloadGraphComponent;

WorkloadGraphComponent.propTypes = {
  className: PropTypes.string,
  data: PropTypes.array.isRequired
};

WorkloadGraphComponent.defaultProps = {
  className: 'workload-graph'
};
