/**
 * A IFFE to ignore warnings of prop-types validation
 * from Material-UI components used with scoped-css-loader.
 *
 * They are invalid and will not appear in prod builds, they are
 * cumbersome for development for now.
 *
 * TODO: investigate if there is another option when there is time.
 * For now it looks like there is not.
 */

/* eslint-disable */
(function clearMUIDataVWarnings() {
  const error = console.error.bind(console);
  const partial = /Warning:.*data-v-/;

  console.error = (message, ...args) => {
    if (typeof message === 'string' && message.search(partial) !== -1) {
      return;
    }

    error(message, ...args);
  };
})();

/* eslint-enable */
