import React from 'react';
import { connect } from 'react-redux';
import moment from 'moment';
import { debounce } from 'lodash';
import { Link } from 'react-router-dom';

import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';

import { Header } from 'core/components/Header';
import { AddFilter } from 'dashboard/components/add-filter/add-filter';

import { SearchInput, Center } from 'shared/components';
import { Loader } from 'shared/components/loaders';
import { Main, Paper, Overflow } from 'shared/components/layout';
import { JobResults } from 'shared/components/JobResults';
import { actions } from 'shared/store/actions';
import { jobsSearchedSelector } from 'shared/store/state';

import './JobsSearchResults.local.scss';

export class JobsSearchResults extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      searchTerm: '',
      selectedFilter: null
    };
  }

  apiSearchWithDebounce = debounce(searchedTerm => {
    this.getSearchResults(searchedTerm);
  }, 500);

  componentDidMount() {
    const query = this.props.location.search.substring(1);
    const params = query.split('&');
    const searchTerm = params[0].split('=')[1];
    let selectedFilter = null;
    if (params[1]) {
      selectedFilter = {
        type: params[1].split('=')[0],
        value: params[1].split('=')[1]
      };
    }
    this.setState({ searchTerm, selectedFilter }, () => {
      this.getSearchResults(searchTerm);
    });
  }

  render() {
    const { getSearchedJobs } = this.props;
    return (
      <>
        <Header>
          <Breadcrumbs separator={<NavigateNextIcon fontSize="small" />} aria-label="breadcrumb">
            <Link to="/jobs">Jobs</Link>
            <div>Search-results</div>
          </Breadcrumbs>
        </Header>

        <Main>
          <Paper>
            <Paper.Header>
              <div className="search-area">
                <SearchInput value={this.state.searchTerm} onChange={this.handleOnChange} />
                <AddFilter
                  filters={this.state.selectedFilter}
                  updateFilters={this.updateSelectedFilter}
                />
              </div>
            </Paper.Header>
            {getSearchedJobs.get('fetching') ? (
              <Center>
                <Loader>Loading jobs</Loader>
              </Center>
            ) : (
              <Paper.Content>
                <Overflow>
                  <div className="content-area">
                    <JobResults jobs={this.props.getSearchedJobs.get('content')} />
                  </div>
                </Overflow>
              </Paper.Content>
            )}
          </Paper>
        </Main>
      </>
    );
  }

  getSearchResults = searchedTerm => {
    const QSP = {
      name_filter: searchedTerm,
      page_size: 2147483645,
      date_invoke: '2000-01-01',
      period: 36500
    };

    if (this.state.selectedFilter) {
      switch (this.state.selectedFilter.type) {
        case 'user':
          QSP.owner = this.state.selectedFilter.value;
          break;
        case 'status':
          QSP.status = this.state.selectedFilter.value.toUpperCase();
          break;
        case 'day':
          QSP.date_invoke = moment(this.state.selectedFilter.value).format('YYYY-MM-DD');
          QSP.period = 1;
          break;
        default:
          break;
      }
    }

    // date_invoke and period are required in order for the API to work
    // those values are hardcored just to make sure the call will cover all available data
    this.props.searchJobs(QSP);
  };

  handleOnChange = e => {
    const searchedTerm = e.target.value;
    this.setState({ searchTerm: searchedTerm });

    if (searchedTerm.length === 3) {
      this.getSearchResults(searchedTerm);
    } else {
      this.apiSearchWithDebounce(searchedTerm);
    }
  };

  updateSelectedFilter = filter => {
    this.setState({ selectedFilter: filter }, () => {
      this.getSearchResults(this.state.searchTerm);
    });
  };
}

export const mapStateToProps = state => ({
  getSearchedJobs: jobsSearchedSelector(state)
});

export const mapDispatchToProps = dispatch => ({
  searchJobs: data => dispatch(actions.searchJobs.dispatchLoading(data))
});

export const ConnectedJobsSearchResults = connect(
  mapStateToProps,
  mapDispatchToProps
)(JobsSearchResults);
