import { filterStatusActions } from './filter-status.action';

import { checkDaemon } from 'shared/api/daemon';

export const actions = {
  ...filterStatusActions
};

/** TODO: The Above actions should be expanded to the new structure at some point */

export function checkDeamonStatus() {
  return function daemon_thunk(dispatch) {
    dispatch({
      type: 'DAEMON_STATUS',
      promise: checkDaemon()
    });
  };
}
