import React from 'react';
import { connect } from 'react-redux';
import { func, array, bool, object } from 'prop-types';
import moment from 'moment';

import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import AddIcon from '@material-ui/icons/Add';

import { getCurrentUserDetails } from 'session/store/selectors';
import { isOnlySelectGroup } from 'entities/utils/privileges';
import { Center, RangeDatePicker } from 'shared/components';
import { Loader } from 'shared/components/loaders';
import { Main, Paper } from 'shared/components/layout';
import { globalFiltersSelector } from 'shared/store/state';
import { actions } from 'shared/store/actions';
import { Button } from 'shared/components/controls';
import { Header } from 'core/components/Header';
import { loadAllAccountsThunk, loadProtocolsThunk } from './store/thunks';
import { Treeview } from './treeview';
import { ConnectedBreadcrumbsTitle } from './containers/Breadcrumbs/Breadcrumbs';
import { ContentSwitch } from './components/ContentSwitch/ContentSwitch';

import { AddServerCard } from './containers/AddServerCard/AddServerCard';
import { AddDatabaseCard } from './containers/AddDatabaseCard/AddDatabaseCard';
import { ImportCSV } from './containers/ImportCSV/ImportCSV';

import './root.local.scss';

export class ExplorerPage extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      anchorElement: null
    };
  }

  componentDidMount() {
    const { loadProtocols, loadAllAccounts } = this.props;

    loadProtocols();
    loadAllAccounts();
  }

  render() {
    const {
      treeviewData,
      isLoading,
      storeState: { level }
    } = this.props;

    const isDisabled = isOnlySelectGroup(this.props.userDetails);
    return (
      <>
        <Header>
          <h1 className="header-title">System / Explorer</h1>
          {level === 'workload' || level === 'license' ? (
            <RangeDatePicker
              onDatesChange={this.onChangeDatesHandler}
              startDate={moment.unix(this.props.globalFilters.get('startDate'))}
              endDate={moment.unix(this.props.globalFilters.get('endDate'))}
            />
          ) : null}
        </Header>
        <Main>
          {isLoading ? (
            <Center>
              <Loader>Loading accounts...</Loader>
            </Center>
          ) : (
            <>
              <Treeview data={treeviewData} />
              <Paper style={{ width: 'calc(100% - 232px)' }}>
                <Paper.Header>
                  <h1 className="breadcrumbs-title">
                    <ConnectedBreadcrumbsTitle />
                  </h1>
                  <div className="action-container">
                    <Button
                      startIcon={<AddIcon />}
                      className="action-btn"
                      aria-controls="simple-menu"
                      aria-haspopup="true"
                      onClick={this.handleClick}
                      disabled={isDisabled}
                    >
                      Add connection
                    </Button>
                    <Menu
                      id="connections-menu"
                      keepMounted
                      anchorEl={this.state.anchorElement}
                      open={Boolean(this.state.anchorElement)}
                      onClose={this.handleClose}
                    >
                      <MenuItem onClick={this.handleClose}>
                        <AddServerCard disabled={isDisabled} />
                      </MenuItem>
                      <MenuItem onClick={this.handleClose}>
                        <AddDatabaseCard disabled={isDisabled} />
                      </MenuItem>
                      <MenuItem onClick={this.handleClose}>
                        <ImportCSV disabled={isDisabled} />
                      </MenuItem>
                    </Menu>
                  </div>
                </Paper.Header>
                <ContentSwitch activePage={level} />
              </Paper>
            </>
          )}
        </Main>
      </>
    );
  }

  handleClick = event => {
    this.setState({ anchorElement: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ anchorElement: null });
  };

  onChangeDatesHandler = range => {
    if (range.startDate && range.endDate) {
      this.props.setGlobalFilters(range);
    }
  };
}

ExplorerPage.propTypes = {
  treeviewData: array,
  loadAllAccounts: func,
  isLoading: bool,
  storeState: object,
  globalFilters: object,
  setGlobalFilters: func
};

export default connect(
  state => ({
    treeviewData: state.explorer.structured?.dbms || [],
    isLoading: state.explorer.flags.accounts.loading || state.explorer.flags.protocols.loading,
    storeState: state.explorer.state,
    globalFilters: globalFiltersSelector(state),
    userDetails: getCurrentUserDetails(state)
  }),
  dispatch => ({
    loadAllAccounts: () => dispatch(loadAllAccountsThunk()),
    loadProtocols: () => dispatch(loadProtocolsThunk()),
    setGlobalFilters: data => dispatch(actions.setGlobalFilters.dispatchLoading(data))
  })
)(ExplorerPage);
