import _ from 'lodash';

export const groupForm = (prototype, form = null) => {
  if (form === null) {
    form = {
      name: null,
      description: null,
      prototype: prototype.name,
      version: prototype.version,
      groups: [...prototype.groups],
      values: prototype.fields.map(field => {
        return { ...field, value: field.deflt };
      })
    };
  }

  if (!form.hasOwnProperty('groups')) {
    form.groups = [...prototype.groups];
    form.values = prototype.fields.map(field => {
      const value = form.values.find(value => value.position === field.position);
      return {
        ...value,
        ...field
      };
    });
  }

  if (form.hasOwnProperty('values')) {
    const maxPosition = _.max(form.values.map(value => value.position));

    // important point
    if (form.groups.length > 0) {
      // Mark any existing groups as non-virtual
      form.groups = form.groups.map(group => {
        return { ...group, virtual: false };
      });
      // Make sure groups are properly sorted
      form.groups = _.sortBy(form.groups, 'lower');
      // First we need to create any virtual groups, if needed
      let position = 0;
      while (position <= maxPosition) {
        // First, find the group of which this value is a member, if any
        const currentGroup = form.groups.find(
          group => group.lower <= position && group.upper > position
        );
        if (!!currentGroup) {
          // We already have a group for this position. Leapfrog to the next
          position = currentGroup.upper;
        } else {
          // OK, we couldn't find the current group, so find the next one
          const nextGroup = form.groups.find(group => group.lower > position);
          if (!!nextGroup) {
            // Add a new virtual group that covers this position up to the next group
            form.groups = [
              {
                name: null,
                virtual: true,
                lower: position,
                upper: nextGroup.lower,
                iteration_limit: 1
              },
              ...form.groups
            ];
            // Leapfrog
            position = nextGroup.upper;
          } else {
            // We're at the end. Add a covering virtual group
            form.groups.push({
              name: null,
              virtual: true,
              lower: position,
              upper: maxPosition + 1,
              iteration_limit: 1
            });
          }
        }
      }
    } else {
      // We have no groups at all. Create a single virtual group for all values.
      form.groups.push({
        name: null,
        virtual: true,
        lower: 0,
        upper: maxPosition + 1,
        iteration_limit: 1
      });
    }

    form.groups = _.sortBy(form.groups, 'lower').map(group => {
      group.instances = [];
      return group;
    });
    form.values = _.sortBy(form.values, 'position');
    // OK, we have the groups, now we need to add the instances and fields
    for (let group of form.groups) {
      const values = form.values.filter(
        value => value.position >= group.lower && value.position < group.upper
      );
      if (!group.virtual || group.iteration_limit > 1) {
        // How many instances do we need?
        const instanceCount = Math.max(
          1,
          _.max(values.map(value => (value.value === null ? 1 : value.value.length)))
        );
        for (let g = 0; g < instanceCount; g++) {
          let instance = {
            instance: g,
            fields: values.map(value => {
              let field = {
                name: value.name,
                description: value.description,
                meta: value.meta,
                position: value.position,
                data_type: value.data_type,
                is_repeatable: value.is_repeatable,
                is_required: value.is_required,
                values: []
              };
              if (Array.isArray(value.value) && value.value.length > g) {
                if (value.is_repeatable) {
                  for (let f = 0; f < value.value[g].length; f++) {
                    field.values.push({
                      instance: f,
                      value: value.value[g][f]
                    });
                  }
                } else {
                  field.values.push({
                    instance: 0,
                    value: value.value[g]
                  });
                }
              }
              return field;
            })
          };
          group.instances.push(instance);
        }
      } else {
        // Iteration limit is 1
        let instance = {
          instance: 0,
          fields: values.map(value => {
            let field = {
              name: value.name,
              description: value.description,
              meta: value.meta,
              position: value.position,
              data_type: value.data_type,
              is_repeatable: value.is_repeatable,
              is_required: value.is_required,
              values: []
            };
            if (field.is_repeatable) {
              if (Array.isArray(value.value)) {
                for (let f = 0; f < value.value.length; f++) {
                  field.values.push({
                    instance: f,
                    value: value.value[f]
                  });
                }
              }
            } else {
              field.values.push({
                instance: 0,
                value: value.value
              });
            }
            return field;
          })
        };
        group.instances.push(instance);
      }
    }
    delete form.values;
  }
  // For repeating groups and fields, there must always be one "empty" group and/or field at the end.
  for (let group of form.groups) {
    // if (group.iteration_limit > 1) {
    //     const instance = _.last(group.instances)
    //     const values = _.flatten(instance.fields.map(field => field.values.map(value => value.value)))
    //     // If at least one value is non-null, then we need to add a new empty group at the end.
    //     if (!_.every(values, value => value === null)) {
    //         // OK, we need to add another empty instance
    //         group.instances.push({
    //             instance: instance.instance + 1,
    //             fields: instance.fields.map(field => {
    //                 return {
    //                     ...field,
    //                     values: []
    //                 }
    //             })
    //         })
    //     }
    // }

    // Now we check the individual fields
    // Each field must have at least one value.
    // For repeating fields, there must always be a final empty instance.
    for (let instance of group.instances) {
      for (let field of instance.fields) {
        if (field.values.length === 0) {
          field.values.push({
            instance: 0,
            value: null
          });
          // } else if (field.is_repeatable) {
          //     const value = _.last(field.values)
          //     if (value.value !== null) {
          //         field.values.push({
          //             instance: value.instance + 1,
          //             value: null
          //         })
          //     }
        }
      }
    }
  }
  return form;
};

export const ungroupForm = form => {
  let packed = {
    description: form.description,
    values: []
  };
  let names = {};
  for (const group of form.groups) {
    for (const instance of group.instances) {
      for (const field of instance.fields) {
        names[field.name] = {
          position: field.position,
          grouped: !group.virtual || group.iteration_limit > 1,
          is_repeatable: field.is_repeatable
        };
      }
    }
  }
  for (const name in names) {
    const info = names[name];
    const group = form.groups.find(
      group => group.lower <= info.position && group.upper > info.position
    );
    if (info.grouped && info.is_repeatable) {
      // Array of arrays
      let values = [];
      for (const instance of group.instances) {
        let subvalues = instance.fields
          .find(field => field.name === name)
          .values.map(value => value.value);
        if (_.last(subvalues) === null) {
          subvalues = _.dropRight(subvalues, 1);
        }
        values.push(subvalues);
        while (Array.isArray(_.last(values)) && _.last(values).length === 0) {
          values = _.dropRight(values, 1);
        }
      }
      packed.values.push({ name, value: values });
    } else if (info.grouped) {
      // Just a plain array
      let values = [];
      for (const instance of group.instances) {
        let subvalues = instance.fields
          .find(field => field.name === name)
          .values.map(value => value.value);
        values.push(subvalues[0]);
      }
      while (values.length > 0 && _.last(values) === null) {
        values = _.dropRight(values, 1);
      }
      packed.values.push({ name, value: values });
    } else if (info.is_repeatable) {
      // Just a plain array
      const instance = group.instances[0];
      let values = instance.fields
        .find(field => field.name === name)
        .values.map(value => value.value);
      while (values.length > 0 && _.last(values) === null) {
        values = _.dropRight(values, 1);
      }
      packed.values.push({ name, value: values });
    } else {
      // Simple value
      const instance = group.instances[0];
      const value = instance.fields.find(field => field.name === name).values[0].value;
      packed.values.push({ name, value });
    }
  }
  packed.values = packed.values.filter(value => value.value !== null);
  return packed;
};
