import React from 'react';
import PropTypes from 'prop-types';
import { useLocation } from 'react-router-dom';
import moment from 'moment';

import { useSelector, useDispatch } from 'react-redux';

import { actions } from 'shared/store/actions';
import { globalFiltersSelector } from 'shared/store/state';

export const JobsInvocations = ({ children }) => {
  const location = useLocation();
  const query = new URLSearchParams(location.search);

  const dispatch = useDispatch();
  const getJobs = data => {
    dispatch(actions.getJobs.dispatchLoading(data));
    dispatch(actions.getConnections.dispatchLoading(data));
  };
  const globalFilters = useSelector(globalFiltersSelector);

  React.useEffect(() => {
    const days = moment
      .unix(globalFilters.get('endDate'), 'YYYY-MM-DD')
      .diff(moment.unix(globalFilters.get('startDate'), 'YYYY-MM-DD'), 'days');

    getJobs({
      date_invoke: moment.unix(globalFilters.get('startDate')).format('YYYY-MM-DD'),
      period: days + 1,
      page_size: 2147483645,
      shell_host_address_filter: query.get('shell_host_address_filter') || '',
      platform_name_filter: query.get('platform_filter_name') || '',
      platform_dbms_filter: query.get('platform_filter_dbms') || '',
      status: query.get('status') || '',
      database_name_filter: query.get('database_name_filter') || '',
      owner: query.get('user_filter') || ''
    });
  }, []);

  return children;
};
JobsInvocations.propTypes = {
  children: PropTypes.any.isRequired
};
