import React from 'react';
import { Button } from 'shared/components/controls';

import './ListOutput.local.scss';

export const ListOutput = ({ output, type, errors, monitoring }) => {
  let content;

  if (monitoring) {
    return (
      <div className="list">
        <h3>Monitor</h3>
        <p>Invocation id: {monitoring}</p>
        <Button to={`/jobs/job?invocation=${monitoring}`} color="primary">
          Go to Monitored Invocation
        </Button>
      </div>
    );
  }

  if (type === 'fail') {
    content = (
      <>
        <p className="category error">The following error(s) occurred:</p>
        <div className="list error">
          <code>
            <pre>{errors.join('\n')}</pre>
          </code>
        </div>
        {output && output.length > 0 && (
          <>
            <p className="category">Output:</p>
            <div className="list">
              <code>
                <pre>{output.map(row => row.data.text).join('')}</pre>
              </code>
            </div>
          </>
        )}
      </>
    );
  } else {
    content = (
      <div className="list">
        <code>
          <pre>{output.map(row => row.data.text).join('')}</pre>
        </code>
      </div>
    );
  }

  return content;
};
