import React, { useState, useEffect, useMemo } from 'react';
import { object, array, string, func } from 'prop-types';
import { connect } from 'react-redux';
import { produce } from 'immer';

import AddIcon from '@material-ui/icons/Add';
import GroupAddIcon from '@material-ui/icons/GroupAdd';
import GroupIcon from '@material-ui/icons/Group';
import Modal from '@material-ui/core/Modal';
import Avatar from '@material-ui/core/Avatar';

import { CONSTANTS } from 'config';
import { RestClient } from 'shared/lib/rest';

import { Button, Checkbox } from 'shared/components/controls';

import { Paper, Center } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';
import { ModalHeader, Result } from 'shared/components/overlays';
import { capitalizeFirstLetter } from 'shared/lib/capitalize-first-letter';

import { AvatarCard } from '../../../../components/AvatarCard/AvatarCard';

import { SearchableList } from '../../../../components/SearchableList/SearchableList';
import { getGroupDescription } from '../../../../store/selectors';

import './EditUserGroups.local.scss';
import { addUserToGroupsThunk, removeUserFromGroupsThunk } from '../../../../store/thunks';

export const EditUserGroups = ({ user, groups, addToGroups, flags, retrieveGroupDescription }) => {
  const [open, setOpen] = useState(false);
  const [isFormChanged, setIsFormChanged] = useState(false);

  const openModal = () => {
    setOpen(true);
  };

  const closeModal = () => {
    setOpen(false);
    setIsFormChanged(false);
  };

  if (user === 'mas') {
    return null;
  }

  /* eslint-disable jsx-a11y/click-events-have-key-events */
  return (
    <>
      <div tabIndex={0} role="button" className="edit-user-groups" onClick={openModal}>
        <AvatarCard label="Add to group" avatar={{ fallback: <AddIcon /> }} />
      </div>
      <Modal
        aria-labelledby="edit-user-groups-modal-title"
        aria-describedby="edit-user-groups-modal-description"
        open={open}
        onClose={closeModal}
        disableBackdropClick
      >
        <div
          style={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            width: 'calc(75vw)',
            height: 'calc(75vh)'
          }}
        >
          <Paper>
            <ModalHeader
              avatar={
                <Avatar className="generic-avatar" alt="edit-user-groups">
                  <GroupAddIcon />
                </Avatar>
              }
              title="Add to Group"
              desc="Please select the groups you want to add this user to"
              aria={{
                title: 'edit-user-groups-modal-title',
                desc: 'edit-user-groups-modal-description'
              }}
              onClose={closeModal}
              changedForm={isFormChanged}
            />

            <Paper.Content scrollable={false}>
              <GroupsList
                user={user}
                selected={groups}
                flags={flags}
                closeModal={closeModal}
                onAdd={(userName, selections) => {
                  return addToGroups(userName, selections);
                }}
                setIsFormChanged={setIsFormChanged}
                retrieveGroupDescription={retrieveGroupDescription}
              />
            </Paper.Content>
          </Paper>
        </div>
      </Modal>
    </>
  );
};

EditUserGroups.propTypes = {
  user: string,
  groups: array,
  addToGroups: func,
  flags: object
};

export const EditUserGroupsContainer = connect(
  state => {
    const user = selectedUser(state);

    return {
      user: user.name,
      groups: user.member_of,
      flags: state.usersManager.flags.user,
      retrieveGroupDescription: name => getGroupDescription(name, state.groups.all)
    };
  },
  dispatch => ({
    addToGroups: (user, groups) => dispatch(addUserToGroupsThunk({ user, groups })),
    removeFromGroups: (user, groups) => dispatch(removeUserFromGroupsThunk({ user, groups }))
    /** add to group thunk here */
    /** remove from group thunk here */
  })
)(EditUserGroups);

function selectedUser(state) {
  return state.usersManager.users.selected;
}

const GroupsList = ({
  user,
  selected,
  onAdd,
  closeModal,
  setIsFormChanged,
  retrieveGroupDescription
}) => {
  const [state, setState] = useState({
    selections: [],
    loading: true,
    done: false,
    all: [],
    showAll: false
  });

  const filtered = useMemo(() => {
    return state.showAll
      ? state.all
          .filter(g => g.name !== 'user')
          .sort((a, b) => a.name.toLowerCase().localeCompare(b.name.toLowerCase()))
      : state.all
          .filter(g => selected && !selected.includes(g.name) && g.name !== 'user')
          .sort((a, b) => a.name.toLowerCase().localeCompare(b.name.toLowerCase()));
  }, [state.all, state.showAll, selected]);

  useEffect(() => {
    RestClient.get(CONSTANTS.BASE.ENDPOINTS.GROUPS, { page_size: 2147483645 })
      .then(response => {
        const data = response.data.data.groups;

        setState(prev =>
          produce(prev, draft => {
            draft.all = data;
            draft.done = true;
            draft.loading = false;
          })
        );
      })
      .catch(error => {
        setState(prev =>
          produce(prev, draft => {
            draft.all = [];
            draft.error = error;
            draft.done = true;
            draft.loading = false;
          })
        );
      });
  }, []);

  if (state.loading) {
    return <Loader>Loading...</Loader>;
  }

  if (state.done && !state.all.length) {
    return <p>Could not retrieve all groups</p>;
  }

  if (state.success) {
    return (
      <>
        <Center>
          <Result type="success">
            <Result.Text>Added to selected groups succesfully!</Result.Text>
          </Result>
        </Center>

        <Paper.Footer>
          <Button
            variant="contained"
            color="primary"
            onClick={() => {
              closeModal();
            }}
          >
            Confirm
          </Button>
        </Paper.Footer>
      </>
    );
  }

  if (state.fail) {
    return (
      <>
        <Center>
          <Result type="fail">
            <Result.Text>{capitalizeFirstLetter(state.fail)}</Result.Text>
          </Result>
        </Center>

        <Paper.Footer>
          <Button
            variant="contained"
            color="primary"
            onClick={() => {
              closeModal();
            }}
          >
            Close
          </Button>
        </Paper.Footer>
      </>
    );
  }

  return (
    <>
      <Paper.Content>
        <div className="seachable-list-container">
          <SearchableList
            items={filtered}
            filter={(item, query) => item.name.toLowerCase().includes(query.toLowerCase())}
            variant="row"
            visibleScroll
          >
            {entry => {
              return (
                <div className="card-wrap" style={{ width: '178px' }}>
                  <AvatarCard
                    onClick={() => {
                      setState(prev =>
                        produce(prev, draft => {
                          const selections = new Set(prev.selections);

                          if (selections.has(entry.name)) {
                            selections.delete(entry.name);
                          } else {
                            selections.add(entry.name);
                          }

                          draft.selections = [...selections];
                        })
                      );
                      setIsFormChanged(true);
                    }}
                    disabled={state.showAll ? selected.includes(entry.name) : false}
                    selected={state.selections.includes(entry.name)}
                    label={entry.name}
                    avatar={{ fallback: <GroupIcon /> }}
                    description={retrieveGroupDescription(entry.name)}
                  />
                </div>
              );
            }}
          </SearchableList>
        </div>
      </Paper.Content>
      <Paper.Footer>
        <Paper.Footer.Left>
          <Checkbox
            checked={state.showAll}
            onInput={() => setState(prev => ({ ...prev, showAll: !prev.showAll }))}
            label="Show groups that the user is a part of"
          />
        </Paper.Footer.Left>
        <Paper.Footer.Right>
          <Button
            disabled={!state.selections.length}
            variant="contained"
            color="primary"
            onClick={() => {
              setState(prev =>
                produce(prev, draft => {
                  draft.loading = true;
                })
              );

              onAdd(user, state.selections)
                .then(response => {
                  if (typeof response === 'object' && response.type === 'fail') {
                    setState(
                      produce(state, draft => {
                        draft.fail = response.message;
                      })
                    );
                  } else {
                    setState(
                      produce(state, draft => {
                        draft.success = true;
                      })
                    );
                  }
                })
                .catch(err => {
                  setState(
                    produce(state, draft => {
                      draft.fail = err;
                    })
                  );
                });
            }}
          >
            Add
          </Button>
        </Paper.Footer.Right>
      </Paper.Footer>
    </>
  );
};

GroupsList.propTypes = {
  user: string,
  selected: array,
  onAdd: func,
  closeModal: func
};
