import React from 'react';
import {
  mdiHelpRhombusOutline,
  mdiTable,
  mdiFileTableBox,
  mdiArrangeBringToFront,
  mdiFlashCircle
} from '@mdi/js';
import SvgIcon from '@material-ui/core/SvgIcon';
import { Center } from 'shared/components';
import { Overflow } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';

import './root.local.scss';

const KPIDisplay = ({ value, suffix, colorFormatting = false }) => {
  let fill;
  if (value > 75) {
    fill = '#ED3A4A';
  } else if (value > 50) {
    fill = '#F1BD5C';
  } else {
    fill = '#34CC88';
  }
  const digits = value > 10 ? 0 : 2;
  const valueString = value.toLocaleString(undefined, {
    minimumFractionDigits: 0,
    maximumFractionDigits: digits
  });
  return (
    <span style={colorFormatting ? { color: fill } : {}}>
      {valueString}
      {suffix}
    </span>
  );
};

const NoDataComponent = ({ name }) => (
  <div className="content dashed-and-centered">
    <SvgIcon>
      <path d={mdiHelpRhombusOutline} />
    </SvgIcon>
    <p>Please select a {name} in order to view its children</p>
  </div>
);

const StorageDetailsList = ({ data, isLoading }) => {
  if (isLoading) {
    return (
      <Center>
        <Loader>Loading data...</Loader>
      </Center>
    );
  }

  const { tablespace, files } = data;

  if (!tablespace || !tablespace.ns) {
    return <NoDataComponent name="tablespace" />;
  }

  return (
    <div className="content">
      <div className="detail-row">
        <div className="ts-name">
          <SvgIcon>
            <path d={mdiFileTableBox} />
          </SvgIcon>
          {tablespace.ns}
        </div>
        <div className="details">
          <div className="block">
            <div className="top">Total:</div>
            <div>
              <KPIDisplay value={tablespace.totalGB} suffix="GB" />
            </div>
          </div>
          <div className="block">
            <div className="top">Used size:</div>
            <div>
              <KPIDisplay value={tablespace.usedGB} suffix="GB" />
            </div>
          </div>
          <div className="block">
            <div className="top">Used %:</div>
            <div>
              <KPIDisplay value={tablespace.usage} suffix="%" colorFormatting />
            </div>
          </div>
          <div className="block">
            <div className="top">Free size:</div>
            <div>
              <KPIDisplay value={tablespace.freeGB} suffix="GB" />
            </div>
          </div>
          <div className="block">
            <div className="top">Free %:</div>
            <div>
              <KPIDisplay value={tablespace.freePerc} suffix="%" />
            </div>
          </div>
        </div>
      </div>
      {files.map(({ PCT_USED, FILE_NAME, SIZE_MB, MAXSIZE_MB }, index) => {
        const filePath = FILE_NAME.split('/');
        const [fileName, extension] = filePath[filePath.length - 1].split('.');
        return (
          <div key={index} className="detail-row">
            <div className="file-name">
              <SvgIcon>
                <path d={mdiTable} />
              </SvgIcon>
              {fileName}
              <span className="extension">.{extension}</span>
            </div>
            <div className="details">
              <div className="block">
                <div className="top">Used %:</div>
                <div>
                  <KPIDisplay value={PCT_USED} suffix="%" colorFormatting />
                </div>
              </div>
              <div className="block">
                <div className="top">Size:</div>
                <div>
                  <KPIDisplay value={SIZE_MB / 1024} suffix="GB" />
                </div>
              </div>
              <div className="block">
                <div className="top">Max size:</div>
                <div>
                  <KPIDisplay value={MAXSIZE_MB / 1024} suffix="GB" />
                </div>
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export const StorageViewer = ({ data, isLoading }) => {
  return (
    <div className="viewer">
      <div className="title">
        <SvgIcon>
          <path d={mdiTable} />
        </SvgIcon>
        Tables
      </div>
      <StorageDetailsList data={data} isLoading={isLoading} />
    </div>
  );
};

export const ObjectsViewer = ({ data, isLoading }) => {
  return (
    <div className="viewer">
      <div className="title">
        <SvgIcon>
          <path d={mdiArrangeBringToFront} />
        </SvgIcon>
        Objects
      </div>
      {!data || data.length === 0 ? (
        <NoDataComponent name="object" />
      ) : (
        <div className="content">
          <Overflow>
            {!isLoading ? (
              data.map(({ OBJECT_NAME, CREATED, OBJECT_ID, LAST_DDL_TIME }) => {
                return (
                  <div key={OBJECT_ID} className="detail-row">
                    <div className="file-name">
                      <SvgIcon>
                        <path d={mdiFlashCircle} />
                      </SvgIcon>
                      {OBJECT_NAME}
                    </div>
                    <div className="details">
                      <div className="object-block">
                        <div className="top">Last DDL Time</div>
                        <div>{LAST_DDL_TIME}</div>
                      </div>
                      <div className="object-block">
                        <div className="top">Created</div>
                        <div>{CREATED}</div>
                      </div>
                      {/* Designs have some buttons here */}
                    </div>
                  </div>
                );
              })
            ) : (
              <Center>
                <Loader>Loading data...</Loader>
              </Center>
            )}
          </Overflow>
        </div>
      )}
    </div>
  );
};
