import { CONSTANTS } from 'config';
import { RestClient } from 'shared/lib/rest';

export const downloadFile = async query => {
  const formatQuery = {
    lms_repo: query.repo_working_dir,
    repo_shell: query.repo_shell
  };

  try {
    const response = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.DOWNLOAD_PACKAGING, formatQuery);
    const fileName = response.headers['content-disposition'].split('filename=')[1] || 'ct_cpuq.tar';
    const blobData = [response.data];
    const blob = new Blob(blobData, { type: 'application/octet-stream' });
    const blobURL = (window.URL ? window.URL : window.webkitURL).createObjectURL(blob);
    const tempLink = document.createElement('a');
    tempLink.style.display = 'none';
    tempLink.href = blobURL;
    tempLink.setAttribute('download', fileName);

    document.body.appendChild(tempLink);
    tempLink.click();

    document.body.removeChild(tempLink);
    window.URL.revokeObjectURL(blobURL);
  } catch (error) {
    console.log('[download] error: ', error); // eslint-disable-line
  }
};
