import React from 'react';
import { connect } from 'react-redux';
import * as PropTypes from 'prop-types';
import classnames from 'classnames';

import SvgIcon from '@material-ui/core/SvgIcon';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import Icon from '@mdi/react';
import {
  mdiProgressClock,
  mdiCheckCircleOutline,
  mdiCalendarArrowRight,
  mdiStopCircleOutline,
  mdiAlertRhombusOutline
} from '@mdi/js';

import { actions } from 'dashboard/store/actions';
import { filterStatusSelector } from 'dashboard/store/state';
import { LightTooltip } from 'shared/components/tooltip/Tooltip';

import './job-status.local.scss';

export class JobStatus extends React.PureComponent {
  render() {
    return <>{this.renderContent()}</>;
  }

  filterByStatus = status => {
    if (this.props.storeStatus === status.toUpperCase()) {
      this.props.setStatus('ALL');
    } else {
      this.props.setStatus(status.toUpperCase());
    }
  };

  renderContent() {
    let icon = null;
    let status = '';
    switch (this.props.status) {
      case 'EXECUTING':
        icon = (
          <SvgIcon>
            <Icon path={mdiProgressClock} />
          </SvgIcon>
        );
        status = 'executing';
        break;
      case 'SUCCEEDED':
        icon = (
          <SvgIcon>
            <Icon path={mdiCheckCircleOutline} />
          </SvgIcon>
        );
        status = 'succeeded';
        break;
      case 'SCHEDULED':
        icon = (
          <SvgIcon>
            <Icon path={mdiCalendarArrowRight} />
          </SvgIcon>
        );
        status = 'scheduled';
        break;
      case 'ABORTED':
        icon = (
          <SvgIcon>
            <Icon path={mdiStopCircleOutline} />
          </SvgIcon>
        );
        status = 'aborted';
        break;
      case 'FAILED':
        icon = (
          <SvgIcon>
            <Icon path={mdiAlertRhombusOutline} />
          </SvgIcon>
        );
        status = 'failed';
        break;
      default:
        // error message
        break;
    }

    if (icon) {
      return (
        <div
          className={classnames(`job-status ${status}`, {
            active: this.props.status === this.props.active
          })}
          role="presentation"
          onClick={() => this.filterByStatus(status)}
        >
          <div className="header">
            <div className="icon">{icon}</div>
            <div className="count">
              {this.props.count}
              {this.props.status === this.props.active && (
                <LightTooltip placement="right" title="Active filter">
                  <CheckCircleIcon className="active-filter" />
                </LightTooltip>
              )}
            </div>
          </div>
          <div className="status">{this.props.status}</div>
        </div>
      );
    }
    // error case
    return null;
  }
}

JobStatus.propTypes = {
  status: PropTypes.string.isRequired,
  count: PropTypes.number.isRequired,
  setStatus: PropTypes.func
};

export const mapStateToProps = state => ({
  storeStatus: filterStatusSelector(state)
});

export const mapDispatchToProps = dispatch => ({
  setStatus: status => dispatch(actions.setFilterStatus.dispatchEvent(status))
});

export default connect(mapStateToProps, mapDispatchToProps)(JobStatus);
