export function mapUsersData(payload) {
  return payload.data.users;
}

export function mapLoadedUser(payload) {
  return payload.data.users[0]; // the first returned user is the one in question
}

export function mapSortInsensitive(payload) {
  return payload.slice().sort(function sortInsensitive(a, b) {
    return a.name.toLowerCase().localeCompare(b.name.toLowerCase());
  });
}
