import React from 'react';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Paper } from 'shared/components/layout';
import { Result } from 'shared/components/overlays/Result/Result';
import { Button } from 'shared/components/controls';

import WarningIcon from '@material-ui/icons/Warning';
import SvgIcon from '@material-ui/core/SvgIcon';

import './ErrorModal.local.scss';

export const ErrorModal = ({ open, actions, message }) => {
  return (
    <Modal open={open} actions={actions}>
      <Paper
        style={{
          width: '50vw',
          height: '50vh'
        }}
      >
        <ModalHeader
          title="Warning"
          icon={
            <SvgIcon>
              <WarningIcon />
            </SvgIcon>
          }
        />
        <Paper.Content scrollable={false}>
          <Result type="fail">
            <div className="content">{message}</div>
          </Result>
        </Paper.Content>
        <Paper.Footer>
          <Paper.Footer.Right>
            <Button
              onClick={() => {
                actions.close();
              }}
            >
              OK
            </Button>
          </Paper.Footer.Right>
        </Paper.Footer>
      </Paper>
    </Modal>
  );
};
