import React from 'react';
import moment from 'moment';
import { connect } from 'react-redux';
import { object, func, array, bool } from 'prop-types';

import Button from '@material-ui/core/Button';

import { Paper } from 'shared/components/layout';
import { Center } from 'shared/components';
import { Loader } from 'shared/components/loaders';
import { globalFiltersSelector } from 'shared/store/state';
import { changeStateThunk, loadAllLicensesThunk } from '../../store/thunks';
import { NavigationTab } from '../../components/NavigationTab/NavigationTab';
import { LicenseList } from './LicenseList/LicenseList';
import { ConnectedReport } from './Report/Report';

import './License.local.scss';

export class License extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      selectedLicenses: [],
      singleLicense: null,
      page: 1 // 1 = licenses list, 2 = license report
    };
  }

  componentDidMount() {
    this.getLicenseData();
  }

  componentDidUpdate(prevProps) {
    if (this.props.globalFilters !== prevProps.globalFilters) {
      this.getLicenseData();
    }
  }

  render() {
    const {
      storeState: { level, path },
      licenseList,
      isLoading
    } = this.props;
    const { selectedLicenses, page, singleLicense } = this.state;
    const [protocol] = path.split('--');
    return (
      <>
        <Paper.Header>
          <NavigationTab active={level} onClick={this.changeTab} protocol={protocol} />
        </Paper.Header>
        {page === 1 &&
          (isLoading ? (
            <Paper.Content>
              <Center>
                <Loader>Loading reports</Loader>
              </Center>
            </Paper.Content>
          ) : (
            <>
              <Paper.Content>
                <LicenseList
                  data={licenseList}
                  selectedLicenses={selectedLicenses}
                  setSelectedLicensesCallback={this.setSelectedLicenses}
                  compareWithLatestCallback={this.compareWithLatest}
                  licenseReportCallback={this.licenseReport}
                />
              </Paper.Content>
              {licenseList.length > 0 && (
                <Paper.Footer>
                  <Button
                    className="compare-btn"
                    disabled={selectedLicenses.length !== 2}
                    variant="contained"
                    onClick={() => this.compareReports()}
                    data-qa="compare-reports"
                  >
                    Compare
                  </Button>
                </Paper.Footer>
              )}
            </>
          ))}

        {page === 2 && (
          <>
            <Paper.Content>
              <ConnectedReport licenses={selectedLicenses} license={singleLicense} />
            </Paper.Content>
            <Paper.Footer>
              <Paper.Footer.Left>
                <Button
                  data-qa="back-to-licenses"
                  className="back-btn"
                  onClick={() => this.showLicenseList()}
                >
                  Back
                </Button>
              </Paper.Footer.Left>
            </Paper.Footer>
          </>
        )}
      </>
    );
  }

  changeTab = newTab => {
    this.props.changeState({ level: newTab });
  };

  getLicenseData = () => {
    const {
      storeState: { path },
      globalFilters
    } = this.props;
    const ancestors = path.split('--').filter(e => e);
    const days = moment
      .unix(globalFilters.get('endDate'), 'YYYY-MM-DD')
      .diff(moment.unix(globalFilters.get('startDate'), 'YYYY-MM-DD'), 'days');
    const payload = {
      date_invoke: moment.unix(globalFilters.get('startDate')).format('YYYY-MM-DD'),
      period: days + 1,
      page_size: 2147483645,
      status: 'SUCCEEDED',
      name_filter: 'vendita.oracle.rdbms.util.monitor.licensed_feature_usage.full_report',
      protocol_filter: ancestors[0],
      shell_host_address_filter: ancestors[1],
      database_name_filter: ancestors[2]
    };
    this.props.loadAllLicenses(payload);
  };

  setSelectedLicenses = license => {
    const { selectedLicenses } = this.state;
    // check if license was already selected
    const exists = selectedLicenses.findIndex(e => e.uuid === license.uuid);

    // if exists, remove it from array
    if (exists > -1) {
      const newSelectedLicenses = [...selectedLicenses];
      newSelectedLicenses.splice(exists, 1);
      this.setState({ selectedLicenses: newSelectedLicenses });
    } else if (selectedLicenses.length < 2) {
      // if there are less than two elements, add it
      this.setState({ selectedLicenses: [...selectedLicenses, license] });
    }
  };

  compareReports = () => {
    this.setState({ page: 2, singleLicense: null });
  };

  compareWithLatest = (latestReport, selectedReport) => {
    const comparable = [];
    comparable.push(latestReport, selectedReport);
    this.setState({ selectedLicenses: comparable, page: 2, singleLicense: null });
  };

  showLicenseList = () => {
    this.setState({ page: 1, selectedLicenses: [], singleLicense: null });
  };

  licenseReport = license => {
    this.setState({ singleLicense: license, page: 2 });
  };
}

License.propTypes = {
  storeState: object,
  globalFilters: object,
  changeState: func,
  loadAllLicenses: func,
  licenseList: array,
  isLoading: bool
};

export const ConnectedLicense = connect(
  state => ({
    storeState: state.explorer.state,
    globalFilters: globalFiltersSelector(state),
    licenseList: state.explorer.licenses,
    isLoading: state.explorer.flags.license.loading
  }),
  dispatch => ({
    loadAllLicenses: payload => dispatch(loadAllLicensesThunk(payload)),
    changeState: newState => dispatch(changeStateThunk(newState))
  })
)(License);
