export const RSAKey = props => {
  return <Text multiline {...props} />;
};

export const FQName = ({ recieved, ...props }) => {
  const { values } = useFormikContext();

  const fqname = recieved?.fqname || recieved?.name || null;
  let name = values[`fqname--${props?.name}`];
  let namespace = values['fqname--namespace'];

  // use the value received from the invocation
  if (fqname && name === undefined) {
    const parts = fqname.split('.');

    if (parts.length === 1) {
      [name] = parts;
    }

    if (parts.length > 1) {
      namespace = _.initial(parts).join('.');
      name = _.last(parts);
    }
  }

  // use the value that was manually typed
  if (name !== undefined && name.slice(-1) !== '.') {
    const parts = name.split('.');

    if (parts.length === 1) {
      [name] = parts;
    }

    if (parts.length > 1) {
      namespace = _.initial(parts).join('.');
      name = _.last(parts);
    }
  }

  return (
    <>
      <Text
        style={{ marginBottom: '30px' }}
        value={name}
        description="The relative or fully-qualified name of the process to be created."
        required
        name={`fqname--${props?.name}`}
        label="name"
        disabled={props.disabled}
      />
      <NamespaceSelect
        name="fqname--namespace"
        label="Parent namespace"
        description="namespace (leave blank for relative name)"
        value={namespace}
        disabled={props.disabled}
        type="fqname"
      />
    </>
  );
};

export function QualifiedName(props) {
  const validations = useMemo(() => check.merge(check.masName, props.required ?? check.required), [
    props.required
  ]);

  return <Text validate={validations} {...props} type="text" />;
}

/**
 * TODO:
 * These should be retireved through a connection
 * Platforms are protocols associated for accounts
 * For now they can be hardcoded
 */
export const AccountSelect = ({ protocols = [], ...props }) => {
  const [showUrls, setShowUrls] = useState(false);
  const boxName = `${props.name}-show-urls`;

  const showToggle = useCallback(e => {
    setShowUrls(e.target.checked);
  }, []);

  return (
    <Ensure type="account" inlineLoader>
      {({ data: accounts }) => {
        let list = accounts;

        if (protocols && protocols.length) {
          list = accounts.filter(account => protocols.includes(account.protocol));
        }

        const options = list.map(account => ({
          label: showUrls ? formatAccount(account) : account.name,
          value: account.name
        }));

        /**
          TODO: filter by protocol
         */
        if (protocols.length) {
          accounts.filter(Boolean);
        }

        return (
          <FormGroup>
            <Text {...props} options={options} />
            <Checkbox
              size="small"
              id={boxName}
              name={boxName}
              onChange={showToggle}
              disabled={props.disabled}
              label="Show Account URLs"
            />
          </FormGroup>
        );
      }}
    </Ensure>
  );
};

export const PLATFORMS = [
  { label: 'All', value: 'All', tester: /^./ },
  { label: 'MariaDB', value: 'MariaDB', tester: /mariadb\./ },
  { label: 'MySQL', value: 'MySQL', tester: /mysql\./ },
  { label: 'Oracle', value: 'Oracle', tester: /oracle\./ },
  { label: 'PostgreSQL', value: 'PostgreSQL', tester: /pgsql\./ },
  {
    label: 'Microsoft SQL Server',
    value: 'Microsoft SQL Server',
    tester: /microsoft\.sqlserver\./
  },
  { label: 'Oracle RDS', value: 'Oracle RDS', tester: /.rds\.oracle\./ }
];

export const PlatformSelect = props => {
  const { values } = useFormikContext();

  let platformValue = values?.platform ?? 'All';
  const processValue = values?.process ?? '';

  if (processValue) {
    const availablePlatforms = PLATFORMS.slice(1).reverse();
    platformValue =
      availablePlatforms.find(platform => platform.tester.test(processValue))?.value ?? 'All';
  }

  return <Text type="text" value={platformValue} name="platform" {...props} options={PLATFORMS} />;
};

export const ProcessSelect = props => {
  const { values } = useFormikContext();
  const platformValue = values?.platform ?? '';

  let platform = PLATFORMS[0];

  if (platformValue) {
    platform = PLATFORMS.find(p => p.value === platformValue);
  }

  return (
    <Ensure type="namespace" inlineLoader>
      <Ensure type="process" inlineLoader>
        {({ data }) => {
          let processes = data;

          if (platform && platform.value !== 'all') {
            processes = data.filter(process => platform.tester.test(process.name));
          }

          /** TODO, CHECK */
          processes = processes.filter(process => {
            if (processes.meta && process.meta.appearance) {
              return process.meta.appearance !== 'Hidden';
            }

            return true;
          });

          // display only processes that have the is_executable === true
          // the others need to be compiled first
          const OPTIONS = processes
            .filter(p => p.is_executable)
            .map(item => ({
              value: item.name,
              label: item.name,
              description: item.description,
              disabled: item.meta && item.meta.appearance === 'Greyed'
            }));

          return <Text type="text" defaultValue="" name="process" {...props} options={OPTIONS} />;
        }}
      </Ensure>
    </Ensure>
  );
};

export const NamespaceSelect = ({ value, ...props }) => {
  const { values } = useFormikContext();
  const defaultValue = values.namespace || '';
  let customVal = value;

  return (
    <Ensure type="namespace" pipe>
      {({ status, data }) => {
        if (status === 'fetching' || !data) {
          return <ParameterLoader>Loading namespaces...</ParameterLoader>;
        }

        if (status === 'failed') {
          return <ParameterLoader>Failed to load namespaces.</ParameterLoader>;
        }

        let namespaces = data;

        namespaces = namespaces.filter(namespace => {
          if (namespace.meta && namespace.meta.appearance) {
            return namespace.meta.appearance !== 'Hidden';
          }

          return true;
        });

        const OPTIONS = namespaces.map(item => ({
          value: item.name,
          label: item.name,
          disabled: item.meta && item.meta.appearance === 'Greyed'
        }));

        if (props?.type === 'fqname') {
          customVal = OPTIONS.find(o => o.value === value)?.value || '';
        }

        return (
          <Text
            type="text"
            defaultValue={defaultValue}
            name="namespace"
            value={customVal}
            {...props}
            options={OPTIONS}
          />
        );
      }}
    </Ensure>
  );
};

export const FormPrototypeSelect = props => {
  return (
    <Ensure type="prototype" inlineLoader>
      {({ data: prototypes }) => {
        const list = prototypes || [];

        const options = list.map(prototype => ({
          label: prototype.name,
          value: prototype.name
        }));

        return <Text fullWidth {...props} description="" options={options} />;
      }}
    </Ensure>
  );
};

export const ModeSelect = props => {
  const { values } = useFormikContext();
  const defaultValue = values.mode || '';

  return (
    <Ensure type="type" pipe>
      {({ status, data }) => {
        if (status === 'fetching' || !data) {
          return <ParameterLoader>Loading type...</ParameterLoader>;
        }

        if (status === 'failed') {
          return <ParameterLoader>Failed to load types.</ParameterLoader>;
        }

        const modes = data.filter(type => type.name === 'mas0300.sys.account.privilege_mode')[0];

        if (modes.meta && modes.meta.appearance === 'Hidden') {
          return null;
        }

        const OPTIONS =
          modes.enumerations &&
          modes.enumerations.map(item => ({
            value: item.label,
            label: item.label,
            disabled: item.meta && item.meta.appearance === 'Greyed'
          }));

        return (
          <Text
            type="text"
            defaultValue={defaultValue}
            disabled={modes.meta && modes.meta.appearance === 'Greyed'}
            name="process"
            {...props}
            options={OPTIONS}
          />
        );
      }}
    </Ensure>
  );
};

function variadicListReducer(list, action) {
  switch (action.type) {
    case 'add_new_parameter':
      return produce(list, draft => {
        draft.push({ name: '', type: '', deflt: undefined, description: null, value: '' });
      });

    case 'remove_parameter':
      return produce(list, draft => {
        draft.pop();
      });

    default:
      return list;
  }
}

function useVariadicList(recieved) {
  const [list, dispatch] = useReducer(variadicListReducer, recieved?.replvars || []);

  return { list, dispatch };
}

export const Variadic = ({ recieved, disabled }) => {
  const { values } = useFormikContext();
  const displayDeftlDesc = selectedProcess !== 'vendita.oracle.rdbms.cli.sqlplus.execute';

  const { list, dispatch } = useVariadicList(recieved);

  const selectedProcess = values.process;

  return (
    <Section>
      <Section.Header>
        <Section.Title>replvars</Section.Title>
      </Section.Header>
      <Section.Content>
        <Ensure type="type">
          {({ data: datatypes }) => {
            const options = datatypes
              .filter(dataType => {
                if (
                  selectedProcess === 'vendita.oracle.rdbms.cli.sqlplus.script' ||
                  selectedProcess === 'mas0300.os.svc.db.data.sql.query_import' ||
                  selectedProcess === 'vendita.oracle.rdbms.cli.sqlplus.execute' ||
                  selectedProcess === 'dbatools.oracle.run.import_sqlplus_query'
                ) {
                  return dataType.name === 'string' || dataType.name === 'integer';
                }
                return true;
              })
              .map(dataType => {
                return {
                  label: dataType.name,
                  value: dataType.name
                };
              });

            return (
              <>
                <div>
                  {list.map((item, index) => (
                    <React.Fragment key={index}>
                      <VariadicInstance
                        data={item}
                        index={index}
                        defltDesc={displayDeftlDesc}
                        types={options}
                        disabled={disabled}
                      />
                    </React.Fragment>
                  ))}
                </div>
                <div
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'flex-end'
                  }}
                >
                  {list.length > 0 && (
                    <Button
                      color="secondary"
                      onClick={() => {
                        dispatch({ type: 'remove_parameter' });
                      }}
                      disabled={disabled}
                    >
                      Remove
                    </Button>
                  )}
                  <Button
                    onClick={() => {
                      dispatch({ type: 'add_new_parameter' });
                    }}
                    disabled={disabled}
                  >
                    Add
                  </Button>
                </div>
              </>
            );
          }}
        </Ensure>
      </Section.Content>
    </Section>
  );
};

function VariadicInstance({ data = {}, index, defltDesc, types, disabled }) {
  const name_base = `replvar--${index}`;

  let parsedDeflt = data.deflt;

  if (data.deflt) {
    parsedDeflt = data.deflt.replace("str('\"'", '').replace("'\"')", '');
  }

  return (
    <>
      <Text
        style={{ marginBottom: '15px' }}
        label="Name"
        type="text"
        name={`${name_base}--name`}
        value={data.name}
        disabled={disabled}
        validate={check.merge(check.firstCharLetter, check.masName)}
        required
      />
      <Text
        style={{ marginBottom: '15px' }}
        label="Type"
        type="text"
        name={`${name_base}--type`}
        options={types}
        value={data.type}
        disabled={disabled}
        required
      />
      {defltDesc && (
        <>
          <Text
            style={{ marginBottom: '15px' }}
            label="Deflt"
            type="text"
            name={`${name_base}--deflt`}
            value={parsedDeflt}
            disabled={disabled}
            validate={check.firstCharLetter}
          />
          <Text
            style={{ marginBottom: '30px' }}
            label="Description"
            type="text"
            name={`${name_base}--description`}
            value={data.description}
            defaultValue={null}
            disabled={disabled}
            validate={check.firstCharLetter}
          />
        </>
      )}
      {!defltDesc && (
        <Text
          style={{ marginBottom: '15px' }}
          label="Value"
          type="text"
          name={`${name_base}--value`}
          value={data.value}
          disabled={disabled}
        />
      )}
    </>
  );
}

export const Multiline = props => {
  const [open, setOpen] = useState(false);
  const { values } = useFormikContext();
  const value = values[props.name];

  return (
    <>
      <Text
        InputProps={{
          startAdornment: (
            <InputAdornment>
              <LightTooltip title="Expand">
                <SvgIcon
                  style={{ cursor: 'pointer' }}
                  onClick={() => {
                    setOpen(true);
                  }}
                >
                  <path d={mdiArrowExpand} />
                </SvgIcon>
              </LightTooltip>
            </InputAdornment>
          )
        }}
        value={value}
        isPopup={open}
        multiline
        {...props}
      />
      <Modal open={open}>
        <Paper
          style={{
            width: '75vw',
            height: '75vh'
          }}
        >
          <Paper.Content>
            <Text value={value} multiline isPopup={open} variant="standard" {...props} />
          </Paper.Content>
          <Paper.Footer>
            <Paper.Footer.Right>
              <Button onClick={() => setOpen(false)}>Close</Button>
            </Paper.Footer.Right>
          </Paper.Footer>
        </Paper>
      </Modal>
    </>
  );
};

export const Replvalues = ({ recieved, disabled, ...props }) => {
  const { values } = useFormikContext();
  const keys = Object.keys(values);
  const replvars = keys.filter(key => key.startsWith('replvar'));
  const valuesNr = replvars.length / 4;
  const replvaluesParams = [];

  if (valuesNr > 0) {
    for (let i = 0; i < valuesNr; i++) {
      const type =
        values[`replvar--${i}--type`] === 'integer' ? 'number' : values[`replvar--${i}--type`];
      const label = values[`replvar--${i}--name`];
      const name = `replvalue--${i}`;
      const value =
        recieved?.replvalues && recieved.replvalues[values[`replvar--${i}--name`]]
          ? recieved.replvalues[values[`replvar--${i}--name`]]
          : values[`replvalue--${i}`];
      replvaluesParams.push({ type, label, name, value });
    }
  }

  if (replvaluesParams.length > 0) {
    return (
      <>
        <Section>
          <Section.Header>
            <Section.Title>replvalues</Section.Title>
          </Section.Header>
        </Section>
        {replvaluesParams.map((repl, index) => {
          return (
            <Text
              key={index}
              style={{ marginBottom: '15px' }}
              label={repl.label}
              type={repl.type}
              name={repl.name}
              value={repl.value}
              disabled={disabled}
              description={props.description}
            />
          );
        })}
      </>
    );
  }

  return (
    <Section>
      <Section.Header>
        <Section.Title>replvalues</Section.Title>
      </Section.Header>
    </Section>
  );
};

export const Uuid = props => {
  return <Text validate={check.uuid} {...props} />;
};
