import * as Yup from 'yup';

export const mergeValidations = (...callbacks) => value => {
  let error;

  callbacks.forEach(provided => {
    if (provided && typeof provided === 'function') {
      error = provided(value);
    }
  });

  return error;
};

export const checkRequired = async value => {
  try {
    await Yup.string()
      .nullable()
      .required('Field is required')
      .validate(value);
  } catch (e) {
    return e.message;
  }
};

export const checkRequiredNotNullable = async value => {
  try {
    await Yup.string()
      .required('Field is required')
      .validate(value);
  } catch (e) {
    return e.message;
  }
};

export const checkEmail = value => {
  try {
    Yup.string()
      .nullable()
      .email('Invalid email format.')
      .validateSync(value);
  } catch (e) {
    return e.message;
  }
};

export const checkPhoneNumber = async value => {
  try {
    await Yup.string()
      .nullable()
      .matches(/^(?:\+?1)? *(?:\( *[0-9]{3} *\)|[0-9]{3}(?: *-|)) *[0-9]{3} *- *[0-9]{4}$/, {
        excludeEmptyString: true,
        message: 'A valid phone number of form xxx-xxx-xxxx is required.'
      })
      .validate(value);
  } catch (e) {
    return e.message;
  }
};

export const checkUnsignedInt = async value => {
  try {
    await Yup.string()
      .nullable()
      .matches(/^[-]!$/, {
        excludeEmptyString: true,
        message: 'The parameter must be unsigned.'
      })
      .validate(value);
  } catch (e) {
    return e.message;
  }
};

export const checkName = value => {
  if (!/^(?:[a-z_][a-z0-9_\\.]*)$/i.test(value) || (value && value.substr(-1) === '.')) {
    return 'A valid MAS relative name is required.';
  }
};

export const checkFirstLetter = async value => {
  try {
    await Yup.string()
      .nullable()
      .matches(/^[a-zA-Z].*/, {
        excludeEmptyString: true,
        message: 'First character must be a letter'
      })
      .validate(value);
  } catch (e) {
    return e.message;
  }
};

export const checkUuid = value => {
  if (value) {
    if (!/^[a-fA-F0-9-]+$/.test(value)) {
      return 'Only hexadecimal values (0-9, a-f, A-F) and hyphens ("-") are allowed';
    }
    const copy = value.replace(/-/g, '');
    if (copy.length !== 32) {
      return 'A string of 32 hexadecimal with hyphens ("-") is required (ex: xxxxxxxx-xxxx-xxxx-xxxx-xxxxxxxxxxxx)';
    }
  }
};

export const noop = () => {};

export default {
  merge: mergeValidations,
  required: checkRequired,
  required_not_null: checkRequiredNotNullable,
  email: checkEmail,
  phone: checkPhoneNumber,
  unsigned_int: checkUnsignedInt,
  unsigned_float: noop,
  signed_float: noop,
  masName: checkName,
  firstCharLetter: checkFirstLetter,
  uuid: checkUuid
};
