import React from 'react';
import { connect } from 'react-redux';
import _ from 'lodash';
import { Formik, Form } from 'formik';
// import moment from 'moment';

import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import InputAdornment from '@material-ui/core/InputAdornment';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiPlaySpeed, mdiHelpCircleOutline, mdiCubeScan } from '@mdi/js';
import Tooltip from '@material-ui/core/Tooltip';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';

import { Loader } from 'shared/components/loaders';
import { Center } from 'shared/components';
import { Paper } from 'shared/components/layout';
import { getCurrentUserDetails } from 'session/store/selectors';
import { isOnlySelectGroup } from 'entities/utils/privileges';
import { fetchEntity } from 'entities/actions';
import { loadProcessDetailsThunk } from 'explorer/store/thunks';
import normalizeParameter from '../../utils/normalizeParameter';
import { getInitialValues, formatParameters } from '../../utils/formikFormatter';
import { RestClient } from 'shared/lib/rest';
import { CONSTANTS } from 'config';

import { TextField as Input } from '../../controls/TextField/TextField';
import { DropdownField } from '../../controls/DropdownField/DropdownField';
import { OutputModal } from '../../components/OutputModal/OutputModal';

import './VMware.local.scss';

const processName = 'vendita.oracle.rdbms.util.lms.vsphere_scan';

export class VMware extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      invocation: undefined,
      outputOpen: false,
      output: undefined
    };
  }

  poller = undefined;

  async componentDidMount() {
    const { request, entitiesStatus, requestProcessData, process } = this.props;

    if (entitiesStatus.prototype === 'not-fetched' || entitiesStatus.prototype === 'failed') {
      request('prototype');
    }

    if (entitiesStatus.type === 'not-fetched' || entitiesStatus.type === 'failed') {
      request('type');
    }

    if (entitiesStatus.account === 'not-fetched' || entitiesStatus.account === 'failed') {
      request('account');
    }

    if (!process) {
      requestProcessData({ name: 'vmware', process: processName });
    }
  }

  componentWillUnmount() {
    // to conclude the polling,
    clearTimeout(this.poller);
    this.poller = undefined;
  }

  render() {
    const isDisabled = isOnlySelectGroup(this.props.userDetails);
    const { entitiesStatus, datatypes, prototypes, isProcessLoading, process } = this.props;
    let parameters = null;

    const loadingPrototypes = entitiesStatus.prototype === 'fetching';
    const loadingDatatypes = entitiesStatus.type === 'fetching';
    const loadingAccounts = entitiesStatus.account === 'fetching';

    if (process) {
      parameters = process.parameters.map(p => normalizeParameter(p, datatypes, prototypes));
      parameters = _.sortBy(parameters, 'position');
    }

    if (
      !process &&
      !isProcessLoading &&
      !loadingDatatypes &&
      !loadingPrototypes &&
      !loadingAccounts
    ) {
      return <Center>{`There is a problem with ${processName} proces`}</Center>;
    }
    return isProcessLoading || loadingPrototypes || loadingDatatypes || loadingAccounts ? (
      <Center>
        <Loader>Loading {processName} information</Loader>
      </Center>
    ) : (
      <>
        <Formik
          initialValues={getInitialValues(parameters)}
          onSubmit={async values => {
            try {
              const payload = {
                process: processName,
                parameters: formatParameters(values),
                timestamp: null
              };

              const response = await RestClient.post(CONSTANTS.BASE.ENDPOINTS.JOBS, payload);
              if (
                response.data.data &&
                response.data.data.invocations &&
                response.data.data.invocations[0]
              ) {
                this.setState(
                  {
                    invocation: response.data.data.invocations[0],
                    outputOpen: true,
                    output: undefined
                  },
                  () => {
                    this.runPoller();
                  }
                );
              }
            } catch (error) {
              console.log('[License collection: VMware] submit form error: ', error); // eslint-disable-line
            }
          }}
        >
          {({ dirty, isSubmitting }) => {
            return (
              <Form className="vmware-form">
                <Paper.Content>
                  <div className="info">
                    <h1>Job</h1>
                    <p>vSphere for Oracle audit</p>
                  </div>
                  <FormControl className="field full-width" variant="outlined">
                    <InputLabel htmlFor="outlined-adornment-process">Process</InputLabel>
                    <OutlinedInput
                      id="outlined-adornment-process"
                      type="text"
                      readOnly
                      disabled
                      value={processName}
                      labelWidth={70}
                      startAdornment={
                        <InputAdornment position="start">
                          <SvgIcon>
                            <Icon path={mdiPlaySpeed} />
                          </SvgIcon>
                        </InputAdornment>
                      }
                      endAdornment={
                        <LightTooltip placement="bottom-end" title={process.description}>
                          <InputAdornment position="end">
                            <SvgIcon>
                              <Icon path={mdiHelpCircleOutline} />
                            </SvgIcon>
                          </InputAdornment>
                        </LightTooltip>
                      }
                    />
                  </FormControl>
                  <div className="info">
                    <h1>Job Parameters</h1>
                    <p>SSH connection to repo server, working directory in the repo host</p>
                  </div>
                  <div className="form-fields-container">
                    {parameters.map(parameter => {
                      return (
                        <div key={parameter.name} className="form-field">
                          {this.chooseField(parameter, isDisabled)}
                        </div>
                      );
                    })}
                  </div>
                </Paper.Content>
                <Paper.Footer>
                  <Button
                    className="btn output"
                    disabled={!this.state.invocation}
                    data-qa="view-output"
                    onClick={() => this.setState({ outputOpen: true })}
                  >
                    View Output
                  </Button>
                  <Button
                    className="btn submit"
                    type="submit"
                    disabled={!dirty || isSubmitting}
                    variant="contained"
                    data-qa="submit"
                  >
                    Submit
                  </Button>
                </Paper.Footer>
              </Form>
            );
          }}
        </Formik>
        {this.renderModal({
          title: 'VMware Data Requirements Collection',
          icon: (
            <SvgIcon>
              <Icon path={mdiCubeScan} />
            </SvgIcon>
          )
        })}
      </>
    );
  }

  runPoller = () => {
    const { uuid, status, date_invoke } = this.state.invocation;

    if (uuid === null) return;
    if (typeof status !== 'string') return;
    if (typeof date_invoke !== 'string') return;

    // const date = moment(date_invoke);
    // // If 'now' is later, interval is positive. If it's earlier, it's negative
    // let interval = date.diff(moment());

    // switch (status) {
    //   case 'SCHEDULED':
    //   case 'DELAYED':
    //     // interval = interval < 2500 ? 2500 : interval;
    //     // break;
    //   case 'EXECUTING':
    //     // If we're executing, interval should in theory be negative because
    //     // it was scheduled in the past.
    //     interval = Math.abs(interval);
    //     interval = interval > 10000 ? 10000 : 2500;
    //     break;
    //   default:
    //     break;
    // }
    // based on invocation, call and replace invocation object
    // add output in case request invocation.status === "FAILED" || "SUCCEEDED"

    // set timer ticket
    this.poller = setTimeout(this.pollInvocation(uuid), 3000);
  };

  pollInvocation = uuid => async () => {
    let { invocation } = this.state;

    if (!invocation || invocation.uuid !== uuid) return;

    try {
      const response = await RestClient.get(`${CONSTANTS.BASE.ENDPOINTS.JOBS}/${uuid}`);

      if (
        response.data.data &&
        response.data.data.invocations &&
        response.data.data.invocations[0]
      ) {
        invocation = response.data.data.invocations[0]; // eslint-disable-line

        if (invocation.status === 'SUCCEEDED' || invocation.status === 'FAILED') {
          const outputReponse = await RestClient.get(
            `${CONSTANTS.BASE.ENDPOINTS.JOBS}/${uuid}/display`,
            { page_size: 2147483645 }
          );
          if (
            outputReponse.data.data &&
            outputReponse.data.data.outputs &&
            outputReponse.data.data.outputs.length > 0
          ) {
            this.setState({ output: outputReponse.data.data.outputs, invocation });
            // to conclude the polling,
            clearTimeout(this.poller);
            this.poller = undefined;
          } else {
            this.setState({ invocation });
            clearTimeout(this.poller);
            this.poller = undefined;
          }
        } else {
          this.setState({ invocation }, () => this.runPoller());
        }
      }
    } catch (error) {
      console.log('[pollInvocation] error: ', error); // eslint-disable-line
    }
  };

  renderModal = modalDetails => {
    if (!this.state.invocation) return null;

    const actions = {
      open: () => {
        this.setState({ outputOpen: true });
      },
      close: () => {
        this.setState({ outputOpen: false });
      }
    };

    return (
      <OutputModal
        open={this.state.outputOpen}
        actions={actions}
        output={this.state.output}
        invocation={this.state.invocation}
        modalDetails={modalDetails}
        processName={processName}
        messages={['Preparing job...', 'Executing job...', 'Loading job output...']}
      />
    );
  };

  chooseField(parameter, disabled) {
    if (parameter.data_type === 'string') {
      return <Input parameter={parameter} disabled={disabled} />;
    }

    return (
      <DropdownField
        parameter={parameter}
        outputOpen
        datatypes={this.props.datatypes}
        rawAccounts={this.props.rawAccounts}
        disabled={disabled}
      />
    );
  }
}

export const ConnectedVMware = connect(
  state => ({
    prototypes: state.entities.data?.prototype?.data?.data?.prototypes || [],
    datatypes: state.entities.data?.type?.data?.data?.data_types || [],
    rawAccounts: state.entities.data?.account?.data?.data?.accounts || [],
    userDetails: getCurrentUserDetails(state),
    entitiesStatus: state.entities.fetch_status,
    process: state.explorer.collectionProcesses.vmware,
    isProcessLoading: state.explorer.flags.collectionProcesses.vmware.loading
  }),
  dispatch => ({
    requestProcessData: data => dispatch(loadProcessDetailsThunk(data)),
    request: data => dispatch(fetchEntity(data))
  })
)(VMware);

const LightTooltip = withStyles(() => ({
  tooltip: {
    color: '#fff',
    backgroundColor: '#787E89',
    letterSpacing: '0.4px',
    fontSize: '12px',
    lineHeight: '16px',
    fontFamily: 'Open Sans'
  }
}))(Tooltip);
