import React, { useReducer, createContext } from 'react';

import _ from 'lodash';

import { produce } from 'immer';

import { Formik, Form } from 'formik';
import { SubmitButton } from 'shared/components/form/SubmitButton';
import { Paper, Section, Row, Column } from 'shared/components/layout';

import { Text } from 'shared/components/form/editors/Generic';
import { QualifiedName, FormPrototypeSelect } from 'shared/components/form/editors/Custom';

import { space_summon } from 'shared/components/form/parameters/normalizeParameter';

import { FormParameters } from './FormParameters';

import { groupForm, ungroupForm } from './utils';

import { createForm, updateForm } from 'shared/api/form';

const groupsReducer = (state, action) => {
  switch (action.type) {
    case 'ADD_GROUP_INSTANCE': {
      return produce(state, draft => {
        draft.groups[action.pointers.group].instances.push(action.instance);
      });
    }

    case 'REMOVE_GROUP_INSTANCE': {
      const { pointers } = action;

      return produce(state, draft => {
        draft.groups[pointers.group].instances.splice(pointers.instance, 1);
      });
    }
    case 'ADD_FIELD_VALUE': {
      const { pointers } = action;

      return produce(state, draft => {
        const field =
          draft.groups[pointers.group].instances[pointers.instance].fields[pointers.field];

        field.values.push({ instance: field.values.length, value: null });
      });
    }
    case 'REMOVE_FIELD_VALUE': {
      const { pointers } = action;

      return produce(state, draft => {
        draft.groups[pointers.group].instances[pointers.instance].fields[
          pointers.field
        ].values.splice(action.index, 1);
      });
    }

    default:
      return state;
  }
};

export const PackedContext = createContext({ groups: [] });

export function FormEditor({ isNew, form, prototype, setResult, formRef }) {
  const initialPacked = groupForm(prototype, _.cloneDeep(form));
  /** Prepare initial relative name */
  const split_name = form?.name?.split?.('.') || [''];

  initialPacked.name = _.last(split_name);

  const [packed, dispatch] = useReducer(groupsReducer, initialPacked);

  const contextValue = { packed, dispatch };

  return (
    <PackedContext.Provider value={contextValue}>
      <Formik
        initialValues={{}}
        validateOnChange={false}
        onSubmit={(values, actions) =>
          handleSubmit({
            form,
            packed,
            isNew,
            prototype,
            values,
            actions,
            setResult
          })
        }
        innerRef={formRef}
      >
        {({ values }) => {
          return (
            <>
              <Paper.Content scrollable>
                <Form>
                  <Section>
                    <Section.Header>
                      <Section.Title>Form details</Section.Title>
                    </Section.Header>
                    <Section.Content>
                      <Row>
                        <QualifiedName name="form_name" label="Name" value={packed.name} required />
                      </Row>
                      <Row>
                        <Text
                          name="description"
                          label="Description"
                          value={packed.description}
                          multiline
                          required
                        />
                      </Row>
                      <Row>
                        <Column>
                          <FormPrototypeSelect
                            value={prototype?.name}
                            disabled
                            label="Form"
                            name="prototype"
                          />
                        </Column>
                      </Row>
                      <FormParameters packed={packed} prototype={prototype} />
                    </Section.Content>
                  </Section>
                </Form>
              </Paper.Content>

              <Paper.Footer>
                <Paper.Footer.Right>
                  <SubmitButton variant="contained">Save</SubmitButton>
                </Paper.Footer.Right>
              </Paper.Footer>
            </>
          );
        }}
      </Formik>
    </PackedContext.Provider>
  );
}

async function handleSubmit({
  prototype,
  packed,
  values,
  actions,
  setResult,
  refresh,
  isNew,
  form
}) {
  const { form_name, description, prototype: protoName, ...fields } = values;

  const extracted = extractFieldInfo(fields);

  const exclusions = [null, 'None', ''];

  const filteredExtractions = extracted.filter(entry => !exclusions.includes(entry.val));

  const packedCopy = _.cloneDeep(packed);

  /**
   * Update name and description
   */

  /**
   * extract and remap field values.
   */
  filteredExtractions.forEach(entry => {
    const path = `groups[${entry.group}].instances[${entry.instance}].fields[${entry.field}].values[${entry.index}].value`;

    _.set(packedCopy, path, entry.value);
  });

  /**
   * unpack and prepare for sending
   */
  const unpacked = ungroupForm(packedCopy);

  /**
   * Very Important!
   * Please read.
   * Another cleanup process is needed.
   * So there cannot be values for repeatable parameters where there is an empty array.
   * It requires for some DAMNED reason, to have at least one null value.
   */
  unpacked.values = unpacked.values.map(value => {
    if (Array.isArray(value.value)) {
      /** for some reason, it's not removed from the first iteration for values. */
      const filtered = { ...value, value: value.value.filter(v => !!v) };

      if (filtered.value.length === 0) {
        filtered.value = [null];
      }

      return filtered;
    }

    return value;
  });

  const name = form_name;

  unpacked.name = name;

  if (!isNew) {
    const rename = [..._.initial(form.name.split('.')), form_name].join('.');
    if (rename !== form.name) {
      unpacked.rename = rename;
      delete unpacked.name;
    }
  }

  unpacked.description = description;
  unpacked.prototype = protoName;
  unpacked.version = prototype.version;

  const request = isNew ? createForm : updateForm;

  const request_args = [unpacked];

  if (!isNew) {
    // prepend name to request arguments.
    request_args.unshift(form.name);
  }

  try {
    await request(...request_args);
    setResult('success');
  } catch (e) {
    setResult('fail');
  }
}

// /** berry important :) */
function extractFieldInfo(all) {
  const key_struct = /^group-(\d+)-instance-(\d+)-field-(\d+)-value-(\d+)---(\w+)$/;

  return Object.entries(all).map(([key, value]) => {
    const matched = key_struct.exec(key);

    return {
      name: space_summon(matched[5]),
      index: matched[4],
      field: matched[3],
      instance: matched[2],
      lower: matched[1],
      group: matched[1],
      value
    };
  });
}
