import React from 'react';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiPercent } from '@mdi/js';

import { CircleImg, NumberLabel, StringLabel } from 'shared/components';

import { UsageEntry } from '../UsageEntry/UsageEntry';

import './UsageListing.local.scss';

export const UsageListing = ({ usage }) => {
  return (
    <>
      <header>
        <CircleImg
          image={
            <SvgIcon>
              <Icon path={mdiPercent} />
            </SvgIcon>
          }
        />
        <NumberLabel number={usage.highLoadCount} />
        <StringLabel text="servers with high load" />
      </header>
      <div className="servers-list">
        {usage.servers.map(server => (
          <UsageEntry key={server.address} server={server} />
        ))}
      </div>
    </>
  );
};
