import React, { useState, useEffect, useRef } from 'react';
import { connect } from 'react-redux';
import { Formik, Form } from 'formik';
import _ from 'lodash';
import { fetchEntity } from '../actions';

import { patchConnection } from 'shared/api/accounts';

import { Switch } from 'shared/components';
import { Button, Checkbox } from 'shared/components/controls';
import { ModeSelect } from 'shared/components/form/editors/Custom';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Result } from 'shared/components/overlays/Result/Result';
import { SubmitButton } from 'shared/components/form/SubmitButton';
import { Paper, Section, Row, Column } from 'shared/components/layout';

import { Text, Password } from 'shared/components/form/editors/Generic';

import { fetchProtocols } from 'shared/api/protocols';

import SvgIcon from '@material-ui/core/SvgIcon';
import VpnKeyIcon from '@material-ui/icons/VpnKey';

export const ConnectedEditConnection = connect(null, {
  request: fetchEntity
})(EditConnection);

function EditConnection({ connection, isDisabled, type, request }) {
  if (type === '') return null;

  const [open, setOpen] = useState(false);
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };
  const formRef = useRef();
  const [result, setResult] = useState('');
  const [passwordEditable, setPasswordEditable] = useState(false);
  const [showRSA, setShowRSA] = useState(false);
  const [protocols, setProtocols] = useState([]);
  const initialValues = {
    name: connection.name || '',
    description: connection.description || '',
    host: connection.address || '',
    port: connection.port || '',
    user: connection.user || '',
    password: connection.user_key || '',
    confirm_password: connection.user_key || '',
    rsa_key: connection.user_key || '',
    protocol: connection.protocol
  };

  if (type === 'database') {
    initialValues.database = connection.special.database || '';
    initialValues.mode = connection.special.mode || '';
  }

  const extraValidations = values => {
    const errors = {};
    if (!showRSA) {
      if (values.password !== values.confirm_password) {
        errors.confirm_password = 'Passwords do not match';
      }
    }

    if (values.port) {
      if (!/^\d+$/.test(values.port)) {
        errors.port = 'Only numeric values are allowed';
      }
    }

    return errors;
  };

  const getProtocols = () => {
    fetchProtocols().then(response => {
      if (response.result === 'success') {
        setProtocols(response.protocols);
      }
    });
  };

  useEffect(() => {
    return () => {
      setPasswordEditable(false);
    };
  }, [open]);

  return (
    <>
      <Button
        disabled={isDisabled}
        classes={{ root: 'entity-button' }}
        onClick={() => {
          if (type === 'database') {
            getProtocols();
          }
          setOpen(true);
        }}
      >
        Edit
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            width: '75vw',
            height: '75vh'
          }}
        >
          <ModalHeader
            title="Edit Connection"
            icon={
              <SvgIcon>
                <VpnKeyIcon />
              </SvgIcon>
            }
            formikRef={formRef}
          />
          <Switch value={result}>
            <Switch.Case value="success">
              <Result type="success">
                <Result.Text>Connection was edited successfully!</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                      setResult('');
                    }}
                  >
                    Done
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="fail">
              <Result type="fail">
                <Result.Text>An error occurred and the connection could not be edited.</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                      setResult('');
                    }}
                  >
                    Close
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="">
              <Formik
                initialValues={initialValues}
                validate={extraValidations}
                onSubmit={values => {
                  handleSubmit(setResult, request, protocols, showRSA, type, connection, values);
                }}
                innerRef={formRef}
              >
                {() => {
                  const selectedProtocol =
                    connection.protocol &&
                    protocols.find(protocol => protocol.name === connection.protocol);
                  const allowRSA = type === 'database' ? !!selectedProtocol?.allow_rsa_key : true;
                  const withDatabase = selectedProtocol?.special_required?.includes('database');
                  const withMode = selectedProtocol?.name === 'ORACLE';

                  return (
                    <>
                      <Paper.Content>
                        <Form>
                          <Section>
                            <Section.Header>
                              <Section.Title>Details:</Section.Title>
                            </Section.Header>
                            <Row>
                              <Column>
                                <Row>
                                  <Text name="name" label="Name" value={connection.name} required />
                                </Row>
                                <Row>
                                  <Text
                                    name="protocol"
                                    label="Protocol"
                                    value={connection.protocol}
                                    disabled
                                  />
                                </Row>
                              </Column>
                              <Column>
                                <Text
                                  name="description"
                                  value={connection.description}
                                  label="Description"
                                  multiline
                                  required
                                />
                              </Column>
                            </Row>
                          </Section>
                          <Section>
                            <Section.Header>
                              <Section.Title>Connection Info:</Section.Title>
                            </Section.Header>
                            <Row>
                              <Column style={{ maxWidth: '80%', width: '80%' }}>
                                <Text
                                  name="host"
                                  label="Host"
                                  value={connection.address}
                                  disabled
                                  required
                                />
                              </Column>
                              <Column style={{ maxWidth: '20%', width: '20%' }}>
                                <Text name="port" required label="Port" value={connection.port} />
                              </Column>
                            </Row>
                            {type === 'database' && (
                              <Row>
                                {withDatabase && (
                                  <Column
                                    style={{ minWidth: '80%', width: '100%', maxWidth: '100%' }}
                                  >
                                    <Text
                                      name="database"
                                      label="Database"
                                      value={connection.special.database}
                                      required
                                    />
                                  </Column>
                                )}
                                {withMode && (
                                  <Column style={{ maxWidth: '20%' }}>
                                    <ModeSelect name="mode" label="Mode" />
                                  </Column>
                                )}
                              </Row>
                            )}
                            <Row>
                              <Column>
                                <Text name="user" label="User" value={connection.user} required />
                              </Column>
                              {passwordEditable && (
                                <PasswordOrRSA allowed={allowRSA} showRSA={showRSA} />
                              )}
                            </Row>
                          </Section>
                        </Form>
                      </Paper.Content>
                      <Paper.Footer>
                        <Paper.Footer.Left>
                          {!passwordEditable && (
                            <Button onClick={() => setPasswordEditable(true)}>
                              Change existing password
                            </Button>
                          )}
                          {passwordEditable && allowRSA && (
                            <Checkbox
                              checked={showRSA}
                              onInput={() => setShowRSA(prev => !prev)}
                              label="Use RSA key"
                            />
                          )}
                        </Paper.Footer.Left>
                        <Paper.Footer.Right>
                          <SubmitButton variant="contained" confirmation>
                            Save
                          </SubmitButton>
                        </Paper.Footer.Right>
                      </Paper.Footer>
                    </>
                  );
                }}
              </Formik>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
}

async function handleSubmit(setResult, request, protocols, showRSA, type, connection, values) {
  const selectedProtocol = protocols.find(protocol => protocol.name === values.protocol);
  const allowRSA = type === 'database' ? !!selectedProtocol?.allow_rsa_key : true;
  const withDatabase = selectedProtocol?.special_required?.includes('database');
  const withMode = selectedProtocol?.name === 'ORACLE';

  const request_body = {
    rename: values.name,
    user: values.user,
    description: values.description,
    protocol: values.protocol,
    address: values.host,
    user_key: (allowRSA && showRSA ? values.rsa_key : values.password) || undefined,
    port: Number.parseInt(values.port, 10),
    special: {
      ...(withDatabase && { database: values.database }),
      ...(withMode && { mode: values.mode })
    }
  };

  if (_.isEmpty(request_body.special)) {
    delete request_body.special;
  }

  patchConnection(connection.name, request_body)
    .then(() => {
      setResult('success');
      request('account');
    })
    .catch(error => {
      console.log('[edit connection] patch connection error: ', error); // eslint-disable-line
      setResult('fail');
    });
}

const PasswordOrRSA = ({ allowed, showRSA }) => {
  return (
    <Column>
      <Switch value={allowed && showRSA}>
        <Switch.Case value>
          <Text name="rsa_key" label="RSA Key" multiline required />
        </Switch.Case>
        <Switch.Case value={false}>
          <Row>
            <Password name="password" label="Password" required />
          </Row>
          <Row>
            <Password name="confirm_password" label="Confirm Password" required />
          </Row>
        </Switch.Case>
      </Switch>
    </Column>
  );
};
