import { produce } from 'immer';

function unchanged(state) {
  return state;
}

export function createReducer(reducers, initialState = {}) {
  const reducerInitialState = reducers.initialState || initialState;

  return function r(state = reducerInitialState, action) {
    const { type } = action;

    if (!type) {
      console.error('[createReducer] action does not have a type', action); // eslint-disable-line
      return unchanged(state);
    }

    const reducer = reducers[action.type];

    if (reducer) {
      return produce(state, nextState => {
        reducer(nextState, action, state);
      });
    }

    return unchanged(state);
  };
}
