import React, { useEffect, useState, useCallback } from 'react';
import { object, func, bool } from 'prop-types';
import { connect } from 'react-redux';
import { useParams, useHistory } from 'react-router-dom';

import { Switch, Center } from 'shared/components';
import { Paper } from 'shared/components/layout';
import { Button } from 'shared/components/controls';
import { Result } from 'shared/components/overlays/Result/Result';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Loader } from 'shared/components/loaders';

import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiTrashCan } from '@mdi/js';

import { loadGroupThunk, removeGroupThunk } from '../../store/thunks';
import { PrivilegesContainer } from './containers/Privileges';
import { GroupUsersContainer } from './containers/GroupUsers';
import { SubGroupsContainer } from './containers/SubGroups';

import './GroupView.local.scss';

export const GroupView = ({ group, removing, removeGroup }) => {
  const [result, setResult] = useState('attempt');
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: useCallback(() => {
      setOpen(true);
    }, []),
    close: useCallback(() => {
      setResult('attempt');
      setOpen(false);
    }, [])
  };

  const deleteGroup = useCallback(() => {
    removeGroup(group.name)
      .then(() => {
        setResult('attempt');
        setOpen(false);
        history.push('/system/mas-accounts/groups');
      })
      .catch(() => {
        setResult('attempt');
        setOpen(false);
        console.log('Could not remove group!'); // eslint-disable-line
      });
  }, [group]);

  /* This needs to be written as a result check or 404 page for a group. */
  if (!group) {
    return <></>;
  }

  const history = useHistory();

  return (
    <>
      <Paper flex="column">
        <Paper.Header>
          <Paper.Title>{group.name}</Paper.Title>
        </Paper.Header>

        <Paper.Content scrollable>
          <PrivilegesContainer />
          <GroupUsersContainer />
          <SubGroupsContainer />
        </Paper.Content>
        <Paper.Footer>
          <Paper.Footer.Left>
            <Button color="secondary" disabled={removing} onClick={modalActions.open}>
              Remove
            </Button>
          </Paper.Footer.Left>
        </Paper.Footer>
      </Paper>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            minWidth: '400px',
            maxHeight: '368px',
            maxWidth: '65vw'
          }}
        >
          <ModalHeader
            title="Remove group"
            icon={
              <SvgIcon>
                <path d={mdiTrashCan} />
              </SvgIcon>
            }
          />
          <Switch value={result}>
            <Switch.Case value="attempt">
              <Paper.Content scrollable={false}>
                <Result type="fail">
                  <BodyText>
                    Are you sure you want to remove group {group.name}? This could affect the way
                    the user will be able to interact with the app.
                  </BodyText>
                </Result>
              </Paper.Content>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    color="primary"
                    variant="contained"
                    onClick={() => {
                      setResult('removing');
                      deleteGroup();
                    }}
                  >
                    Yes
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="removing">
              <Paper.Content scrollable={false}>
                <Center>
                  <Loader>Removing...</Loader>
                </Center>
              </Paper.Content>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
    </>
  );
};

GroupView.propTypes = {
  group: object,
  removing: bool,
  removeGroup: func
};

export const ConnectedGroupView = connect(
  state => ({
    group: state.groups.selected,
    removing: state.groups.ops.remove.loading
  }),
  dispatch => ({
    removeGroup: name => dispatch(removeGroupThunk(name))
  })
)(GroupView);

export const GroupLoader = ({ loadGroup, loading }) => {
  const { groupName } = useParams();
  useEffect(() => {
    if (groupName) {
      loadGroup(groupName);
    }
  }, [groupName]);

  return (
    <>
      {loading ? (
        <Loader>
          Loading Group <b>{groupName}</b>
        </Loader>
      ) : (
        <ConnectedGroupView />
      )}
    </>
  );
};

GroupLoader.propTypes = {
  loadGroup: func,
  loading: bool
};

export const ConnectedGroupLoader = connect(
  state => ({
    loading: state.groups.ops.selected.loading
  }),
  dispatch => ({ loadGroup: name => dispatch(loadGroupThunk(name)) })
)(GroupLoader);

function BodyText({ children }) {
  return (
    <h2
      style={{
        fontFamily: 'Open Sans',
        fontStyle: 'normal',
        fontWeight: 'bold',
        fontSize: '14px',
        lineHeight: '20px',
        letterSpacing: '0.25px',
        marginTop: '24px'
      }}
    >
      {children}
    </h2>
  );
}
