export function hasWritePrivileges(entity) {
  return entity?.privileges?.effective?.includes?.('write');
}

export function hasOnlyReadPrivilege(entity) {
  if (entity?.privileges?.effective?.length === 1 && entity?.privileges?.effective[0] === 'read') {
    return true;
  }

  return false;
}

export function isOnlySelectGroup(details) {
  if (!details) return false;
  const userDetails = details.toJS();

  if (!userDetails.member_of) return false;

  if (
    (userDetails.member_of.length === 1 && userDetails.member_of[0] === 'select') ||
    (userDetails.member_of.length === 2 &&
      userDetails.member_of.includes('select') &&
      userDetails.member_of.includes('user'))
  ) {
    return true;
  }

  return false;
}

export function isAdmin(details) {
  if (!details) return false;

  const userDetails = details.toJS();

  if (!userDetails.member_of) return false;

  if (userDetails.member_of.includes('admin')) {
    return true;
  }

  return false;
}
