import { createSelector } from 'reselect';

const localState = state => state.session;

export const authorizationSelector = createSelector(localState, state =>
  state.get('authorization')
);

export const currentUserSelector = createSelector(localState, state => state.get('currentUser'));

export const loginSelector = createSelector(localState, state => state.get('login'));

export const beVersionSelector = createSelector(localState, state => state.get('beVersion'));
