import React from 'react';
import { string, array, bool, func, shape } from 'prop-types';
import { connect } from 'react-redux';

import './Privileges.local.scss';

import { Checkbox } from 'shared/components/controls';
import { grantPrivilegeThunk, revokePrivilegeThunk } from '../../../../store/thunks';

export const Privileges = ({ user, privileges, disabled, grant, revoke }) => {
  const actions = { grant, revoke };

  return (
    <div className="user-privileges">
      <ul className="privileges-list">
        {privileges.map(entry => {
          return (
            <li key={entry.key} className="privileges-item">
              <Privilege user={user} privilege={entry} actions={actions} disabled={disabled} />
            </li>
          );
        })}
      </ul>
    </div>
  );
};

Privileges.propTypes = {
  user: string,
  privileges: array,
  disabled: bool,
  grant: func,
  revoke: func
};

export const Privilege = ({ user, privilege, disabled, actions }) => {
  const { key, granted, type } = privilege;
  const handler = () => {
    const thunk = granted ? actions.revoke : actions.grant;

    thunk(user, key, type);
  };

  return (
    <Checkbox
      data-qa={`privilege-${key}`}
      disabled={disabled}
      label={capitalize(key)}
      checked={granted}
      onChange={handler}
    />
  );
};

Privilege.propTypes = {
  user: string,
  privilege: shape({
    key: string,
    type: string,
    granted: bool
  }),
  disabled: bool,
  actions: shape({
    grant: func,
    revoke: func
  })
};

export const PrivilegesContainer = connect(
  state => {
    const user = selectedUser(state);

    return {
      user: user.name,
      disabled: user?.name === 'mas' || state.usersManager.flags.privileges.disabled,
      privileges: mapPrivileges(user.class_privileges)
    };
  },
  dispatch => ({
    grant: (user, key, type) =>
      dispatch(
        grantPrivilegeThunk({
          user,
          type,
          key
        })
      ),
    revoke: (user, key, type) =>
      dispatch(
        revokePrivilegeThunk({
          user,
          type,
          key
        })
      )
  })
)(Privileges);

function selectedUser(state) {
  return state.usersManager.users.selected;
}

function mapPrivileges(data) {
  if (!data) return [];

  const {
    effective,
    actual: { granted }
  } = data;

  const keys = Object.keys(effective).sort();

  return keys.reduce((list, key) => {
    const privilege = {
      key,
      /**
       * Fix: create is given as implicit type for grant/revoke.
       */
      type: effective[key][0] || 'create',
      granted: !!granted[key].length
    };

    list.push(privilege);

    return list;
  }, []);
}

function capitalize(str) {
  return str.charAt(0).toUpperCase() + str.slice(1);
}
