import { createSelector } from 'reselect';

import { connectionsSelector } from '../state';

export const getJobsFiltersOptions = createSelector(connectionsSelector, connections => {
  const filters = {
    address: [], // connections - server
    protocol: [], // connections - dbms
    status: ['EXECUTING', 'SUCCEEDED', 'SCHEDULED', 'ABORTED', 'FAILED', 'DELAYED', 'KILLED'], // hardcoded
    database: [] // connections -  database
  };

  connections.get('content').forEach(connection => {
    const address = connection.get('address');
    const protocol = connection.get('protocol');
    const database = connection.getIn(['special', 'database']);

    if (address && !filters.address.includes(address)) {
      filters.address.push(address);
    }

    if (protocol && !filters.protocol.includes(protocol)) {
      if (protocol !== 'SSH') {
        filters.protocol.push(protocol);
      }
    }

    if (database && !filters.database.includes(database)) {
      filters.database.push(database);
    }
  });
  return filters;
});
