import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { string, object } from 'prop-types';
import classnames from 'classnames';

import Avatar from '@material-ui/core/Avatar';

import './GroupCard.local.scss';

/* eslint-disable jsx-a11y/no-noninteractive-tabindex */

export const GroupCard = ({ href, group }) => {
  const { pathname } = useLocation();

  const { name, src } = group;
  return (
    <Link
      to={href}
      className={classnames('group-card', { selected: pathname.endsWith(`/${name}`) })}
      data-qa={`group-card-of-${name}`}
    >
      <Avatar className="group-avatar" alt={name} src={src}>
        {group.name[0].toUpperCase()}
      </Avatar>
      <span className="group-name">{name}</span>
    </Link>
  );
};

GroupCard.propTypes = {
  href: string,
  group: object
};
