import {
  TextParameter,
  BooleanParameter,
  IntegerParameter,
  EmailParameter,
  PhoneNumberParameter,
  ProcessParameter,
  VariadicParameter,
  AccountParameter,
  FQNameParameter,
  PasswordParameter,
  MultilineParameter,
  ReplvaluesParameter,
  UuidParameter
} from './variants';

import { want, wantComponent } from 'shared/lib/want';

/**
 * Main switcher for Parameter fields.
 */
export const WantParameter = wantComponent(
  want(
    {
      text: TextParameter,
      number: IntegerParameter,
      select: TextParameter,
      phone: PhoneNumberParameter,
      boolean: BooleanParameter,
      email: EmailParameter,
      fqname: FQNameParameter,
      account: AccountParameter,
      process: ProcessParameter,
      variadic_parameter: VariadicParameter,
      password: PasswordParameter,
      multiline: MultilineParameter,
      replvalues: ReplvaluesParameter,
      uuid: UuidParameter
    },
    TextParameter,
    props => {
      const { editor } = props.parameter;

      return editor.type;
    }
  )
);

export default WantParameter;
