import React, { useState, useEffect, useContext, useCallback } from 'react';

import { Paper, Center, NavigationTab } from 'shared/components/layout';
import { Loader } from 'shared/components/loaders';
import { Result, ModalContext } from 'shared/components/overlays';
import { Button, Checkbox as LabeledCheckbox } from 'shared/components/controls';

import { getPrivileges, setPrivileges } from 'shared/api/privileges';

import './PrivilegesEditor.local.scss';
import { mdiAccount } from '@mdi/js';
import SvgIcon from '@material-ui/core/SvgIcon';
import Checkbox from '@material-ui/core/Checkbox';

import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';

export const PrivilegesEditor = ({ entity }) => {
  const hasAlias = !!entity.alias;
  const {
    loading,
    data,
    editAliasParent,
    updating,
    setEditAliasParent,
    updatePrivilege
  } = usePrivileges(entity, hasAlias);
  const [tab, switchTab] = useState('users');
  const { actions } = useContext(ModalContext);

  if (loading) {
    return (
      <Center>
        <Loader>Loading privileges for entity</Loader>
      </Center>
    );
  }

  if (!data) {
    return (
      <Center>
        <Result type="fail">
          <Result.Text compact>Could not load privileges list for {entity.name}</Result.Text>
        </Result>
      </Center>
    );
  }

  return (
    <>
      <Paper.Header>
        <NavigationTab
          active={tab}
          tabs={[
            { label: 'Users', value: 'users' },
            { label: 'Groups', value: 'groups' }
          ]}
          onClick={switchTab}
        />
      </Paper.Header>
      <PrivilegesList
        privileges={data.privileges.filter(p => {
          if (p.role === 'mas') {
            return false;
          }
          if (p.role === 'admin') {
            return false;
          }
          if (tab === 'users') {
            return !p.is_group;
          }

          return p.is_group;
        })}
        updater={privilege => {
          updatePrivilege(privilege);
        }}
        updating={updating}
        entityType={entity.entity}
        entityName={hasAlias && editAliasParent ? entity.alias : entity.name}
        isAlias={hasAlias && !editAliasParent}
      />
      <Paper.Footer>
        {!!entity.alias && (
          <Paper.Footer.Left>
            <LabeledCheckbox
              color="primary"
              icon={<CheckBoxOutlineBlankIcon />}
              checkedIcon={<CheckBoxIcon />}
              checked={editAliasParent}
              onChange={() => {
                setEditAliasParent(!editAliasParent);
              }}
              label="Show parent privileges"
            />
          </Paper.Footer.Left>
        )}
        <Paper.Footer.Right>
          <Button color="primary" variant="contained" onClick={actions.close}>
            Done
          </Button>
        </Paper.Footer.Right>
      </Paper.Footer>
    </>
  );
};

function PrivilegesList({ privileges, updating, updater, entityType, entityName, isAlias }) {
  const types = privilegeTypes(entityType, isAlias);

  function renderType(type) {
    return (
      <div className="column role" key={type}>
        {type}
      </div>
    );
  }

  function mapByEntity(user_or_group) {
    return (
      <div className="item" key={`alias-${isAlias}-role-${user_or_group.role}`}>
        <div className="column role">
          <SvgIcon color="primary">
            <path d={mdiAccount} />
          </SvgIcon>
          {user_or_group.role}
        </div>
        <div className="types">
          {types.map(type => {
            const granted = user_or_group.effective.includes(type);
            return (
              <div
                className="column type "
                key={`c-alias-${isAlias}-role-${user_or_group.role}-group-${user_or_group.is_group}-type-${type}-${granted}`}
              >
                <Checkbox
                  key={`alias-${isAlias}-role-${user_or_group.role}-group-${user_or_group.is_group}-type-${type}-${granted}`}
                  classes={{ root: 'privilege-checkbox' }}
                  checked={granted}
                  disabled={updating}
                  onChange={() => {
                    updater({
                      privilege: type,
                      enitity_name: entityName,
                      alias: isAlias,
                      role: user_or_group.role,
                      role_type: user_or_group.is_group ? 'group' : 'user',
                      granted
                    });
                  }}
                />
              </div>
            );
          })}
        </div>
      </div>
    );
  }

  return (
    <div className="privileges">
      <div className="header">
        {renderType('Name')}
        <div className="types">{types.map(renderType)}</div>
      </div>
      <div className="list">
        {privileges
          .sort((a, b) => a?.role?.toLowerCase()?.localeCompare(b?.role?.toLowerCase()))
          .map(mapByEntity)}
      </div>
    </div>
  );
}

function privilegeTypes(entityType, alias = false) {
  const type = entityType.toLowerCase();
  const privileges = [];
  if (!alias) {
    if (type === 'namespace') {
      privileges.push('affiliate');
    } else if (['account', 'constant', 'form', 'process'].includes(type)) {
      privileges.push('execute');
    }
  }
  privileges.push('read', 'write');
  return privileges;
}

function usePrivileges(entity, hasAlias) {
  const [editAliasParent, setEditAliasParent] = useState(!hasAlias);
  const [loading, setLoading] = useState(true);
  const [updating, setUpdating] = useState(false);
  const [data, setData] = useState(null);

  useEffect(() => {
    getPrivileges(
      hasAlias && editAliasParent ? entity.alias : entity.name,
      hasAlias && !editAliasParent
    )
      .then(resp => {
        setData(resp?.data?.data?.entities?.[0]);
        setLoading(false);
      })
      .catch(() => {
        setData(null);
        setLoading(false);
      });
  }, [editAliasParent]);

  const updatePrivilege = useCallback(
    privilege => {
      setUpdating(true);
      setPrivileges(privilege)
        .then(resp => {
          setData(resp?.data?.data?.entities?.[0]);
          setUpdating(false);
        })
        .catch(() => {
          setUpdating(false);
        });
    },
    [entity, editAliasParent]
  );

  return {
    loading,
    data,
    editAliasParent,
    updating,
    setEditAliasParent,
    setUpdating,
    setData,
    updatePrivilege
  };
}
