import React from 'react';

import { ConnectedFormModal } from './form-actions/FormModal';

const ActionsPrototype = ({ entity }) => {
  const disableActions = entity?.meta?.appearance === 'Greyed';

  return (
    <>
      <ConnectedFormModal isNew prototype={entity} disabled={disableActions} />
    </>
  );
};

export default ActionsPrototype;
