import React from 'react';

import { BarChart, Bar, XAxis, Cell, ResponsiveContainer, YAxis, CartesianGrid } from 'recharts';

import './OptionsChart.local.scss';

export const OptionsChart = ({ data }) => {
  const renderCustomXAxisTick = ({ x, y, payload }) => {
    const index = data.findIndex(sv => sv.label === payload.value);
    return (
      <g transform={`translate(${x},${y})`}>
        <text className="label" x={0} y={0} dy={20} textAnchor="middle" fill={data[index].color}>
          {payload.value}
        </text>
      </g>
    );
  };

  return (
    <ResponsiveContainer>
      <BarChart data={data}>
        <CartesianGrid strokeDasharray="3 3" vertical={false} />
        <XAxis dataKey="label" tickSize={false} axisLine={false} tick={renderCustomXAxisTick} />
        <YAxis
          width={20}
          tickSize={false}
          axisLine={false}
          allowDecimals={false}
          type="number"
          domain={[0, 'dataMax']}
          stroke="rgba(4, 17, 37, 0.87)"
        />
        <Bar dataKey="value">
          {data.map((entry, index) => {
            return <Cell key={`${entry.name}-${index}`} fill={entry.color} />;
          })}
        </Bar>
      </BarChart>
    </ResponsiveContainer>
  );
};
