import { InvocationStatus } from './constants';

export const initialState = {
  loading: true,
  query: { processed: false },
  fetchedInvocation: false,
  invocation: { status: 'NOT_FETCHED' }
};

export function PROCESS_QUERY(nextState, { uuid, process }) {
  nextState.query = { processed: true, uuid, process };
  if (uuid) {
    nextState.invocation = { status: 'NOT_FETCHED' };
  } else {
    nextState.invocation = { status: '__NEW__', process: process || '' };
  }
}

export function FETCH_INVOCATION_SUCCESS(nextState, { result }) {
  const [recieved] = result.data.data.invocations;
  nextState.invocation = recieved;
}

export function FETCH_INVOCATION_FAILURE(nextState, { process }) {
  nextState.invocation = { status: InvocationStatus.NEW, process };
}

export function REPLACE_INVOCATION(nextState, { invocation }) {
  nextState.invocation = invocation;
}
