import { createSelector } from 'reselect';

export const ENTITY_TYPES = [
  {
    type: 'account',
    title: 'CONNECTION',
    dataEnclosure: 'accounts',
    query: '*'
  },
  {
    type: 'constant',
    title: 'CONSTANT',
    dataEnclosure: 'constants',
    query: '*'
  },
  {
    type: 'exception',
    title: 'EXCEPTION',
    dataEnclosure: 'exceptions',
    query: '*'
  },
  {
    type: 'form',
    title: 'FORM',
    dataEnclosure: 'forms',
    query: '*'
  },
  {
    type: 'namespace',
    title: 'NAMESPACE',
    query: '*',
    dataEnclosure: 'namespaces'
  },
  {
    type: 'process',
    query: '*',
    title: 'PROCESS',
    dataEnclosure: 'processes'
  },
  {
    type: 'prototype',
    query: '*',
    title: 'PROTOTYPE',
    dataEnclosure: 'prototypes'
  },
  {
    type: 'schedule',
    title: 'SCHEDULE',
    dataEnclosure: 'schedules',
    query: '*'
  },
  {
    type: 'type',
    query: '*',
    title: 'TYPE',
    dataEnclosure: 'data_types'
  }
];

export const ENTITY_TYPES_FETCH = ENTITY_TYPES.concat({
  type: 'alias'
});

const ROOT_ENTITY = {
  name: 'root',
  entity: 'root',
  description: 'Main container for all of the entities within MAS.'
};

export const getBreadcrumbs = state => state.entities.breadcrumbs;

function collectData(data, { type, dataEnclosure }, namespacesWithMeta) {
  let collection = data[type]?.data?.data?.[dataEnclosure];

  if (collection) {
    const aliases = data.alias?.data?.data.aliases
      .filter(item => item.alias_type === type)
      .map(item => {
        const { entity, alias, alias_type, ...props } = item;

        /**
         * Merging properties that the alias does not have,
         * with properties the entity it aliases, has.
         * Will avoid having to reconcile this at the usage level.
         * And keep aliases viewable here.
         */
        const aliasedEntity = data[type]?.data?.data[dataEnclosure].find(e => e.name === alias);

        return { ...(aliasedEntity && aliasedEntity), alias, ...props, entity: alias_type };
      });

    if (aliases) {
      /** Concat the aliases for this collection with the collection itself */
      collection = data[type]?.data?.data[dataEnclosure].concat(aliases);
    }

    /* filter out colllection items where appearance meta is hidden */

    /** FIXME:
     *  what about aliases ? If their aliasing root entity would have meta apearance hidden, should it as well ?
     *  */
    if (Array.isArray(namespacesWithMeta)) {
      collection = collection.reduce((acc, dataItem) => {
        const ownNamespace = namespacesWithMeta.find(ns => dataItem.name.startsWith(ns.name));

        if (ownNamespace) {
          if (ownNamespace?.meta?.appearance === 'Hidden') {
            return acc;
          }

          return acc.concat([
            {
              ...dataItem,
              meta: {
                ...dataItem.meta,
                ...ownNamespace.meta
              }
            }
          ]);
        }

        return acc.concat(dataItem);
      }, []);
    }
  }

  return collection;
}

export const entitySelectors = ENTITY_TYPES.reduce((selectors, def) => {
  selectors[def.type] = createSelector(
    state => state.entities.data,
    state => state.entities.fetch_status,
    state => namespaceWithMetaSelector(state),
    (data, status, namespacesWithMeta) => {
      return {
        data: collectData(data, def, namespacesWithMeta),
        status: status?.[def.type]
      };
    }
  );

  return selectors;
}, {});

export const namespaceWithMetaSelector = createSelector(
  state => state.entities.data,
  data => {
    const dataNamespaces = data.namespace?.data?.data?.namespaces;
    if (Array.isArray(dataNamespaces)) {
      return dataNamespaces.filter(
        item => item.meta && item.meta.appearance && item.meta.appearance !== 'Normal'
      );
    }
    return [];
  }
);

export const getFilteredEntities = createSelector(
  state => state.entities.data,
  state => state.entities.filters,
  state => namespaceWithMetaSelector(state),
  (data, filters, namespacesWithMeta) => {
    const types =
      filters.type === 'all'
        ? ENTITY_TYPES
        : ENTITY_TYPES.filter(type => type.type === filters.type || type.type === 'namespace');
    return types
      .reduce((accumulator, type) => {
        return accumulator.concat(collectData(data, type, namespacesWithMeta));
      }, [])
      .filter(Boolean);
  }
);

export const getEntitiesAsHashMap = createSelector(
  getFilteredEntities,
  state => state.entities.filters,
  (entities, filters) => {
    let filteredEntities = entities;
    if (filters.type !== 'all' && filters.type !== 'namespace') {
      filteredEntities = entities.filter(item => item.entity !== 'namespace');
    }
    if (filters.search) {
      filteredEntities = filteredEntities.filter(item => {
        if (filters.type !== 'all' && filters.type !== 'namespace') {
          const domainArray = item.name.split('.');
          const shortName = domainArray[domainArray.length - 1];
          return shortName.includes(filters.search);
        }
        return item.name.includes(filters.search);
      });
    }
    return filteredEntities.reduce((accumulator, entity) => {
      const paths = entity.name.includes('.') ? entity.name.split('.') : [];
      paths.forEach((path, index) => {
        const key = paths.slice(0, index).join('.');
        if (!accumulator[key]) {
          const findEntity = entities.find(item => item.name === key);
          accumulator[key] = findEntity || {
            name: key,
            notPersisted: true
          };
        }
      });
      accumulator[entity.name] = entity;

      return accumulator;
    }, {});
  }
);

export const getEntities = createSelector(
  getBreadcrumbs,
  getEntitiesAsHashMap,
  (breadcrumbs, entitiesMap) => {
    const entities = Object.values(entitiesMap);
    const roots = Object.keys(entitiesMap)
      .filter((entityName, index, names) => {
        return (
          !entityName.includes('.') &&
          (names.find(name => name.startsWith(`${entityName}.`)) ||
            entitiesMap[entityName].entity === 'namespace')
        );
      })
      .map(key => ({
        ...entitiesMap[key],
        isSelected: breadcrumbs.includes(entitiesMap[key].name)
      }));
    const columns = breadcrumbs.map((breadcrumb, index) => {
      return {
        level: index,
        path: index === 0 ? '.' : breadcrumbs.slice(0, index).join('.'),
        rows: []
      };
    });
    columns.push({
      level: breadcrumbs.length,
      path: `${breadcrumbs.join('.')}.`,
      rows: []
    });

    return entities.reduce((accumulator, entity) => {
      const path = entity.name.split('.');
      if (accumulator.length < path.length) {
        return accumulator;
      }
      const bucket = accumulator[path.length - 1];
      if (!bucket) {
        return accumulator;
      }
      if (bucket.path === '.') {
        bucket.rows = roots;
        return accumulator;
      }

      if (!entity.name.startsWith(bucket.path)) {
        return accumulator;
      }
      const name = entity.name.replace(bucket.path, '').replace('.', '');
      const entityLevel = entity.name.split('.').length - 1;
      bucket.rows.push({
        ...entity,
        name,
        isSelected: breadcrumbs[entityLevel] ? breadcrumbs[entityLevel] === name : false
      });

      return accumulator;
    }, columns);
  }
);

export const getSelectedEntity = createSelector(
  getEntitiesAsHashMap,
  getBreadcrumbs,
  (entities, breadcrumbs) => {
    const path = breadcrumbs.join('.');
    const entity = entities[path];
    return entity?.notPersisted ? ROOT_ENTITY : entity;
  }
);

export const namespaceChildrenListSelector = createSelector(
  getSelectedEntity,
  getEntitiesAsHashMap,
  (selectedEntity, entities) => {
    const list = Object.keys(entities);
    const namespace = `${selectedEntity.name}.`;

    return list.filter(name => name.startsWith(namespace) && name !== namespace);
  }
);
