import React from 'react';

import { any, func } from 'prop-types';

import { Center } from 'shared/components/layout';

import './job-list.local.scss';

export const JobList = ({ items, filters, children }) => {
  const noneAvailable = items.size === 0;

  if (noneAvailable) {
    return <NoJobs />;
  }

  return (
    <div className="list-container">
      <ul>
        {items.map((item, index, list) => (
          <li key={index} className="list-item">
            {children(item, index, filters, list)}
          </li>
        ))}
      </ul>
    </div>
  );
};

JobList.propTypes = {
  items: any,
  children: func
};

JobList.defaultProps = {
  items: []
};

function NoJobs() {
  return (
    <Center>
      <p>There are no jobs available with the selected filters</p>
    </Center>
  );
}
