import React, { useMemo, useContext } from 'react';

import { Section, Row, Column } from 'shared/components/layout';
import { Button } from 'shared/components/controls';
import { Ensure } from 'core/components/Ensure';

import { Parameter } from 'shared/components/form/parameters';

import normalize from 'shared/components/form/parameters/normalizeParameter';

import { PackedContext } from './FormEditor';

export const FormParameters = ({ packed, prototype }) => {
  return (
    <Section>
      <Section.Header></Section.Header>
      <Section.Content>
        <Ensure type="form">
          {({ data: forms }) => (
            <Ensure type="type">
              {({ data: datatypes }) => (
                <Ensure type="prototype">
                  {({ data: prototypes }) => (
                    <Ensure type="process">
                      <FieldsAndGroupsEditor
                        prototype={prototype}
                        groups={packed.groups}
                        prototypes={prototypes}
                        datatypes={datatypes}
                        forms={forms}
                      />
                    </Ensure>
                  )}
                </Ensure>
              )}
            </Ensure>
          )}
        </Ensure>
      </Section.Content>
    </Section>
  );
};

const DataContext = React.createContext({ datatypes: [], forms: [], prototypes: [] });

function FieldsAndGroupsEditor({ groups, datatypes, prototypes, forms }) {
  const entities = useMemo(
    () => ({
      datatypes,
      prototypes,
      forms
    }),
    [datatypes, prototypes, forms]
  );

  return (
    <>
      <DataContext.Provider value={entities}>
        {groups.map((group, index) => (
          <FieldGroup key={index} pointer={index} group={group} />
        ))}
      </DataContext.Provider>
    </>
  );
}

function FieldGroup({ group, pointer }) {
  return (
    <>
      {group.instances.map((instance, index) => (
        <GroupInstance
          key={`${pointer}|${index}`}
          group={group}
          instance={instance}
          pointers={{ group: pointer, instance: index }}
          count={group.instances.length}
        />
      ))}
    </>
  );
}

function GroupInstance({ group, instance, pointers, count }) {
  let title = group.name ? `Group ${group.name}` : null;
  if (group.iteration_limit > 1) {
    title = group.name
      ? `Group ${group.name} (${pointers.instance + 1} of ${count})`
      : `Group (Unnamed) (${pointers.instance + 1} of ${count})`;
  }

  const canAdd =
    group.iteration_limit > 1 && count - 1 === pointers.instance && count < group.iteration_limit;

  const { dispatch } = useContext(PackedContext);

  return (
    <div>
      <Section.Title>
        <div
          style={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between'
          }}
        >
          {title}
          {count !== 1 && (
            <Button
              color="secondary"
              onClick={() => {
                dispatch({
                  type: 'REMOVE_GROUP_INSTANCE',
                  pointers
                });
              }}
            >
              Remove Group
            </Button>
          )}
        </div>
      </Section.Title>
      <div>
        {instance.fields.map((field, index) => (
          <Field
            key={`${pointers.group}|${pointers.instance}|${field.name}`}
            group={group}
            instance={instance}
            pointers={{ ...pointers, field: index }}
            field={field}
          />
        ))}
      </div>
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'flex-end'
        }}
      >
        {canAdd && (
          <Button
            onClick={() => {
              const fields = instance.fields.map(field => {
                return {
                  ...field,
                  values: [{ instance: 0, value: null }]
                };
              });
              const newInstance = {
                ...instance,
                instance: instance.instance + 1,
                fields
              };

              dispatch({
                type: 'ADD_GROUP_INSTANCE',
                pointers,
                instance: newInstance
              });
            }}
          >
            Add Group
          </Button>
        )}
      </div>
    </div>
  );
}

function Field({ field, pointers }) {
  return (
    <div>
      {field.values.map((value, index) => (
        <Value
          key={`${pointers.group}|${pointers.instance}|${field.name}|${index}`}
          field={field}
          value={value.value}
          pointers={{ ...pointers, value: index }}
          count={field.values.length}
        />
      ))}
    </div>
  );
}

function Value({ field, value, pointers, count }) {
  const packet = useContext(DataContext);

  const canRemove = field.is_repeatable && count !== 1;
  const canAdd = field.is_repeatable && pointers.value + 1 === count;

  const { dispatch } = useContext(PackedContext);

  const label = field.is_repeatable
    ? `${field.name} (${pointers.value + 1} of ${count})`
    : field.name;

  function uniquedName(name, idx) {
    const string = `group-${idx.group}-instance-${idx.instance}-field-${idx.field}-value-${idx.value}---${field.name}`;

    return string;
  }

  const parameter = normalize(
    {
      name: uniquedName(field.name, pointers),
      provided_field_name: field.name,
      is_required: field.is_required,
      value,
      data_type: field.data_type,
      description: field.description,
      deflt: field.deflt,
      meta: field.meta
    },
    packet.datatypes,
    packet.prototypes,
    packet.forms
  );

  // for some reason, is_required is set as false for a supported parameter (eg: mas0300.sys.invocation.server_list prototype)
  // FIX ME: need to investigate further, for now, will make the value the same as field
  parameter.is_required = field.is_required;
  // console.log(parameter, field);

  const is_supported = parameter.is_supported && parameter.editor.type !== 'hidden';

  if (!is_supported) {
    return (
      <Row key={pointers.value}>
        <p>{field.name} (Unsuported)</p>
        <Parameter
          normalized={{ ...parameter, is_required: false, editor: { type: 'text' } }}
          disabled
          label={label}
        />
      </Row>
    );
  }

  return (
    <Row key={pointers.value}>
      <Column key={field.name}>
        <Parameter normalized={parameter} label={label} />
        <div
          style={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'flex-end'
          }}
        >
          {canAdd && canRemove && (
            <Button
              color="secondary"
              onClick={() => {
                dispatch({
                  type: 'REMOVE_FIELD_VALUE',
                  pointers
                });
              }}
            >
              Remove
            </Button>
          )}
          {canAdd && (
            <div
              style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'flex-end'
              }}
            >
              <Button
                onClick={() => {
                  dispatch({
                    type: 'ADD_FIELD_VALUE',
                    pointers
                  });
                }}
              >
                Add
              </Button>
            </div>
          )}
        </div>
      </Column>
    </Row>
  );
}
