import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';

import { FormattedMessage } from 'react-intl';

import PlayCircleOutlineIcon from '@material-ui/icons/PlayCircleOutline';
import ErrorOutlineIcon from '@material-ui/icons/ErrorOutline';
import Tooltip from '@material-ui/core/Tooltip';

import { withStyles } from '@material-ui/core/styles';

import { daemonPoller } from '../../../../store/tasks';
import { isDaemonRunning } from '../../../../store/selectors';

import './daemon.local.scss';

const LightTooltip = withStyles(() => ({
  tooltip: {
    color: '#fff',
    backgroundColor: '#787E89',
    letterSpacing: '0.4px',
    fontSize: '12px',
    lineHeight: '16px',
    fontFamily: 'Open Sans'
  }
}))(Tooltip);

export const Daemon = () => {
  const running = useSelector(isDaemonRunning);

  useEffect(() => {
    daemonPoller.start();

    return () => {
      daemonPoller.stop();
    };
  }, []);

  return (
    <div className="daemon qa-daemon-status">
      {running && (
        <LightTooltip placement="bottom-start" title={<FormattedMessage id="daemon.running" />}>
          <div className="status running">
            <PlayCircleOutlineIcon />
          </div>
        </LightTooltip>
      )}

      {!running && (
        <LightTooltip placement="bottom-start" title={<FormattedMessage id="daemon.not.running" />}>
          <div className="status not-running">
            <ErrorOutlineIcon className="not-running" />
          </div>
        </LightTooltip>
      )}
    </div>
  );
};
