import React from 'react';
import { hasWritePrivileges } from '../utils/privileges';

import { ConnectedEditNamespace } from './namespace-actions/EditNamespace';
import { ConnectedNewNamespace } from './namespace-actions/NewNamespace';
import { ConnectedDeleteNamespace } from './namespace-actions/DeleteNamespace';

const ActionsNamespace = ({ entity }) => {
  const disableActions = entity?.meta?.appearance === 'Greyed';
  const hasWrite = hasWritePrivileges(entity);

  return (
    <>
      <ConnectedNewNamespace namespace={entity} />
      {hasWrite && <ConnectedEditNamespace namespace={entity} />}
      {deleteRights(entity, hasWrite) && (
        <ConnectedDeleteNamespace entity={entity} disabled={disableActions} />
      )}
    </>
  );
};

function deleteRights(entity, hasWrite) {
  return hasWrite && entity.owner !== 'postgres';
}

export default ActionsNamespace;
