import React, { useState, useRef } from 'react';
import { string } from 'prop-types';
import { connect } from 'react-redux';
import { Formik, Form, Field } from 'formik';

import GroupIcon from '@material-ui/icons/Group';
import Avatar from '@material-ui/core/Avatar';

import { Input, Button } from 'shared/components/controls';

import { Paper, Row } from 'shared/components/layout';
import { Modal, ModalHeader, Result } from 'shared/components/overlays';
import { Center } from 'shared/components';

import './ChangePassword.local.scss';

import { CONSTANTS } from 'config';
import { RestClient } from 'shared/lib/rest';

export const ChangePassword = ({ user }) => {
  const [open, setOpen] = useState(false);
  /**
   * Backend call result
   */
  const [result, setResult] = useState({ done: false, error: undefined });
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setResult({ done: false, error: undefined });
      setOpen(false);
    }
  };
  const formRef = useRef();
  const parseError = error => {
    if (
      error?.response?.data?.messages &&
      error.response.data.messages.length > 0 &&
      error.response.data.messages[0]?.text
    ) {
      return error.response.data.messages[0]?.text.split('\n')[0];
    }

    return 'Password Change Failed!';
  };

  return (
    <>
      <Button color="primary" onClick={modalActions.open}>
        Change Password
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            width: '600px',
            height: '450px'
          }}
        >
          <ModalHeader
            avatar={
              <Avatar className="generic-avatar" alt="edit-user-password">
                <GroupIcon />
              </Avatar>
            }
            title="Change Password"
            formikRef={formRef}
          />
          {result.done ? (
            <>
              <Center>
                <Result type={result.error ? 'fail' : 'success'}>
                  <Result.Text>{result.error ? result.error : 'Password Changed!'}</Result.Text>
                </Result>
              </Center>
              <Paper.Footer></Paper.Footer>
            </>
          ) : (
            <Formik
              initialValues={{
                password: '',
                confirm: ''
              }}
              validate={values => {
                const errors = {};

                if (!values.password) {
                  errors.password = 'Field is required';
                }

                if (!values.confirm) {
                  errors.confirm = 'Please reenter your new password';
                }

                if (values.password !== values.confirm) {
                  errors.confirm = 'Passwords do not match';
                }

                if (values.password) {
                  if (/[$\\/":;']/.test(values.password)) {
                    errors.password = '$ \\ / " : ; \' are not an allowed characters';
                  } else if (values.password.length < 8) {
                    errors.password = 'Password must contain at least eight characters';
                  } else if (!/[a-z]/.test(values.password)) {
                    errors.password =
                      'Password must have at least a lower case letter (ex: a, b, etc.)';
                  } else if (!/[A-Z]/.test(values.password)) {
                    errors.password =
                      'Password must have at least a upper case letter (ex: A, B, etc.)';
                  } else if (!/[0-9]/.test(values.password)) {
                    errors.password =
                      'Password must have at least one number digit (ex: 0, 1, 2, etc.)';
                  } else if (!/[!@#%^&*()[\]{}+=\-_.,<>]/.test(values.password)) {
                    errors.password =
                      'Password must have at least one special character (! @ # % ^ & * ( ) [ ] { } + = - _ , .<>)';
                  }
                }

                return errors;
              }}
              onSubmit={async (values, actions) => {
                actions.setSubmitting(true);

                try {
                  await RestClient.patch(`${CONSTANTS.BASE.ENDPOINTS.USERS}/${user}`, {
                    password: values.password
                  });

                  actions.setSubmitting(false);
                  setResult({ done: true });
                } catch (e) {
                  actions.setSubmitting(false);
                  setResult({ done: true, error: parseError(e) });
                }
              }}
              innerRef={formRef}
            >
              {({ dirty, isSubmitting, isValid, handleSubmit }) => (
                <>
                  <Paper.Content scrollable>
                    <Form className="password-form">
                      <Row>
                        <Field name="password">
                          {({ field, meta }) => {
                            return (
                              <Input
                                {...field}
                                type="password"
                                error={!!meta.error}
                                helperText={meta.error}
                                required
                                label="Password"
                                placeholder="New Password"
                              />
                            );
                          }}
                        </Field>
                      </Row>
                      <Row>
                        <Field name="confirm">
                          {({ field, meta }) => {
                            return (
                              <Input
                                {...field}
                                type="password"
                                required
                                error={!!meta.error}
                                helperText={meta.error}
                                label="Confirm Password"
                                placeholder="Confirm New Password"
                              />
                            );
                          }}
                        </Field>
                      </Row>
                    </Form>
                  </Paper.Content>
                  <Paper.Footer>
                    <Paper.Footer.Right>
                      <Button
                        color="primary"
                        variant="contained"
                        onClick={handleSubmit}
                        disabled={!dirty || !isValid || isSubmitting}
                      >
                        Save
                      </Button>
                    </Paper.Footer.Right>
                  </Paper.Footer>
                </>
              )}
            </Formik>
          )}
        </Paper>
      </Modal>
    </>
  );
};

ChangePassword.propTypes = {
  user: string
};

export const ChangePasswordContainer = connect(state => {
  const user = selectedUser(state);

  return { user: user.name };
})(ChangePassword);

function selectedUser(state) {
  return state.usersManager.users.selected;
}
