import React, { useState } from 'react';

import { Header } from 'core/components/Header';
import { Main, Paper } from 'shared/components/layout';
import { Switch } from 'shared/components';

import { Navigation } from './components/Navigation/Navigation';
import { ConnectedHardware } from './Pages/Hardware/Hardware';
import { ConnectedSoftware } from './Pages/Software/Software';
import { ConnectedVMware } from './Pages/VMware/VMware';
import { ConnectedPackaging } from './Pages/Packaging/Packaging';

export const LicenseCollection = () => {
  const [activeTab, setActiveTab] = useState(0);

  function changeActiveTab(newActiveTab) {
    setActiveTab(newActiveTab);
  }

  return (
    <>
      <Header title="Compliance / License collection" />
      <Main>
        <Paper>
          <Paper.Header>
            <Navigation active={activeTab} onClickCallback={changeActiveTab} />
          </Paper.Header>
          <Paper.Content>
            <Switch value={activeTab}>
              <Switch.Case value={0}>
                <ConnectedHardware />
              </Switch.Case>
              <Switch.Case value={1}>
                <ConnectedSoftware />
              </Switch.Case>
              <Switch.Case value={2}>
                <ConnectedVMware />
              </Switch.Case>
              <Switch.Case value={3}>
                <ConnectedPackaging />
              </Switch.Case>
            </Switch>
          </Paper.Content>
        </Paper>
      </Main>
    </>
  );
};
