import { put, takeEvery } from '@redux-saga/core/effects';

import { actions } from 'shared/store/actions';

function* globalFilters(filters) {
  yield put(actions.setGlobalFilters.dispatchSuccess(filters.payload));
}

export function* GlobalFiltersEffect() {
  yield takeEvery(actions.setGlobalFilters.getLoadingAction(), globalFilters);
}
