import React from 'react';
import * as PropTypes from 'prop-types';

import { slugify } from 'shared/lib';

import './rest-errors.local.scss';

export class RestErrorsComponent extends React.PureComponent {
  render() {
    const { className } = this.props;

    return (
      <div className={className}>
        {this.props.errors.map((error) => (
          <div key={slugify(error.get('message'))}>
            {error.get('message')}
          </div>
        ))}
      </div>
    );
  }
}

RestErrorsComponent.propTypes = {
  errors: PropTypes.object.isRequired,
  className: PropTypes.string,
};

RestErrorsComponent.defaultProps = {
  className: 'rest-errors',
};
