import { RestClient } from 'shared/lib/rest';

export function getAlias(name) {
  return RestClient.get(`/alias/${name}`, {
    page_size: 2147483645
  });
}

export function patchAlias(name, body) {
  return RestClient.patch(`/alias/${name}`, body, {
    page_size: 2147483645
  });
}

export function postAlias(body) {
  return RestClient.post('/alias', body, {
    page_size: 2147483645
  });
}
