import moment from 'moment';
import _ from 'lodash';

export function getWorkloadData(jobs, dateRange) {
  const isSingleDay =
    moment(dateRange.currentDate).format('MMM D YY') ===
    moment(dateRange.endDate).format('MMM D YY');

  if (jobs.length === 0) return [];

  const jobsWithFormattedInvokedDate = _.orderBy(jobs, ['date_invoke'], ['asc']).map(job => ({
    ...job,
    date_invoke: isSingleDay
      ? `${moment(job.date_invoke).format('H')}:00`
      : moment(job.date_invoke).format('MMM D YY')
  }));
  const groupJobsByDate = _.groupBy(jobsWithFormattedInvokedDate, 'date_invoke');
  const data = [];

  // if no process was executed during selected interval, simply return an empty array
  if (Object.keys(groupJobsByDate).length === 0) {
    return [];
  }

  Object.keys(groupJobsByDate).forEach(date => {
    const item = { name: date, SUCCEEDED: 0, FAILED: 0, ABORTED: 0, total: 0 };
    groupJobsByDate[date].forEach(el => {
      item[el.status]++;
      if (el.status === 'SUCCEEDED' || el.status === 'FAILED' || el.status === 'ABORTED') {
        item.total++;
      }
    });

    data.push(item);
  });

  // display all days between the first one that run a process and the last one
  // or all hours from 0->23
  const intervalArray = [];
  const daylist = isSingleDay ? getHoursArray(dateRange) : getDaysArray(dateRange);
  const totalDays = daylist.reduce((o, key) => ({ ...o, [key]: '' }), {});

  // update the total days from the interval array with the days that have some processes executed
  data.forEach(day => {
    totalDays[day.name] = day;
  });

  Object.keys(totalDays).forEach(date => {
    // for the days in the interval that have no executed process, add an empty entry
    if (typeof totalDays[date] === 'string') {
      totalDays[date] = {
        name: date,
        SUCCEEDED: 0,
        FAILED: 0,
        ABORTED: 0,
        total: 0
      };
    }
    intervalArray.push(totalDays[date]);
  });

  return intervalArray;
}
/**
 * Creates an array for the selected interval of days
 */
function getDaysArray(dateRange) {
  let { currentDate } = dateRange;
  const { endDate } = dateRange;
  const dateArray = [];

  while (currentDate <= endDate) {
    dateArray.push(moment(currentDate).format('MMM D YY'));
    currentDate = moment(currentDate).add(1, 'days');
  }

  return dateArray;
}

/**
 *
 * Creates an array for all the hours in a day 0 -> 23
 */
function getHoursArray() {
  const dateArray = [];

  for (let i = 0; i < 24; i++) {
    dateArray.push(`${i}:00`);
  }
  return dateArray;
}
