import React, { useState } from 'react';

import { Result } from 'shared/components/overlays';

import { Switch } from 'shared/components';

import { Formik, Form } from 'formik';

import { Button, Checkbox } from 'shared/components/controls';
import { Text, Password } from 'shared/components/form/editors/Generic';

import { Paper, Center, Section, Row, Column } from 'shared/components/layout';

import { postAccount, patchAccount } from 'shared/api/accounts';
import { SubmitButton } from 'shared/components/form/SubmitButton';
import _ from 'lodash';

export const DatabaseForm = ({
  account = {},
  protocols = [],
  modes,
  handleSubmission,
  editing,
  isNew,
  formRef
}) => {
  const [passwordEditable, setPasswordEditable] = useState(!editing);
  const [submission, setSubmission] = useState({});
  const allowedProtocols = protocols.filter(p => {
    const special = p?.special_required;

    return special && special.includes('database');
  });
  const [showRSA, setShowRSA] = useState(false);

  const handleAccountCreation = getCreateAccountHandler({
    protocols: allowedProtocols,
    modes,
    showRSA,
    isNew,
    setSubmission,
    account
  });

  const availablePorts = allowedProtocols.reduce((map, p) => {
    map[p.name] = p.default_port;

    return map;
  }, {});

  function mapAccountValues(entry = {}) {
    return {
      name: _.get(entry, 'name', ''),
      protocol: _.get(entry, 'protocol', ''),
      description: _.get(entry, 'description', ''),

      host: _.get(entry, 'address', ''),

      port: _.get(entry, 'port', availablePorts[_.get(entry, 'protocol')]),

      database: _.get(entry, 'special.database', ''),
      user: _.get(entry, 'user', ''),
      mode: _.get(entry, 'special.mode', '')
    };
  }

  const initialValues = mapAccountValues(account);

  const extraValidations = values => {
    const errors = {};

    if (passwordEditable && !showRSA) {
      if (values.password !== values.confirm_password) {
        errors.confirm_password = 'Passwords do not match';
      }
    }

    if (values.port) {
      if (!/^\d+$/.test(values.port)) {
        errors.port = 'Only numeric values are allowed';
      }
    }

    return errors;
  };

  return (
    <Switch value={submission?.result}>
      <Switch.Case value="success">
        <Center>
          <Result type="success">
            <Result.Text>{isNew ? 'Added new' : 'Updated'} account.</Result.Text>
          </Result>
        </Center>
        <Paper.Footer>
          <Paper.Footer.Right>
            <Button onClick={loadNewAccountHandler(submission.accounts, handleSubmission)}>
              Done
            </Button>
          </Paper.Footer.Right>
        </Paper.Footer>
      </Switch.Case>
      <Switch.Case value="fail">
        <Center>
          <Result type="fail">
            <Result.Text>Could not {isNew ? 'create new' : 'update'} account.</Result.Text>
            <Result.Messages messages={submission.messages} />
          </Result>
        </Center>
        <Paper.Footer></Paper.Footer>
      </Switch.Case>
      <Switch.Case value={undefined}>
        <Formik
          initialValues={initialValues}
          onSubmit={handleAccountCreation}
          validate={extraValidations}
          validateOnBlur
          // validateOnChange={false}
          validateOnMount={false}
          innerRef={formRef}
        >
          {({ values }) => {
            const selectedProtocol =
              values.protocol && protocols.find(protocol => protocol.name === values.protocol);
            const allowRSA = !!selectedProtocol?.allow_rsa_key;
            const withDatabase = selectedProtocol?.special_required?.includes('database');
            const withMode = selectedProtocol?.name === 'ORACLE';

            return (
              <>
                <Paper.Content>
                  <Form>
                    <Section>
                      <Section.Header>
                        <Section.Title>Details:</Section.Title>
                      </Section.Header>
                      <Section.Content>
                        <Row>
                          <Column>
                            <Row padded>
                              <Text name="name" required label="Name" value={initialValues.name} />
                            </Row>
                            <Row padded>
                              <Column>
                                <Text
                                  name="protocol"
                                  required
                                  label="Protocol"
                                  reactToChange={(value, formik) => {
                                    formik.setFieldValue('port', availablePorts[value]);
                                  }}
                                  options={allowedProtocols.map(p => ({
                                    value: p.name,
                                    label: p.name
                                  }))}
                                  value={initialValues.protocol}
                                />
                              </Column>
                            </Row>
                          </Column>
                          <Column>
                            <Text
                              multiline
                              name="description"
                              label="Description"
                              required
                              value={initialValues.description}
                            />
                          </Column>
                        </Row>
                      </Section.Content>
                    </Section>
                    <Section>
                      <Section.Header>
                        <Section.Title>Connection Info:</Section.Title>
                      </Section.Header>
                      <Section.Content>
                        <Row>
                          <Column style={{ maxWidth: '80%', width: '80%' }}>
                            <Text name="host" required label="Host" value={initialValues.host} />
                          </Column>
                          <Column style={{ maxWidth: '20%', width: '20%' }}>
                            <Text name="port" required label="Port" value={initialValues.port} />
                          </Column>
                        </Row>
                        <Row>
                          {withDatabase && (
                            <Column style={{ minWidth: '80%', width: '100%', maxWidth: '100%' }}>
                              <Text
                                name="database"
                                required
                                label="Database"
                                value={initialValues.database}
                              />
                            </Column>
                          )}
                          {withMode && (
                            <Column style={{ maxWidth: '20%' }}>
                              <Text
                                name="mode"
                                label="Mode"
                                required
                                options={modes.map(mode => ({
                                  value: mode.label,
                                  label: mode.label
                                }))}
                                value={initialValues.mode}
                              />
                            </Column>
                          )}
                        </Row>
                        <Row>
                          <Column>
                            <Text
                              name="user"
                              label="User"
                              required
                              value={initialValues.user || ''}
                            />
                          </Column>
                          {passwordEditable && (
                            <PasswordOrRSA allowed={allowRSA} showRSA={showRSA} />
                          )}
                        </Row>
                      </Section.Content>
                    </Section>
                  </Form>
                </Paper.Content>

                <Paper.Footer>
                  {editing && (
                    <Paper.Footer.Left>
                      {!passwordEditable && (
                        <Button onClick={() => setPasswordEditable(true)}>
                          Change existing password
                        </Button>
                      )}
                      {passwordEditable && allowRSA && (
                        <Checkbox
                          checked={showRSA}
                          onInput={() => setShowRSA(prev => !prev)}
                          label="Use RSA key"
                        />
                      )}
                    </Paper.Footer.Left>
                  )}
                  {!editing && allowRSA && (
                    <Paper.Footer.Left>
                      <Checkbox
                        checked={showRSA}
                        onInput={() => setShowRSA(prev => !prev)}
                        label="Use RSA key"
                      />
                    </Paper.Footer.Left>
                  )}
                  <Paper.Footer.Right>
                    <SubmitButton confirmation>Submit</SubmitButton>
                  </Paper.Footer.Right>
                </Paper.Footer>
              </>
            );
          }}
        </Formik>
      </Switch.Case>
    </Switch>
  );
};

const PasswordOrRSA = ({ allowed, showRSA }) => (
  <Column>
    <Switch value={allowed && showRSA}>
      <Switch.Case value>
        <Text multiline name="rsa_key" label="RSA Key" required />
      </Switch.Case>
      <Switch.Case value={false}>
        <Row padded>
          <Password name="password" required label="Password" />
        </Row>
        <Row padded>
          <Password name="confirm_password" required label="Confirm Password" />
        </Row>
      </Switch.Case>
    </Switch>
  </Column>
);

function getCreateAccountHandler({ protocols, showRSA, setSubmission, isNew, account }) {
  return function handleAccountCreation(values) {
    const selectedProtocol = protocols.find(protocol => protocol.name === values.protocol);
    const allowRSA = !!selectedProtocol?.allow_rsa_key;
    const withDatabase = selectedProtocol?.special_required?.includes('database');
    const withMode = selectedProtocol?.name === 'ORACLE';

    const request_body = {
      user: values.user,
      description: values.description,
      protocol: values.protocol,
      address: values.host,
      user_key: allowRSA && showRSA ? values.rsa_key : values.password,
      port: Number.parseInt(values.port, 10),
      special: {
        ...(withDatabase && { database: values.database }),
        ...(withMode && { mode: values.mode })
      }
    };

    if (isNew) {
      request_body.name = values.name;
    } else {
      request_body.rename = values.name;
    }

    if (isNew) {
      postAccount(request_body).then(response => {
        setSubmission({
          ...response
        });
      });
    } else {
      patchAccount(account.name, request_body).then(response => {
        setSubmission({
          ...response
        });
      });
    }
  };
}

function loadNewAccountHandler(accounts, handler) {
  return function loadNewAccountFromResult() {
    // const newAccount = accounts[0];

    handler();
  };
}
