import React from 'react';
import * as PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { actions } from 'shared/store/actions';
import { jobsSearchedSelector } from 'shared/store/state';

import { FormattedMessage } from 'react-intl';
import { debounce } from 'lodash';
import { mdiChevronRight } from '@mdi/js';
import Icon from '@mdi/react';
import Paper from '@material-ui/core/Paper';
import SvgIcon from '@material-ui/core/SvgIcon';
import SearchIcon from '@material-ui/icons/Search';
import ClearIcon from '@material-ui/icons/Clear';

import { SearchInput } from 'shared/components';
import { SearchResults } from '../../components';
import { setFilterSearch, setFilterType } from 'entities/actions';

import './search-area.local.scss';

export class Search extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      showSearchResults: false,
      searchTerm: ''
    };
  }

  apiSearchWithDebounce = debounce(searchedTerm => {
    if (this.state.showSearchResults) {
      this.getSearchResults(searchedTerm);
    }
  }, 500);

  render() {
    const {
      className,
      getSearchedJobs,
      entitiesStatus,
      setSearchFilter,
      setTypeFilter
    } = this.props;
    return (
      <Paper
        className={`${className} ${this.state.showSearchResults ? 'no-bottom-radius' : ''}
        `}
      >
        <SearchInput value={this.state.searchTerm} onChange={this.handleOnChange} />

        {this.state.searchTerm.length > 0 && (
          <div className="clear-search">
            <ClearIcon onClick={this.clearSearch} />
          </div>
        )}
        {this.state.showSearchResults && (
          <div className="search-results-container">
            <SearchResults
              category="jobs"
              totalResults={getSearchedJobs.get('total_count')}
              content={getSearchedJobs.get('content')}
              isLoading={getSearchedJobs.get('fetching')}
              displayedResults={3}
              filters={{
                nameFilter: this.state.searchTerm
              }}
            />
            <SearchResults
              category="processes"
              totalResults={
                this.filterProcesses(entitiesStatus.process, this.state.searchTerm).count
              }
              content={
                this.filterProcesses(entitiesStatus.process, this.state.searchTerm).processes
              }
              isLoading={entitiesStatus.process === 'fetching'}
              displayedResults={3}
              searchedTerm={this.state.searchTerm}
              setFilterSearch={setSearchFilter}
              setFilterType={setTypeFilter}
            />
            <div className="full-results-container qa-full-search-results">
              <div className="icon-container">
                <SearchIcon />
              </div>
              <div className="content">
                <FormattedMessage id="dashboard.view-full-search-results" />
              </div>
              <div className="open qa-open-full-results">
                <SvgIcon>
                  <Icon path={mdiChevronRight} />
                </SvgIcon>
              </div>
            </div>
          </div>
        )}
      </Paper>
    );
  }

  getSearchResults = searchedTerm => {
    const QSP = {
      name_filter: searchedTerm,
      page_size: 3,
      page: 1,
      date_invoke: '2000-01-01',
      period: 36500
    };

    // date_invoke and period are required in order for the API to work
    // those values are hardcored just to make sure the call will cover all available data
    this.props.searchJobs(QSP);
  };

  filterProcesses = (processStatus, searchedTerm) => {
    if (processStatus === 'fetching' || !searchedTerm) {
      return { processes: [], count: 0 };
    }
    if (this.props.storedProcesses && this.props.storedProcesses.length > 0) {
      const results = this.props.storedProcesses.filter(p => p.name.includes(searchedTerm));

      return { processes: results.slice(0, 3), count: results.length };
    }
    return { processes: [], count: 0 };
  };

  handleOnChange = e => {
    const searchedTerm = e.target.value;
    this.setState({ searchTerm: searchedTerm });

    if (searchedTerm.length < 3 && this.state.showSearchResults) {
      this.setState({ showSearchResults: false });
    } else if (searchedTerm.length === 3) {
      if (!this.state.showSearchResults) {
        this.setState({ showSearchResults: true }, () => {
          this.getSearchResults(searchedTerm);
        });
      } else {
        this.apiSearchWithDebounce(searchedTerm);
      }
    } else if (searchedTerm.length > 3) {
      if (!this.state.showSearchResults) {
        // used for pasted value
        this.setState({ showSearchResults: true }, () => {
          this.getSearchResults(searchedTerm);
        });
      } else {
        this.apiSearchWithDebounce(searchedTerm);
      }
    }
  };

  clearSearch = () => {
    this.setState({
      showSearchResults: false,
      searchTerm: ''
    });
  };
}

Search.propTypes = {
  className: PropTypes.string,
  searchJobs: PropTypes.func.isRequired,
  getSearchedJobs: PropTypes.object.isRequired
};

Search.defaultProps = {
  className: 'search-container'
};

export const mapStateToProps = state => ({
  getSearchedJobs: jobsSearchedSelector(state),
  entitiesStatus: state.entities.fetch_status,
  storedProcesses: state.entities.data?.process?.data?.data?.processes
});

export const mapDispatchToProps = dispatch => ({
  searchJobs: data => dispatch(actions.searchJobs.dispatchLoading(data)),
  setSearchFilter: data => dispatch(setFilterSearch(data)),
  setTypeFilter: data => dispatch(setFilterType(data))
});

export default connect(mapStateToProps, mapDispatchToProps)(Search);
