import React, { createContext } from 'react';
import uuid from 'uuid';
import MUIModal from '@material-ui/core/Modal';
import { bool, string, oneOfType, node, func, shape } from 'prop-types';

import './Modal.local.scss';

import { ModalHeader } from '../ModalHeader/ModalHeader';

export const ModalContext = createContext({});

export const Modal = ({ open, actions, id: providedId, children }) => {
  const id = providedId || uuid();
  const aria = {
    'aria-labelledby': `${id}-modal-title`,
    'aria-describedby': `${id}-modal-description`
  };

  return (
    <ModalContext.Provider value={{ aria, actions }}>
      <MUIModal {...aria} open={open} onClose={actions?.close} disableBackdropClick>
        <div className="overlay">
          {typeof children === 'function' ? children({ id, aria, open, actions }) : children}
        </div>
      </MUIModal>
    </ModalContext.Provider>
  );
};

Modal.propTypes = {
  actions: shape({
    open: func,
    close: func
  }),
  children: oneOfType([node, func]),
  open: bool,
  id: string
};

Modal.ModalHeader = ModalHeader;
