import React from 'react';
import { string, array, object } from 'prop-types';
import GroupAddIcon from '@material-ui/icons/GroupAdd';
import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiDeleteCircleOutline } from '@mdi/js';
import { ScrollableContainer } from 'shared/components/layout';

import { AvatarCard } from '../../../../components/AvatarCard/AvatarCard';

import { GroupsSelection } from './GroupsSelection';

import './SubGroups.local.scss';

export const SubGroups = ({ selections, helpers, retrieveGroupDescription }) => {
  return (
    <ScrollableContainer variant="row white">
      {passed => (
        <ul {...passed}>
          {selections.map(group_name => (
            <li className="card-item" key={group_name}>
              <AvatarCard
                label={group_name}
                avatar={{ fallback: <GroupAddIcon /> }}
                action={
                  <SvgIcon
                    onClick={e => {
                      e.preventDefault();
                      e.stopPropagation();

                      const existing = selections.indexOf(group_name);

                      if (existing !== -1) {
                        helpers.remove(existing);
                      }
                    }}
                  >
                    <path d={mdiDeleteCircleOutline} />
                  </SvgIcon>
                }
                description={retrieveGroupDescription(group_name)}
              />
            </li>
          ))}
          <li className="card-item" key="add-group-to-groups">
            <GroupsSelection
              selected={selections}
              helpers={helpers}
              retrieveGroupDescription={retrieveGroupDescription}
            />
          </li>
        </ul>
      )}
    </ScrollableContainer>
  );
};

SubGroups.propTypes = {
  username: string,
  selections: array,
  helpers: object
};
