import { RestClient } from 'shared/lib/rest';

export async function deleteEntity(name, type) {
  try {
    const { data } = await RestClient.delete(`/${type}/${name}`);

    return { result: data.result };
  } catch (e) {
    return { result: 'fail' };
  }
}

export function deleteInvocation(uuid) {
  return RestClient.delete(`/invocation/${uuid}`);
}
