import React from 'react';
import * as PropTypes from 'prop-types';

import './circle-img.local.scss';

export class CircleImgComponent extends React.PureComponent {
  render() {
    return (
      <div className={this.props.className}>
        {this.props.image}
      </div>
    );
  }
}

export default CircleImgComponent;

CircleImgComponent.propTypes = {
  className: PropTypes.string,
  image: PropTypes.object.isRequired,
};

CircleImgComponent.defaultProps = {
  className: 'circle-img',
};
