import React from 'react';
import classnames from 'classnames';

import './Navigation.local.scss';

const data = [
  { label: '1. Hardware Data Requirements Collection' },
  { label: '2. Software Data Requirements Collection' },
  { label: '3. vSphere Data Requirements Collection' },
  { label: '4. Packaging Process' }
];

export const Navigation = ({ active, onClickCallback }) => {
  return (
    <div className="nav-chip-container">
      {data.map((chip, index) => (
        <div
          className={classnames('nav-chip', { active: active === index })}
          key={index}
          onClick={() => onClickCallback(index)}
          role="presentation"
          data-qa={`chip-nav-${index}`}
        >
          {chip.label}
        </div>
      ))}
    </div>
  );
};
