import { RestClient } from 'shared/lib/rest';
import { CONSTANTS } from 'config';

export async function postAccount(body) {
  try {
    const { data } = await RestClient.post(CONSTANTS.BASE.ENDPOINTS.ACCOUNTS, body);

    return { result: data.result, accounts: data.data.accounts };
  } catch (e) {
    return { result: 'fail', accounts: [], messages: e.response.data?.messages };
  }
}

export async function patchAccount(name, body) {
  try {
    const { data } = await RestClient.patch(`${CONSTANTS.BASE.ENDPOINTS.ACCOUNTS}/${name}`, body);

    return { result: data.result, accounts: data.data.accounts };
  } catch (e) {
    return { result: 'fail', accounts: [], messages: e.response.data?.messages };
  }
}

export function getConnection(name) {
  return RestClient.get(`${CONSTANTS.BASE.ENDPOINTS.ACCOUNTS}/${name}`, {
    page_size: 2147483645
  });
}

export function patchConnection(name, body) {
  return RestClient.patch(`${CONSTANTS.BASE.ENDPOINTS.ACCOUNTS}/${name}`, body, {
    page_size: 2147483645
  });
}

export function postFile(file) {
  return RestClient.post(CONSTANTS.BASE.ENDPOINTS.UPLOAD_ACCOUNTS, file);
}
