import { createSelector } from 'reselect';

export const getQuery = createSelector(
  state => state.invocation_viewer.query,
  query => query
);

export const getInvocation = createSelector(
  state => state.invocation_viewer.invocation,
  invocation => invocation
);

export const getInvocationStatus = createSelector(getInvocation, invocation => invocation.status);
