import { RestClient } from 'shared/lib/rest';
import { CONSTANTS } from 'config';

export async function fetchDataTypes(type = '') {
  try {
    const { data } = await RestClient.get(
      `${CONSTANTS.BASE.ENDPOINTS.DATATYPES}${type ? `/${type}` : ''}`
    );

    return { result: data.result, data_types: data.data.data_types };
  } catch (e) {
    return { result: 'fail', data_types: [] };
  }
}
