import { RestClient } from 'shared/lib/rest';

export function postForm(name, body) {
  return RestClient.post(`/form/${name}/clone`, body, {
    page_size: 2147483645
  });
}

export function createForm(body) {
  return RestClient.post('/form', body, {
    page_size: 2147483645
  });
}

export function updateForm(name, body) {
  return RestClient.patch(`/form/${name}`, body, {
    page_size: 2147483645
  });
}
