import { RestClient } from 'shared/lib/rest';
import { CONSTANTS } from 'config';

export async function fetchProtocols() {
  try {
    const { data } = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.PROTOCOLS);

    return { result: data.result, protocols: data.data.protocols };
  } catch (e) {
    return { result: 'fail', protocols: [] };
  }
}
