export const InvocationStatus = {
  NEW: '__NEW__',
  ALL: '__ALL__',
  NOT_FETCHED: 'NOT_FETCHED',
  ABORTED: 'ABORTED',
  SCHEDULED: 'SCHEDULED',
  SUCCEEDED: 'SUCCEEDED',
  FAILED: 'FAILED',
  EXECUTING: 'EXECUTING',
  DELAYED: 'DELAYED',
  KILLED: 'KILLED'
};

export const AllowOutputFor = [
  InvocationStatus.ABORTED,
  InvocationStatus.KILLED,
  InvocationStatus.FAILED,
  InvocationStatus.SUCCEEDED,
  InvocationStatus.EXECUTING
];

export const AllowGeneralEditingFor = [InvocationStatus.NEW];

export const CannotShowStatusFor = [
  InvocationStatus.NEW,
  InvocationStatus.ALL,
  InvocationStatus.NOT_FETCHED
];

export const FAILURES = [
  InvocationStatus.FAILED,
  InvocationStatus.ABORTED,
  InvocationStatus.KILLED
];

export const SUCESSES = [InvocationStatus.SUCCEEDED];

export const WARNINGS = [InvocationStatus.DELAYED, InvocationStatus.SCHEDULED];
