import { put, takeEvery } from '@redux-saga/core/effects';

import { actions } from 'shared/store/actions';
import { RestClient } from 'shared/lib/rest';
import { CONSTANTS } from 'config';

function* searchJobs(query) {
  try {
    const response = yield RestClient.get(CONSTANTS.BASE.ENDPOINTS.JOBS, query.payload);

    yield put(actions.searchJobs.dispatchSuccess(response.data));
  } catch (errors) {
    yield put(actions.searchJobs.dispatchFail(errors));
  }
}

export function* SearchJobsEffect() {
  yield takeEvery(actions.searchJobs.getLoadingAction(), searchJobs);
}
