export default deflt => {
  if (typeof deflt !== 'string') return null;
  deflt = deflt.trim();
  switch (deflt) {
    case 'bool(True)':
      return true;
    case 'bool(False)':
      return false;
    case 'dict()':
      return {};
    case 'list()':
      return [];
    default: {
      const regex = /^([a-z][a-z0-9]*)\((.*)\)$/i;
      const matches = regex.exec(deflt);
      if (Array.isArray(matches)) {
        switch (matches[1]) {
          case 'int':
            return Number.parseInt(matches[2]); // eslint-disable-line
          case 'str': {
            // Python's escaping is very close to JavaScript's, so
            // we'll take a chance here.
            const json = `[${matches[2]}]`;
            try {
              return JSON.parse(json)[0];
            } catch (e) {
              // Sorry, no dice
              return null;
            }
          }

          default:
            return null;
        }
      } else if (/^[a-z][a-z0-9]*$/i.test(deflt)) {
        // Enumeration literal
        return deflt;
      }
    }
  }
  return null;
};
