import React from 'react';
import moment from 'moment';
import classnames from 'classnames';

import Tooltip from '@material-ui/core/Tooltip';
import { withStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiDns, mdiCubeOutline, mdiCubeScan, mdiMemory, mdiCalendarBlank } from '@mdi/js';
import Cores from '../../../../assets/images/server_core.svg';

import './ServerCard.local.scss';

export const ServerCard = ({ server, selected, toggleSelectedCallback, type }) => {
  if (type === 'virtual') {
    return (
      <div className="virtual-server-card">
        <Avatar className="server-avatar">
          <SvgIcon>
            <Icon path={mdiCubeOutline} />
          </SvgIcon>
        </Avatar>
        <div className="server-info">
          <span className="name">{server.vcenterservername}</span>
          <div className="details">
            <div className="feature">
              <span className="icon">
                <SvgIcon>
                  <Icon path={mdiCubeScan} />
                </SvgIcon>
              </span>
              <span className="value">{server.hosts_nr}</span>
              <span className="label">Hosts</span>
            </div>
            <div className="feature">
              <span className="icon">
                <SvgIcon>
                  <Icon path={mdiMemory} />
                </SvgIcon>
              </span>
              <span className="value">{server.cpu}</span>
              <span className="label">CPUs</span>
            </div>
            <div className="feature">
              <span className="icon">
                <img className="img-responsive" src={Cores} alt="server core icon" />
              </span>
              <span className="value">{server.cpucores}</span>
              <span className="label">Cores</span>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div
      className={classnames('server-card', { selected })}
      onClick={() => toggleSelectedCallback(server)}
      role="presentation"
    >
      <Avatar className="server-avatar">
        <SvgIcon>
          <Icon path={mdiDns} />
        </SvgIcon>
      </Avatar>
      <LightTooltip placement="bottom" title={server.name}>
        <span className="server-name">{server.name}</span>
      </LightTooltip>
      <LightTooltip
        placement="bottom"
        title={`Last updated: ${moment(server.collDate).format('MMM DD YYYY')}`}
      >
        <span className="calendar-icon">
          <SvgIcon>
            <Icon path={mdiCalendarBlank} />
          </SvgIcon>
        </span>
      </LightTooltip>
    </div>
  );
};

const LightTooltip = withStyles(() => ({
  tooltip: {
    color: '#fff',
    backgroundColor: '#787E89',
    letterSpacing: '0.4px',
    fontSize: '12px',
    lineHeight: '16px',
    fontFamily: 'Open Sans'
  }
}))(Tooltip);
