import React from 'react';
import { connect } from 'react-redux';
import { string, func, object } from 'prop-types';

import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';

import { changeStateThunk } from '../../store/thunks';

import './Breadcrumbs.local.scss';

export class BreadcrumbsTitle extends React.PureComponent {
  render() {
    const {
      storeState: { path }
    } = this.props;

    return this.renderBreadcrumbs(path);
  }

  renderBreadcrumbs = path => {
    const links = path
      .split(this.props.separator)
      .filter(e => e)
      .reduce(
        (routes, entry, index, list) => {
          const previous = list.slice(0, index);
          previous.push(entry);
          const routePath = previous.join(this.props.separator);

          const route = { label: entry, route: routePath };
          routes.push(route);

          return routes;
        },
        [{ label: 'DBMS', route: '' }]
      );

    return (
      <Breadcrumbs separator={<NavigateNextIcon fontSize="small" />} aria-label="breadcrumb">
        {links.map(link => {
          return (
            <div
              data-qa={`breadcrumbs-${link.label}`}
              className="path"
              role="presentation"
              key={link.route}
              onClick={() => this.navigateTo(link.route)}
            >
              {link.label}
            </div>
          );
        })}
      </Breadcrumbs>
    );
  };

  navigateTo = selectedLink => {
    if (selectedLink === this.props.storeState.path) {
      return;
    }

    this.props.changeState({ path: selectedLink });

    this.setActiveLevel(selectedLink);
  };

  setActiveLevel = selectedLink => {
    const linkElements = selectedLink.split(this.props.separator).filter(e => e);

    switch (linkElements.length) {
      case 0:
        this.props.changeState({ level: 'all-dbms' });
        break;
      case 1:
        this.props.changeState({ level: 'all-servers' });
        break;
      case 2:
        this.props.changeState({ level: 'all-databases' });
        break;
      default:
        break;
    }
  };
}

BreadcrumbsTitle.propTypes = {
  storeState: object,
  changeState: func,
  separator: string
};

BreadcrumbsTitle.defaultProps = {
  separator: '--'
};

export const ConnectedBreadcrumbsTitle = connect(
  state => ({
    storeState: state.explorer.state
  }),
  dispatch => ({
    changeState: newState => dispatch(changeStateThunk(newState))
  })
)(BreadcrumbsTitle);
