const STATES = {
  loading: 'LOADING',
  fail: 'FAIL',
  success: 'SUCCESS'
};

export const SEPARATOR = '__';

export function type(name, stateKey) {
  const state = STATES[stateKey];

  if (state) {
    return name + SEPARATOR + state;
  }

  return name;
}

/**
 *
 * These could be written as classes with static methods.
 *
 * */

export const types = {
  action: type,
  loading: function loadingType(name) {
    return type(name, 'loading');
  },
  success: function successType(name) {
    return type(name, 'success');
  },
  fail: function failType(name) {
    return type(name, 'fail');
  }
};

export const creators = {
  action: function action(name, payload) {
    return { type: name, payload };
  },
  loading: function loadingAction(name, payload) {
    return { type: type(name, 'loading'), payload };
  },
  success: function successAction(name, payload) {
    return { type: type(name, 'success'), payload };
  },
  fail: function failAction(name, payload) {
    return { type: type(name, 'fail'), payload };
  }
};
