import React from 'react';
import * as PropTypes from 'prop-types';

import Button from '@material-ui/core/Button';
import MenuList from '@material-ui/core/MenuList';
import MenuItem from '@material-ui/core/MenuItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import Popover from '@material-ui/core/Popover';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import classnames from 'classnames';

import './dropdown.local.scss';

export class DropdownComponent extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      isDropdownOpen: false
    };

    this.anchorRef = React.createRef();
  }

  componentDidMount() {
    if (this.props.isDropdownOpen) {
      this.setState({ isDropdownOpen: true });
    }
  }

  render() {
    const { items } = this.props;
    let parentWidth;

    if (this.anchorRef && this.anchorRef.current && this.anchorRef.current.offsetWidth) {
      parentWidth = this.anchorRef.current.offsetWidth;
    }
    return (
      <>
        {this.props.target(
          { isOpen: this.state.isDropdownOpen, ref: this.anchorRef },
          { toggle: this.handleToggle }
        )}
        {items && (
          <Popover
            open={this.state.isDropdownOpen}
            anchorEl={this.anchorRef.current}
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'left'
            }}
            transformOrigin={{
              vertical: 'top',
              horizontal: 'left'
            }}
          >
            <ClickAwayListener onClickAway={this.setCloseDropdown}>
              <MenuList className="dropdown-menu-container" style={{ width: parentWidth }}>
                {items.map((item, index) => (
                  <MenuItem
                    className={classnames('', { disabled: item.disabled })}
                    key={index}
                    onClick={() => this.onClickCallback(index)}
                  >
                    {item.icon && <ListItemIcon className="icon">{item.icon}</ListItemIcon>}
                    {item.name}
                  </MenuItem>
                ))}
              </MenuList>
            </ClickAwayListener>
          </Popover>
        )}
      </>
    );
  }

  handleToggle = () => {
    if (this.props.disabled) {
      return false;
    }
    this.setState(prevState => ({
      isDropdownOpen: !prevState.isDropdownOpen
    }));
  };

  setCloseDropdown = event => {
    if (this.anchorRef.current && this.anchorRef.current.contains(event.target)) {
      return;
    }

    this.setState({ isDropdownOpen: false });
  };

  onClickCallback = itemIndex => {
    this.props.onSelect(itemIndex, this.props.items[itemIndex]);
    this.setState({ isDropdownOpen: false });
  };
}

export default DropdownComponent;

DropdownComponent.propTypes = {
  onSelect: PropTypes.func,
  target: PropTypes.func,
  items: PropTypes.array,
  isDropdownOpen: PropTypes.bool
};

DropdownComponent.defaultProps = {
  onSelect: () => {},
  items: [],
  target: (meta, actions) => (
    <Button ref={meta.ref} onClick={actions.toggle}>
      {meta.isOpen ? 'Close' : 'Dropdown'}
    </Button>
  ),
  isDropdownOpen: false
};
